import {
  Model,
  Collection,
  NodeDefinition,
  NodeContext,
  GraphModel,
} from "@fluxscape/runtime";

interface Internal {
  name?: string;
  setWith?: string;
  value?: any;
  variablesModel: any; // Adjust type as necessary
  hasScheduledStore?: boolean;
}

const SetVariableNodeDefinition: NodeDefinition<Internal> = {
  name: "Set Variable",
  docs: "https://docs.noodl.net/nodes/data/variable/set-variable",
  category: "Data",
  usePortAsLabel: "name",
  color: "data",
  initialize() {
    const internal = this._internal;
    internal.variablesModel = Model.get("--ndl--global-variables");
  },
  getInspectInfo() {
    if (this._internal.name) {
      return this._internal.variablesModel.get(this._internal.name);
    }
    return "[No value set]";
  },
  outputs: {
    done: {
      type: "signal",
      displayName: "Done",
      group: "Events",
    },
  },
  inputs: {
    name: {
      type: {
        name: "string",
        identifierOf: "VariableName",
        identifierDisplayName: "Variable names",
      },
      displayName: "Name",
      group: "General",
      set(value: string) {
        this._internal.name = value;
      },
    },
    setWith: {
      type: {
        name: "enum",
        enums: [
          { label: "String", value: "string" },
          { label: "Boolean", value: "boolean" },
          { label: "Number", value: "number" },
          { label: "Empty string", value: "emptyString" },
          { label: "Date", value: "date" },
          { label: "Object", value: "object" },
          { label: "Array", value: "array" },
          { label: "Any", value: "*" },
        ],
        allowEditOnly: true,
      },
      displayName: "Set as",
      default: "*",
      group: "General",
      set(value: string) {
        this._internal.setWith = value;
      },
    },
    do: {
      displayName: "Do",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleStore();
      },
    },
  },
  methods: {
    setValue(value: any) {
      this._internal.value = value;
    },
    scheduleStore() {
      // @ts-expect-error
      if (this.hasScheduledStore) return;
      // @ts-expect-error
      this.hasScheduledStore = true;

      const internal = this._internal;
      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.hasScheduledStore = false;

        let value = internal.setWith === "emptyString" ? "" : internal.value;

        if (internal.setWith === "object" && typeof value === "string") {
          value = Model.get(value);
        }

        if (internal.setWith === "array" && typeof value === "string") {
          value = Collection.get(value);
        }

        if (internal.setWith === "boolean") value = !!value;

        internal.variablesModel.set(internal.name, value, {
          forceChange: true,
        });
        this.sendSignalOnOutput("done");
      });
    },
    registerInputIfNeeded(name: string) {
      if (this.hasInput(name)) {
        return;
      }

      if (name === "value") {
        this.registerInput(name, {
          // @ts-expect-error
          set: this.setValue.bind(this),
        });
      }
    },
  },
};

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  graphModel.on("nodeAdded.Set Variable", (node: any) => {
    const _updatePorts = () => {
      const ports: any[] = [];

      if (node.parameters.setWith !== "emptyString") {
        ports.push({
          type:
            node.parameters.setWith !== undefined
              ? node.parameters.setWith
              : "*",
          plug: "input",
          group: "General",
          name: "value",
          displayName: "Value",
        });
      }

      context.editorConnection?.sendDynamicPorts(node.id, ports);
    };

    _updatePorts();

    node.on("parameterUpdated", () => {
      _updatePorts();
    });
  });
}

export { SetVariableNodeDefinition as node };
