import { CloudStore } from "@fluxscape/runtime/src/api/cloudstore";
import { CloudFile } from "@fluxscape/runtime/src/api/cloudfile";

interface UploadOptions {
  onProgress?: (progress: number) => void;
}

const files = {
  async upload(file: File, options?: UploadOptions): Promise<CloudFile> {
    return new Promise((resolve, reject) => {
      CloudStore.instance.uploadFile({
        file,
        // @ts-expect-error process is not typed
        onUploadProgress: (p: number) => {
          options?.onProgress?.(p);
        },
        success: (response: any) => {
          resolve(new CloudFile(response));
        },
        error: (e: any) => {
          reject(e);
        },
      });
    });
  },
};

export default files;
