interface SetterArgs {
  valueChangedToTrue: () => void;
}

export function createSetter(args: SetterArgs): (value: any) => void {
  let currentValue: boolean = false;

  return function (value: any): void {
    value = value ? true : false;
    // value changed from false to true
    if (value && currentValue === false) {
      // @ts-expect-error
      args.valueChangedToTrue.call(this);
    }
    currentValue = value;
  };
}
