import { NodeDefinition, Collection } from "@fluxscape/runtime";

function CSVToArray(strData: string, strDelimiter: string = ","): string[][] {
  const objPattern = new RegExp(
    "(\\" +
      strDelimiter +
      "|\\r?\\n|\\r|^)" +
      '(?:"([^"]*(?:""[^"]*)*)"|' +
      '([^"\\' +
      strDelimiter +
      "\\r\\n]*))",
    "gi"
  );

  const arrData: string[][] = [[]];
  let arrMatches: RegExpExecArray | null;
  let prevLastIndex: number;

  while (
    (arrMatches = objPattern.exec(strData)) &&
    // @ts-expect-error
    prevLastIndex !== objPattern.lastIndex
  ) {
    prevLastIndex = objPattern.lastIndex;
    const strMatchedDelimiter = arrMatches[1];

    if (strMatchedDelimiter.length && strMatchedDelimiter !== strDelimiter) {
      arrData.push([]);
    }

    let strMatchedValue: string;

    if (arrMatches[2]) {
      strMatchedValue = arrMatches[2].replace(new RegExp('""', "g"), '"');
    } else {
      strMatchedValue = arrMatches[3];
    }

    arrData[arrData.length - 1].push(strMatchedValue);
  }

  return arrData;
}

interface Internal {
  collection: Collection<any>;
  type?: string;
  csv?: string;
  json?: string;
  hasScheduledParseData?: boolean;
}

const CSVNode: NodeDefinition<Internal> = {
  name: "Static Data",
  docs: "https://docs.noodl.net/nodes/data/array/static-array",
  displayNodeName: "Static Array",
  shortDesc: "Store static data to populate a Collection with items.",
  category: "Data",
  color: "data",
  nodeDoubleClickAction: [{ focusPort: "JSON" }, { focusPort: "CSV" }],
  getInspectInfo() {
    if (this._internal.collection) {
      return [{ type: "value", value: this._internal.collection.items }];
    }
  },
  dynamicports: [
    {
      name: "conditionalports/extended",
      condition: "type = csv OR type NOT SET",
      inputs: ["csv"],
    },
    {
      name: "conditionalports/extended",
      condition: "type = json",
      inputs: ["json"],
    },
  ],
  inputs: {
    type: {
      type: {
        name: "enum",
        enums: [
          { label: "CSV", value: "csv" },
          { label: "JSON", value: "json" },
        ],
        allowEditOnly: true,
      },
      displayName: "Type",
      group: "General",
      default: "csv",
      set(value: string) {
        this._internal.type = value;
      },
    },
    csv: {
      type: { name: "string", codeeditor: "text", allowEditOnly: true },
      displayName: "CSV",
      group: "General",
      set(value: string) {
        this._internal.csv = value;
        // @ts-expect-error
        this.scheduleParseData();
      },
    },
    json: {
      type: { name: "string", codeeditor: "json", allowEditOnly: true },
      displayName: "JSON",
      group: "General",
      set(value: string) {
        this._internal.json = value;
        // @ts-expect-error
        this.scheduleParseData();
      },
    },
  },
  outputs: {
    items: {
      type: "array",
      displayName: "Items",
      group: "General",
      getter() {
        return this._internal.collection;
      },
    },
    count: {
      type: "number",
      displayName: "Count",
      group: "General",
      get() {
        return this._internal.collection ? this._internal.collection.size() : 0;
      },
    },
  },
  methods: {
    scheduleParseData() {
      const internal = this._internal;
      if (!internal.hasScheduledParseData) {
        internal.hasScheduledParseData = true;
        // @ts-expect-error
        this.scheduleAfterInputsHaveUpdated(this.parseData.bind(this));
      }
    },
    parseData() {
      const internal = this._internal;
      internal.hasScheduledParseData = false;
      internal.collection = Collection.get();

      if (internal.type === undefined || internal.type === "csv") {
        // @ts-expect-error
        const data = CSVToArray(internal.csv);
        const json: any[] = [];
        const fields = data[0];
        for (let i = 1; i < data.length; i++) {
          const row = data[i];
          const obj: any = {};
          for (let j = 0; j < fields.length; j++) {
            obj[fields[j]] = row[j];
          }
          json.push(obj);
        }

        internal.collection.set(json);
        this.flagOutputDirty("items");
        this.flagOutputDirty("count");
      } else if (internal.type === "json") {
        if (this.context.editorConnection) {
          this.context.editorConnection.clearWarning(
            // @ts-expect-error
            this.nodeScope.componentOwner.name,
            this.id,
            "json-parse-warning"
          );
        }

        try {
          // @ts-expect-error
          const json = JSON.parse(internal.json);
          internal.collection.set(json);
          this.flagOutputDirty("items");
          this.flagOutputDirty("count");
        } catch (e) {
          if (this.context.editorConnection) {
            this.context.editorConnection.sendWarning(
              // @ts-expect-error
              this.nodeScope.componentOwner.name,
              this.id,
              "json-parse-warning",
              {
                showGlobally: true,
                // @ts-expect-error
                message: e.message,
              }
            );
          }
        }
      }
    },
  },
};

export const node = CSVNode;
export function setup() {
  // Setup logic if needed
}
