import UserService from "./userservice";
import { GraphModel, NodeContext, NodeDefinition } from "@fluxscape/runtime";

interface Internal {
  userProperties: Record<string, any>;
  username: string;
  password: string;
  email: string;
  error: string;
  signUpScheduled: boolean;
}

export const node: NodeDefinition<Internal> = {
  name: "net.noodl.user.SignUp",
  docs: "https://docs.noodl.net/nodes/data/user/sign-up",
  displayNodeName: "Sign Up",
  category: "Cloud Services",
  color: "data",
  initialize() {
    const internal = this._internal;
    internal.userProperties = {};
    internal.signUpScheduled = false;
  },
  outputs: {
    success: {
      type: "signal",
      displayName: "Success",
      group: "Events",
    },
    failure: {
      type: "signal",
      displayName: "Failure",
      group: "Events",
    },
    error: {
      type: "string",
      displayName: "Error",
      group: "Error",
      getter() {
        return this._internal.error;
      },
    },
  },
  inputs: {
    signup: {
      displayName: "Do",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleSignUp();
      },
    },
    username: {
      displayName: "Username",
      type: "string",
      group: "General",
      set(value: string) {
        this._internal.username = value;
      },
    },
    password: {
      displayName: "Password",
      type: "string",
      group: "General",
      set(value: string) {
        this._internal.password = value;
      },
    },
    email: {
      displayName: "Email",
      type: "string",
      group: "General",
      set(value: string) {
        this._internal.email = value;
      },
    },
  },
  methods: {
    setError(err: string) {
      this._internal.error = err;
      this.flagOutputDirty("error");
      this.sendSignalOnOutput("failure");

      if (this.context.editorConnection) {
        this.context.editorConnection.sendWarning(
          // @ts-expect-error
          this.nodeScope.componentOwner.name,
          this.id,
          "user-login-warning",
          {
            message: err,
            showGlobally: true,
          }
        );
      }
    },
    clearWarnings() {
      if (this.context.editorConnection) {
        this.context.editorConnection.clearWarning(
          // @ts-expect-error
          this.nodeScope.componentOwner.name,
          this.id,
          "user-login-warning"
        );
      }
    },
    scheduleSignUp() {
      const internal = this._internal;

      // @ts-expect-error
      if (this.signUpScheduled === true) return;
      // @ts-expect-error
      this.signUpScheduled = true;

      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.signUpScheduled = false;

        UserService.instance.signUp({
          username: this._internal.username,
          password: this._internal.password,
          email: this._internal.email,
          properties: internal.userProperties,
          success: () => {
            this.sendSignalOnOutput("success");
          },
          error: (e: string) => {
            // @ts-expect-error
            this.setError(e);
          },
        });
      });
    },
    setUserProperty(name: string, value: any) {
      this._internal.userProperties[name] = value;
    },
    registerInputIfNeeded(name: string) {
      if (this.hasInput(name)) {
        return;
      }

      if (name.startsWith("prop-")) {
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setUserProperty.bind(this, name.substring("prop-".length)),
        });
      }
    },
  },
};

function updatePorts(
  nodeId: string,
  _parameters: any,
  editorConnection: any,
  systemCollections: any
) {
  const ports: any[] = [];

  if (systemCollections) {
    // Fetch ports from collection keys
    const c = systemCollections.find((c: any) => c.name === "_User");
    if (c && c.schema && c.schema.properties) {
      const props = c.schema.properties;
      const _ignoreKeys = [
        "authData",
        "password",
        "username",
        "createdAt",
        "updatedAt",
        "emailVerified",
        "email",
      ];
      for (const key in props) {
        if (_ignoreKeys.indexOf(key) !== -1) continue;

        const p = props[key];
        if (ports.find((_p) => _p.name === key)) continue;

        if (p.type === "Relation") {
          // Handle Relation type if needed
        } else {
          // Other schema type ports
          const _typeMap: Record<string, string> = {
            String: "string",
            Boolean: "boolean",
            Number: "number",
            Date: "date",
          };

          ports.push({
            type: {
              name: _typeMap[p.type] ? _typeMap[p.type] : "*",
            },
            plug: "input",
            group: "Properties",
            name: "prop-" + key,
            displayName: key,
          });
        }
      }
    }
  }

  editorConnection.sendDynamicPorts(nodeId, ports);
}

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  function _managePortsForNode(node: any) {
    updatePorts(
      node.id,
      node.parameters,
      context.editorConnection,
      graphModel.getMetaData("systemCollections")
    );

    node.on("parameterUpdated", function (_event: any) {
      updatePorts(
        node.id,
        node.parameters,
        context.editorConnection,
        graphModel.getMetaData("systemCollections")
      );
    });

    graphModel.on("metadataChanged.systemCollections", function (data: any) {
      updatePorts(node.id, node.parameters, context.editorConnection, data);
    });
  }

  graphModel.on("editorImportComplete", () => {
    graphModel.on("nodeAdded.net.noodl.user.SignUp", function (node: any) {
      _managePortsForNode(node);
    });

    for (const node of graphModel.getNodesWithType("net.noodl.user.SignUp")) {
      _managePortsForNode(node);
    }
  });
}
