import difference from "lodash.difference";
import { NodeDefinition } from "../../nodedefinition";
import type { NodeContext } from "../../nodecontext";
import type { Node } from "../../node";
import type { EditorConnection } from "../../editorconnection";
import type { GraphModel } from "../../models/graphmodel";
import type { Event } from "../../editormodeleventshandler";
import type { NodeModel } from "../../models/nodemodel";

interface Internal {
  scope: Record<string, any>;
  hasScheduledEvaluation: boolean;
  code?: string;
  cachedValue: number;
  currentExpression: string;
  compiledFunction?: Function;
  inputNames: string[];
  inputValues: any[];
}

export const node: NodeDefinition<Internal> = {
  name: "Expression",
  docs: "https://docs.noodl.net/nodes/math/expression",
  usePortAsLabel: "expression",
  category: "CustomCode",
  color: "javascript",
  nodeDoubleClickAction: {
    focusPort: "Expression",
  },
  searchTags: ["javascript"],
  initialize() {
    const internal = this._internal;

    internal.scope = {};
    internal.hasScheduledEvaluation = false;

    internal.code = undefined;
    internal.cachedValue = 0;
    internal.currentExpression = "";
    internal.compiledFunction = undefined;
    internal.inputNames = [];
    internal.inputValues = [];
  },
  getInspectInfo() {
    return this._internal.cachedValue;
  },
  inputs: {
    expression: {
      group: "General",
      inputPriority: 1,
      type: {
        name: "string",
        allowEditOnly: true,
        codeeditor: "javascript",
      },
      displayName: "Expression",
      set(value: string) {
        const internal = this._internal;
        internal.currentExpression =
          functionPreamble + "return (" + value + ");";
        internal.compiledFunction = undefined;

        const newInputs = parsePorts(value);

        const inputsToAdd = difference(newInputs, internal.inputNames);
        const inputsToRemove = difference(internal.inputNames, newInputs);

        const self = this;
        inputsToRemove.forEach(function (name: string) {
          self.deregisterInput(name);
          delete internal.scope[name];
        });

        inputsToAdd.forEach(function (name: string) {
          if (self.hasInput(name)) {
            return;
          }

          self.registerInput(name, {
            set(value: any) {
              internal.scope[name] = value;
              if (!this.isInputConnected("run")) {
                // @ts-expect-error
                this._scheduleEvaluateExpression();
              }
            },
          });

          internal.scope[name] = 0;
          (self as any)._inputValues[name] = 0;
        });

        internal.inputNames = Object.keys(internal.scope);
        if (!this.isInputConnected("run"))
          (this as any)._scheduleEvaluateExpression();
      },
    },
    run: {
      group: "Actions",
      displayName: "Run",
      type: "signal",
      valueChangedToTrue() {
        (this as any)._scheduleEvaluateExpression();
      },
    },
  },
  outputs: {
    result: {
      group: "Result",
      type: "*",
      displayName: "Result",
      getter() {
        if (!this._internal.currentExpression) {
          return 0;
        }

        return this._internal.cachedValue;
      },
    },
    isTrue: {
      group: "Result",
      type: "boolean",
      displayName: "Is True",
      getter() {
        if (!this._internal.currentExpression) {
          return false;
        }

        return !!this._internal.cachedValue;
      },
    },
    isFalse: {
      group: "Result",
      type: "boolean",
      displayName: "Is False",
      getter() {
        if (!this._internal.currentExpression) {
          return true;
        }

        return !this._internal.cachedValue;
      },
    },
    isTrueEv: {
      group: "Events",
      type: "signal",
      displayName: "On True",
    },
    isFalseEv: {
      group: "Events",
      type: "signal",
      displayName: "On False",
    },
  },
  prototypeExtensions: {
    registerInputIfNeeded(name: string) {
      if (this.hasInput(name)) {
        return;
      }

      this._internal.scope[name] = 0;
      (this as any)._inputValues[name] = 0;

      this.registerInput(name, {
        set(value: any) {
          this._internal.scope[name] = value;
          if (!this.isInputConnected("run"))
            (this as any)._scheduleEvaluateExpression();
        },
      });
    },
    _scheduleEvaluateExpression() {
      const internal = this._internal;
      if (internal.hasScheduledEvaluation !== false) {
        return;
      }

      internal.hasScheduledEvaluation = true;

      this.flagDirty();
      this.scheduleAfterInputsHaveUpdated(function () {
        const lastValue = internal.cachedValue;
        internal.cachedValue = (this as any)._calculateExpression();
        if (lastValue !== internal.cachedValue) {
          this.flagOutputDirty("result");
          this.flagOutputDirty("isTrue");
          this.flagOutputDirty("isFalse");
        }
        if (internal.cachedValue) this.sendSignalOnOutput("isTrueEv");
        else this.sendSignalOnOutput("isFalseEv");
        internal.hasScheduledEvaluation = false;
      });
    },
    _calculateExpression() {
      const internal = this._internal;

      if (!internal.compiledFunction) {
        internal.compiledFunction = (this as any)._compileFunction();
      }
      for (let i = 0; i < internal.inputNames.length; ++i) {
        const inputValue = internal.scope[internal.inputNames[i]];
        internal.inputValues[i] = inputValue;
      }
      try {
        if (!internal.compiledFunction) {
          throw new Error("Compiled function is undefined");
        }
        return internal.compiledFunction.apply(null, internal.inputValues);
      } catch (e) {
        if (e instanceof Error) {
          console.error("Error in expression:", e.message);
        }
      }
      return undefined;
    },
    // @ts-expect-error
    _compileFunction() {
      const expression = this._internal.currentExpression;
      const args = Object.keys(this._internal.scope);

      const key = expression + args.join(" ");

      if (!compiledFunctionsCache[key]) {
        args.push(expression);

        try {
          compiledFunctionsCache[key] = construct(Function, args);
        } catch (e) {
          if (e instanceof Error) {
            console.error("Failed to compile JS function", e.message);
          }
        }
      }
      return compiledFunctionsCache[key];
    },
  },
};

const functionPreamble = [
  "var min = Math.min," +
    "    max = Math.max," +
    "    cos = Math.cos," +
    "    sin = Math.sin," +
    "    tan = Math.tan," +
    "    sqrt = Math.sqrt," +
    "    pi = Math.PI," +
    "    round = Math.round," +
    "    floor = Math.floor," +
    "    ceil = Math.ceil," +
    "    abs = Math.abs," +
    "    random = Math.random;",
].join("");

function construct(
  constructor: Function,
  args: any[]
): (this: Node<Internal>, ...args: any[]) => void {
  function F(this: any) {
    return constructor.apply(this, args);
  }
  F.prototype = constructor.prototype;
  // @ts-expect-error
  return new F();
}

const compiledFunctionsCache: Record<
  string,
  (this: Node<Internal>, ...args: any[]) => void
> = {};

const portsToIgnore = [
  "min",
  "max",
  "cos",
  "sin",
  "tan",
  "sqrt",
  "pi",
  "round",
  "floor",
  "ceil",
  "abs",
  "random",
  "Math",
  "window",
  "document",
  "undefined",
  "Vars",
  "true",
  "false",
  "null",
  "Boolean",
];

function parsePorts(expression: string): string[] {
  const ports: string[] = [];

  function addPort(name: string): void {
    if (portsToIgnore.indexOf(name) !== -1) return;
    if (ports.some((p) => p === name)) return;

    ports.push(name);
  }

  // First remove all strings
  expression = expression
    .replace(/\"([^\"]*)\"/g, "")
    .replace(/\'([^\']*)\'/g, "");

  // Extract identifiers
  const identifiers = expression.matchAll(/[a-zA-Z\_\$][a-zA-Z0-9\.\_\$]*/g);
  for (const _id of identifiers) {
    let name = _id[0];
    if (name.indexOf(".") !== -1) {
      name = name.split(".")[0]; // Take first symbol on "." sequence
    }

    addPort(name);
  }

  return ports;
}

function updatePorts(
  nodeId: string,
  expression: string,
  editorConnection: EditorConnection
): void {
  const portNames = parsePorts(expression);

  const ports = portNames.map((name) => ({
    group: "Parameters",
    name,
    type: {
      name: "*",
      editAsType: "string",
    },
    plug: "input",
  }));

  editorConnection.sendDynamicPorts(nodeId, ports);
}

function evalCompileWarnings(
  editorConnection: EditorConnection,
  node: NodeModel
): void {
  try {
    new Function(node.parameters.expression);
    editorConnection.clearWarning(
      node.component!.name,
      node.id,
      "expression-compile-error"
    );
  } catch (e) {
    if (e instanceof Error) {
      editorConnection.sendWarning(
        node.component!.name,
        node.id,
        "expression-compile-error",
        {
          message: e.message,
        }
      );
    }
  }
}

export function setup(
  context: { editorConnection?: EditorConnection },
  graphModel: GraphModel
): void {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  graphModel.on("nodeAdded.Expression", (node) => {
    if (node.parameters.expression) {
      updatePorts(
        node.id,
        node.parameters.expression,
        context.editorConnection!
      );
      evalCompileWarnings(context.editorConnection!, node);
    }
    node.on("parameterUpdated", (event: Event) => {
      if (event.name === "expression") {
        updatePorts(
          node.id,
          node.parameters.expression,
          context.editorConnection!
        );
        evalCompileWarnings(context.editorConnection!, node);
      }
    });
  });
}
