import { EventSender } from "./eventsender";
import { ActiveWarnings } from "./editorconnection.activewarnings";
import { guid } from "./utils";

export interface Platform {
  webSocketClass?: typeof WebSocket;
  webSocketOptions?: any;
  getCurrentTime: () => number;
  isRunningLocally?: () => boolean;
  requestUpdate?: (callback: () => void) => void;
  objectToString?: (obj: any) => string;
}

interface EditorConnectionOptions {
  runtimeType?: string;
  platform?: Platform;
}

interface ServiceRequest {
  token: string;
  clientId: string;
  [key: string]: any;
}

export interface ConnectionPulse {
  timestamp: number;
  connections: string[];
}

export class EditorConnection extends EventSender {
  private runtimeType: string | undefined;
  private platform: Platform;
  private ws: typeof WebSocket;
  private wsOptions: any;
  private reconnectOnClose: boolean;
  private enableDebugger: boolean;
  private lastSendTimestamp: number;
  private sendQueue: any[];
  private sendTimer: ReturnType<typeof setTimeout> | undefined;
  private activeWarnings: ActiveWarnings;
  private socket?: WebSocket;

  clientId: string = "";
  debugInspectorsEnabled?: boolean;

  constructor(opts: EditorConnectionOptions = {}) {
    super();

    this.runtimeType = opts.runtimeType;
    this.platform = opts.platform || { getCurrentTime: () => Date.now() };
    // @ts-expect-error
    this.ws =
      opts.platform?.webSocketClass ||
      (typeof WebSocket !== "undefined" ? WebSocket : undefined);
    this.wsOptions = opts.platform?.webSocketOptions;
    this.reconnectOnClose = true;
    this.enableDebugger = false;

    this.lastSendTimestamp = 0;
    this.sendQueue = [];
    this.sendTimer = undefined;

    //used to optimize warnings so we're not sending unneccessary warnings.
    //Can slow down the editor in large projects
    this.activeWarnings = new ActiveWarnings();
  }

  isRunningLocally(): boolean {
    const runningLocallyInBrowser =
      (this.platform.isRunningLocally && this.platform.isRunningLocally()) ||
      (typeof document !== "undefined" &&
        (document.location.hostname === "localhost" ||
          document.location.hostname === "127.0.0.1"));
    return runningLocallyInBrowser;
  }

  connect(address: string): void {
    this.socket = this.wsOptions
      ? new this.ws(address, this.wsOptions)
      : new this.ws(address);

    this.socket.addEventListener("open", () => {
      this.clientId = guid();
      this.socket?.send(
        JSON.stringify({
          cmd: "register",
          type: "viewer",
          clientId: this.clientId,
        })
      );
      this.emit("connected");
    });

    this.socket.addEventListener("close", (event: CloseEvent) => {
      if (this.reconnectOnClose) {
        this.reconnect(address);
      }
      console.log("Editor connection closed", event.code, event.reason);
      this.emit("connectionClosed");
    });

    this.socket.addEventListener("error", () => {
      console.log("Editor connection error, trying to reconnect");
    });

    this.socket.addEventListener("message", async (e: MessageEvent) => {
      // NOTE: When the data is too big it seems to change from string to a blob
      const text = typeof e.data === "string" ? e.data : await e.data.text();
      const message = JSON.parse(text);

      let content;

      if (message.cmd === "registered") {
        //ignore
      } else if (message.cmd === "export") {
        content = JSON.parse(message.content);
        if (message.type === "full" && message.target === this.clientId) {
          this.emit("exportDataFull", content);
        }
      } else if (message.cmd === "hoverStart") {
        this.emit("hoverStart", message.content.id);
      } else if (message.cmd === "hoverEnd") {
        this.emit("hoverEnd", message.content.id);
      } else if (message.cmd === "refresh") {
        this.emit("reload");
      } else if (message.cmd === "debugInspectors") {
        if (this.debugInspectorsEnabled) {
          content = JSON.parse(message.content);
          this.emit("debugInspectorsUpdated", content.inspectors);
        }
      } else if (message.cmd === "debuggingEnabled") {
        if (this.isRunningLocally()) {
          content = JSON.parse(message.content);
          this.emit("debuggingEnabledChanged", content.enabled);
        }
      } else if (message.cmd === "getConnectionValue") {
        if (this.isRunningLocally()) {
          content = JSON.parse(message.content);
          await this.emit("getConnectionValue", {
            clientId: content.clientId,
            connectionId: content.connectionId,
          });
        }
      } else if (message.cmd === "modelUpdate") {
        await this.emit("modelUpdate", message.content);
      } else if (message.cmd === "publish") {
        // TODO: What is this?
        // Services.pubsub.routeMessage(message); // Publish a message from the pubsub service
      } else if (message.cmd === "noodlModules") {
        this.emit("noodlModules", JSON.parse(message.content));
      } else if (message.cmd === "mqttUpdate") {
        this.emit("mqttUpdate", message.content);
      } else if (message.cmd === "activeComponentChanged") {
        this.emit("activeComponentChanged", message.component);
      } else {
        console.log("Command not implemented", message);
      }
    });
  }

  reconnect(address: string): void {
    setTimeout(() => {
      this.connect(address);
    }, 2000);
  }

  isConnected(): boolean {
    return this.socket !== undefined && this.socket.readyState === this.ws.OPEN;
  }

  //JSON replacer to make cyclic objects non-cyclic.
  //Using this example: https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Errors/Cyclic_object_value#examples
  private getCircularReplacer() {
    const seen = new WeakSet();
    return (key: string, value: any) => {
      if (typeof value === "object" && value !== null) {
        if (seen.has(value)) {
          return "[Circular]";
        }
        seen.add(value);
      }
      return value;
    };
  }

  send(data: any): void {
    const now = this.platform.getCurrentTime();
    const dt = now - this.lastSendTimestamp;

    //Send objects as json and capture exceptions
    const trySend = (msg: any) => {
      try {
        this.socket?.send(JSON.stringify(msg));
      } catch (e: any) {
        if (e.message && e.message.startsWith("Converting circular")) {
          //the object is circular, try to address it
          try {
            this.socket?.send(JSON.stringify(msg, this.getCircularReplacer()));
          } catch (e) {
            //still failed, give up
            console.log("failed to send message to editor", msg, e);
          }
        } else {
          //message couldn't be serialized to json
          console.log("failed to send message to editor", msg, e);
        }
      }
    };

    //batch messages so they're only sent at most every 200ms
    //note that the first message will always be sent immediately, and the ones for 200ms after
    //that one will be queued. So initial message response time is as low as possible (for hover etc)
    if (dt < 200 || this.sendTimer || !this.isConnected()) {
      this.sendQueue.push(data);
      if (!this.sendTimer) {
        this.sendTimer = setTimeout(() => {
          if (this.isConnected() === false) {
            return;
          }

          //send messages in chunks. If we send too many at once the editor UI can freeze for a while
          //since it's handling these in the renderer process
          const chunkSize = 50;
          for (let i = 0; i < this.sendQueue.length; i += chunkSize) {
            const chunk = this.sendQueue.slice(i, i + chunkSize);
            trySend(chunk);
          }

          this.sendQueue = [];
          this.sendTimer = undefined;
          this.lastSendTimestamp = this.platform.getCurrentTime();
        }, 100);
      }
    } else {
      this.lastSendTimestamp = now;
      trySend(data);
    }
  }

  sendInspectId(id: string): void {
    this.send({
      cmd: "select",
      type: "viewer",
      content: JSON.stringify({ id: id }),
    });
  }

  sendSelectComponent(componentName: string): void {
    this.send({
      cmd: "select",
      type: "viewer",
      content: JSON.stringify({ componentName }),
    });
  }

  sendPulsingConnections(connectionMap: Record<string, ConnectionPulse>): void {
    const connectionsToPulse: any[] = [];
    Object.keys(connectionMap).forEach((c) => {
      const connection = connectionMap[c];
      connectionsToPulse.push(...connection.connections);
    });

    this.send({
      cmd: "connectiondebugpulse",
      type: "viewer",
      content: JSON.stringify({
        connectionsToPulse: connectionsToPulse,
      }),
    });
  }

  sendDebugInspectorValues(inspectors: any): void {
    this.send({
      cmd: "debuginspectorvalues",
      type: "viewer",
      content: { inspectors },
    });
  }

  sendConnectionValue(connectionId: string, value: any): void {
    this.send({
      cmd: "connectionValue",
      type: "viewer",
      content: { connectionId, value },
    });
  }

  private dynamicPortsHash: Record<string, string> = {};

  private _detectRename(
    before: string[],
    after: string[]
  ): { before: string; after: string } | undefined {
    if (!before || !after) return;

    if (before.length !== after.length) return; // Must be of same length

    const res: { before?: string; after?: string } = {};
    for (let i = 0; i < before.length; i++) {
      if (after.indexOf(before[i]) === -1) {
        if (res.before) return; // Can only be one from before that is missing
        res.before = before[i];
      }

      if (before.indexOf(after[i]) === -1) {
        if (res.after) return; // Only one can be missing,otherwise we cannot match
        res.after = after[i];
      }
    }

    return res.before && res.after
      ? { before: res.before, after: res.after }
      : undefined;
  }

  sendDynamicPorts(id: string, ports: any[], options?: any): void {
    const hash = JSON.stringify(ports);
    if (this.dynamicPortsHash[id] === hash) {
      // Make sure we don't resend the same port data
      return;
    }

    if (
      this.dynamicPortsHash[id] &&
      ports &&
      options &&
      options.detectRenamed
    ) {
      const detectRenamed = Array.isArray(options.detectRenamed)
        ? options.detectRenamed
        : [options.detectRenamed];

      const renamed: any[] = [];
      detectRenamed.forEach((d: any) => {
        let before = JSON.parse(this.dynamicPortsHash[id]);
        let after = [...ports];

        // Filter ports with correct prefix and plug
        if (d.prefix) {
          before = before.filter((p: any) => p.name.startsWith(d.prefix));
          after = after.filter((p: any) => p.name.startsWith(d.prefix));
        }

        if (d.plug) {
          before = before.filter((p: any) => p.plug === d.plug);
          after = after.filter((p: any) => p.plug === d.plug);
        }

        // Remove the prefix
        after = after.map((p: any) =>
          p.name.substring((d.prefix || "").length)
        );
        before = before.map((p: any) =>
          p.name.substring((d.prefix || "").length)
        );

        // Find the one that is renamed (if any)
        const res = this._detectRename(before, after);
        if (res) {
          renamed.push({
            plug: d.plug,
            patterns: [(d.prefix || "") + "{{*}}"],
            before: res.before,
            after: res.after,
          });
        }
      });
      if (renamed.length > 0) options.renamed = renamed;

      delete options.detectRenamed;
    }

    this.dynamicPortsHash[id] = hash;

    this.send({
      cmd: "instanceports",
      type: "viewer",
      content: JSON.stringify({
        nodeid: id,
        ports: ports,
        options: options,
      }),
    });
  }

  sendWarning(
    componentName: string,
    nodeId: string,
    key: string,
    warning: any
  ): void {
    const isNewWarning = this.activeWarnings.setWarning(nodeId, key, warning);

    if (isNewWarning) {
      this.send({
        cmd: "showwarning",
        type: "viewer",
        content: JSON.stringify({
          componentName: componentName,
          nodeId: nodeId,
          key: key,
          warning: warning,
        }),
      });
    }
  }

  clearWarning(componentName: string, nodeId: string, key: string): void {
    const hasWarning = this.activeWarnings.clearWarning(nodeId, key);

    if (hasWarning) {
      this.send({
        cmd: "showwarning",
        type: "viewer",
        content: JSON.stringify({
          componentName: componentName,
          nodeId: nodeId,
          key: key,
          warning: undefined,
        }),
      });
    }
  }

  clearWarnings(componentName: string, nodeId: string): void {
    const hasWarnings = this.activeWarnings.clearWarnings(nodeId);

    if (hasWarnings) {
      this.send({
        cmd: "clearwarnings",
        type: "viewer",
        content: JSON.stringify({
          componentName: componentName,
          nodeId: nodeId,
        }),
      });
    }
  }

  sendPatches(patches: any): void {
    this.send({
      cmd: "patchproject",
      type: "viewer",
      content: JSON.stringify(patches),
    });
  }

  requestFullExport(): void {
    this.send({
      cmd: "register",
      type: "viewer",
    });
  }

  requestNoodlModules(): void {
    this.send({
      cmd: "getNoodlModules",
      type: "viewer",
    });
  }

  private serviceRequests: Record<string, (response: any) => void> = {};

  sendServiceRequest(
    request: ServiceRequest,
    callback: (response: any) => void
  ): void {
    request.token = guid();
    request.clientId = this.clientId;
    this.serviceRequests[request.token] = callback;
    this.send(request);
  }

  close(): void {
    this.reconnectOnClose = false;

    if (this.isConnected() === false) {
      return;
    }

    this.socket?.close();
  }

  sendNodeLibrary(nodelibrary: any): void {
    this.send({
      cmd: "nodelibrary",
      type: "viewer",
      runtimeType: this.runtimeType,
      content: nodelibrary,
      clientId: this.clientId,
    });
  }

  sendComponentMetadata(componentName: string, key: string, data: any): void {
    this.send({
      cmd: "componentMetadata",
      type: "viewer",
      content: JSON.stringify({
        componentName,
        key,
        data,
      }),
    });
  }

  sendProjectMetadata(key: string, data: any): void {
    this.send({
      cmd: "projectMetadata",
      type: "viewer",
      content: JSON.stringify({
        key,
        data,
      }),
    });
  }
}
