import { NodeDefinition } from "../../nodedefinition";

interface Internal {
  format: string;
  cachedResult: string;
  resultDirty: boolean;
  inputValues: Record<string, any>;
}

export const node: NodeDefinition<Internal> = {
  name: "String Format",
  docs: "https://docs.noodl.net/nodes/string-manipulation/string-format",
  category: "String Manipulation",
  usePortAsLabel: "format",
  portLabelTruncationMode: "length" as const,
  initialize() {
    const internal = this._internal;
    internal.format = "";
    internal.cachedResult = "";
    internal.resultDirty = false;
    internal.inputValues = {};
  },
  getInspectInfo() {
    // @ts-expect-error
    return this.formatValue();
  },
  inputs: {
    format: {
      type: { name: "string", multiline: true },
      displayName: "Format",
      set(value: string): void {
        if (this._internal.format === value) return;

        this._internal.format = value;
        this._internal.resultDirty = true;
        // @ts-expect-error
        this.scheduleFormat();
      },
    },
  },
  outputs: {
    formatted: {
      type: "string",
      displayName: "Formatted",
      get() {
        // @ts-expect-error
        return this.formatValue();
      },
    },
  },
  methods: {
    formatValue(): string {
      const internal = this._internal;

      if (internal.resultDirty) {
        let formatted = internal.format;

        const matches = internal.format.match(/\{[A-Za-z0-9_]*\}/g);
        let inputs: string[] = [];
        if (matches) {
          inputs = matches.map((name) => {
            return name.replace("{", "").replace("}", "");
          });
        }

        inputs.forEach((name) => {
          const v = internal.inputValues[name];
          formatted = formatted.replace(
            "{" + name + "}",
            v !== undefined ? v : ""
          );
        });

        internal.cachedResult = formatted;
        internal.resultDirty = false;
      }

      return internal.cachedResult;
    },
    registerInputIfNeeded(name: string): void {
      if (this.hasInput(name)) {
        return;
      }

      this.registerInput(name, {
        set(value) {
          if (this._internal.inputValues[name] === value) return;

          this._internal.inputValues[name] = value;
          this._internal.resultDirty = true;
          // @ts-expect-error
          this.scheduleFormat();
        },
      });
    },
    scheduleFormat(): void {
      // @ts-expect-error
      if (this.formatScheduled) return;

      // @ts-expect-error
      this.formatScheduled = true;
      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.formatValue();
        this.flagOutputDirty("formatted");
        // @ts-expect-error
        this.formatScheduled = false;
      });
    },
  },
};

interface EditorConnection {
  isRunningLocally: () => boolean;
  sendDynamicPorts: (id: string, ports: Port[]) => void;
}

interface Port {
  name: string;
  type: string;
  plug: string;
}

interface GraphNode {
  id: string;
  parameters: {
    format?: string;
  };
  on: (event: string, callback: (event: any) => void) => void;
}

interface GraphModel {
  on: (event: string, callback: (node: GraphNode) => void) => void;
}

interface Context {
  editorConnection?: EditorConnection;
}

function updatePorts(
  id: string,
  format: string,
  editorConnection: EditorConnection
): void {
  const inputs = format.match(/\{[A-Za-z0-9_]*\}/g) || [];
  const portsNames = inputs.map((def) => {
    return def.replace("{", "").replace("}", "");
  });

  const ports = portsNames
    //get unique names
    .filter((value, index, self) => {
      return self.indexOf(value) === index;
    })
    //and map names to ports
    .map((name) => {
      return {
        name: name,
        type: "string",
        plug: "input",
      };
    });

  editorConnection.sendDynamicPorts(id, ports);
}

export function setup(context: Context, graphModel: GraphModel): void {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  graphModel.on("nodeAdded.String Format", (node: GraphNode) => {
    if (node.parameters.format) {
      updatePorts(node.id, node.parameters.format, context.editorConnection!);
    }
    node.on("parameterUpdated", (event: { name: string }) => {
      if (event.name === "format") {
        updatePorts(
          node.id,
          node.parameters.format!,
          context.editorConnection!
        );
      }
    });
  });
}
