import NoodlRuntime, { guid, ModelScope } from "@fluxscape/runtime";
import { EventEmitter } from "events";
import { CloudStore } from "@fluxscape/runtime/src/api/cloudstore";

interface UserServiceOptions {
  username: string;
  password: string;
  email?: string;
  properties?: Record<string, any>;
  sessionToken?: string;
  token?: string;
  newPassword?: string;

  success?: (response: any) => void;
  error?: (error: any) => void;
}

interface FetchCurrentUserOptions {
  sessionToken?: string;
  success: (response: any) => void;
  error: (error: any) => void;
}

class UserService {
  static forScope(_modelScope: ModelScope) {
    // On the viewer, always return main scope
    return UserService.instance;
  }

  static _instance: UserService | undefined;
  static get instance() {
    if (UserService._instance === undefined)
      UserService._instance = new UserService();
    return UserService._instance;
  }

  private events: EventEmitter;
  private appId?: string;
  private endpoint?: string;
  public current?: any;

  constructor() {
    this._initCloudServices();

    this.events = new EventEmitter();
    this.events.setMaxListeners(100000);

    // Check for current user session, and validate if it exists
    const currentUser = this.getUserFromLocalStorage();

    if (currentUser) {
      this.current = this.getUserModel();
      this.fetchCurrentUser({
        success: () => {},
        error: () => {
          // The session is no longer valid
          delete localStorage["Parse/" + this.appId + "/currentUser"];
          delete this.current;
          this.events.emit("sessionLost");
        },
      });
    }
  }

  private getUserFromLocalStorage(): any | undefined {
    const currentUser = localStorage["Parse/" + this.appId + "/currentUser"];
    if (currentUser) {
      try {
        return JSON.parse(currentUser);
      } catch (e) {
        // do nothing
      }
    }
    return undefined;
  }

  private _initCloudServices() {
    const cloudServices = NoodlRuntime.instance.getMetaData("cloudservices");

    if (cloudServices) {
      this.appId = cloudServices.appId;
      this.endpoint = cloudServices.endpoint;
    }
  }

  public on(...args: Parameters<EventEmitter["on"]>) {
    this.events.on(...args);
  }

  public off(...args: Parameters<EventEmitter["off"]>) {
    this.events.off(...args);
  }

  private _makeRequest(path: string, options: any) {
    if (!this.endpoint) {
      if (options.error) {
        options.error({ error: "No active cloud service", status: 0 });
      }
      return;
    }

    const xhr = new XMLHttpRequest();

    xhr.onreadystatechange = () => {
      if (xhr.readyState === 4) {
        let json;
        try {
          json = JSON.parse(xhr.response);
        } catch (e) {}

        if (xhr.status === 200 || xhr.status === 201) {
          options.success(json || xhr.response);
        } else {
          options.error(
            json || { error: xhr.responseText, status: xhr.status }
          );
        }
      }
    };

    xhr.open(options.method || "GET", this.endpoint + path, true);

    xhr.setRequestHeader("X-Parse-Application-Id", this.appId!);

    // Installation Id
    let _iid = localStorage["Parse/" + this.appId + "/installationId"];
    if (_iid === undefined) {
      _iid = localStorage["Parse/" + this.appId + "/installationId"] = guid();
    }
    xhr.setRequestHeader("X-Parse-Installation-Id", _iid);

    // Check for current users
    if (options.sessionToken) {
      xhr.setRequestHeader("X-Parse-Session-Token", options.sessionToken);
    } else {
      const currentUser = this.getUserFromLocalStorage();
      if (currentUser !== undefined) {
        xhr.setRequestHeader("X-Parse-Session-Token", currentUser.sessionToken);
      }
    }

    xhr.setRequestHeader("Content-Type", "application/json");
    xhr.send(JSON.stringify(options.content));
  }

  public logIn(options: UserServiceOptions) {
    this._makeRequest("/login", {
      method: "POST",
      content: {
        username: options.username,
        password: options.password,
        _method: "GET",
      },
      success: (response: any) => {
        // Store current user
        localStorage["Parse/" + this.appId + "/currentUser"] =
          JSON.stringify(response);
        this.current = this.getUserModel(); // Make sure the user model is updated
        options?.success?.(response);
        this.events.emit("loggedIn");
      },
      error: (e: any) => {
        options?.error?.(e.error);
      },
    });
  }

  public logOut(options: { success: () => void; error: (error: any) => void }) {
    this._makeRequest("/logout", {
      method: "POST",
      content: {},
      success: (response: any) => {
        // Store current user
        delete localStorage["Parse/" + this.appId + "/currentUser"];
        delete this.current;
        options.success();
        this.events.emit("loggedOut");
      },
      error: (e: any) => {
        options.error(e.error);
      },
    });
  }

  public signUp(options: UserServiceOptions) {
    // make a shallow copy to feed through CloudStore._serializeObject, which will modify the object
    const additionalUserProps = options.properties
      ? CloudStore._serializeObject({ ...options.properties }, "_User")
      : {};

    this._makeRequest("/users", {
      method: "POST",
      content: {
        ...additionalUserProps,
        username: options.username,
        password: options.password,
        email: options.email,
      },
      success: (response: any) => {
        // Store current user
        const _cu = {
          ...response,
          username: options.username,
          ...options.properties,
        };
        localStorage["Parse/" + this.appId + "/currentUser"] =
          JSON.stringify(_cu);
        this.current = this.getUserModel(); // Make sure the user model is updated
        options?.success?.(response);
        this.events.emit("loggedIn");
      },
      error: (e: any) => {
        options?.error?.(e.error);
      },
    });
  }

  public setUserProperties(options: {
    properties: Record<string, any>;
    email?: string;
    username?: string;
    success: (response: any) => void;
    error: (error: any) => void;
  }) {
    const _cu = this.getCurrentUser();
    if (_cu !== undefined) {
      // make a shallow copy to feed through CloudStore._serializeObject, which will modify the object
      const propsToSave = CloudStore._serializeObject(
        { ...options.properties },
        "_User"
      );

      const _content = {
        email: options.email,
        username: options.username,
        ...propsToSave,
      };

      delete _content.emailVerified; // Remove props you cannot set
      delete _content.createdAt;
      delete _content.updatedAt;

      this._makeRequest("/users/" + _cu.objectId, {
        method: "PUT",
        content: _content,
        success: (response: any) => {
          // Store current user
          Object.assign(_cu, _content);
          localStorage["Parse/" + this.appId + "/currentUser"] =
            JSON.stringify(_cu);
          this.current = this.getUserModel(); // Make sure the user model is updated
          options.success(response);
        },
        error: (e: any) => {
          options.error(e.error);
        },
      });
    }
  }

  public fetchCurrentUser(options: FetchCurrentUserOptions) {
    this._makeRequest("/users/me", {
      method: "GET",
      sessionToken: options.sessionToken,
      success: (response: any) => {
        // Store current user
        localStorage["Parse/" + this.appId + "/currentUser"] =
          JSON.stringify(response);
        this.current = this.getUserModel(); // Make sure the user model is updated
        this.events.emit("sessionGained");
        options.success(response);
      },
      error: (e: any) => {
        if (e.code === 209) {
          delete localStorage["Parse/" + this.appId + "/currentUser"];
          this.events.emit("sessionLost");
        }
        options.error(e.error);
      },
    });
  }

  public verifyEmail(options: {
    username: string;
    token: string;
    success: () => void;
    error: (error: any) => void;
  }) {
    this._makeRequest(
      "/apps/" +
        this.appId +
        "/verify_email?username=" +
        options.username +
        "&token=" +
        options.token,
      {
        method: "GET",
        success: (response: any) => {
          if (response.indexOf("Successfully verified your email") !== -1) {
            options.success();
          } else if (response.indexOf("Invalid Verification Link")) {
            options.error("Invalid verification token");
          } else {
            options.error("Failed to verify email");
          }
        },
        error: (e: any) => {
          options.error(e.error);
        },
      }
    );
  }

  public sendEmailVerification(options: {
    email: string;
    success: () => void;
    error: (error: any) => void;
  }) {
    this._makeRequest("/verificationEmailRequest", {
      method: "POST",
      content: { email: options.email },
      success: (response: any) => {
        options.success();
      },
      error: (e: any) => {
        options.error(e.error);
      },
    });
  }

  public resetPassword(options: {
    username: string;
    token: string;
    newPassword: string;
    success: () => void;
    error: (error: any) => void;
  }) {
    this._makeRequest("/apps/" + this.appId + "/request_password_reset", {
      method: "POST",
      content: {
        username: options.username,
        token: options.token,
        new_password: options.newPassword,
      },
      success: (response: any) => {
        if (
          response.indexOf("Password successfully reset") !== -1 ||
          response.indexOf("Successfully updated your password") !== -1
        ) {
          options.success();
        } else if (response.indexOf("Invalid Link")) {
          options.error("Invalid verification token");
        } else {
          options.error("Failed to verify email");
        }
      },
      error: (e: any) => {
        options.error(e.error);
      },
    });
  }

  public requestPasswordReset(options: {
    email: string;
    success: () => void;
    error: (error: any) => void;
  }) {
    this._makeRequest("/requestPasswordReset", {
      method: "POST",
      content: { email: options.email },
      success: (response: any) => {
        options.success();
      },
      error: (e: any) => {
        options.error(e.error);
      },
    });
  }

  public getCurrentUser(): any | undefined {
    const _cu = localStorage["Parse/" + this.appId + "/currentUser"];
    if (_cu !== undefined) return JSON.parse(_cu);
  }

  public getUserModel(): any | undefined {
    const _cu = this.getCurrentUser();
    if (_cu !== undefined) {
      delete _cu.sessionToken;
      delete _cu.ACL;
      delete _cu.className;
      delete _cu.__type;
      return CloudStore._fromJSON(_cu, "_User");
    }
  }
}

// @ts-expect-error
NoodlRuntime.Services.UserService = UserService;

export default UserService;
