import type { NodeDefinition } from "../../nodedefinition";

interface Internal {
  currentValue?: boolean;
}

function invert(value?: boolean): boolean | undefined {
  if (value === undefined) {
    return undefined;
  }
  return !value;
}

export const node: NodeDefinition<Internal> = {
  name: "Inverter",
  docs: "https://docs.noodl.net/nodes/logic/inverter",
  category: "Logic",
  initialize() {
    this._internal.currentValue = undefined;
  },
  getInspectInfo() {
    return String(invert(this._internal.currentValue));
  },
  inputs: {
    value: {
      type: {
        name: "boolean",
      },
      displayName: "Value",
      set(value: boolean) {
        this._internal.currentValue = value;
        this.flagOutputDirty("result");
      },
    },
  },
  outputs: {
    result: {
      type: "boolean",
      displayName: "Result",
      getter() {
        return invert(this._internal.currentValue);
      },
    },
  },
};
