import { Model, Collection, NodeDefinition } from "@fluxscape/runtime";

interface Internal {
  collectionChangedCallback: () => void;
  sourceCollectionChangedCallback: () => void;
  collection: Collection<Model> | undefined;
  collectionId: string;
  pendingSourceCollection: Collection<Model> | undefined;
}

export const node: NodeDefinition<Internal> = {
  name: "Collection2",
  docs: "https://docs.noodl.net/nodes/data/array/array-node",
  displayNodeName: "Array",
  shortDesc:
    "A collection of models, mainly used together with a For Each Node.",
  category: "Data",
  usePortAsLabel: "collectionId",
  color: "data",
  initialize() {
    let collectionChangedScheduled = false;
    this._internal.collectionChangedCallback = () => {
      if (this.isInputConnected("fetch")) return; // Ignore if we have explicit fetch connection

      // Optimize by only updating outputs once
      if (collectionChangedScheduled) return;
      collectionChangedScheduled = true;

      this.scheduleAfterInputsHaveUpdated(() => {
        this.sendSignalOnOutput("changed");
        this.flagOutputDirty("firstItemId");
        this.flagOutputDirty("count");
        collectionChangedScheduled = false;
      });
    };

    // When the source collection has changed, copy items into this collection
    this._internal.sourceCollectionChangedCallback = () => {
      if (this.isInputConnected("store")) return; // Ignore if we have explicit store connection

      // @ts-expect-error
      this.scheduleCopyItems();
    };
  },
  getInspectInfo() {
    const collection = this._internal.collection;
    if (!collection) {
      return { type: "text", value: "[No Array]" };
    }

    return [
      {
        type: "text",
        value: "Id: " + collection.getId(),
      },
      {
        type: "value",
        value: collection.items,
      },
    ];
  },
  inputs: {
    collectionId: {
      type: {
        name: "string",
        identifierOf: "CollectionName",
        identifierDisplayName: "Array Ids",
      },
      displayName: "Id",
      group: "General",
      set(value) {
        if (value instanceof Collection) value = value.getId(); // Can be passed as collection as well
        this._internal.collectionId = value; // Wait to fetch data
        if (!this.isInputConnected("fetch")) {
          // @ts-expect-error
          this.setCollectionID(value);
        } else {
          this.flagOutputDirty("id");
        }
      },
    },
    items: {
      type: "array",
      group: "General",
      displayName: "Items",
      set(value) {
        if (value === undefined) return;
        if (value === this._internal.collection) return;

        this._internal.pendingSourceCollection = value;
        if (!this.isInputConnected("store")) {
          // Don't auto copy if we have connections to store
          this.scheduleAfterInputsHaveUpdated(() => {
            // @ts-expect-error
            this.setSourceCollection(value);
          });
        }
      },
    },
    fetch: {
      displayName: "Fetch",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleSetCollection();
      },
    },
  },
  outputs: {
    id: {
      type: "string",
      displayName: "Id",
      group: "General",
      getter() {
        return this._internal.collection
          ? this._internal.collection.getId()
          : this._internal.collectionId;
      },
    },
    items: {
      type: "array",
      displayName: "Items",
      group: "General",
      getter() {
        return this._internal.collection;
      },
    },
    firstItemId: {
      type: "string",
      displayName: "First Item Id",
      group: "General",
      getter() {
        if (this._internal.collection) {
          const firstItem = this._internal.collection.get(0);
          if (firstItem !== undefined) return firstItem.getId();
        }
      },
    },
    count: {
      type: "number",
      displayName: "Count",
      group: "General",
      getter() {
        return this._internal.collection ? this._internal.collection.size() : 0;
      },
    },
    changed: {
      group: "Events",
      type: "signal",
      displayName: "Changed",
    },
    fetched: {
      group: "Events",
      type: "signal",
      displayName: "Fetched",
    },
  },
  prototypeExtensions: {
    setCollectionID(id: string) {
      // @ts-expect-error
      this.setCollection(Collection.get(id));
    },
    setCollection(collection: Collection<Model>) {
      if (this._internal.collection) {
        // Remove old listener if existing
        this._internal.collection.off(
          "change",
          // @ts-expect-error
          this._internal.collectionChangedCallback
        );
      }

      this._internal.collection = collection;
      this.flagOutputDirty("id");
      // @ts-expect-error
      collection.on("change", this._internal.collectionChangedCallback);

      this.flagOutputDirty("items");
      this.flagOutputDirty("firstItemId");
      this.flagOutputDirty("count");
    },
    setSourceCollection(collection: Collection<Model>) {
      const internal = this._internal;

      if (
        // @ts-expect-error
        internal.sourceCollection &&
        // @ts-expect-error
        internal.sourceCollection instanceof Collection
      ) {
        // Remove old listener if existing
        // @ts-expect-error
        internal.sourceCollection.off(
          "change",
          internal.sourceCollectionChangedCallback
        );
      }

      // @ts-expect-error
      internal.sourceCollection = collection;
      // @ts-expect-error
      if (internal.sourceCollection instanceof Collection) {
        // @ts-expect-error
        internal.sourceCollection.on(
          "change",
          internal.sourceCollectionChangedCallback
        );
      }

      // @ts-expect-error
      this._copySourceItems();
    },
    scheduleSetCollection() {
      // @ts-expect-error
      if (this.hasScheduledSetCollection) return;
      // @ts-expect-error
      this.hasScheduledSetCollection = true;

      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.hasScheduledSetCollection = false;
        // @ts-expect-error
        this.setCollectionID(this._internal.collectionId);
        this.sendSignalOnOutput("fetched");
      });
    },
    scheduleStore() {
      // @ts-expect-error
      if (this.hasScheduledStore) return;
      // @ts-expect-error
      this.hasScheduledStore = true;

      const internal = this._internal;
      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.hasScheduledStore = false;
        // @ts-expect-error
        this.setSourceCollection(internal.pendingSourceCollection);
      });
    },
    _copySourceItems() {
      const internal = this._internal;

      if (
        internal.collection === undefined &&
        !this.isInputConnected("fetch")
      ) {
        // @ts-expect-error
        this.setCollection(Collection.get());
      }
      // @ts-expect-error
      internal.collection && internal.collection.set(internal.sourceCollection);
    },
    scheduleCopyItems() {
      // @ts-expect-error
      if (this.hasScheduledCopyItems) return;
      // @ts-expect-error
      this.hasScheduledCopyItems = true;

      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.hasScheduledCopyItems = false;
        // @ts-expect-error
        this._copySourceItems();
      });
    },
    _onNodeDeleted() {
      // @ts-expect-error
      Node.prototype._onNodeDeleted.call(this);

      if (this._internal.collection) {
        // Remove old listener if existing
        this._internal.collection.off(
          "change",
          // @ts-expect-error
          this._internal.collectionChangedCallback
        );
      }
    },
  },
};
