import type NoodlRuntime from "./runtime";

import * as componentinputs from "./nodes/componentinputs";
import * as componentoutputs from "./nodes/componentoutputs";

import * as runtasks from "./nodes/std-library/runtasks";

// Data
import * as restnode from "./nodes/std-library/data/restnode";

// Custom code
import * as expression from "./nodes/std-library/expression";
import * as simplejavascript from "./nodes/std-library/simplejavascript";

// Records
import * as dbcollectionnode2 from "./nodes/std-library/data/dbcollectionnode2";
import * as dbmodelnode2 from "./nodes/std-library/data/dbmodelnode2";
import * as setdbmodelpropertiesnode from "./nodes/std-library/data/setdbmodelpropertiesnode";
import * as deletedbmodelpropertiesnode from "./nodes/std-library/data/deletedbmodelpropertiesnode";
import * as newdbmodelpropertiesnode from "./nodes/std-library/data/newdbmodelpropertiesnode";
import * as dbmodelnodeAddrelation from "./nodes/std-library/data/dbmodelnode-addrelation";
import * as dbmodelnodeRemoverelation from "./nodes/std-library/data/dbmodelnode-removerelation";
import * as filterdbmodelsnode from "./nodes/std-library/data/filterdbmodelsnode";

// Object
import * as modelnode2 from "./nodes/std-library/data/modelnode2";
import * as setmodelpropertiesnode from "./nodes/std-library/data/setmodelpropertiesnode";
import * as newmodelnode from "./nodes/std-library/data/newmodelnode";

// Cloud
import * as cloudfilenode from "./nodes/std-library/data/cloudfilenode";
import * as dbconfig from "./nodes/std-library/data/dbconfig";

// Variables
import * as number from "./nodes/std-library/variables/number";
import * as string from "./nodes/std-library/variables/string";
import * as boolean from "./nodes/std-library/variables/boolean";

// Utils
import * as condition from "./nodes/std-library/condition";
import * as and from "./nodes/std-library/and";
import * as or from "./nodes/std-library/or";
import * as booleantostring from "./nodes/std-library/booleantostring";
import * as datetostring from "./nodes/std-library/datetostring";
import * as stringmapper from "./nodes/std-library/stringmapper";
import * as inverter from "./nodes/std-library/inverter";
import * as substring from "./nodes/std-library/substring";
import * as stringformat from "./nodes/std-library/stringformat";
import * as counter from "./nodes/std-library/counter";
import * as uniqueid from "./nodes/std-library/uniqueid";

// User
import * as setuserproperties from "./nodes/std-library/user/setuserproperties";
import * as user from "./nodes/std-library/user/user";

export function registerNodes(noodlRuntime: NoodlRuntime): void {
  [
    componentinputs,
    componentoutputs,

    runtasks,

    // Data
    restnode,

    // Custom code
    expression,
    simplejavascript,

    // Records
    dbcollectionnode2,
    dbmodelnode2,
    setdbmodelpropertiesnode,
    deletedbmodelpropertiesnode,
    newdbmodelpropertiesnode,
    dbmodelnodeAddrelation,
    dbmodelnodeRemoverelation,
    filterdbmodelsnode,

    // Object
    modelnode2,
    setmodelpropertiesnode,
    newmodelnode,

    // Cloud
    cloudfilenode,
    dbconfig,

    // Variables
    number,
    string,
    boolean,

    // Utils
    condition,
    and,
    or,
    booleantostring,
    datetostring,
    stringmapper,
    inverter,
    substring,
    stringformat,
    counter,
    uniqueid,

    // User
    setuserproperties,
    user,

    // @ts-expect-error
  ].forEach((node) => noodlRuntime.registerNode(node));
}
