import { EventSender } from "../eventsender";
import { Variant } from "../variants";
import type { ComponentModel, Port } from "./componentmodel";

// TODO: What is this? Node?
export interface NodeData {
  id: string;
  type: string;
  parameters?: Record<string, any>;
  stateParameters?: Record<string, Record<string, any>>;
  stateTransitions?: Record<string, Record<string, any>>;
  defaultStateTransitions?: Record<string, any>;
  ports?: Port[];
  variant?: string;
  name?: string;
  children?: NodeData[];
  metadata?: Record<string, any>;
}

export class NodeModel extends EventSender {
  id: string;
  type: string;
  inputs: any[];
  outputs: any[];
  children: NodeModel[];
  parameters: Record<string, any>;
  stateParameters?: Record<string, Record<string, any>>;
  stateTransitions?: Record<string, Record<string, any>>;
  defaultStateTransitions?: Record<string, any>;
  inputPorts: Record<string, Port>;
  outputPorts: Record<string, Port>;
  parent?: NodeModel;
  variant?: Variant | string;
  component?: ComponentModel;

  // removeListenersWithRef: (ref: any) => void;

  constructor(id: string, type: string) {
    super();

    this.id = id;
    this.type = type;

    this.inputs = [];
    this.outputs = [];
    this.children = [];
    this.parameters = {};
    this.inputPorts = {};
    this.outputPorts = {};
  }

  setParameter(name: string, value: any, state?: string): void {
    if (state) {
      if (!this.stateParameters) this.stateParameters = {};
      if (!this.stateParameters[state]) this.stateParameters[state] = {};

      if (value === undefined) {
        delete this.stateParameters[state][name];
      } else {
        this.stateParameters[state][name] = value;
      }
    } else {
      if (value === undefined) {
        delete this.parameters[name];
      } else {
        this.parameters[name] = value;
      }
    }

    this.emit("parameterUpdated", { name, value, state });
  }

  setParameters(parameters: Record<string, any>): void {
    Object.keys(parameters).forEach((name) => {
      this.setParameter(name, parameters[name]);
    });
  }

  setStateParameters(parameters: Record<string, Record<string, any>>): void {
    this.stateParameters = parameters;
  }

  setStateTransitions(
    stateTransitions: Record<string, Record<string, any>>
  ): void {
    this.stateTransitions = stateTransitions;
  }

  setStateTransitionParamter(
    parameter: string,
    curve: any,
    state: string
  ): void {
    if (!this.stateTransitions) {
      this.stateTransitions = {};
    }

    if (curve) {
      this.stateTransitions[state][parameter] = curve;
    } else {
      delete this.stateTransitions[state][parameter];
    }
  }

  setDefaultStateTransition(stateTransition: any, state: string): void {
    if (!this.defaultStateTransitions) {
      this.defaultStateTransitions = {};
    }
    this.defaultStateTransitions[state] = stateTransition;
  }

  addInputPort(port: Port): void {
    this.inputPorts[port.name] = port;
    this.emit("inputPortAdded", port);
  }

  getInputPort(portName: string): Port | undefined {
    return this.inputPorts[portName];
  }

  getInputPorts(): Record<string, Port> {
    return this.inputPorts;
  }

  removeInputPortWithName(portName: string): void {
    if (this.inputPorts.hasOwnProperty(portName)) {
      const port = this.inputPorts[portName];
      delete this.inputPorts[portName];
      this.emit("inputPortRemoved", port);
    }
  }

  updateInputPortTypes(ports: Record<string, Port>): void {
    let changed = false;
    for (const key in ports) {
      if (this.inputPorts[key] !== undefined) {
        this.inputPorts[key].type = ports[key].type;
        changed = true;
      }
    }
    changed && this.emit("inputPortTypesUpdated");
  }

  addOutputPort(port: Port): void {
    this.outputPorts[port.name] = port;
    this.emit("outputPortAdded", port);
  }

  getOutputPort(portName: string): Port | undefined {
    return this.outputPorts[portName];
  }

  getOutputPorts(): Record<string, Port> {
    return this.outputPorts;
  }

  removeOutputPortWithName(portName: string): void {
    if (this.outputPorts.hasOwnProperty(portName)) {
      const port = this.outputPorts[portName];
      delete this.outputPorts[portName];
      this.emit("outputPortRemoved", port);
    }
  }

  updateOutputPortTypes(ports: Record<string, Port>): void {
    let changed = false;
    for (const key in ports) {
      if (this.outputPorts[key] !== undefined) {
        this.outputPorts[key].type = ports[key].type;
        changed = true;
      }
    }
    changed && this.emit("outputPortTypesUpdated");
  }

  addChild(child: NodeModel, index?: number): void {
    child.parent = this;
    if (index === undefined) {
      this.children.push(child);
    } else {
      this.children.splice(index, 0, child);
    }
    this.emit("childAdded", child);
  }

  removeChild(child: NodeModel): void {
    child.parent = undefined;
    const index = this.children.indexOf(child);
    this.children.splice(index, 1);
    this.emit("childRemoved", child);
  }

  reset(): void {
    this.removeAllListeners();
  }

  setVariant(variant: Variant | string): void {
    this.variant = variant;
    this.emit("variantUpdated", variant);
  }

  static createFromExportData(nodeData: NodeData): NodeModel {
    const node = new NodeModel(nodeData.id, nodeData.type);
    nodeData.parameters && node.setParameters(nodeData.parameters);
    nodeData.stateParameters &&
      node.setStateParameters(nodeData.stateParameters);
    nodeData.stateTransitions &&
      node.setStateTransitions(nodeData.stateTransitions);

    if (nodeData.defaultStateTransitions) {
      for (const state in nodeData.defaultStateTransitions) {
        node.setDefaultStateTransition(
          nodeData.defaultStateTransitions[state],
          state
        );
      }
    }

    nodeData.ports &&
      nodeData.ports.forEach((port) => {
        //some ports are incorrectly named outputs instead of output, patch it here so
        //the rest of the code doesn't need to care
        // TODO: WTF...
        // if (port.plug === "outputs") {
        //   port.plug = "output";
        // }

        if (port.plug === "input" || port.plug === "input/output") {
          node.addInputPort(port);
        }
        if (port.plug === "output" || port.plug === "input/output") {
          node.addOutputPort(port);
        }
      });

    nodeData.variant && node.setVariant(nodeData.variant);

    return node;
  }
}
