import type { Node } from "./node";

interface OutputPropertyArgs {
  getter?: () => any;
  owner: Node<any>;
  name: string;
  type?: string;
  onFirstConnectionAdded?: () => void;
  onLastConnectionRemoved?: () => void;
}

interface Connection {
  node: Node;
  inputPortName: string;
}

export class OutputProperty {
  private getter: () => any;
  private connections: Connection[];
  private _owner: Node;
  private _name: string;
  private onFirstConnectionAdded?: () => void;
  private onLastConnectionRemoved?: () => void;
  private _id?: string;
  private _lastUpdateIteration?: number;
  private valuesSendThisIteration: number = 0;

  constructor(args: OutputPropertyArgs) {
    if (!args.owner) {
      throw new Error("Owner must be set");
    }
    this.getter = args.getter || (() => undefined);
    this.connections = [];
    this._owner = args.owner;
    this._name = args.name;
    this.onFirstConnectionAdded = args.onFirstConnectionAdded;
    this.onLastConnectionRemoved = args.onLastConnectionRemoved;
  }

  get name(): string {
    return this._name;
  }

  get owner(): Node {
    return this._owner;
  }

  get value(): any {
    return this.getter.call(this.owner);
  }

  get id(): string {
    if (!this._id) {
      this._id = this.owner.id + this.name;
    }
    return this._id;
  }

  registerConnection(node: Node<any>, inputPortName: string): void {
    this.connections.push({
      node,
      inputPortName,
    });

    if (this.connections.length === 1 && this.onFirstConnectionAdded) {
      this.onFirstConnectionAdded.call(this.owner);
    }
  }

  deregisterConnection(node: Node<any>, inputPortName: string): void {
    for (let i = 0; i < this.connections.length; i++) {
      const connection = this.connections[i];
      if (
        connection.node === node &&
        connection.inputPortName === inputPortName
      ) {
        this.connections.splice(i, 1);
        break;
      }
    }

    if (this.connections.length === 0 && this.onLastConnectionRemoved) {
      this.onLastConnectionRemoved.call(this.owner);
    }
  }

  flagDependeesDirty(value?: any): void {
    for (let i = 0, len = this.connections.length; i < len; i++) {
      this.connections[i].node.flagDirty();
    }
  }

  sendValue(value: any): void {
    if (this._lastUpdateIteration !== (this.owner as any)._updatedAtIteration) {
      this._lastUpdateIteration = (this.owner as any)._updatedAtIteration;
      this.valuesSendThisIteration = 0;
    } else {
      this.valuesSendThisIteration++;
    }

    if (this.valuesSendThisIteration > 500) {
      (this.owner as any)._cyclicLoop = true;
    }

    for (let i = 0, len = this.connections.length; i < len; i++) {
      const connection = this.connections[i];
      (connection.node as any)._setValueFromConnection(
        connection.inputPortName,
        value
      );
    }
  }

  hasConnections(): boolean {
    return this.connections.length > 0;
  }
}
