import { randomString } from "./utils";

interface ModelData {
  id?: string;
  [key: string]: any;
}

interface ModelSetArgs {
  resolve?: boolean;
  forceChange?: boolean;
  silent?: boolean;
}

interface ModelGetArgs {
  resolve?: boolean;
}

export class Model {
  private id: string;
  _class?: string;
  data: ModelData;
  private listeners?: Record<string, Function[]>;
  private static _models: Record<string, Model> = {};

  constructor(id: string, data: ModelData) {
    this.id = id;
    this.data = data;
  }

  private static models = Model._models;
  private static proxies: Record<string, any> = {};

  static _modelProxyHandler = {
    get: function (target: Model, prop: string | symbol, receiver: any) {
      if (typeof target[prop as keyof Model] === "function")
        // @ts-expect-error
        return target[prop as keyof Model].bind(target);
      else if (prop in target) return Reflect.get(target, prop, receiver);
      else return target.get(prop.toString());
    },
    set: function (obj: Model, prop: string | symbol, value: any) {
      if (prop === "_class") {
        (obj as any)._class = value;
      } else if (prop === "id") {
        console.log(
          `Noodl.Object warning: id is readonly (Id is ${obj.id}, trying to set to ${value})`
        );
        return true;
      } else {
        obj.set(prop.toString(), value);
      }
      return true;
    },
    ownKeys(target: Model) {
      return Reflect.ownKeys(target.data);
    },
    getOwnPropertyDescriptor(target: Model, prop: string | symbol) {
      return Object.getOwnPropertyDescriptor(target.data, prop.toString());
    },
  };

  static get(id?: string): Model {
    if (id === undefined) id = Model.guid();
    if (!Model.models[id]) {
      Model.models[id] = new Model(id, {});
      Model.proxies[id] = new Proxy(Model.models[id], Model._modelProxyHandler);
    }
    return Model.proxies[id];
  }

  static create(data?: ModelData): Model {
    const modelData = data ? data : {};
    const m = Model.get(modelData.id);
    for (const key in modelData) {
      if (key === "id") continue;
      m.set(key, modelData[key]);
    }
    return m;
  }

  static exists(id: string): boolean {
    return Model.models[id] !== undefined;
  }

  static instanceOf(collection: any): boolean {
    return collection instanceof Model || collection.target instanceof Model;
  }

  static guid(): string {
    return randomString(10);
  }

  on(event: string, listener: Function): void {
    if (!this.listeners) this.listeners = {};
    if (!this.listeners[event]) this.listeners[event] = [];
    this.listeners[event].push(listener);
  }

  off(event: string, listener: Function): void {
    if (!this.listeners) return;
    if (!this.listeners[event]) return;
    const idx = this.listeners[event].indexOf(listener);
    if (idx !== -1) this.listeners[event].splice(idx, 1);
  }

  notify(event: string, args: any): void {
    if (!this.listeners) return;
    if (!this.listeners[event]) return;

    const l = this.listeners[event].slice();
    for (let i = 0; i < l.length; i++) {
      l[i](args);
    }
  }

  setAll(obj: ModelData): void {
    for (const i in obj) {
      if (i === "id") continue;
      if (this.data[i] !== obj[i]) {
        const old = this.data[i];
        this.data[i] = obj[i];
        this.notify("change", { name: i, value: obj[i], old });
      }
    }
  }

  fill(value: any = null): void {
    for (const key in this.data) {
      if (key === "id") continue;
      const temp = this.data[key];
      this.data[key] = value;
      this.notify("change", { name: key, value: this.data[key], old: temp });
    }
  }

  set(name: string, value: any, args?: ModelSetArgs): void {
    if (args?.resolve && name.indexOf(".") !== -1) {
      const path = name.split(".");
      let model: Model = this;
      for (let i = 0; i < path.length - 1; i++) {
        const v = model.get(path[i]);
        if (Model.instanceOf(v)) model = v;
        else return;
      }
      model.set(path[path.length - 1], value);
      return;
    }

    const forceChange = args?.forceChange;
    const oldValue = this.data[name];
    this.data[name] = value;

    if ((forceChange || oldValue !== value) && !args?.silent) {
      this.notify("change", { name, value, old: oldValue });
    }
  }

  getId(): string {
    return this.id;
  }

  get(name: string, args?: ModelGetArgs): any {
    if (args?.resolve && name.indexOf(".") !== -1) {
      const path = name.split(".");
      let model: Model = this;
      for (let i = 0; i < path.length - 1; i++) {
        const v = model.get(path[i]);
        if (Model.instanceOf(v)) model = v;
        else return;
      }
      return model.get(path[path.length - 1]);
    }

    return this.data[name];
  }

  toJSON(): ModelData {
    return Object.assign({}, this.data, { id: this.id });
  }
}

export class ModelScope {
  private models: Record<string, Model> = {};
  private proxies: Record<string, any> = {};
  private _cloudStore?: any;

  get(id?: string): Model {
    if (id === undefined) id = Model.guid();
    if (!this.models[id]) {
      this.models[id] = new Model(id, {});
      this.proxies[id] = new Proxy(this.models[id], Model._modelProxyHandler);
    }
    return this.proxies[id];
  }

  create(data?: ModelData): Model {
    const modelData = data ? data : {};
    const m = this.get(modelData.id);
    for (const key in modelData) {
      if (key === "id") continue;
      m.set(key, modelData[key]);
    }
    return m;
  }

  exists(id: string): boolean {
    return this.models[id] !== undefined;
  }

  instanceOf(collection: any): boolean {
    return collection instanceof Model || collection.target instanceof Model;
  }

  guid(): string {
    return randomString(10);
  }

  reset(): void {
    this.models = {};
    this.proxies = {};
    delete this._cloudStore;
  }
}
