declare global {
  interface Window {
    Noodl: {
      baseUrl?: string;
    };
  }
}

//this just assumes the base url is '/' always
export function getAbsoluteUrl(_url: unknown): string {
  if (_url === null || _url === undefined || _url === "") {
    return window.Noodl.baseUrl || "/";
  }

  //convert to string in case the _url is a Cloud File (which is an object with a custom toString())
  const url = String(_url);

  //only add a the base url if this is a local URL (e.g. not a https url or  base64 string)
  if (
    !url ||
    url[0] === "/" ||
    url.includes("://") ||
    url.startsWith("data:")
  ) {
    return url;
  }

  return (window.Noodl.baseUrl || "/") + url;
}

/**
 * Log an error thrown by the JavaScript nodes.
 */
export function logJavaScriptNodeError(error: unknown): void {
  if (typeof error === "string") {
    console.log("Error in JS node run code.", error);
  } else if (error instanceof Error) {
    console.log(
      "Error in JS node run code.",
      Object.getPrototypeOf(error).constructor.name + ": " + error.message,
      error.stack
    );
  }
}

//adapted from http://stackoverflow.com/questions/105034/how-to-create-a-guid-uuid-in-javascript
export function guid(): string {
  function s4(): string {
    return Math.floor((1 + Math.random()) * 0x10000)
      .toString(16)
      .substring(1);
  }
  return (
    s4() +
    s4() +
    "-" +
    s4() +
    "-" +
    s4() +
    "-" +
    s4() +
    "-" +
    s4() +
    s4() +
    s4()
  );
}

export function randomString(size: number): string {
  if (size === 0) {
    throw new Error("Zero-length randomString is useless.");
  }
  const chars =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZ" + "abcdefghijklmnopqrstuvwxyz" + "0123456789";
  let objectId = "";
  for (let i = 0; i < size; ++i) {
    objectId += chars[Math.floor((1 + Math.random()) * 0x10000) % chars.length];
  }
  return objectId;
}
