import { NodeDefinition } from "@fluxscape/runtime";

interface Internal {
  _animation: any;
}

export const node: NodeDefinition<Internal> = {
  name: "Timer",
  docs: "https://docs.noodl.net/nodes/utilities/delay",
  displayName: "Delay",
  category: "Utilities",
  nodeDoubleClickAction: {
    focusPort: "duration",
  },
  initialize() {
    const self = this;
    this._internal._animation = this.context.timerScheduler.createTimer({
      duration: 0,
      onStart() {
        self.sendSignalOnOutput("timerStarted");
      },
      onFinish() {
        self.sendSignalOnOutput("timerFinished");
      },
    });

    this.addDeleteListener(() => {
      this._internal._animation.stop();
    });
  },
  getInspectInfo() {
    if (this._internal._animation.isRunning()) {
      return (
        Math.floor(this._internal._animation.durationLeft() / 10) / 100 +
        " seconds"
      );
    }
    return "Not running";
  },
  inputs: {
    start: {
      displayName: "Start",
      valueChangedToTrue() {
        if (this._internal._animation._isRunning === false) {
          this._internal._animation.start();
        }
      },
    },
    restart: {
      displayName: "Restart",
      valueChangedToTrue() {
        this._internal._animation.start();
      },
    },
    duration: {
      type: "number",
      displayName: "Duration",
      default: 0,
      set(value) {
        this._internal._animation.duration = value;
      },
    },
    startDelay: {
      type: "number",
      displayName: "Start Delay",
      default: 0,
      set(value) {
        this._internal._animation.delay = value;
      },
    },
    stop: {
      displayName: "Stop",
      valueChangedToTrue() {
        this._internal._animation.stop();
      },
    },
  },
  outputs: {
    timerStarted: {
      type: "signal",
      displayName: "Started",
    },
    timerFinished: {
      type: "signal",
      displayName: "Finished",
    },
  },
};
