import { SeoApi } from "./api/seo";

import { Model, Collection, createRecordsAPI } from "@fluxscape/runtime";
import { default as users } from "./api/users";
import { default as cloudfunctions } from "./api/cloudfunctions";
import { default as navigation } from "./api/navigation";
import { default as files } from "./api/files";

export default function createNoodlAPI(noodlRuntime: any): void {
  // Support SSR
  const global: any = typeof window !== "undefined" ? window : globalThis;

  global.Noodl.getProjectSettings =
    noodlRuntime.getProjectSettings.bind(noodlRuntime);
  global.Noodl.getMetaData = noodlRuntime.getMetaData.bind(noodlRuntime);
  global.Noodl.Collection = global.Noodl.Array = Collection;
  global.Noodl.Model = global.Noodl.Object = Model;
  global.Noodl.Variables = global.Noodl.Object.get("--ndl--global-variables");
  global.Noodl.Events = global.Noodl.eventEmitter =
    noodlRuntime.context.eventSenderEmitter;
  global.Noodl.Records = createRecordsAPI();
  global.Noodl.Users = users;
  global.Noodl.CloudFunctions = cloudfunctions;
  global.Noodl.Navigation = navigation;
  global.Noodl.Navigation._noodlRuntime = noodlRuntime;
  global.Noodl.Files = files;
  global.Noodl.SEO = new SeoApi();
  if (!global.Noodl.Env) {
    global.Noodl.Env = {};
  }

  global.Noodl.Arrays = new Proxy(global.Noodl.Array, {
    get(target: any, prop: string | symbol, receiver: any) {
      // @ts-expect-error
      return Noodl.Array.get(prop);
    },
    set(obj: any, prop: string | symbol, value: any) {
      if (!Array.isArray(value)) {
        throw new Error(
          // @ts-expect-error
          "Cannot assign non array value to array with id " + prop
        );
      }
      // @ts-expect-error
      Noodl.Array.get(prop).set(value);
      return true; // Indicate success
    },
  });

  global.Noodl.Objects = new Proxy(global.Noodl.Object, {
    get(target: any, prop: string | symbol, receiver: any) {
      // @ts-expect-error
      return Noodl.Object.get(prop);
    },
    set(obj: any, prop: string | symbol, value: any) {
      // @ts-expect-error
      Noodl.Object.get(prop).setAll(value);
      return true; // Indicate success
    },
  });
}
