import { NodeDefinition } from "../../../nodedefinition";
import { NodeContext } from "../../../nodecontext";
import { GraphModel } from "../../../models/graphmodel";
import { Port } from "../../../models/componentmodel";

const defaultRequestScript =
  "" +
  "//Add custom code to setup the request object before the request\n" +
  "//is made.\n" +
  "//\n" +
  "//*Request.resource     contains the resource path of the request.\n" +
  "//*Request.method       contains the method, GET, POST, PUT or DELETE.\n" +
  "//*Request.headers      is a map where you can add additional headers.\n" +
  "//*Request.parameters   is a map the parameters that will be appended\n" +
  "//                      to the url.\n" +
  "//*Request.content      contains the content of the request as a javascript\n" +
  "//                      object.\n" +
  "//\n" +
  "//*Inputs and *Outputs contain the inputs and outputs of the node.\n";

const defaultResponseScript =
  "" +
  "// Add custom code to convert the response content to outputs\n" +
  "//\n" +
  "//*Response.status    The status code of the response\n" +
  "//*Response.content   The content of the response as a javascript\n" +
  "//                    object.\n" +
  "//*Response.request   The request object that resulted in the response.\n" +
  "//\n" +
  "//*Inputs and *Outputs contain the inputs and outputs of the node.\n";

interface Request {
  resource: string;
  method: string;
  headers: Record<string, string>;
  parameters: Record<string, string>;
  content?: any;
}

interface Response {
  status: number;
  content: any;
  request: Request;
}

interface Internal {
  inputValues: Record<string, any>;
  outputValues: Record<string, any>;
  outputValuesProxy: any;
  self: any;
  resource?: string;
  method?: string;
  requestFunc?: Function;
  responseFunc?: Function;
  hasScheduledFetch?: boolean;
  inspectData?: any;
}

export const node: NodeDefinition<Internal> = {
  name: "REST2",
  displayNodeName: "REST",
  docs: "https://docs.noodl.net/nodes/data/rest",
  category: "Data",
  color: "data",
  searchTags: ["http", "request", "fetch"],
  initialize() {
    this._internal.inputValues = {};
    this._internal.outputValues = {};

    this._internal.outputValuesProxy = new Proxy(this._internal.outputValues, {
      set: (obj, prop: string, value) => {
        if (value !== this._internal.outputValues[prop]) {
          // @ts-expect-error
          this.registerOutputIfNeeded("out-" + prop);

          this._internal.outputValues[prop] = value;
          this.flagOutputDirty("out-" + prop);
        }
        return true;
      },
    });

    this._internal.self = {};
  },
  getInspectInfo() {
    return this._internal.inspectData
      ? { type: "value", value: this._internal.inspectData }
      : { type: "text", value: "[Not executed yet]" };
  },
  inputs: {
    resource: {
      type: "string",
      displayName: "Resource",
      group: "Request",
      default: "/",
      set(value: string) {
        this._internal.resource = value;
      },
    },
    method: {
      type: {
        name: "enum",
        enums: [
          { label: "GET", value: "GET" },
          { label: "POST", value: "POST" },
          { label: "PUT", value: "PUT" },
          { label: "PATCH", value: "PATCH" },
          { label: "DELETE", value: "DELETE" },
        ],
      },
      displayName: "Method",
      group: "Request",
      default: "GET",
      set(value: string) {
        this._internal.method = value;
      },
    },
    requestScript: {
      type: {
        name: "string",
        allowEditOnly: true,
        codeeditor: "javascript",
      },
      displayName: "Request",
      default: defaultRequestScript,
      group: "Scripts",
      set(script: string) {
        try {
          this._internal.requestFunc = new Function(
            "Inputs",
            "Outputs",
            "Request",
            script
          );
        } catch (e) {
          console.log(e);
        }
      },
    },
    responseScript: {
      type: {
        name: "string",
        allowEditOnly: true,
        codeeditor: "javascript",
      },
      displayName: "Response",
      default: defaultResponseScript,
      group: "Scripts",
      set(script: string) {
        try {
          this._internal.responseFunc = new Function(
            "Inputs",
            "Outputs",
            "Response",
            script
          );
        } catch (e) {
          console.log(e);
        }
      },
    },
    fetch: {
      type: "signal",
      displayName: "Fetch",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleFetch();
      },
    },
    cancel: {
      type: "signal",
      displayName: "Cancel",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.cancelFetch();
      },
    },
  },
  outputs: {
    failure: {
      type: "signal",
      displayName: "Failure",
      group: "Events",
    },
    success: {
      type: "signal",
      displayName: "Success",
      group: "Events",
    },
    canceled: {
      type: "signal",
      displayName: "Canceled",
      group: "Events",
    },
  },
  prototypeExtensions: {
    getScriptOutputValue(name: string) {
      return this._internal.outputValues[name];
    },
    setScriptInputValue(name: string, value: any) {
      return (this._internal.inputValues[name] = value);
    },
    registerOutputIfNeeded(name: string) {
      if (this.hasOutput(name)) {
        return;
      }

      if (name.startsWith("out-"))
        return this.registerOutput(name, {
          // @ts-expect-error
          getter: this.getScriptOutputValue.bind(
            this,
            name.substring("out-".length)
          ),
        });
    },
    registerInputIfNeeded(name: string) {
      if (this.hasInput(name)) {
        return;
      }

      if (name.startsWith("in-"))
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setScriptInputValue.bind(
            this,
            name.substring("in-".length)
          ),
        });
    },
    scheduleFetch() {
      const internal = this._internal;
      if (!internal.hasScheduledFetch) {
        internal.hasScheduledFetch = true;
        // @ts-expect-error
        this.scheduleAfterInputsHaveUpdated(this.doFetch.bind(this));
      }
    },
    doResponse(status: number, response: any, request: Request) {
      if (this._internal.responseFunc) {
        this._internal.responseFunc.apply(this._internal.self, [
          this._internal.inputValues,
          this._internal.outputValuesProxy,
          { status, content: response, request },
        ]);
      }

      this._internal.inspectData = { status, content: response };

      if (status >= 200 && status < 300) {
        this.sendSignalOnOutput("success");
      } else {
        this.sendSignalOnOutput("failure");
      }
    },
    doExternalFetch(request: Request) {
      let url = request.resource;

      if (Object.keys(request.parameters).length > 0) {
        const parameters = Object.keys(request.parameters).map((p) => {
          return p + "=" + encodeURIComponent(request.parameters[p]);
        });
        url += "?" + parameters.join("&");
      }

      // @ts-expect-error
      if (typeof _noodl_cloud_runtime_version === "undefined") {
        const xhr = new XMLHttpRequest();
        // @ts-expect-error
        this._xhr = xhr;

        xhr.open(request.method, url, true);
        for (const header in request.headers) {
          xhr.setRequestHeader(header, request.headers[header]);
        }
        xhr.onreadystatechange = () => {
          let sentResponse = false;

          if (xhr.readyState === 4) {
            const statusCode = xhr.status;
            const responseType = xhr.getResponseHeader("content-type");
            const rawResponse = xhr.response;
            // @ts-expect-error
            delete this._xhr;

            if (responseType) {
              // @ts-expect-error
              responseType = responseType.toLowerCase();
              const responseData =
                responseType.indexOf("json") !== -1
                  ? JSON.parse(rawResponse)
                  : rawResponse;

              // @ts-expect-error
              this.doResponse(statusCode, responseData, request);
              sentResponse = true;
            }

            if (sentResponse === false) {
              // @ts-expect-error
              this.doResponse(statusCode, rawResponse, request);
            }
          }
        };
        xhr.onerror = () => {
          // @ts-expect-error
          delete this._xhr;
          this.sendSignalOnOutput("failure");
        };

        xhr.onabort = () => {
          // @ts-expect-error
          delete this._xhr;
          this.sendSignalOnOutput("canceled");
        };

        if (request.content) {
          xhr.setRequestHeader("Content-Type", "application/json");
          xhr.send(JSON.stringify(request.content));
        } else {
          xhr.send();
        }
      } else {
        const headers = {
          ...request.headers,
          ...(request.content ? { "Content-Type": "application/json" } : {}),
        };

        fetch(url, {
          method: request.method,
          headers,
          body: request.content ? JSON.stringify(request.content) : undefined,
        })
          .then((response) => {
            const responseType = response.headers.get("content-type");
            if (responseType) {
              if (responseType.indexOf("/json") !== -1) {
                response.json().then((json) => {
                  // @ts-expect-error
                  this.doResponse(response.status, json, request);
                });
              } else {
                if (this.context.editorConnection) {
                  this.context.editorConnection.sendWarning(
                    this.nodeScope!.componentOwner.name,
                    this.id,
                    "rest-run-waring-",
                    {
                      message:
                        "REST only supports json content type in response.",
                    }
                  );
                }
              }
            } else {
              response.text().then((raw) => {
                // @ts-expect-error
                this.doResponse(response.status, raw, request);
              });
            }
          })
          .catch((e) => {
            console.log("REST: Failed to request", url);
            console.log(e);
            this.sendSignalOnOutput("failure");
          });
      }
    },
    doFetch() {
      this._internal.hasScheduledFetch = false;

      let resource = this._internal.resource;
      if (resource) {
        for (const key in this._internal.inputValues) {
          resource = resource.replace(
            "{" + key + "}",
            this._internal.inputValues[key]
          );
        }
      }

      const request: Request = {
        // @ts-expect-error
        resource,
        headers: {},
        method:
          this._internal.method !== undefined ? this._internal.method : "GET",
        parameters: {},
      };

      if (this._internal.requestFunc) {
        this._internal.requestFunc.apply(this._internal.self, [
          this._internal.inputValues,
          this._internal.outputValuesProxy,
          request,
        ]);
      }

      // @ts-expect-error
      this.doExternalFetch(request);
    },
    cancelFetch() {
      // @ts-expect-error
      if (typeof _noodl_cloud_runtime_version === "undefined") {
        // @ts-expect-error
        this._xhr && this._xhr.abort();
      } else {
        if (this.context.editorConnection) {
          this.context.editorConnection.sendWarning(
            this.nodeScope!.componentOwner.name,
            this.id,
            "rest-run-waring-",
            {
              message: "REST doesn't support cancel in cloud functions.",
            }
          );
        }
      }
    },
  },
};

function parseScriptForErrors(
  script: string | undefined,
  args: string[],
  name: string,
  node: any,
  context: NodeContext,
  ports: Port[]
) {
  if (!context.editorConnection) {
    return;
  }

  context.editorConnection.clearWarning(
    node.component.name,
    node.id,
    "rest-run-waring-" + name
  );

  if (script === undefined) {
    context.editorConnection.clearWarning(
      node.component.name,
      node.id,
      "rest-parse-waring-" + name
    );
    return;
  }

  try {
    new Function(...args, script);
    context.editorConnection.clearWarning(
      node.component.name,
      node.id,
      "rest-parse-waring-" + name
    );
  } catch (e: any) {
    context.editorConnection.sendWarning(
      node.component.name,
      node.id,
      "rest-parse-waring-" + name,
      {
        message: "<strong>" + name + "</strong>: " + e.message,
        showGlobally: true,
      }
    );
  }

  function exists(port: string): boolean {
    return ports.find((p) => p.name === port) !== undefined;
  }

  const scriptWithoutComments = script.replace(/\/\*[\s\S]*?\*\/|\/\/.*/g, "");
  const inputs = scriptWithoutComments.match(/Inputs\.[A-Za-z0-9]+/g);
  if (inputs) {
    const unique: Record<string, boolean> = {};
    inputs.forEach((v) => {
      unique[v.substring("Inputs.".length)] = true;
    });

    Object.keys(unique).forEach((p) => {
      if (exists("in-" + p)) return;

      ports.push({
        name: "in-" + p,
        displayName: p,
        plug: "input",
        type: "*",
        group: "Inputs",
      });
    });
  }

  const outputs = scriptWithoutComments.match(/Outputs\.[A-Za-z0-9]+/g);
  if (outputs) {
    const unique: Record<string, boolean> = {};
    outputs.forEach((v) => {
      unique[v.substring("Outputs.".length)] = true;
    });

    Object.keys(unique).forEach((p) => {
      if (exists("out-" + p)) return;

      ports.push({
        name: "out-" + p,
        displayName: p,
        plug: "output",
        type: "*",
        group: "Outputs",
      });
    });
  }
}

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (!context.editorConnection) {
    return;
  }

  function managePortsForNode(node: any) {
    function updatePorts() {
      if (!node.parameters) {
        return;
      }

      const ports: Port[] = [];
      function exists(name: string): boolean {
        for (let i = 0; i < ports.length; i++)
          if (ports[i].name === name && ports[i].plug === "input") return true;
        return false;
      }

      if (node.parameters.resource) {
        const inputs = node.parameters.resource.match(/\{[A-Za-z0-9_]*\}/g);
        if (inputs) {
          for (const def of inputs) {
            const name = def.replace("{", "").replace("}", "");
            if (exists("in-" + name)) continue;

            ports.push({
              name: "in-" + name,
              displayName: name,
              type: "string",
              plug: "input",
              group: "Inputs",
            });
          }
        }
      }

      if (node.parameters["requestScript"]) {
        parseScriptForErrors(
          node.parameters["requestScript"],
          ["Inputs", "Outputs", "Request"],
          "Request script",
          node,
          context,
          ports
        );
      }

      if (node.parameters["responseScript"]) {
        parseScriptForErrors(
          node.parameters["responseScript"],
          ["Inputs", "Outputs", "Response"],
          "Response script",
          node,
          context,
          ports
        );
      }

      context.editorConnection!.sendDynamicPorts(node.id, ports);
    }

    updatePorts();
    node.on("parameterUpdated", () => {
      updatePorts();
    });
  }

  graphModel.on("editorImportComplete", () => {
    graphModel.on("nodeAdded.REST2", (node: any) => {
      managePortsForNode(node);
    });

    for (const node of graphModel.getNodesWithType("REST2")) {
      managePortsForNode(node);
    }
  });
}
