import UserService from "./userservice";
import { NodeDefinition } from "@fluxscape/runtime";

interface Internal {
  username: string;
  password: string;
  error: string;
  logInScheduled: boolean;
}

export const node: NodeDefinition<Internal> = {
  name: "net.noodl.user.LogIn",
  docs: "https://docs.noodl.net/nodes/data/user/log-in",
  displayNodeName: "Log In",
  category: "Cloud Services",
  color: "data",
  outputs: {
    success: {
      type: "signal",
      displayName: "Success",
      group: "Events",
    },
    failure: {
      type: "signal",
      displayName: "Failure",
      group: "Events",
    },
    error: {
      type: "string",
      displayName: "Error",
      group: "Error",
      getter() {
        return this._internal.error;
      },
    },
  },
  inputs: {
    login: {
      displayName: "Do",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleLogIn();
      },
    },
    username: {
      displayName: "Username",
      type: "string",
      group: "General",
      set(value: string) {
        this._internal.username = value;
      },
    },
    password: {
      displayName: "Password",
      type: "string",
      group: "General",
      set(value: string) {
        this._internal.password = value;
      },
    },
  },
  methods: {
    setError(err: string) {
      this._internal.error = err;
      this.flagOutputDirty("error");
      this.sendSignalOnOutput("failure");

      if (this.context.editorConnection) {
        this.context.editorConnection.sendWarning(
          // @ts-expect-error
          this.nodeScope.componentOwner.name,
          this.id,
          "user-login-warning",
          {
            message: err,
            showGlobally: true,
          }
        );
      }
    },
    clearWarnings() {
      if (this.context.editorConnection) {
        this.context.editorConnection.clearWarning(
          // @ts-expect-error
          this.nodeScope.componentOwner.name,
          this.id,
          "user-login-warning"
        );
      }
    },
    scheduleLogIn() {
      // @ts-expect-error
      if (this.logInScheduled === true) return;
      // @ts-expect-error
      this.logInScheduled = true;

      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.logInScheduled = false;

        UserService.instance.logIn({
          username: this._internal.username,
          password: this._internal.password,
          success: () => {
            this.sendSignalOnOutput("success");
          },
          error: (e: string) => {
            // @ts-expect-error
            this.setError(e);
          },
        });
      });
    },
  },
};
