import { GraphModel } from "@fluxscape/runtime";
import { NodeContext } from "@fluxscape/runtime/src/nodecontext";
import { NodeDefinition } from "@fluxscape/runtime/src/nodedefinition";

interface Internal {
  popupParams: any;
  closeResults: any;
  hasScheduledShow: boolean;
  target: string;
}

export const node: NodeDefinition<Internal> = {
  name: "NavigationShowPopup",
  displayNodeName: "Show Popup",
  category: "Navigation",
  docs: "https://docs.noodl.net/nodes/popups/show-popup",
  initialize() {
    this._internal.popupParams = {};
    this._internal.closeResults = {};
  },
  inputs: {
    target: {
      type: "component",
      displayName: "Target",
      group: "General",
      set(value: any) {
        this._internal.target = value;
      },
    },
    show: {
      type: "signal",
      displayName: "Show",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleShow();
      },
    },
  },
  outputs: {
    Closed: {
      type: "signal",
    },
  },
  methods: {
    setPopupParam(param: string, value: any) {
      this._internal.popupParams[param] = value;
    },
    getCloseResult(param: string) {
      return this._internal.closeResults[param];
    },
    scheduleShow() {
      const internal = this._internal;
      if (!internal.hasScheduledShow) {
        internal.hasScheduledShow = true;
        this.scheduleAfterInputsHaveUpdated(() => {
          internal.hasScheduledShow = false;
          // @ts-expect-error
          this.show();
        });
      }
    },
    show() {
      if (this._internal.target === undefined) return;

      this.context.showPopup(
        this._internal.target,
        this._internal.popupParams,
        {
          // @ts-expect-error
          senderNode: this.nodeScope.componentOwner,
          onClosePopup: (action: string | undefined, results: any) => {
            this._internal.closeResults = results;

            for (const key in results) {
              if (this.hasOutput("closeResult-" + key)) {
                this.flagOutputDirty("closeResult-" + key);
              }
            }

            if (!action) {
              this.sendSignalOnOutput("Closed");
            } else {
              this.sendSignalOnOutput(action);
            }
          },
        }
      );
    },
    registerInputIfNeeded(name: string) {
      if (this.hasInput(name)) {
        return;
      }

      if (name.startsWith("popupParam-"))
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setPopupParam.bind(
            this,
            name.substring("popupParam-".length)
          ),
        });
    },
    registerOutputIfNeeded(name: string) {
      if (this.hasOutput(name)) {
        return;
      }

      if (name.startsWith("closeResult-"))
        return this.registerOutput(name, {
          // @ts-expect-error
          getter: this.getCloseResult.bind(
            this,
            name.substring("closeResult-".length)
          ),
        });

      if (name.startsWith("closeAction-"))
        return this.registerOutput(name, {
          getter() {
            /** No needed for signals */
          },
        });
    },
  },
};

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  function _managePortsForNode(node: any) {
    function _updatePorts() {
      const ports: any[] = [];

      const targetComponentName = node.parameters["target"];
      if (targetComponentName !== undefined) {
        // @ts-expect-error
        const c = graphModel.components[targetComponentName];
        if (c) {
          for (const inputName in c.inputPorts) {
            const o = c.inputPorts[inputName];
            ports.push({
              name: "popupParam-" + inputName,
              displayName: inputName,
              type: o.type || "*",
              plug: "input",
              group: "Params",
            });
          }

          for (const _n of c.getNodesWithType("NavigationClosePopup")) {
            if (_n.parameters["closeActions"] !== undefined) {
              _n.parameters["closeActions"].split(",").forEach((a: string) => {
                if (ports.find((p) => p.name === a)) return;

                ports.push({
                  name: "closeAction-" + a,
                  displayName: a,
                  type: "signal",
                  plug: "output",
                  group: "Close Actions",
                });
              });
            }

            if (_n.parameters["results"] !== undefined) {
              _n.parameters["results"].split(",").forEach((p: string) => {
                ports.push({
                  name: "closeResult-" + p,
                  displayName: p,
                  type: "*",
                  plug: "output",
                  group: "Close Results",
                });
              });
            }
          }
        }
      }
      context.editorConnection?.sendDynamicPorts(node.id, ports);
    }

    function _trackTargetComponent(name: string | undefined) {
      if (name === undefined) return;
      // @ts-expect-error
      const c = graphModel.components[name];
      if (c === undefined) return;

      c.on("inputPortAdded", _updatePorts);
      c.on("inputPortRemoved", _updatePorts);

      for (const _n of c.getNodesWithType("NavigationClosePopup")) {
        _n.on("parameterUpdated", _updatePorts);
      }

      c.on("nodeAdded", (_n: any) => {
        if (_n.type === "NavigationClosePopup") {
          _n.on("parameterUpdated", _updatePorts);
          _updatePorts();
        }
      });

      c.on("nodeWasRemoved", (_n: any) => {
        if (_n.type === "NavigationClosePopup") _updatePorts();
      });
    }

    _updatePorts();
    _trackTargetComponent(node.parameters["target"]);

    node.on("parameterUpdated", function (event: { name: string }) {
      if (event.name === "target") {
        _updatePorts();
        _trackTargetComponent(node.parameters["target"]);
      }
    });
  }

  graphModel.on("editorImportComplete", () => {
    graphModel.on("nodeAdded.NavigationShowPopup", function (node: any) {
      _managePortsForNode(node);
    });

    for (const node of graphModel.getNodesWithType("NavigationShowPopup")) {
      _managePortsForNode(node);
    }
  });
}
