import type { GraphModel } from "./models/graphmodel";
import type { NodeContext } from "./nodecontext";
import type { InputDefinition, NodeDefinition } from "./nodedefinition";
import type { NodeScope } from "./nodescope";

export interface NodeSetup {
  metadata: NodeDefinition;
  setupNumberedInputDynamicPorts?: (
    context: NodeContext,
    graphModel: GraphModel
  ) => void;
  (context: any, id: string, nodeScope: any): any;
}

export class NodeRegister {
  private _constructors: { [key: string]: NodeSetup };
  private context: NodeContext;

  constructor(context: NodeContext) {
    this._constructors = {};
    this.context = context;
  }

  register(nodeDefinition: NodeSetup): void {
    const name = nodeDefinition.metadata.name;
    this._constructors[name] = nodeDefinition;
  }

  createNode(name: string, id: string, nodeScope: NodeScope): any {
    if (!this._constructors.hasOwnProperty(name)) {
      throw new Error("Unknown node type with name " + name);
    }

    return this._constructors[name](this.context, id, nodeScope);
  }

  getNodeMetadata(type: string): NodeDefinition {
    if (!this._constructors.hasOwnProperty(type)) {
      throw new Error("Unknown node type with name " + type);
    }

    return this._constructors[type].metadata;
  }

  hasNode(type: string): boolean {
    return this._constructors.hasOwnProperty(type);
  }

  getInputType(type: string, inputName: string): InputDefinition {
    const metadata = this.getNodeMetadata(type);
    return metadata.inputs?.[inputName]?.type;
  }
}
