export default function registerPolyfills(): void {
  if (!String.prototype.matchAll) {
    // old iPads and browsers (~2018 or earlier)
    // @ts-ignore
    String.prototype.matchAll = function matchAll(
      this: string,
      pattern: string | RegExp
    ): RegExpMatchArray[] {
      const regex = new RegExp(pattern as string, "g");
      const matches: RegExpMatchArray[] = [];

      const match_result = this.match(regex);

      if (match_result) {
        for (let index in match_result) {
          const item = match_result[index];
          matches[index] = item.match(new RegExp(pattern as string))!;
        }
      }
      return matches;
    };
  }
}
