import type { NodeContext } from "../../../nodecontext";
import type { GraphModel } from "../../../models/graphmodel";
import type { Port } from "../../../models/componentmodel";
import { NodeDefinition } from "../../../nodedefinition";

interface Internal {
  userProperties: { [key: string]: any };
  error?: string;
  email?: string;
  username?: string;
}

interface SchemaProperty {
  type: string;
}

interface Collection {
  name: string;
  schema?: {
    properties: {
      [key: string]: SchemaProperty;
    };
  };
}

export const node: NodeDefinition<Internal> = {
  name: "net.noodl.user.SetUserProperties",
  docs: "https://docs.noodl.net/nodes/data/user/set-user-properties",
  displayNodeName: "Set User Properties",
  category: "Cloud Services",
  color: "data",
  initialize() {
    const internal = this._internal;
    internal.userProperties = {};
  },
  outputs: {
    success: {
      type: "signal",
      displayName: "Success",
      group: "Events",
    },
    failure: {
      type: "signal",
      displayName: "Failure",
      group: "Events",
    },
    error: {
      type: "string",
      displayName: "Error",
      group: "Error",
      getter() {
        return this._internal.error;
      },
    },
  },
  inputs: {
    store: {
      displayName: "Do",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleStore();
      },
    },
    email: {
      displayName: "Email",
      type: "string",
      group: "General",
      set(value: string) {
        this._internal.email = value;
      },
    },
    username: {
      displayName: "Username",
      type: "string",
      group: "General",
      set(value: string) {
        this._internal.username = value;
      },
    },
  },
  methods: {
    setError(err: string) {
      this._internal.error = err;
      this.flagOutputDirty("error");
      this.sendSignalOnOutput("failure");

      if (this.context.editorConnection) {
        this.context.editorConnection.sendWarning(
          this.nodeScope!.componentOwner.name,
          this.id,
          "user-set-warning",
          {
            message: err,
            showGlobally: true,
          }
        );
      }
    },
    clearWarnings() {
      if (this.context.editorConnection) {
        this.context.editorConnection.clearWarning(
          this.nodeScope!.componentOwner.name,
          this.id,
          "user-set-warning"
        );
      }
    },
    scheduleStore() {
      const internal = this._internal;

      // @ts-expect-error
      if (this.storeScheduled === true) return;
      // @ts-expect-error
      this.storeScheduled = true;

      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.storeScheduled = false;

        // @ts-expect-error
        const UserService = NoodlRuntime.Services.UserService;
        UserService.forScope(this.nodeScope!.modelScope).setUserProperties({
          email: this._internal.email,
          username: this._internal.username,
          properties: internal.userProperties,
          success: () => {
            this.sendSignalOnOutput("success");
          },
          error: (e: string) => {
            // @ts-expect-error
            this.setError(e);
          },
        });
      });
    },
    setUserProperty(name: string, value: any) {
      this._internal.userProperties[name] = value;
    },
    registerInputIfNeeded(name: string) {
      if (this.hasInput(name)) {
        return;
      }

      if (name.startsWith("prop-"))
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setUserProperty.bind(this, name.substring("prop-".length)),
        });
    },
  },
};

function updatePorts(
  nodeId: string,
  parameters: any,
  editorConnection: any,
  systemCollections: Collection[]
) {
  const ports: Port[] = [];

  if (systemCollections) {
    // Fetch ports from collection keys
    const c = systemCollections.find((c) => c.name === "_User");
    if (c && c.schema && c.schema.properties) {
      const props = c.schema.properties;

      const _ignoreKeys =
        // @ts-expect-error
        typeof _noodl_cloud_runtime_version === "undefined"
          ? [
              "authData",
              "createdAt",
              "updatedAt",
              "email",
              "username",
              "emailVerified",
              "password",
            ]
          : ["authData", "createdAt", "updatedAt", "email", "username"];

      for (const key in props) {
        if (_ignoreKeys.indexOf(key) !== -1) continue;

        const p = props[key];
        if (ports.find((_p) => _p.name === key)) continue;

        if (p.type === "Relation") {
        } else {
          // Other schema type ports
          const _typeMap: { [key: string]: string } = {
            String: "string",
            Boolean: "boolean",
            Number: "number",
            Date: "date",
          };

          ports.push({
            type: {
              name: _typeMap[p.type] ? _typeMap[p.type] : "*",
            },
            plug: "input",
            group: "Properties",
            name: "prop-" + key,
            displayName: key,
          });
        }
      }
    }
  }

  editorConnection.sendDynamicPorts(nodeId, ports);
}

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  function _managePortsForNode(node: any) {
    updatePorts(
      node.id,
      node.parameters,
      context.editorConnection,
      graphModel.getMetaData("systemCollections")
    );

    node.on("parameterUpdated", function (event: any) {
      updatePorts(
        node.id,
        node.parameters,
        context.editorConnection,
        graphModel.getMetaData("systemCollections")
      );
    });

    graphModel.on("metadataChanged.systemCollections", function (data: any) {
      updatePorts(node.id, node.parameters, context.editorConnection, data);
    });
  }

  graphModel.on("editorImportComplete", () => {
    graphModel.on(
      "nodeAdded.net.noodl.user.SetUserProperties",
      function (node: any) {
        _managePortsForNode(node);
      }
    );

    for (const node of graphModel.getNodesWithType(
      "net.noodl.user.SetUserProperties"
    )) {
      _managePortsForNode(node);
    }
  });
}
