import type { NodeDefinition } from "../../nodedefinition";

interface Internal {
  inputs: boolean[];
  result?: boolean;
}

function and(values: boolean[]): boolean {
  //if none are false, then return true
  return values.length > 0 && values.some((v) => !v) === false;
}

export const node: NodeDefinition<Internal> = {
  name: "And",
  docs: "https://docs.noodl.net/nodes/logic/and",
  category: "Logic",
  initialize() {
    this._internal = {
      inputs: [],
    };
  },
  getInspectInfo() {
    return and(this._internal.inputs);
  },
  numberedInputs: {
    input: {
      displayPrefix: "Input",
      type: "boolean",
      createSetter(index: number) {
        return function (
          this: {
            _internal: Internal;
            flagOutputDirty: (name: string) => void;
          },
          value: unknown
        ) {
          const boolValue = value ? true : false;

          if (this._internal.inputs[index] === boolValue) {
            return;
          }

          this._internal.inputs[index] = boolValue;
          const result = and(this._internal.inputs);

          if (this._internal.result !== result) {
            this._internal.result = result;
            this.flagOutputDirty("result");
          }
        };
      },
    },
  },
  outputs: {
    result: {
      type: "boolean",
      displayName: "Result",
      get() {
        return this._internal.result;
      },
    },
  },
};
