import { Transition } from "./transition";

export class NoneTransition extends Transition {
  from: any;
  to: any;

  constructor(from: any, to: any, params: any) {
    super();

    this.from = from;
    this.to = to;

    this.timing = { dur: 0, delay: 0 };
  }

  update(t: number): void {}

  forward(t: number): void {}

  back(t: number): void {}

  static ports(): any[] {
    return [];
  }
}
