import { Model } from "./model";
import { Port, PortType } from "./models/componentmodel";
import { getAbsoluteUrl } from "./utils";

interface UserObject {
  inputs?: Record<string, any>;
  outputs?: Record<string, any>;
  setup?: Function;
  run?: Function;
  change?: Function;
  destroy?: Function;
  methods?: Record<string, Function>;
  signals?: Record<string, Function>;
  changed?: Record<string, Function>;
}

interface NodeAPI {
  Inputs: Record<string, any>;
  Outputs: Record<string, any>;
  Signals: Record<string, Function>;
  Setters: Record<string, Function>;
  OnInit?: Function;
  OnDestroy?: Function;
  OnInputsChanged?: Function;
  setOutputs?: (outputs: Record<string, any>) => void;
}

interface ParserOptions {
  node?: any;
}

interface PortOptions {
  skipInputs?: boolean;
  skipOutputs?: boolean;
  skipOutputSignals?: boolean;
  skipRegularOutputs?: boolean;
  inputType?: PortType;
  inputGroup?: string;
  inputPrefix?: string;
  outputPrefix?: string;
}

const userFunctionsCache: Record<string, Function> = {};

export class JavascriptNodeParser {
  inputs: Record<string, any>;
  outputs: Record<string, any>;
  error?: string;
  code: string;
  // @ts-ignore
  apis: {
    Node: NodeAPI;
  };
  setup?: Function;
  change?: Function;
  destroy?: Function;
  definedObject?: any;

  constructor(code: string, options?: ParserOptions) {
    this.inputs = {};
    this.outputs = {};
    this.code = code;

    const node = options ? options.node : undefined;
    this._initializeAPIs();

    let userCode = userFunctionsCache[code];
    if (!userCode) {
      try {
        userCode = new Function(
          // @ts-ignore
          ["define", "script", "Node", "Component"],
          JavascriptNodeParser.getCodePrefix() + code
        );
        userFunctionsCache[code] = userCode;
      } catch (e: unknown) {
        this.error = e instanceof Error ? e.message : String(e);
        console.error(e);
      }
    }
    if (userCode) {
      try {
        userCode(
          this.define.bind(this),
          this.script.bind(this),
          // @ts-expect-error
          this.apis.Node,
          node ? JavascriptNodeParser.getComponentScopeForNode(node) : {}
        );

        this._afterSourced();
      } catch (e: unknown) {
        this.error = e instanceof Error ? e.message : String(e);
        console.error(e);
      }
    }
  }

  define(userObject: UserObject): void {
    this.inputs = userObject.inputs || {};
    this.outputs = userObject.outputs || {};
    this.setup = userObject.setup;
    this.change = userObject.run || userObject.change;
    this.destroy = userObject.destroy;

    this.definedObject = userObject;
  }

  script(userObject: UserObject): void {
    const _userObjectExtended = _scriptExtend(userObject);
    this.inputs = _userObjectExtended.inputs || {};
    this.outputs = _userObjectExtended.outputs || {};
    this.setup = _userObjectExtended.setup;
    this.change = _userObjectExtended.run || _userObjectExtended.change;
    this.destroy = _userObjectExtended.destroy;

    this.definedObject = _userObjectExtended;
  }

  _initializeAPIs(): void {
    this.apis = {
      Node: {
        Inputs: {},
        Outputs: {},
        Signals: {},
        Setters: {},
      },
    };
  }

  _afterSourced(): void {
    if (this.definedObject !== undefined) return;

    const _Node = this.apis.Node;

    this.inputs = Object.assign({}, _Node.Inputs || {});
    this.outputs = Object.assign({}, _Node.Outputs || {});

    this.setup = function (
      inputs: Record<string, any>,
      outputs: Record<string, any>
    ) {
      const _this = this;

      _Node.setOutputs = function (_outputs: Record<string, any>) {
        for (const key in _outputs) {
          outputs[key] = _outputs[key];
          // @ts-ignore
          _this.flagOutputDirty(key);
        }
      };

      _Node.OnInit && _Node.OnInit.apply(this);
    };

    this.destroy = _Node.OnDestroy || this.destroy;
    this.change = (
      inputs: Record<string, any>,
      outputs: Record<string, any>,
      changedInputs: Record<string, any>
    ) => {
      for (const key in changedInputs) {
        if (typeof _Node.Setters[key] === "function") {
          _Node.Setters[key](inputs[key]);
        }
      }

      if (typeof _Node.OnInputsChanged === "function") {
        _Node.OnInputsChanged();
      }
    };

    this.definedObject = {
      inputs: this.inputs,
      outputs: this.outputs,
      setup: this.setup,
      destroy: this.destroy,
      change: this.change,
    };

    if (_Node.Signals !== undefined) {
      for (const key in _Node.Signals) {
        if (typeof _Node.Signals[key] === "function") {
          this.inputs[key] = "signal";
          this.definedObject[key] = _Node.Signals[key];
        }
      }
    }
  }

  getPorts(): Port[] {
    const ports: Port[] = [];

    Object.keys(this.inputs).forEach((name) => {
      const inputPort = this.inputs[name];

      const port: Port = {
        name,
        plug: "input",
        type: undefined,
        group: "Inputs",
      };

      if (typeof inputPort === "string") {
        port.type = {
          name: inputPort,
        };
      } else {
        Object.assign(port, inputPort);
      }

      ports.push(port);
    });

    Object.keys(this.outputs).forEach((name) => {
      ports.push({
        name,
        type: {
          name: this.outputs[name],
        },
        plug: "output",
        group: "Outputs",
      });
    });

    JavascriptNodeParser.parseAndAddPortsFromScript(this.code, ports, {});

    return ports;
  }

  static createFromCode(
    code: string,
    options?: ParserOptions
  ): JavascriptNodeParser {
    return new JavascriptNodeParser(code, options);
  }

  static createFromURL(
    url: string,
    callback: (parser: JavascriptNodeParser) => void,
    options?: ParserOptions
  ): void {
    url = getAbsoluteUrl(url);

    const xhr = new XMLHttpRequest();
    xhr.open("GET", url, true);
    xhr.onreadystatechange = function () {
      if (this.readyState === 4 || this.readyState === XMLHttpRequest.DONE) {
        callback(new JavascriptNodeParser(this.response));
      }
    };
    xhr.onerror = function () {
      console.log("Failed to request", url);
    };
    xhr.send();
  }

  static parseAndAddPortsFromScript(
    script: string,
    ports: Port[],
    options: PortOptions
  ): void {
    function _exists(port: string | { name: string; plug: string }): boolean {
      if (typeof port === "string")
        return ports.find((p) => p.name === port) !== undefined;
      else
        return (
          ports.find((p) => p.name === port.name && p.plug === port.plug) !==
          undefined
        );
    }

    function _addPortsFromMatch(
      match: IterableIterator<RegExpMatchArray>,
      options: {
        type: PortType;
        plug: Port["plug"];
        group: string;
        prefix: string;
      }
    ): void {
      if (match === undefined || match === null) return;

      const unique: Record<string, boolean> = {};
      for (const _s of match) {
        let name = _s[1];
        if (name === undefined) continue;

        unique[name] = true;
      }

      Object.keys(unique).forEach((p) => {
        if (
          _exists({
            name: options.prefix + p,
            plug: options.plug,
          })
        )
          return;

        ports.push({
          name: options.prefix + p,
          displayName: p,
          plug: options.plug,
          type: options.type,
          group: options.group,
        });
      });
    }

    if (!options.skipInputs) {
      _addPortsFromMatch(script.matchAll(/Inputs\.([A-Za-z0-9_]+)/g), {
        type: options.inputType || "*",
        plug: "input",
        group: options.inputGroup || "Inputs",
        prefix: options.inputPrefix || "",
      });

      _addPortsFromMatch(
        script.matchAll(/Inputs\s*\[\s*(?:'|")(.*)(?:'|")\s*\]/g),
        {
          type: options.inputType || "*",
          plug: "input",
          group: options.inputGroup || "Inputs",
          prefix: options.inputPrefix || "",
        }
      );
    }

    if (!options.skipOutputs) {
      if (!options.skipOutputSignals) {
        _addPortsFromMatch(
          script.matchAll(/Outputs\.([A-Za-z0-9]+)\s*\(\s*\)/g),
          {
            type: "signal",
            plug: "output",
            group: "Outputs",
            prefix: options.outputPrefix || "",
          }
        );

        _addPortsFromMatch(
          script.matchAll(/Outputs\s*\[\s*(?:'|")(.*)(?:'|")\s*\]\(\s*\)/g),
          {
            type: "signal",
            plug: "output",
            group: "Outputs",
            prefix: options.outputPrefix || "",
          }
        );
      }

      if (!options.skipRegularOutputs) {
        _addPortsFromMatch(script.matchAll(/Outputs\.([A-Za-z0-9_]+)/g), {
          type: "*",
          plug: "output",
          group: "Outputs",
          prefix: options.outputPrefix || "",
        });

        _addPortsFromMatch(
          script.matchAll(/Outputs\s*\[\s*\"([^\"]*)\"\s*\]/g),
          {
            type: "*",
            plug: "output",
            group: "Outputs",
            prefix: options.outputPrefix || "",
          }
        );
      }
    }
  }

  static getCodePrefix(): string {
    return "const Script = (typeof Node !== 'undefined')?Node:undefined;\n";
  }

  static createNoodlAPI(): any {
    return typeof window !== "undefined" && (window as any).Noodl !== undefined
      ? (window as any).Noodl
      : {};
  }

  static getComponentScopeForNode(node: any): any {
    const componentId = node.nodeScope.componentOwner.getInstanceId();

    if (_componentScopes[componentId] === undefined) {
      _componentScopes[componentId] = {};

      const componentObject = (node.nodeScope.modelScope || Model).get(
        "componentState" + componentId
      );

      _componentScopes[componentId].Object = componentObject;
    }

    const parentComponentObjectId = _findParentComponentStateModelId(node);
    const parentComponentObject =
      parentComponentObjectId !== undefined
        ? (node.nodeScope.modelScope || Model).get(parentComponentObjectId)
        : undefined;

    _componentScopes[componentId].ParentObject = parentComponentObject;

    _componentScopes[componentId].RepeaterObject = _findForEachModel(node);

    return _componentScopes[componentId];
  }
}

function _scriptExtend(userObject: UserObject): any {
  const _extended = {
    inputs: userObject.inputs || {},
    outputs: userObject.outputs || {},

    setup: function (
      inputs: Record<string, any>,
      outputs: Record<string, any>
    ) {
      this.inputs = inputs;
      this.outputs = outputs;

      // @ts-expect-error
      this.setOutputs = function (states: Record<string, any>) {
        for (const key in states) {
          this.outputs[key] = states[key];
          // @ts-ignore
          this.flagOutputDirty(key);
        }
      };

      if (userObject.methods) {
        for (const key in userObject.methods) {
          // @ts-ignore
          this[key] = userObject.methods[key];
        }
      }

      userObject.setup && userObject.setup.apply(this);
    },

    destroy: function (
      inputs: Record<string, any>,
      outputs: Record<string, any>
    ) {
      this.inputs = inputs;
      this.outputs = outputs;

      userObject.destroy && userObject.destroy.apply(this);
    },

    change: function (
      inputs: Record<string, any>,
      outputs: Record<string, any>
    ) {
      this.inputs = inputs;
      this.outputs = outputs;

      // @ts-ignore
      const old = this._oldInputs || {};

      if (userObject.changed) {
        for (const key in inputs) {
          if (inputs[key] !== old[key]) {
            const changedFunction = userObject.changed[key];
            if (typeof changedFunction === "function")
              changedFunction.apply(this, [inputs[key], old[key]]);
          }
        }
      }

      // @ts-ignore
      this._oldInputs = Object.assign({}, inputs);
    },
  };

  if (userObject.signals) {
    for (const key in userObject.signals) {
      // @ts-ignore
      _extended[key] = userObject.signals[key];
      _extended.inputs[key] = "signal";
    }
  }

  return _extended;
}

const _componentScopes: Record<string, any> = {};

function _findParentComponentStateModelId(node: any): string | undefined {
  function getParentComponent(component: any): any {
    let parent;
    if (component.getRoots().length > 0) {
      const root = component.getRoots()[0];

      if (root.getVisualParentNode) {
        if (root.getVisualParentNode()) {
          parent = root.getVisualParentNode().nodeScope.componentOwner;
        }
      } else if (root.parentNodeScope) {
        parent = component.parentNodeScope.componentOwner;
      }
    } else if (component.parentNodeScope) {
      parent = component.parentNodeScope.componentOwner;
    }

    if (
      parent &&
      parent.nodeScope &&
      parent.nodeScope.componentOwner !== component
    ) {
      if (
        parent.nodeScope.getNodesWithType("net.noodl.ComponentObject").length >
        0
      ) {
        return parent;
      }

      if (parent.nodeScope.getNodesWithType("Component State").length > 0) {
        return parent;
      }

      return getParentComponent(parent);
    }
  }

  const parent = getParentComponent(node.nodeScope.componentOwner);
  if (!parent) return;

  return "componentState" + parent.getInstanceId();
}

function _findForEachModel(node: any): any {
  let component = node.nodeScope.componentOwner;
  while (
    component !== undefined &&
    component._forEachModel === undefined &&
    component.parentNodeScope
  ) {
    component = component.parentNodeScope.componentOwner;
  }
  return component !== undefined ? component._forEachModel : undefined;
}
