import type { NodeDefinition } from "../../nodedefinition";

interface Internal {
  inputs: boolean[];
}

function isTrue(value: boolean): boolean {
  return value ? true : false;
}

export const node: NodeDefinition<Internal> = {
  name: "Or",
  docs: "https://docs.noodl.net/nodes/logic/or",
  category: "Logic",
  initialize() {
    this._internal.inputs = [];
  },
  getInspectInfo(): boolean {
    return this._internal.inputs.some(isTrue);
  },
  numberedInputs: {
    input: {
      type: "boolean",
      displayPrefix: "Input",
      createSetter(index: number) {
        return function (value: boolean): void {
          if (this._internal.inputs[index] === value) {
            return;
          }

          this._internal.inputs[index] = value;
          this.flagOutputDirty("result");
        };
      },
    },
  },
  outputs: {
    result: {
      type: "boolean",
      displayName: "Result",
      getter: function (): boolean {
        return this._internal.inputs.some(isTrue);
      },
    },
  },
};
