import { NodeDefinition } from "@fluxscape/runtime";

interface Internal {
  _currentInputValue: number;
  _remappedValue: number;
  _minInputValue: number;
  _maxInputValue: number;
  _minOutputValue: number;
  _maxOutputValue: number;
  _clampOutput: boolean;
}

export const node: NodeDefinition<Internal> = {
  name: "Number Remapper",
  docs: "https://docs.noodl.net/nodes/math/number-remapper",
  category: "Math",
  initialize() {
    const internal = this._internal;
    internal._currentInputValue = 0;
    internal._remappedValue = 0;
    internal._minInputValue = 0;
    internal._maxInputValue = 0;
    internal._minOutputValue = 0;
    internal._maxOutputValue = 1;
    internal._clampOutput = true;
  },
  getInspectInfo() {
    return this._internal._remappedValue;
  },
  inputs: {
    inputValue: {
      group: "Value to Remap",
      type: {
        name: "number",
        allowConnectionOnly: true,
      },
      default: 0,
      displayName: "Input Value",
      set(value: number) {
        this._internal._currentInputValue = value;
        // @ts-expect-error
        this._calculateNewOutputValue();
      },
    },
    minInputValue: {
      group: "Input Parameters",
      type: {
        name: "number",
      },
      default: 0,
      displayName: "Input Minimum",
      set(value: number) {
        this._internal._minInputValue = value;
        // @ts-expect-error
        this._calculateNewOutputValue();
      },
    },
    maxInputValue: {
      group: "Input Parameters",
      type: {
        name: "number",
      },
      default: 0,
      displayName: "Input Maximum",
      set(value: number) {
        this._internal._maxInputValue = value;
        // @ts-expect-error
        this._calculateNewOutputValue();
      },
    },
    minOutputValue: {
      group: "Output Parameters",
      type: {
        name: "number",
      },
      default: 0,
      displayName: "Output Minimum",
      set(value: number) {
        this._internal._minOutputValue = value;
        // @ts-expect-error
        this._calculateNewOutputValue();
      },
    },
    maxOutputValue: {
      group: "Output Parameters",
      type: {
        name: "number",
      },
      default: 1,
      displayName: "Output Maximum",
      set(value: number) {
        this._internal._maxOutputValue = value;
        // @ts-expect-error
        this._calculateNewOutputValue();
      },
    },
    clamp: {
      group: "Output Parameters",
      type: {
        name: "boolean",
        allowEditOnly: true,
      },
      default: true,
      displayName: "Clamp Output",
      set(value: boolean) {
        this._internal._clampOutput = value;
        // @ts-expect-error
        this._calculateNewOutputValue();
      },
    },
  },
  outputs: {
    remappedValue: {
      type: "number",
      displayName: "Remapped Value",
      group: "Outputs",
      getter() {
        return this._internal._remappedValue;
      },
    },
  },
  prototypeExtensions: {
    _calculateNewOutputValue() {
      let normalizedValue: number;
      const internal = this._internal;

      if (internal._maxInputValue === internal._minInputValue) {
        normalizedValue = 0;
      } else {
        normalizedValue =
          (internal._currentInputValue - internal._minInputValue) /
          (internal._maxInputValue - internal._minInputValue);
      }

      if (internal._clampOutput) {
        normalizedValue = Math.max(0, Math.min(1, normalizedValue));
      }
      internal._remappedValue =
        internal._minOutputValue +
        normalizedValue * (internal._maxOutputValue - internal._minOutputValue);
      this.flagOutputDirty("remappedValue");
    },
  },
};
