import { GraphModel, NodeContext, NodeDefinition } from "@fluxscape/runtime";

interface Internal {
  outputValues: Record<string, any>;
  outputNames: string[];
  eventReceived: boolean;
  _isEnabled: boolean;
  channelName: string;
  consume: string;
  onEventReceivedCallback?: (eventData: any) => void | null;
}

export const node: NodeDefinition<Internal> = {
  name: "Event Receiver",
  docs: "https://docs.noodl.net/nodes/events/receive-event",
  displayNodeName: "Receive Event",
  category: "Events",
  usePortAsLabel: "channelName",
  color: "component",
  initialize() {
    this._internal.outputValues = {};
    this._internal.outputNames = [];
    this._internal.eventReceived = false;
    this._internal._isEnabled = true;
    this._internal.channelName = "";
  },
  inputs: {
    enabled: {
      displayName: "Enabled",
      type: "boolean",
      default: true,
      set(value) {
        this._internal._isEnabled = value;
      },
    },
    consume: {
      displayName: "Consume",
      type: {
        name: "enum",
        enums: [
          { label: "Never", value: "never" },
          { label: "Always", value: "always" },
        ],
      },
      default: "never",
      set(value) {
        this._internal.consume = value;
      },
    },
    channelName: {
      type: { name: "string", identifierOf: "EventChannelName" },
      displayName: "Channel",
      set(value) {
        if (this._internal.onEventReceivedCallback) {
          //remove old listener
          this.context.eventSenderEmitter.removeListener(
            this._internal.channelName,
            this._internal.onEventReceivedCallback
          );
          // @ts-expect-error
          this._internal.onEventReceivedCallback = null;
        }

        this._internal.channelName = value;
        // @ts-expect-error
        this.registerListenersForChannel(value);
      },
    },
  },
  outputs: {
    eventReceived: {
      displayName: "Received",
      type: "signal",
    },
  },
  prototypeExtensions: {
    registerOutputIfNeeded(name) {
      if (this.hasOutput(name)) {
        return;
      }

      this._internal.outputNames.push(name);
      this.registerOutput(name, {
        getter: () => this._internal.outputValues[name],
      });
    },
    // @ts-expect-error
    handleEvent(eventData) {
      if (!this._internal._isEnabled) {
        return;
      }
      this.sendSignalOnOutput("eventReceived");

      for (const name in eventData) {
        if (this.hasOutput(name)) {
          this._internal.outputValues[name] = eventData[name];
          this.flagOutputDirty(name);
        }
      }

      return this._internal.consume === "always";
    },
    onEventReceived(eventData) {
      // @ts-expect-error
      this.handleEvent(eventData);
    },
    _onNodeDeleted() {
      // @ts-expect-error
      Node.prototype._onNodeDeleted.call(this);
      if (this._internal.onEventReceivedCallback) {
        const eventEmitter = this.context.eventSenderEmitter;
        eventEmitter.removeListener(
          this._internal.channelName,
          this._internal.onEventReceivedCallback
        );
      }
    },
    registerListenersForChannel(channelName) {
      const eventEmitter = this.context.eventSenderEmitter;
      // @ts-expect-error
      this._internal.onEventReceivedCallback = this.onEventReceived.bind(this);
      // @ts-expect-error
      eventEmitter.on(channelName, this._internal.onEventReceivedCallback);

      this.context.eventEmitter.once("applicationDataReloaded", () => {
        if (this._internal.onEventReceivedCallback) {
          eventEmitter.removeListener(
            channelName,
            this._internal.onEventReceivedCallback
          );
        }
      });
    },
    // @ts-expect-error
    getChannelName() {
      return this._internal.channelName;
    },
  },
};

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  function onEventReceiver(node) {
    let channelName = node.parameters.channelName;

    function _collectPayloadPorts() {
      const eventSenders = graphModel.getNodesWithType("Event Sender");
      const matching = eventSenders.filter(
        (node) => node.parameters.channelName === channelName
      );

      const portKeys: Record<string, boolean> = {};
      matching.forEach((node) => {
        const ports = node.parameters.payload
          ? node.parameters.payload.split(",")
          : [];
        for (const key of ports) {
          portKeys[key] = true;
        }
      });

      const ports = Object.keys(portKeys).map((key) => ({
        name: key,
        type: "*",
        plug: "output",
        displayName: key,
      }));

      context.editorConnection?.sendDynamicPorts(node.id, ports, {
        detectRenamed: {
          plug: "output",
        },
      });
    }

    _collectPayloadPorts();
    node.on("parameterUpdated", function (event) {
      if (event.name === "channelName") {
        channelName = event.value;
        _collectPayloadPorts();
      }
    });

    function _trackEventSender(node) {
      node.on("inputPortAdded", _collectPayloadPorts);
      node.on("inputPortRemoved", _collectPayloadPorts);
      node.on("parameterUpdated", function (event) {
        if (event.name === "channelName") {
          _collectPayloadPorts();
        }
      });
    }

    graphModel.getNodesWithType("Event Sender").forEach(_trackEventSender);
    graphModel.on("nodeAdded.Event Sender", _trackEventSender);
    graphModel.on("nodeRemoved.Event Sender", _collectPayloadPorts);
  }

  graphModel.on("editorImportComplete", () => {
    graphModel.on("nodeAdded.Event Receiver", onEventReceiver);
    graphModel.getNodesWithType("Event Receiver").forEach(onEventReceiver);
  });
}
