import { Collection } from "../../../collection";
import { Model } from "../../../model";

interface NodeDefinition {
  node: {
    name?: string;
    category?: string;
    color?: string;
    inputs?: Record<string, any>;
    outputs?: Record<string, any>;
    methods?: Record<string, any>;
    dynamicports?: any[];
    usePortAsLabel?: string;
    initialize?: () => void;
    getInspectInfo?: () => any;
  };
  setup?: (context: any, graphModel: any) => void;
}

interface Internal {
  modelId?: string;
  model?: typeof Model;
  inputValues: Record<string, any>;
  inputTypes: Record<string, string>;
}

function _addBaseInfo(def: NodeDefinition): void {
  Object.assign(def.node, {
    category: "Data",
    color: "data",
  });
}

function _addModelId(
  def: NodeDefinition,
  opts?: { includeInputs?: boolean; includeOutputs?: boolean }
): void {
  const _includeInputs = opts === undefined || opts.includeInputs;
  const _includeOutputs = opts === undefined || opts.includeOutputs;

  Object.assign(def.node, {
    inputs: def.node.inputs || {},
    outputs: def.node.outputs || {},
    methods: def.node.methods || {},
  });

  if (_includeInputs) {
    Object.assign(def.node, {
      usePortAsLabel: "modelId",
    });

    def.node.dynamicports = (def.node.dynamicports || []).concat([
      {
        name: "conditionalports/extended",
        condition: "idSource = explicit OR idSource NOT SET",
        inputs: ["modelId"],
      },
    ]);

    // Inputs
    // @ts-expect-error
    Object.assign(def.node.inputs, {
      idSource: {
        type: {
          name: "enum",
          enums: [
            { label: "Specify explicitly", value: "explicit" },
            { label: "From repeater", value: "foreach" },
          ],
          allowEditOnly: true,
        },
        default: "explicit",
        displayName: "Id Source",
        group: "General",
        set(value: string) {
          if (value === "foreach") {
            // @ts-expect-error
            this.scheduleAfterInputsHaveUpdated(() => {
              // Find closest nodescope that have a _forEachModel
              // @ts-expect-error
              let component = this.nodeScope.componentOwner;
              while (
                component !== undefined &&
                component._forEachModel === undefined &&
                component.parentNodeScope
              ) {
                component = component.parentNodeScope.componentOwner;
              }
              // @ts-expect-error
              this.setModel(
                component !== undefined ? component._forEachModel : undefined
              );
            });
          }
        },
      },
      modelId: {
        type: {
          name: "string",
          identifierOf: "ModelName",
          identifierDisplayName: "Object Ids",
        },
        displayName: "Id",
        group: "General",
        set: function (value: string | typeof Model): void {
          if (value instanceof Model) value = value.getId(); // Can be passed as model as well
          // @ts-expect-error
          this._internal.modelId = value; // Wait to fetch data
          // @ts-expect-error
          this.setModelID(value);
        },
      },
    });
  }

  // Outputs
  if (_includeOutputs) {
    // @ts-expect-error
    Object.assign(def.node.outputs, {
      id: {
        type: "string",
        displayName: "Id",
        group: "General",
        getter: function (): string {
          // @ts-expect-error
          return this._internal.model
            ? // @ts-expect-error
              this._internal.model.getId()
            : // @ts-expect-error
              this._internal.modelId;
        },
      },
    });
  }

  // Methods
  // @ts-expect-error
  Object.assign(def.node.methods, {
    setModelID: function (id: string): void {
      // @ts-expect-error
      const model = (this.nodeScope.modelScope || Model).get(id);
      // @ts-expect-error
      this.setModel(model);
    },
    setModel: function (model: typeof Model): void {
      // @ts-expect-error
      this._internal.model = model;
      // @ts-expect-error
      this.flagOutputDirty("id");
    },
  });

  //Inspect model
  if (!def.node.getInspectInfo) {
    def.node.getInspectInfo = function (): any[] | string {
      // @ts-expect-error
      const model = this._internal.model;
      if (!model) return "[No Object]";

      return [
        { type: "text", value: "Id: " + model.getId() },
        { type: "value", value: model.data },
      ];
    };
  }
}

function _addInputProperties(def: NodeDefinition): void {
  const _def: NodeDefinition = {
    node: Object.assign({}, def.node),
    setup: def.setup,
  };
  const _methods = Object.assign({}, def.node.methods);

  Object.assign(def.node, {
    inputs: def.node.inputs || {},
    outputs: def.node.outputs || {},
    methods: def.node.methods || {},
  });

  Object.assign(def, {
    setup: function (context: any, graphModel: any): void {
      if (
        !context.editorConnection ||
        !context.editorConnection.isRunningLocally()
      ) {
        return;
      }

      graphModel.on("nodeAdded." + def.node.name, function (node: any) {
        function _updatePorts(): void {
          const ports: any[] = [];

          const _types = [
            { label: "String", value: "string" },
            { label: "Boolean", value: "boolean" },
            { label: "Number", value: "number" },
            { label: "Date", value: "date" },
            { label: "Array", value: "array" },
            { label: "Object", value: "object" },
            { label: "Any", value: "*" },
          ];

          // Add value outputs
          let properties = node.parameters.properties;
          if (properties) {
            properties = properties ? properties.split(",") : undefined;
            for (const p of properties) {
              // Property input
              ports.push({
                type: {
                  name:
                    node.parameters["type-" + p] === undefined
                      ? "*"
                      : node.parameters["type-" + p],
                },
                plug: "input",
                group: "Property Values",
                displayName: p,
                name: "prop-" + p,
              });

              // Property type
              ports.push({
                type: {
                  name: "enum",
                  enums: _types,
                  allowEditOnly: true,
                },
                plug: "input",
                group: "Property Types",
                displayName: p,
                default: "*",
                name: "type-" + p,
              });
            }
          }

          context.editorConnection.sendDynamicPorts(node.id, ports, {
            detectRenamed: {
              plug: "input",
            },
          });
        }

        _updatePorts();

        node.on("parameterUpdated", function () {
          _updatePorts();
        });
      });

      _def.setup && _def.setup(context, graphModel);
    },
  });

  // Initialize
  Object.assign(def.node, {
    initialize: function (): void {
      // @ts-expect-error
      const internal = this._internal as Internal;
      internal.inputValues = {};
      internal.inputTypes = {};

      _def.node.initialize && _def.node.initialize.call(this);
    },
  });

  // Outputs
  // @ts-expect-error
  Object.assign(def.node.outputs, {});

  // Inputs
  // @ts-expect-error
  Object.assign(def.node.inputs, {
    properties: {
      type: { name: "stringlist", allowEditOnly: true },
      displayName: "Properties",
      group: "Properties to set",
      set: function (): void {},
    },
  });

  // Methods
  // @ts-expect-error
  Object.assign(def.node.methods, {
    _pushInputValues: function (model: typeof Model): void {
      // @ts-expect-error
      const internal = this._internal as Internal;

      const _defaultValueForType: Record<string, any> = {
        boolean: false,
        string: "",
        number: 0,
        date: new Date(),
      };

      const _allKeys: Record<string, boolean> = {};
      for (const key in internal.inputTypes) _allKeys[key] = true;
      for (const key in internal.inputValues) _allKeys[key] = true;

      // @ts-expect-error
      const properties = this.model.parameters.properties || "";

      const validProperties = properties.split(",");

      const keysToSet = Object.keys(_allKeys).filter(
        (key) => validProperties.indexOf(key) !== -1
      );

      for (const i of keysToSet) {
        let value = internal.inputValues[i];

        if (value !== undefined) {
          //Parse array types with string as javascript
          if (
            internal.inputTypes[i] !== undefined &&
            internal.inputTypes[i] === "array" &&
            typeof value === "string"
          ) {
            // @ts-expect-error
            this.context.editorConnection.clearWarning(
              // @ts-expect-error
              this.nodeScope.componentOwner.name,
              // @ts-expect-error
              this.id,
              "invalid-array-" + i
            );

            try {
              value = eval(value); //this might be static data in the form of javascript
            } catch (e) {
              if (value.indexOf("[") !== -1 || value.indexOf("{") !== -1) {
                // @ts-expect-error
                this.context.editorConnection.sendWarning(
                  // @ts-expect-error
                  this.nodeScope.componentOwner.name,
                  // @ts-expect-error
                  this.id,
                  "invalid-array-" + i,
                  {
                    showGlobally: true,
                    // @ts-expect-error
                    message: "Invalid array<br>" + e.toString(),
                  }
                );
                value = [];
              } else {
                //backwards compatibility with how this node used to work
                value = Collection.get(value);
              }
            }
          }
          // Resolve object from IDs
          if (
            internal.inputTypes[i] !== undefined &&
            internal.inputTypes[i] === "object" &&
            typeof value === "string"
          ) {
            // @ts-expect-error
            value = (this.nodeScope.modelScope || Model).get(value);
          }

          // @ts-expect-error
          model.set(i, value, { resolve: true });
        } else {
          // @ts-expect-error
          model.set(i, _defaultValueForType[internal.inputTypes[i]], {
            resolve: true,
          });
        }
      }
    },
    scheduleStore: function (): void {
      // @ts-expect-error
      if (this.hasScheduledStore) return;
      // @ts-expect-error
      this.hasScheduledStore = true;

      // @ts-expect-error
      const internal = this._internal as Internal;
      // @ts-expect-error
      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.hasScheduledStore = false;
        if (!internal.model) return;

        // @ts-expect-error
        this._pushInputValues(internal.model);

        // @ts-expect-error
        this.sendSignalOnOutput("stored");
      });
    },
    registerInputIfNeeded: function (name: string): void {
      // @ts-expect-error
      if (this.hasInput(name)) {
        return;
      }

      if (name.startsWith("prop-"))
        // @ts-expect-error
        this.registerInput(name, {
          // @ts-expect-error
          set: this._setInputValue.bind(this, name.substring("prop-".length)),
        });

      if (name.startsWith("type-"))
        // @ts-expect-error
        this.registerInput(name, {
          // @ts-expect-error
          set: this._setInputType.bind(this, name.substring("type-".length)),
        });

      _methods &&
        _methods.registerInputIfNeeded &&
        // @ts-expect-error
        _def.node.methods.registerInputIfNeeded.call(this, name);
    },
    _setInputValue: function (name: string, value: any): void {
      // @ts-expect-error
      (this._internal as Internal).inputValues[name] = value;
    },
    _setInputType: function (name: string, value: string): void {
      // @ts-expect-error
      (this._internal as Internal).inputTypes[name] = value;
    },
  });
}

export {
  _addInputProperties as addInputProperties,
  _addModelId as addModelId,
  _addBaseInfo as addBaseInfo,
};
