import { Model } from "../../../model";
import * as ModelCRUDBase from "./modelcrudbase";
import { NodeDefinition } from "../../../nodedefinition";

interface Internal {
  hasScheduledNew?: boolean;
  nodeScope: {
    modelScope?: typeof Model;
  };
  _pushInputValues: (model: any) => void;
  setModel: (model: any) => void;
  scheduleAfterInputsHaveUpdated: (callback: () => void) => void;
  sendSignalOnOutput: (name: string) => void;
}

const NewModelNodeDefinition: {
  node: NodeDefinition<Internal>;
} = {
  node: {
    name: "NewModel",
    category: "",
    docs: "https://docs.noodl.net/nodes/data/object/create-new-object",
    displayNodeName: "Create New Object",
    inputs: {
      new: {
        displayName: "Do",
        group: "Actions",
        valueChangedToTrue() {
          // @ts-expect-error
          this.scheduleNew();
        },
      },
    },
    outputs: {
      created: {
        type: "signal",
        displayName: "Done",
        group: "Events",
      },
    },
    methods: {
      scheduleNew() {
        // @ts-expect-error
        if (this.hasScheduledNew) return;
        // @ts-expect-error
        this.hasScheduledNew = true;

        this.scheduleAfterInputsHaveUpdated(() => {
          // @ts-expect-error
          this.hasScheduledNew = false;
          const newModel = (this.nodeScope!.modelScope || Model).get();

          // @ts-expect-error
          this._pushInputValues(newModel);

          // @ts-expect-error
          this.setModel(newModel);

          this.sendSignalOnOutput("created");
        });
      },
    },
  },
};

ModelCRUDBase.addBaseInfo(NewModelNodeDefinition);
ModelCRUDBase.addModelId(NewModelNodeDefinition, { includeOutputs: true });
ModelCRUDBase.addInputProperties(NewModelNodeDefinition);

export const node = NewModelNodeDefinition.node;
