import { NodeDefinition } from "@fluxscape/runtime";
import UserService from "./userservice";

interface Internal {
  error: string;
  logOutScheduled: boolean;
}

export const node: NodeDefinition<Internal> = {
  name: "net.noodl.user.LogOut",
  docs: "https://docs.noodl.net/nodes/data/user/log-out",
  displayNodeName: "Log Out",
  category: "Cloud Services",
  color: "data",
  outputs: {
    success: {
      type: "signal",
      displayName: "Success",
      group: "Events",
    },
    failure: {
      type: "signal",
      displayName: "Failure",
      group: "Events",
    },
    error: {
      type: "string",
      displayName: "Error",
      group: "Error",
      getter() {
        return this._internal.error;
      },
    },
  },
  inputs: {
    login: {
      displayName: "Do",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleLogOut();
      },
    },
  },
  methods: {
    setError(err: string) {
      this._internal.error = err;
      this.flagOutputDirty("error");
      this.sendSignalOnOutput("failure");

      if (this.context.editorConnection) {
        this.context.editorConnection.sendWarning(
          // @ts-expect-error
          this.nodeScope.componentOwner.name,
          this.id,
          "user-login-warning",
          {
            message: err,
            showGlobally: true,
          }
        );
      }
    },
    clearWarnings() {
      if (this.context.editorConnection) {
        this.context.editorConnection.clearWarning(
          // @ts-expect-error
          this.nodeScope.componentOwner.name,
          this.id,
          "user-login-warning"
        );
      }
    },
    scheduleLogOut() {
      // @ts-expect-error
      if (this.logOutScheduled === true) return;
      // @ts-expect-error
      this.logOutScheduled = true;

      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.logOutScheduled = false;

        UserService.instance.logOut({
          success: () => {
            this.sendSignalOnOutput("success");
          },
          error: (e: string) => {
            // @ts-expect-error
            this.setError(e);
          },
        });
      });
    },
  },
};
