import { Node } from "./node";
import { createSetter } from "./edgetriggeredinput";
import type { Port } from "./models/componentmodel";
import { NodeSetup } from "./noderegister";
import { NodeContext } from "./nodecontext";
import { GraphModel } from "./models/graphmodel";

export interface InputDefinition<TInternal extends Record<string, any> = {}> {
  set?: (this: Node<TInternal>, value: any) => void;
  valueChangedToTrue?: (this: Node<TInternal>) => void;
  setUnitType?: string;
  displayName?: string;
  editorName?: string;
  group?: string;
  type?: any;
  default?: any;
  index?: number;
  exportToEditor?: boolean;
  description?: string;
  inputPriority?: number;
  tooltip?: string;
  tab?: string;
  popout?: boolean;
  allowVisualStates?: boolean;
  nodeDoubleClickAction?: any;
  createSetter?: (index: number) => (value: any) => void;
}

export interface OutputDefinition<TInternal extends Record<string, any> = {}> {
  displayName?: string;
  editorName?: string;
  group?: string;
  type?: any;
  index?: number;
  exportToEditor?: boolean;
  description?: string;
  default?: unknown;
  tooltip?: string;
  tab?: string;
  popout?: boolean;
  allowVisualStates?: boolean;
  get?: (this: Node<TInternal>) => any;
  getter?: (this: Node<TInternal>) => any;
}

interface Panel {
  name: string;
  context?: string[];
  title?: string;
  plug?: string;
  type?: {
    name: string;
  };
  canArrangeInGroups?: boolean;
  hidden?: boolean;
}

export type DynamicPort = {
  name?: string;
  condition?: string;
  ports?: any[];
  template?: string;
  port?: any;
  channelPort?: any;
  inputs?: string[];
  outputs?: string[];
};

export interface NodeDefinition<TInternal extends Record<string, any> = {}> {
  _internal?: any;
  displayName?: string;
  inputs?: Record<string, InputDefinition<TInternal>>;
  outputs?: Record<string, OutputDefinition<TInternal>>;
  category: string;
  dynamicports?: DynamicPort[];
  exportDynamicPorts?: any;
  useVariants?: any;
  allowChildren?: boolean;
  allowChildrenWithCategory?: string[];
  singleton?: boolean;
  connectionPanel?: any;
  allowAsChild?: boolean;
  visualStates?: any;
  panels?: Panel[];
  color?: string;
  usePortAsLabel?: any;
  portLabelTruncationMode?: any;
  name: string;
  displayNodeName?: string;
  deprecated?: boolean;
  haveComponentPorts?: boolean;
  version?: string;
  module?: string;
  docs?: string;
  shortDocs?: string;
  shortDesc?: string;
  allowAsExportRoot?: boolean;
  nodeDoubleClickAction?: any;
  searchTags?: string[];
  ports?: Port[];
  numberedInputs?: Record<
    string,
    {
      type: string;
      displayPrefix: string;
      group?: string;
      index?: number;
      createSetter: (
        index: number
      ) => (this: Node<TInternal>, value: any) => void;
    }
  >;
  nodeScopeDidInitialize?: () => void;

  /**
   * deprecated Use methods instead.
   */
  prototypeExtensions?: Record<
    string,
    (
      this: Node<TInternal>,
      ...args: any[]
    ) => void | {
      value: (this: Node<TInternal>, ...args: any[]) => void;
    }
  > & {
    registerOutputIfNeeded?: (this: Node<TInternal>, name: string) => void;
  };

  methods?: Record<string, (this: Node<TInternal>, ...args: any[]) => void> & {
    registerOutputIfNeeded?: (this: Node<TInternal>, name: string) => void;
  };

  initialize?: (this: Node<TInternal>) => void;

  // TODO: getInspectInfo, what more return types are supported?
  getInspectInfo?: (this: Node<TInternal>) =>
    | {
        type: string;
        value: unknown;
      }
    | {
        type: string;
        value: unknown;
      }[]
    | string
    | number
    | boolean
    | undefined;
}

function registerInput(
  object: any,
  metadata: NodeDefinition,
  name: string,
  input: InputDefinition
): void {
  if (object.hasOwnProperty(name)) {
    throw new Error("Input property " + name + " already registered");
  }

  if (!input.set && !input.valueChangedToTrue) {
    input.set = () => {};
  }

  if (input.set) {
    object[name] = {
      set: input.set,
    };

    //types to keep in the input on the node instances
    //color and textStyles are used for style updates
    //array is for supporting eval:ing strings
    const typesToSaveInInput = ["color", "textStyle", "array"];

    typesToSaveInInput.forEach((type) => {
      if (input.type && (input.type === type || input.type.name === type)) {
        object[name].type = type;
      }
    });
  }

  if (input.setUnitType) {
    object[name].setUnitType = input.setUnitType;
  }

  metadata.inputs = metadata.inputs || {};
  metadata.inputs[name] = {
    displayName: input.displayName,
    editorName: input.editorName,
    group: input.group,
    type: input.type,
    default: input.default,
    index: input.index,
    exportToEditor: input.hasOwnProperty("exportToEditor")
      ? input.exportToEditor
      : true,
    inputPriority: input.inputPriority || 0,
    tooltip: input.tooltip,
    tab: input.tab,
    popout: input.popout,
    allowVisualStates: input.allowVisualStates,
    nodeDoubleClickAction: input.nodeDoubleClickAction,
  };

  if (input.valueChangedToTrue) {
    metadata.inputs[name].type = {
      name: "signal",
      allowConnectionsOnly: true,
    };
  }
}

function registerInputs(
  object: any,
  metadata: NodeDefinition,
  inputs: Record<string, InputDefinition>
): void {
  Object.keys(inputs).forEach(function (inputName) {
    registerInput(object, metadata, inputName, inputs[inputName]);
  });
}

function registerNumberedInputs(
  node: any,
  numberedInputs: Record<string, any>
): void {
  for (const inputName of Object.keys(numberedInputs)) {
    registerNumberedInput(node, inputName, numberedInputs[inputName]);
  }
}

function registerNumberedInput(node: any, name: string, input: any): void {
  const oldRegisterInputIfNeeded = node.registerInputIfNeeded;

  node.registerInputIfNeeded = function (inputName: string): void {
    if (oldRegisterInputIfNeeded) {
      oldRegisterInputIfNeeded.call(node, inputName);
    }

    if (node.hasInput(inputName) || !inputName.startsWith(name)) {
      return;
    }

    const index = Number(inputName.slice(name.length + 1)); // inputName is "nameOfInput xxx" where xxx is the index

    node.registerInput(inputName, {
      type: input.type,
      set: input.createSetter.call(node, index),
    });
  };
}

function registerOutputsMetadata(
  metadata: NodeDefinition,
  outputs: Record<string, OutputDefinition>
): void {
  Object.keys(outputs).forEach(function (name) {
    const output = outputs[name];

    metadata.outputs = metadata.outputs || {};
    metadata.outputs[name] = {
      displayName: output.displayName,
      editorName: output.editorName,
      group: output.group,
      type: output.type,
      index: output.index,
      exportToEditor: output.hasOwnProperty("exportToEditor")
        ? output.exportToEditor
        : true,
    };
  });
}

function initializeDefaultValues(
  defaultValues: Record<string, any>,
  inputsMetadata: Record<string, any>
): void {
  Object.keys(inputsMetadata).forEach((name) => {
    const defaultValue = inputsMetadata[name].default;
    if (defaultValue === undefined) return;

    if (inputsMetadata[name].type.defaultUnit) {
      defaultValues[name] = {
        unit: inputsMetadata[name].type.defaultUnit,
        value: defaultValue,
      };
    } else {
      defaultValues[name] = defaultValue;
    }
  });
}

export function defineNode(opts: NodeDefinition): NodeSetup {
  if (!opts.category) {
    throw new Error("Node must have a category");
  }

  if (!opts.name) {
    throw new Error("Node must have a name");
  }

  const metadata: NodeDefinition = {
    inputs: {},
    outputs: {},
    category: opts.category,
    dynamicports: opts.dynamicports,
    exportDynamicPorts: opts.exportDynamicPorts,
    useVariants: opts.useVariants,
    allowChildren: opts.allowChildren,
    allowChildrenWithCategory: opts.allowChildrenWithCategory,
    singleton: opts.singleton,
    connectionPanel: opts.connectionPanel,
    allowAsChild: opts.allowAsChild,
    visualStates: opts.visualStates,
    panels: opts.panels,
    color: opts.color,
    usePortAsLabel: opts.usePortAsLabel,
    portLabelTruncationMode: opts.portLabelTruncationMode,
    name: opts.name,
    displayNodeName: opts.displayNodeName || opts.displayName,
    deprecated: opts.deprecated,
    haveComponentPorts: opts.haveComponentPorts,
    version: opts.version,
    module: opts.module,
    docs: opts.docs,
    allowAsExportRoot: opts.allowAsExportRoot,
    nodeDoubleClickAction: opts.nodeDoubleClickAction,
    searchTags: opts.searchTags,
  };

  opts._internal = opts._internal || {};

  //prototypeExtensions - old API
  //methods - new API
  opts.prototypeExtensions = opts.methods || opts.prototypeExtensions || {};
  opts.inputs = opts.inputs || {};
  opts.outputs = opts.outputs || {};
  opts.initialize = opts.initialize || function () {};

  const inputs: Record<string, any> = {};

  registerInputs(inputs, metadata, opts.inputs);
  registerOutputsMetadata(metadata, opts.outputs);

  class NodeConstructor extends Node {
    constructor(context: any, id: string) {
      super(context, id);
    }
  }

  if (opts.prototypeExtensions) {
    Object.keys(opts.prototypeExtensions).forEach(function (propName) {
      // TODO: WTF is this junk?!
      // @ts-expect-error
      if (!opts.prototypeExtensions![propName].value) {
        // @ts-expect-error
        opts.prototypeExtensions[propName] = {
          // @ts-expect-error
          value: opts.prototypeExtensions[propName],
        };
      }
    });
  }

  // @ts-ignore
  Object.defineProperties(NodeConstructor.prototype, opts.prototypeExtensions);
  Object.defineProperty(NodeConstructor.prototype, "name", {
    value: opts.name,
  });

  if (opts.getInspectInfo)
    NodeConstructor.prototype.getInspectInfo = opts.getInspectInfo;
  if (opts.nodeScopeDidInitialize)
    NodeConstructor.prototype.nodeScopeDidInitialize =
      opts.nodeScopeDidInitialize;

  const nodeDefinition = function (
    context: any,
    id: string,
    nodeScope: any
  ): NodeSetup | NodeConstructor | undefined {
    const node = new NodeConstructor(context, id);

    //create all inputs. Use the inputs object for setters that don't have state and can be shared
    node._inputs = Object.create(inputs);

    //all inputs that use the valueChangedToTrue have state and need to be instanced
    if (opts.inputs) {
      Object.keys(opts.inputs).forEach(function (name) {
        const input = opts.inputs![name];
        if (input.valueChangedToTrue) {
          node._inputs[name] = {
            set: createSetter({
              valueChangedToTrue: input.valueChangedToTrue,
            }),
          };
        }
      });

      if (opts.outputs) {
        Object.keys(opts.outputs).forEach(function (name) {
          const output = opts.outputs![name];
          if (output.type === "signal") {
            node.registerOutput(name, {
              getter() {
                //signals are always emitted as a sequence of false, true, so this getter is never used
                return undefined;
              },
            });
          } else {
            node.registerOutput(name, output);
          }
        });

        opts.numberedInputs &&
          registerNumberedInputs(node, opts.numberedInputs);

        node.nodeScope = nodeScope;
        initializeDefaultValues(node._inputValues, metadata.inputs || {});

        opts.initialize?.call(node);

        return node;
      }

      Object.assign(nodeDefinition, { metadata });

      if (opts.numberedInputs)
        registerSetupFunctionForNumberedInputs(
          nodeDefinition,
          opts.name,
          opts.numberedInputs
        );

      return nodeDefinition;
    }
  };
  nodeDefinition.metadata = metadata;

  return nodeDefinition;
}

function registerSetupFunctionForNumberedInputs(
  nodeDefinition: NodeSetup,
  nodeType: string,
  numberedInputs: Record<string, any>
): void {
  const inputNames = Object.keys(numberedInputs);

  if (!inputNames.length) return;

  nodeDefinition.setupNumberedInputDynamicPorts = function (
    context: NodeContext,
    graphModel: GraphModel
  ): void {
    const editorConnection = context.editorConnection;

    if (!editorConnection || !editorConnection.isRunningLocally()) {
      return;
    }

    function collectPorts(node: any, inputName: string, input: any): any[] {
      const connections = node.component
        .getConnectionsTo(node.id)
        .map((c: any) => c.targetPort);

      const allPortNames = Object.keys(node.parameters).concat(connections);
      const portNames = allPortNames.filter((p) =>
        p.startsWith(inputName + " ")
      );

      //Figure out how many we need to create
      //It needs to be the highest index + 1
      //Only parameters with values are present, e.g. input 3 can be missing even if input 4 is defined
      const maxIndex = portNames.length
        ? 1 +
          Math.max(
            ...portNames.map((p) => Number(p.slice(inputName.length + 1)))
          )
        : 0;
      const numPorts = maxIndex + 1;

      const ports: Port[] = [];

      for (let i = 0; i < numPorts; i++) {
        const port: Port = {
          name: inputName + " " + i,
          displayName: (input.displayPrefix || inputName) + " " + i,
          type: input.type,
          plug: "input",
          group: input.group,
        };

        if (input.hasOwnProperty("index")) {
          port.index = input.index + i;
        }

        ports.push(port);
      }

      return ports;
    }

    function updatePorts(node: any): void {
      const ports = inputNames
        .map((inputName) =>
          collectPorts(node, inputName, numberedInputs[inputName])
        )
        .flat();
      editorConnection?.sendDynamicPorts(node.id, ports);
    }

    graphModel.on("nodeAdded." + nodeType, (node: any) => {
      updatePorts(node);
      node.on("parameterUpdated", () => {
        updatePorts(node);
      });

      node.on("inputConnectionAdded", () => {
        updatePorts(node);
      });

      node.on("inputConnectionRemoved", () => {
        updatePorts(node);
      });
    });
  };
}

export function extend<T extends object, U extends object>(
  obj1: T,
  obj2: U
): T & U {
  for (const p in obj2) {
    if (p === "initialize" && (obj1 as any).initialize) {
      const oldInit = (obj1 as any).initialize;
      (obj1 as any).initialize = function () {
        oldInit.call(this);
        (obj2 as any).initialize.call(this);
      };
      // @ts-ignore
    } else if (obj2[p] && obj2[p].constructor === Object) {
      // @ts-ignore
      (obj1 as any)[p] = extend((obj1 as any)[p] || {}, obj2[p]);
    } else if (
      obj2[p] &&
      // @ts-ignore
      obj2[p].constructor === Array &&
      (obj1 as any)[p] &&
      // @ts-ignore
      (obj1 as any)[p].constructor == Array
    ) {
      (obj1 as any)[p] = (obj1 as any)[p].concat(obj2[p]);
    } else {
      (obj1 as any)[p] = obj2[p];
    }
  }
  return obj1 as T & U;
}
