import type { ComponentModel } from "./models/componentmodel";
import type { NodeModel } from "./models/nodemodel";
import type { NodeContext } from "./nodecontext";
import type { Node } from "./node";
import type { Variant } from "./variants";
import { ComponentInstanceNode } from "./nodes/componentinstance";
import { guid } from "./utils";

export interface Connection {
  sourceId: string;
  sourcePort: string;
  targetId: string;
  targetPort: string;
}

export class NodeScope {
  context: NodeContext;
  nodes: Record<string, Node>;
  componentOwner: any;
  componentInstanceChildren: Record<string, any>;
  componentModel?: ComponentModel;

  modelScope?: any;

  constructor(context: NodeContext, componentOwner: any) {
    this.context = context;
    this.nodes = {};
    this.componentOwner = componentOwner; //Component Instance that owns this NodeScope
    this.componentInstanceChildren = {};
  }

  private verifyData(data: any, requiredKeys: string[]): void {
    requiredKeys.forEach((key) => {
      if (!data[key]) {
        throw new Error("Missing " + key);
      }
    });
  }

  addConnection(connectionData: Connection): void {
    try {
      this.verifyData(connectionData, [
        "sourceId",
        "sourcePort",
        "targetId",
        "targetPort",
      ]);
    } catch (e) {
      // @ts-expect-error
      throw new Error("Error in connection: " + e.message);
    }

    try {
      const sourceNode = this.getNodeWithId(connectionData.sourceId);
      const targetNode = this.getNodeWithId(connectionData.targetId);

      targetNode.registerInputIfNeeded(connectionData.targetPort);
      sourceNode.registerOutputIfNeeded(connectionData.sourcePort);
      targetNode.connectInput(
        connectionData.targetPort,
        sourceNode,
        connectionData.sourcePort
      );
    } catch (e) {
      // @ts-expect-error
      console.error(e.message);
    }
  }

  setNodeParameters(node: any, nodeModel: NodeModel): void {
    const variant = this.context.variants.getVariant(
      nodeModel.type,
      // @ts-expect-error WTF is this codebase?
      nodeModel.variant
    );

    if (variant) {
      //apply the variant (this will also apply the parameters)
      node.setVariant(variant);
    } else {
      const parameters = nodeModel.parameters || {};

      let inputNames = Object.keys(parameters);

      if (this.context.nodeRegister.hasNode(node.name)) {
        const metadata = this.context.nodeRegister.getNodeMetadata(node.name);
        if (metadata.inputs) {
          inputNames.sort((a, b) => {
            const inputA = metadata.inputs![a];
            const inputB = metadata.inputs![b];
            return (inputB?.inputPriority ?? 0) - (inputA?.inputPriority ?? 0);
          });
        }
      }

      inputNames.forEach((inputName) => {
        node.registerInputIfNeeded(inputName);

        //protect against obsolete parameters
        if (node.hasInput(inputName) === false) {
          return;
        }

        node.queueInput(inputName, parameters[inputName]);
      });
    }
  }

  async createNodeFromModel(
    nodeModel: NodeModel,
    updateOnDirtyFlagging?: boolean
  ): Promise<any> {
    if (nodeModel.type === "Component Children") {
      if (nodeModel.parent) {
        const parentInstance = this.getNodeWithId(nodeModel.parent.id);
        this.componentOwner.setChildRoot(parentInstance);
      }
      return;
    }

    let node;
    try {
      node = await this.createNode(nodeModel.type, nodeModel.id);
      node.updateOnDirtyFlagging =
        updateOnDirtyFlagging === false ? false : true;
      node.setNodeModel(nodeModel);
    } catch (e) {
      // @ts-expect-error
      console.error(e.message);
      if (
        this.context.editorConnection &&
        this.context.isWarningTypeEnabled("nodescope")
      ) {
        this.context.editorConnection.sendWarning(
          this.componentOwner.name,
          nodeModel.id,
          "nodelibrary-unknown-node",
          {
            // @ts-expect-error
            message: e.message,
            showGlobally: true,
          }
        );
      }
      return;
    }

    if (nodeModel.variant && node.setVariant)
      node.setVariant(nodeModel.variant);
    this.setNodeParameters(node, nodeModel);

    if (nodeModel.parent) {
      this.insertNodeInTree(node, nodeModel);
    }

    return node;
  }

  insertNodeInTree(nodeInstance: any, nodeModel: NodeModel): void {
    if (!nodeModel.parent) {
      throw new Error("Cannot insert node without parent");
    }

    const parentInstance = this.getNodeWithId(nodeModel.parent.id);
    const childIndex = nodeModel.parent.children.indexOf(nodeModel);

    if (!parentInstance.addChild) {
      throw new Error(
        "Node " +
          parentInstance.id +
          " of type " +
          parentInstance.constructor.name +
          " can't have children"
      );
    }

    parentInstance.addChild(nodeInstance, childIndex);
  }

  getNodeWithId(id: string): any {
    if (!this.nodes.hasOwnProperty(id)) {
      throw new Error("Unknown node id " + id);
    }
    return this.nodes[id];
  }

  hasNodeWithId(id: string): boolean {
    return this.nodes.hasOwnProperty(id);
  }

  createPrimitiveNode(
    name: string,
    id?: string,
    extraProps?: Record<string, any>
  ): any {
    if (!id) id = guid();

    if (this.nodes.hasOwnProperty(id)) {
      throw Error("duplicate id " + id);
    }

    const node = this.context.nodeRegister.createNode(name, id, this);
    if (extraProps) {
      for (const prop in extraProps) {
        node[prop] = extraProps[prop];
      }
    }

    this.nodes[id] = node;
    return node;
  }

  async createNode(
    name: string,
    id?: string,
    extraProps?: Record<string, any>
  ): Promise<any> {
    if (!id) id = guid();

    if (this.nodes.hasOwnProperty(id)) {
      throw Error("duplicate id " + id);
    }

    let node;

    if (this.context.nodeRegister.hasNode(name)) {
      node = this.context.nodeRegister.createNode(name, id, this);
      if (extraProps) {
        for (const prop in extraProps) {
          node[prop] = extraProps[prop];
        }
      }
    } else {
      node = await this.context.createComponentInstanceNode(
        name,
        id,
        this,
        extraProps
      );
      this.componentInstanceChildren[id] = node;
    }

    this.nodes[id] = node;
    return node;
  }

  getNodesWithIdRecursive(id: string): Node[] {
    function findNodesWithIdRec(
      scope: NodeScope,
      id: string,
      result: Node[]
    ): void {
      if (scope.nodes.hasOwnProperty(id)) {
        result.push(scope.nodes[id]);
      }

      const componentIds = Object.keys(scope.nodes).filter((nodeId) => {
        return scope.nodes[nodeId] instanceof ComponentInstanceNode;
      });

      componentIds.forEach((componentId) => {
        const nodeScope = scope.nodes[componentId].nodeScope;
        if (nodeScope) {
          findNodesWithIdRec(nodeScope, id, result);
        }
      });
    }

    const result: Node[] = [];
    findNodesWithIdRec(this, id, result);
    return result;
  }

  getNodesWithType(name: string): Node[] {
    const ids = Object.keys(this.nodes).filter((id) => {
      return this.nodes[id].name === name;
    });
    return ids.map((id) => {
      return this.nodes[id];
    });
  }

  getNodesWithTypeRecursive(name: string): Node[] {
    let result: Node[] = [];

    const findNodesWithTypeRec = () => {
      result = result.concat(this.getNodesWithType(name));

      const componentIds = Object.keys(this.nodes).filter((nodeId) => {
        return this.nodes[nodeId] instanceof ComponentInstanceNode;
      });

      componentIds.forEach((componentId) => {
        const node = this.nodes[componentId];
        if (node && node.nodeScope) {
          const res = node.nodeScope.getNodesWithTypeRecursive(name);
          result = result.concat(res);
        }
      });
    };

    findNodesWithTypeRec();
    return result;
  }

  getAllNodesRecursive(): any[] {
    let result: any[] = [];

    const getAllNodesRec = () => {
      result = result.concat(Object.values(this.nodes));

      const componentIds = Object.keys(this.nodes).filter((nodeId) => {
        return this.nodes[nodeId] instanceof ComponentInstanceNode;
      });
      componentIds.forEach((componentId) => {
        const node = this.nodes[componentId];
        if (node && node.nodeScope) {
          const res = node.nodeScope.getAllNodesRecursive();
          result = result.concat(res);
        }
      });
    };

    getAllNodesRec();
    return result;
  }

  getAllNodesWithVariantRecursive(variant: Variant | string): any[] {
    const nodes = this.getAllNodesRecursive();
    return nodes.filter((node) => node.variant === variant);
  }

  onNodeModelRemoved(nodeModel: NodeModel): void {
    const nodeInstance = this.getNodeWithId(nodeModel.id);

    if (nodeModel.parent) {
      const parentInstance = this.getNodeWithId(nodeModel.parent.id);
      parentInstance.removeChild(nodeInstance);
    }

    nodeInstance._onNodeDeleted();
    delete this.nodes[nodeInstance.id];
    delete this.componentInstanceChildren[nodeInstance.id];
  }

  removeConnection(connectionModel: Connection): void {
    const targetNode = this.getNodeWithId(connectionModel.targetId);
    targetNode.removeInputConnection(
      connectionModel.targetPort,
      connectionModel.sourceId,
      connectionModel.sourcePort
    );
  }

  async setComponentModel(componentModel: ComponentModel): Promise<void> {
    this.componentModel = componentModel;

    const nodes: any[] = [];

    //create all nodes
    for (const nodeModel of componentModel.getAllNodes()) {
      const node = await this.createNodeFromModel(nodeModel, false);
      if (node) nodes.push(node);
    }

    componentModel
      .getAllConnections()
      .forEach((conn) => this.addConnection(conn));

    //now that all nodes and connections are setup, trigger the dirty flagging so nodes can run with all the connections in place
    nodes.forEach((node) => (node.updateOnDirtyFlagging = true));

    nodes.forEach((node) => {
      if (node._dirty) {
        node._performDirtyUpdate();
      }
    });

    componentModel.on(
      "connectionAdded",
      (conn: Connection) => this.addConnection(conn),
      this
    );
    componentModel.on("connectionRemoved", this.removeConnection, this);
    componentModel.on("nodeAdded", this.createNodeFromModel, this);

    componentModel.on(
      "nodeParentWillBeRemoved",
      (nodeModel: NodeModel) => {
        if (nodeModel.type === "Component Children") {
          if (nodeModel.parent) {
            this.componentOwner.setChildRoot(null);
          }
          return;
        }

        const nodeInstance = this.getNodeWithId(nodeModel.id);
        if (nodeInstance.parent) {
          nodeInstance.parent.removeChild(nodeInstance);
        }
      },
      this
    );

    componentModel.on(
      "nodeParentUpdated",
      (nodeModel: NodeModel) => {
        if (nodeModel.type === "Component Children") {
          const parentInstance = nodeModel.parent
            ? this.getNodeWithId(nodeModel.parent.id)
            : null;
          this.componentOwner.setChildRoot(parentInstance);
        } else {
          const nodeInstance = this.getNodeWithId(nodeModel.id);
          this.insertNodeInTree(nodeInstance, nodeModel);
        }
      },
      this
    );

    componentModel.on(
      "nodeRemoved",
      (nodeModel: NodeModel) => {
        if (nodeModel.type !== "Component Children") {
          this.onNodeModelRemoved(nodeModel);
        }
      },
      this
    );

    for (const id in this.nodes) {
      const node = this.nodes[id];
      node?.nodeScopeDidInitialize?.();
    }
  }

  reset(): void {
    if (this.componentModel) {
      this.componentModel.removeListenersWithRef(this);
      this.componentModel = undefined;
    }

    Object.keys(this.nodes).forEach((id) => {
      if (this.nodes.hasOwnProperty(id)) {
        this.deleteNode(this.nodes[id]);
      }
    });
  }

  deleteNode(nodeInstance: any): void {
    if (!this.nodes.hasOwnProperty(nodeInstance.id)) {
      console.error(
        "Node doesn't belong to this scope",
        nodeInstance.id,
        nodeInstance.name
      );
      return;
    }

    if (nodeInstance.parent) {
      nodeInstance.parent.removeChild(nodeInstance);
    }

    //depth first
    if (nodeInstance.getChildren) {
      nodeInstance.getChildren().forEach((child: any) => {
        nodeInstance.removeChild(child);
        //the child might be created in a different scope
        //if the child is a component instance, we want its parent scope, not the inner scope
        const nodeScope = child.parentNodeScope || child.nodeScope;
        nodeScope.deleteNode(child);
      });
    }

    if (this.componentModel) {
      const connectionFrom = this.componentModel.getConnectionsFrom(
        nodeInstance.id
      );
      const connectionTo = this.componentModel.getConnectionsTo(
        nodeInstance.id
      );

      connectionFrom.concat(connectionTo).forEach((connection) => {
        if (
          this.nodes.hasOwnProperty(connection.targetId) &&
          this.nodes.hasOwnProperty(connection.sourceId)
        ) {
          this.removeConnection(connection);
        }
      });
    }

    nodeInstance._onNodeDeleted();
    delete this.nodes[nodeInstance.id];
    delete this.componentInstanceChildren[nodeInstance.id]; //in case this is a component
  }

  sendEventFromThisScope(
    eventName: string,
    data: any,
    propagation?: string,
    sendEventInThisScope?: boolean
  ): boolean {
    if (sendEventInThisScope) {
      const eventReceivers = this.getNodesWithType("Event Receiver").filter(
        (eventReceiver) => {
          // @ts-expect-error TODO: fix this
          return eventReceiver.getChannelName() === eventName;
        }
      );

      for (let i = 0; i < eventReceivers.length; i++) {
        // @ts-expect-error TODO: fix this
        const consumed = eventReceivers[i].handleEvent(data);
        if (consumed) return true;
      }
    }

    if (propagation === "parent" && this.componentOwner.parentNodeScope) {
      // Send event to parent scope
      //either the scope of the visual parent if there is one, otherwise the parent component
      const parentNodeScope = this.componentOwner.parent
        ? this.componentOwner.parent.nodeScope
        : this.componentOwner.parentNodeScope;
      if (!parentNodeScope) return false;
      return parentNodeScope.sendEventFromThisScope(
        eventName,
        data,
        propagation,
        true
      );
    } else if (propagation === "children") {
      // Send event to all child scopes
      const nodes = this.nodes;
      for (const nodeId in nodes) {
        const children = nodes[nodeId].children;
        if (children)
          for (const child of children) {
            if (
              child.name &&
              this.context.hasComponentModelWithName(child.name)
            ) {
              // This is a component instance child
              const consumed = child.nodeScope?.sendEventFromThisScope(
                eventName,
                data,
                propagation,
                true
              );
              if (consumed) return true;
            }
          }
      }
    } else if (propagation === "siblings") {
      // Send event to all siblings, that is all children of the parent scope except this scope
      let parentNodeScope: NodeScope | undefined;
      if (this.componentOwner.parent) {
        parentNodeScope = this.componentOwner.parent.nodeScope;
      } else {
        parentNodeScope = this.componentOwner.parentNodeScope;
      }

      if (!parentNodeScope) return false;

      const nodes = parentNodeScope.nodes;
      for (const nodeId in nodes) {
        const children = nodes[nodeId].children;
        if (children) {
          const _c = children.filter(
            (child) =>
              child.name &&
              this.context.hasComponentModelWithName(child.name) &&
              child.nodeScope !== this
          );
          for (const child of _c) {
            const consumed = child.nodeScope?.sendEventFromThisScope(
              eventName,
              data,
              undefined,
              true
            );
            if (consumed) return true;
          }
        }
      }
    }

    return false;
  }
}
