import * as DbModelCRUDBase from "./dbmodelcrudbase";
import { CloudStore } from "../../../api/cloudstore";
import { NodeDefinition } from "../../../nodedefinition";
import { Model } from "../../../model";

interface Internal {
  storeType?: "cloud" | "local";
  storeProperties?: "specified" | "all";
  model?: Model;
  collectionId?: string;
  inputValues: Record<string, any>;
}

const SetDbModelPropertiedNodeDefinition: { node: NodeDefinition<Internal> } = {
  node: {
    name: "SetDbModelProperties",
    category: "",
    docs: "https://docs.noodl.net/nodes/data/cloud-data/set-record-properties",
    displayNodeName: "Set Record Properties",
    usePortAsLabel: "collectionName",
    dynamicports: [
      {
        name: "conditionalports/extended",
        condition: "storeType = cloud OR storeType NOT SET",
        inputs: ["storeProperties"],
      },
    ],
    inputs: {
      store: {
        displayName: "Do",
        group: "Actions",
        valueChangedToTrue() {
          if (
            this._internal.storeType === undefined ||
            this._internal.storeType === "cloud"
          ) {
            // @ts-expect-error
            this.scheduleSave();
          } else {
            // @ts-expect-error
            this.scheduleStore();
          }
        },
      },
      storeProperties: {
        displayName: "Properties to  store",
        group: "General",
        type: {
          name: "enum",
          enums: [
            { label: "Only specified", value: "specified" },
            { label: "All", value: "all" },
          ],
        },
        default: "specified",
        set: function (this: any, value: "specified" | "all") {
          this._internal.storeProperties = value;
        },
      },
      storeType: {
        displayName: "Store to",
        group: "General",
        type: {
          name: "enum",
          enums: [
            { label: "Cloud and local", value: "cloud" },
            { label: "Local only", value: "local" },
          ],
        },
        default: "cloud",
        set: function (this: any, value: "cloud" | "local") {
          this._internal.storeType = value;
        },
      },
    },
    outputs: {
      stored: {
        type: "signal",
        displayName: "Success",
        group: "Events",
      },
    },
    methods: {
      scheduleSave() {
        const _this = this;
        const internal = this._internal;

        // @ts-expect-error
        if (!this.checkWarningsBeforeCloudOp()) return;

        // @ts-expect-error
        this.scheduleOnce("StorageSave", function () {
          if (!internal.model) {
            // @ts-expect-error
            _this.setError("Missing Record Id");
            return;
          }

          const model = internal.model;
          for (const key in internal.inputValues) {
            model.set(key, internal.inputValues[key], { resolve: true });
          }

          // @ts-expect-error
          CloudStore.forScope(_this.nodeScope.modelScope).save({
            // @ts-expect-error
            collection: internal.collectionId,
            objectId: model.getId(), // Get the objectId part of the model id
            data:
              internal.storeProperties === "all"
                ? model.data
                : internal.inputValues, // Only store input values by default, if not explicitly specified
            // @ts-expect-error
            acl: _this._getACL(),
            success: function (response: Record<string, any>) {
              for (const key in response) {
                model.set(key, response[key]);
              }

              _this.sendSignalOnOutput("stored");
            },
            error: function (err: string) {
              // @ts-expect-error
              _this.setError(err || "Failed to save.");
            },
          });
        });
      },
      scheduleStore() {
        // @ts-expect-error
        if (this.hasScheduledStore) return;
        // @ts-expect-error
        this.hasScheduledStore = true;

        const internal = this._internal;
        this.scheduleAfterInputsHaveUpdated(() => {
          // @ts-expect-error
          this.hasScheduledStore = false;
          if (!internal.model) return;

          for (const i in internal.inputValues) {
            internal.model.set(i, internal.inputValues[i], { resolve: true });
          }
          this.sendSignalOnOutput("stored");
        });
      },
    },
  },
};

DbModelCRUDBase.addBaseInfo(SetDbModelPropertiedNodeDefinition);
DbModelCRUDBase.addModelId(SetDbModelPropertiedNodeDefinition);
DbModelCRUDBase.addInputProperties(SetDbModelPropertiedNodeDefinition);
DbModelCRUDBase.addAccessControl(SetDbModelPropertiedNodeDefinition);

export const node = SetDbModelPropertiedNodeDefinition.node;
