import { ConfigService } from "../../../api/configservice";

interface ConfigNodeDefinition {
  name: string;
  docs: string;
  displayNodeName: string;
  category: string;
  usePortAsLabel: string;
  color: string;
  initialize: () => void;
  getInspectInfo: () => string | { type: string; value: any }[];
  inputs?: Record<string, any>;
  outputs?: Record<string, any>;
  methods: {
    getValue: () => any;
    setInternal: (key: string, value: any) => void;
    registerOutputIfNeeded: (name: string) => void;
    registerInputIfNeeded: (name: string) => void;
  };
}

interface ConfigNode {
  _internal: {
    config?: Record<string, any>;
    configKey?: string;
    useDevValue?: boolean;
    devValue?: any;
  };
  context: {
    editorConnection?: {
      isRunningLocally: () => boolean;
    };
  };
  hasOutput: (name: string) => boolean;
  flagOutputDirty: (name: string) => void;
  hasInput: (name: string) => boolean;
  registerOutput: (name: string, options: { getter: () => any }) => void;
  registerInput: (name: string, options: { set: (value: any) => void }) => void;
}

const ConfigNodeDefinition: ConfigNodeDefinition = {
  name: "DbConfig",
  docs: "https://docs.noodl.net/nodes/data/cloud-data/config",
  displayNodeName: "Config",
  category: "Cloud Services",
  usePortAsLabel: "configKey",
  color: "data",
  initialize: function (this: ConfigNode) {
    const internal = this._internal;

    ConfigService.instance.getConfig().then((config) => {
      internal.config = config;
      if (this.hasOutput("value")) this.flagOutputDirty("value");
    });
  },
  getInspectInfo() {
    // @ts-expect-error
    const value = this.getValue();

    if (value === undefined) return "[No Value]";

    return [{ type: "value", value: value }];
  },
  methods: {
    getValue: function (this: ConfigNode) {
      const internal = this._internal;
      if (
        internal.useDevValue &&
        this.context.editorConnection &&
        this.context.editorConnection.isRunningLocally()
      ) {
        return internal.devValue;
      } else if (
        internal.config !== undefined &&
        internal.configKey !== undefined
      ) {
        return internal.config[internal.configKey];
      }
    },
    setInternal: function (this: ConfigNode, key: string, value: any) {
      this._internal[key] = value;
      if (this.hasOutput("value")) this.flagOutputDirty("value");
    },
    registerOutputIfNeeded: function (this: ConfigNode, name: string) {
      if (this.hasOutput(name)) {
        return;
      }

      if (name === "value")
        return this.registerOutput(name, {
          // @ts-expect-error
          getter: this.getValue.bind(this),
        });
    },
    registerInputIfNeeded: function (this: ConfigNode, name: string) {
      if (this.hasInput(name)) {
        return;
      }

      if (name === "configKey" || name === "useDevValue" || name === "devValue")
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setInternal.bind(this, name),
        });
    },
  },
};

interface GraphModel {
  getMetaData: (key: string) => any;
  getNodesWithType: (type: string) => any[];
  on: (event: string, callback: (data?: any) => void) => void;
}

interface SetupContext {
  editorConnection?: {
    isRunningLocally: () => boolean;
    clearWarning: (componentName: string, id: string, type: string) => void;
    sendWarning: (
      componentName: string,
      id: string,
      type: string,
      options: { showGlobally: boolean; message: string }
    ) => void;
    sendDynamicPorts: (id: string, ports: any[]) => void;
  };
}

export const node = ConfigNodeDefinition;
export function setup(context: SetupContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  function updatePorts(node: any) {
    const ports: any[] = [];

    context.editorConnection!.clearWarning(
      node.component.name,
      node.id,
      "dbconfig-warning"
    );

    const configSchema = graphModel.getMetaData("dbConfigSchema");
    let valueType;

    if (configSchema) {
      const isCloud =
        typeof (global as any)._noodl_cloud_runtime_version !== "undefined";
      ports.push({
        name: "configKey",
        displayName: "Parameter",
        group: "General",
        type: {
          name: "enum",
          enums: Object.keys(configSchema)
            .filter((k) => isCloud || !configSchema[k].masterKeyOnly)
            .map((k) => ({ value: k, label: k })),
          allowEditOnly: true,
        },
        plug: "input",
      });

      if (
        node.parameters["configKey"] !== undefined &&
        configSchema &&
        configSchema[node.parameters["configKey"]]
      ) {
        valueType = configSchema[node.parameters["configKey"]].type;

        if (
          valueType === "string" ||
          valueType === "boolean" ||
          valueType === "number" ||
          valueType === "object" ||
          valueType === "array"
        ) {
          ports.push({
            name: "useDevValue",
            displayName: "Enable",
            group: "Local Override",
            type: "boolean",
            default: false,
            plug: "input",
          });

          if (node.parameters["useDevValue"] === true) {
            ports.push({
              name: "devValue",
              displayName: "Value",
              group: "Local Override",
              type: valueType,
              plug: "input",
            });
          }
        }
      } else if (node.parameters["configKey"] !== undefined) {
        context.editorConnection!.sendWarning(
          node.component.name,
          node.id,
          "dbconfig-warning",
          {
            showGlobally: true,
            message:
              node.parameters["configKey"] +
              " config parameter is missing, add it to your cloud service.",
          }
        );
      }
    } else {
      context.editorConnection!.sendWarning(
        node.component.name,
        node.id,
        "dbconfig-warning",
        {
          showGlobally: true,
          message: "You need an active cloud service.",
        }
      );
    }

    ports.push({
      name: "value",
      displayName: "Value",
      group: "General",
      type: valueType || "*",
      plug: "output",
    });

    context.editorConnection!.sendDynamicPorts(node.id, ports);
  }

  function _managePortsForNode(node: any) {
    updatePorts(node);

    node.on("parameterUpdated", function () {
      updatePorts(node);
    });

    graphModel.on("metadataChanged.dbConfigSchema", function () {
      ConfigService.instance.clearCache();
      updatePorts(node);
    });
  }

  graphModel.on("editorImportComplete", () => {
    graphModel.on("nodeAdded.DbConfig", function (node) {
      _managePortsForNode(node);
    });

    for (const node of graphModel.getNodesWithType("DbConfig")) {
      _managePortsForNode(node);
    }
  });
}
