import { getAbsoluteUrl } from "@fluxscape/runtime";

export class FontLoader {
  static instance = new FontLoader();

  loadedFontFamilies: { [key: string]: boolean };
  fontCssFamiliesAdded: { [key: string]: boolean };
  fontCallbacks: { [key: string]: Function[] };

  constructor() {
    this.loadedFontFamilies = {};
    this.fontCssFamiliesAdded = {};
    this.fontCallbacks = {};

    const defaultFonts = [
      "Arial",
      "Arial Black",
      "Courier New",
      "Helvetica",
      "Impact",
      "Lucida Console",
      "Tahoma",
      "Times New Roman",
    ];

    defaultFonts.forEach((fontFamily) => {
      this.loadedFontFamilies[fontFamily] = true;
    });
  }

  private removeFileEnding(url: string): string {
    return url.replace(/\.[^/.]+$/, "");
  }

  loadFont(fontURL: string): void {
    // Support SSR
    if (typeof document === "undefined") return;

    fontURL = getAbsoluteUrl(fontURL);

    // get file name without path and file ending
    const family = this.removeFileEnding(fontURL).split("/").pop()!;

    // check if it's already loaded
    if (this.loadedFontFamilies[family]) {
      this.fontCallbacks[family]?.forEach((callback) => {
        callback();
      });
      return;
    }

    // check if font is already being loaded, we're just waiting for the callback
    if (this.fontCssFamiliesAdded[family]) {
      return;
    }

    this.fontCssFamiliesAdded[family] = true;

    const newStyle = document.createElement("style");
    newStyle.type = "text/css";

    // @ts-expect-error
    const baseUrl = Noodl.Env["BaseUrl"] || "/";

    if (fontURL.startsWith("/")) {
      fontURL = fontURL.substring(1);
    }

    newStyle.appendChild(
      document.createTextNode(
        `@font-face { font-family: '${family}'; src: url('${baseUrl}${fontURL}'); }\n`
      )
    );
    document.head.appendChild(newStyle);

    // Support SSR
    if (typeof window !== "undefined") {
      const WebFontLoader = require("webfontloader");
      WebFontLoader.load({
        timeout: 1000 * 600, // 10 minutes in case the bandwidth is reeeeeeally low
        custom: {
          families: [family],
        },
        fontactive: (loadedFamily) => {
          this.loadedFontFamilies[loadedFamily] = true;
          this.fontCallbacks[loadedFamily]?.forEach((callback) => {
            callback();
          });
        },
      });
    }
  }

  callWhenFontIsActive(family: string, callback: Function): void {
    if (this.loadedFontFamilies[family]) {
      callback();
      return;
    }

    if (!this.fontCallbacks[family]) {
      this.fontCallbacks[family] = [];
    }
    this.fontCallbacks[family].push(callback);
  }
}
