import { NodeContext } from "./nodecontext";
import { GraphModel, RouterIndex } from "./models/graphmodel";
import { ComponentModel, Port } from "./models/componentmodel";
import { NodeModel } from "./models/nodemodel";
import { NodeScope } from "./nodescope";

export interface Event {
  type: string;
  componentName?: string;
  model?: any;
  nodeId?: string;
  parentId?: string;
  childIndex?: number;
  port?: Port;
  parameters?: Record<string, any>;
  oldParameters?: Record<string, any>;
  parameterName?: string;
  parameterValue?: any;
  state?: any;
  ports?: Port[];
  oldName?: string;
  newName?: string;
  settings?: any;
  key?: string;
  data?: any;
  variant?: any;
  variantName?: string;
  variantTypeName?: string;
  oldVariantName?: string;
  curve?: any;
  name?: string;
}

function difference(array1: string[], array2: string[]): string[] {
  const valueSet = new Set(array2);
  return array1.filter((e) => !valueSet.has(e));
}

async function handleEvent(
  context: NodeContext,
  graphModel: GraphModel,
  event: Event
): Promise<void> {
  function applyPortDelta(
    nodeModel: ComponentModel | NodeModel,
    newPorts: Port[]
  ): void {
    const inputPorts: Record<string, Port> = {};
    const outputPorts: Record<string, Port> = {};

    newPorts.forEach((port: Port) => {
      // TODO: WTF...
      // if (port && port.plug === "outputs") {
      //   port.plug = "output";
      // }

      if (port.plug === "input" || port.plug === "input/output") {
        inputPorts[port.name] = port;
      }
      if (port.plug === "output" || port.plug === "input/output") {
        outputPorts[port.name] = port;
      }
    });

    const existingInputs = Object.keys(nodeModel.getInputPorts());

    const inputPortsToRemove = difference(
      existingInputs,
      Object.keys(inputPorts)
    );
    const inputPortsToAdd = difference(Object.keys(inputPorts), existingInputs);

    nodeModel.updateInputPortTypes(inputPorts);

    inputPortsToRemove.forEach(
      nodeModel.removeInputPortWithName.bind(nodeModel)
    );
    inputPortsToAdd.forEach((portName: string) => {
      nodeModel.addInputPort(inputPorts[portName]);
      if (nodeModel instanceof NodeModel) {
        if (nodeModel.parameters?.hasOwnProperty(portName)) {
          setInputValueOnNodeInstancesWithModel(
            context.rootComponent!.nodeScope,
            nodeModel,
            portName,
            nodeModel.parameters[portName]
          );
        }
      }
    });

    nodeModel.updateOutputPortTypes(outputPorts);

    const existingOutputs = Object.keys(nodeModel.getOutputPorts());

    const outputPortsToRemove = difference(
      existingOutputs,
      Object.keys(outputPorts)
    );
    const outputPortsToAdd = difference(
      Object.keys(outputPorts),
      existingOutputs
    );

    outputPortsToRemove.forEach(
      nodeModel.removeOutputPortWithName.bind(nodeModel)
    );
    outputPortsToAdd.forEach((portName: string) => {
      nodeModel.addOutputPort(outputPorts[portName]);
    });
  }

  function setInputValueOnNodeInstancesWithModel(
    nodeScope: NodeScope,
    nodeModel: NodeModel,
    port: string,
    value: any
  ): void {
    const nodes = nodeScope.getNodesWithIdRecursive(nodeModel.id);
    nodes.forEach((node) => {
      node.queueInput(port, value);
    });
  }

  let componentModel: ComponentModel | undefined;
  if (event.componentName) {
    componentModel = graphModel.getComponentWithName(event.componentName);
    if (!componentModel) {
      return;
    }
  }

  // TODO: WTF...
  // if (event.port && event.port.plug === "outputs") {
  //   event.port.plug = "output";
  // }

  if (!componentModel) {
    // TODO: This is new, why is the code like this after this line?
    throw new Error("Component model not found");
  }

  const eventHandlers: Record<string, (event: Event) => Promise<void> | void> =
    {
      nodeAdded: (event: Event): void => {
        componentModel?.importEditorNodeData(
          event.model,
          event.parentId,
          event.childIndex
        );
      },
      nodeRemoved: async (event: Event): Promise<void> => {
        if (componentModel?.hasNodeWithId(event.model.id)) {
          await componentModel.removeNodeWithId(event.model.id);
        }
      },
      connectionAdded: (event: Event): void => {
        componentModel?.addConnection(event.model);
      },
      connectionRemoved: (event: Event): void => {
        if (!componentModel) {
          return;
        }

        componentModel.removeConnection(event.model);

        const targetNodeModel = componentModel.getNodeWithId(
          event.model.targetId
        )!;
        if (
          componentModel.getConnectionsToPort(
            targetNodeModel.id,
            event.model.targetId
          ).length === 0
        ) {
          let value = targetNodeModel.parameters[event.model.targetPort];
          if (value === undefined) {
            value = context.getDefaultValueForInput(
              targetNodeModel.type,
              event.model.targetPort
            );
          }

          setInputValueOnNodeInstancesWithModel(
            context.rootComponent!.nodeScope,
            targetNodeModel,
            event.model.targetPort,
            value
          );
        }
      },
      parameterChanged: (event: Event): void => {
        if (!componentModel) {
          return;
        }

        const nodeModel = componentModel.getNodeWithId(event.nodeId!);
        if (nodeModel === undefined) {
          console.log("parameterChanged: Unknown node id", event);
          return;
        }

        if (event.parameters) {
          const allParams = new Set([
            ...Object.keys(event.parameters),
            ...Object.keys(event.oldParameters!),
          ]);
          for (const param of allParams) {
            nodeModel.setParameter(param, event.parameters[param]);
          }
        }

        if (event.parameterName) {
          nodeModel.setParameter(
            event.parameterName,
            event.parameterValue,
            event.state
          );
        }
      },
      nodeAttached: (event: Event): void => {
        if (!componentModel) {
          return;
        }

        componentModel.setNodeParent(
          componentModel.getNodeWithId(event.nodeId),
          componentModel.getNodeWithId(event.parentId),
          event.childIndex
        );
      },
      nodeDetached: (event: Event): void => {
        if (!componentModel) {
          return;
        }

        componentModel.setNodeParent(
          componentModel.getNodeWithId(event.nodeId!),
          null
        );
        componentModel.addRootId(event.nodeId!);
      },
      componentAdded: (event: Event): void => {
        graphModel.importComponentFromEditorData(event.model);
      },
      componentRemoved: async (event: Event): Promise<void> => {
        await graphModel.removeComponentWithName(event.componentName);
      },
      rootAdded: (event: Event): void => {
        componentModel?.addRootId(event.nodeId);
      },
      portAdded: (event: Event): void => {
        if (!componentModel) {
          return;
        }

        const nodeModel = componentModel.getNodeWithId(event.nodeId);
        if (!event.port || !nodeModel) {
          return;
        }

        if (event.port.plug === "input" || event.port.plug === "input/output") {
          nodeModel.addInputPort(event.port);

          // TODO: Was here a bug before?
          if (nodeModel.parameters.hasOwnProperty(event.port.name)) {
            setInputValueOnNodeInstancesWithModel(
              context.rootComponent!.nodeScope,
              nodeModel,
              event.port.name,
              nodeModel.parameters[event.port.name]
            );
          }
        }
        if (
          event.port.plug === "output" ||
          event.port.plug === "input/output"
        ) {
          nodeModel.addOutputPort(event.port);
        }
      },
      portRemoved: (event: Event): void => {
        if (!componentModel) {
          return;
        }

        const nodeModel = componentModel.getNodeWithId(event.nodeId);
        if (!event.port || !nodeModel) {
          return;
        }

        if (event.port.plug === "input" || event.port.plug === "input/output") {
          nodeModel.removeInputPortWithName(event.port.name);
        }
        if (
          event.port.plug === "output" ||
          event.port.plug === "input/output"
        ) {
          nodeModel.removeOutputPortWithName(event.port.name);
        }
      },
      nodePortRenamed: (event: Event): void => {
        if (!event.port || !componentModel) {
          return;
        }

        if (event.port.plug === "input" || event.port.plug === "input/output") {
          if (event.nodeId && event.oldName) {
            componentModel.renameInputPortOnNodeWithId(
              event.nodeId,
              event.oldName,
              event.port.name
            );
          }
        }
        if (
          event.nodeId &&
          event.oldName &&
          (event.port.plug === "output" || event.port.plug === "input/output")
        ) {
          componentModel.renameOutputPortOnNodeWithId(
            event.nodeId,
            event.oldName,
            event.port.name
          );
        }
        const node = componentModel.getNodeWithId(event.nodeId);
        if (node?.type === "Component Inputs") {
          componentModel.addInputPort(event.port);
          graphModel
            .getNodesWithType(componentModel.name)
            .forEach((componentInstance) => {
              componentInstance.component.renameInputPortOnNodeWithId(
                componentInstance.id,
                event.oldName,
                event.port?.name
              );
            });
          componentModel.removeInputPortWithName(event.oldName!);
        } else if (node?.type === "Component Outputs") {
          componentModel.addOutputPort(event.port);
          graphModel
            .getNodesWithType(componentModel!.name)
            .forEach((componentInstance) => {
              componentInstance.component.renameOutputPortOnNodeWithId(
                componentInstance.id,
                event.oldName,
                event.port?.name
              );
            });
          componentModel.removeOutputPortWithName(event.oldName!);
        }
      },
      componentPortsUpdated: (event: Event): void => {
        if (componentModel) {
          applyPortDelta(componentModel, event.ports!);
        }
      },
      instancePortsChanged: (event: Event): void => {
        if (!componentModel!.hasNodeWithId(event.nodeId!)) return;
        const nodeModel = componentModel!.getNodeWithId(event.nodeId!);
        if (nodeModel) {
          applyPortDelta(nodeModel, event.ports!);
        }
      },
      componentRenamed: (event: Event): void => {
        graphModel.renameComponent(event.oldName!, event.newName!);
      },
      settingsChanged: (event: Event): void => {
        graphModel.setSettings(event.settings);
      },
      metadataChanged: (event: Event): void => {
        graphModel.setMetaData(event.key!, event.data);
      },
      componentMetadataChanged: (event: Event): void => {
        const c = graphModel.getComponentWithName(event.componentName!);
        if (!c) return;
        c.setMetadata(event.key!, event.data);
      },
      variantParametersChanged: (event: Event): void => {
        if (event.variant) {
          graphModel.updateVariant(event.variant);
        } else {
          graphModel.updateVariantParameter(
            event.variantName!,
            event.variantTypeName!,
            event.parameterName!,
            event.parameterValue,
            event.state
          );

          if (event.parameterValue === undefined) {
            const variant = graphModel.getVariant(
              event.variantTypeName!,
              event.variantName!
            );
            const nodes =
              context.rootComponent!.nodeScope.getAllNodesWithVariantRecursive(
                variant!
              );
            nodes.forEach((node) => {
              node.queueInput(
                event.parameterName,
                node.getParameter(event.parameterName)
              );
            });
          }
        }
      },
      variantDeleted: (event: Event): void => {
        graphModel.deleteVariant(event.variantTypeName!, event.variantName!);
      },
      variantChanged: (event: Event): void => {
        const nodeModel = componentModel!.getNodeWithId(event.nodeId);
        if (nodeModel) {
          const variant = graphModel.getVariant(
            nodeModel.type,
            event.variantName!
          );

          nodeModel.setVariant(variant!);
        }
      },
      variantRenamed: (event: Event): void => {
        const variant = graphModel.getVariant(
          event.variantTypeName!,
          event.oldVariantName!
        );
        if (variant?.variantName) {
          variant.name = variant.variantName;
        }
      },
      defaultStateTransitionChanged: (event: Event): void => {
        const nodeModel = componentModel!.getNodeWithId(event.nodeId);
        nodeModel?.setDefaultStateTransition(event.curve, event.state);
      },
      stateTransitionsChanged: (event: Event): void => {
        const nodeModel = componentModel!.getNodeWithId(event.nodeId);
        if (nodeModel && event.parameterName) {
          nodeModel.setStateTransitionParamter(
            event.parameterName!,
            event.curve,
            event.state
          );
        }
      },
      variantDefaultStateTransitionChanged: (event: Event): void => {
        graphModel.updateVariantDefaultStateTransition(
          event.variantName!,
          event.variantTypeName!,
          event.curve,
          event.state
        );
      },
      variantStateTransitionsChanged: (event: Event): void => {
        graphModel.updateVariantStateTransition(event);
      },
      routerIndexChanged: (event: Event): void => {
        graphModel.routerIndex = event.data as RouterIndex;
      },
    };

  if (eventHandlers.hasOwnProperty(event.type)) {
    await eventHandlers[event.type](event);
    context.scheduleUpdate();
  } else {
    console.log("Unknown event", event);
  }
}

export { handleEvent };
