import { Model, Collection, NodeDefinition } from "@fluxscape/runtime";

interface Internal {
  modifyId?: string;
  collection?: Collection<Model>;
}

export const node: NodeDefinition<Internal> = {
  name: "CollectionRemove",
  docs: "https://docs.noodl.net/nodes/data/array/remove-from-array",
  displayNodeName: "Remove Object From Array",
  shortDesc:
    "A collection of models, mainly used together with a For Each Node.",
  category: "Data",
  usePortAsLabel: "collectionId",
  color: "data",
  initialize: function () {},
  inputs: {
    collectionId: {
      type: {
        name: "string",
        identifierOf: "CollectionName",
        identifierDisplayName: "Array Ids",
      },
      displayName: "Array Id",
      group: "General",
      set: function (value: string | Collection<Model>) {
        if (value instanceof Collection) value = value.getId();
        // Can be passed as collection as well
        // @ts-expect-error
        this.setCollectionID(value);
      },
    },
    modifyId: {
      type: { name: "string", allowConnectionsOnly: true },
      displayName: "Object Id",
      group: "Modify",
      set: function (value: string) {
        this._internal.modifyId = value;
      },
    },
    remove: {
      displayName: "Do",
      group: "Actions",
      valueChangedToTrue: function () {
        const _this = this;
        const internal = this._internal;

        this.scheduleAfterInputsHaveUpdated(function () {
          if (internal.modifyId === undefined) return;
          if (internal.collection === undefined) return;

          const model = Model.get(internal.modifyId);
          internal.collection.remove(model);
          _this.sendSignalOnOutput("modified");
        });
      },
    },
  },
  outputs: {
    modified: {
      group: "Events",
      type: "signal",
      displayName: "Done",
    },
  },
  prototypeExtensions: {
    setCollectionID: function (id: string) {
      // @ts-expect-error
      this.setCollection(Collection.get(id));
    },
    setCollection: function (collection: Collection<Model>) {
      this._internal.collection = collection;
    },
  },
};
