import { Collection } from "../../../collection";
import { Model } from "../../../model";
import { CloudStore } from "../../../api/cloudstore";
import * as QueryUtils from "../../../api/queryutils";
import { NodeContext } from "../../../nodecontext";
import { EditorConnection } from "../../../editorconnection";
import { NodeDefinition } from "../../../nodedefinition";
import { GraphModel } from "../../../models/graphmodel";
import { Port } from "../../../models/componentmodel";

interface Internal {
  collectionChangedCallback: () => void;
  cloudStoreEvents: (args: any) => void;
  enabled: boolean;
  filterSettings: Record<string, any>;
  filterParameters: Record<string, any>;
  collection?: any;
  collectionName?: string;
  visualFilter?: any;
  visualSorting?: any;
  filteredCollection?: any;
}

export const node: NodeDefinition<Internal> = {
  name: "FilterDBModels",
  docs: "https://docs.noodl.net/nodes/data/cloud-data/filter-records",
  displayNodeName: "Filter Records",
  shortDesc: "Filter, sort and limit array",
  category: "Data",
  color: "data",
  initialize() {
    const _this = this;

    this._internal.collectionChangedCallback = function () {
      if (_this.isInputConnected("filter") === true) return;

      // @ts-expect-error
      _this.scheduleFilter();
    };

    this._internal.cloudStoreEvents = function (args: any) {
      if (_this.isInputConnected("filter") === true) return;

      if (_this._internal.visualFilter === undefined) return;
      if (_this._internal.collection === undefined) return;
      if (args.collection !== _this._internal.collectionName) return;

      if (
        args.objectId !== undefined &&
        _this._internal.collection.contains(Model.get(args.objectId))
      )
        // @ts-expect-error
        _this.scheduleFilter();
    };

    CloudStore.instance.on("save", this._internal.cloudStoreEvents);

    this._internal.enabled = true;
    this._internal.filterSettings = {};
    this._internal.filterParameters = {};
  },
  getInspectInfo() {
    const collection = this._internal.filteredCollection;

    if (!collection) {
      return { type: "text", value: "[Not executed yet]" };
    }

    return [
      {
        type: "text",
        value: "Id: " + collection.getId(),
      },
      {
        type: "value",
        value: collection.items,
      },
    ];
  },
  inputs: {
    items: {
      type: "array",
      displayName: "Items",
      group: "General",
      set(value: any) {
        // @ts-expect-error
        this.bindCollection(value);
        // @ts-expect-error
        if (this.isInputConnected("filter") === false) this.scheduleFilter();
      },
    },
    enabled: {
      type: "boolean",
      group: "General",
      displayName: "Enabled",
      default: true,
      set(value: boolean) {
        this._internal.enabled = value;
        // @ts-expect-error
        if (this.isInputConnected("filter") === false) this.scheduleFilter();
      },
    },
    filter: {
      type: "signal",
      group: "Actions",
      displayName: "Filter",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleFilter();
      },
    },
  },
  outputs: {
    items: {
      type: "array",
      displayName: "Items",
      group: "General",
      getter() {
        return this._internal.filteredCollection;
      },
    },
    firstItemId: {
      type: "string",
      displayName: "First Record Id",
      group: "General",
      getter() {
        if (this._internal.filteredCollection !== undefined) {
          const firstItem = this._internal.filteredCollection.get(0);
          if (firstItem !== undefined) return firstItem.getId();
        }
      },
    },
    count: {
      type: "number",
      displayName: "Count",
      group: "General",
      getter() {
        return this._internal.filteredCollection
          ? this._internal.filteredCollection.size()
          : 0;
      },
    },
    modified: {
      group: "Events",
      type: "signal",
      displayName: "Filtered",
    },
  },
  prototypeExtensions: {
    unbindCurrentCollection() {
      const collection = this._internal.collection;
      if (!collection) return;
      collection.off("change", this._internal.collectionChangedCallback);
      this._internal.collection = undefined;
    },
    bindCollection(collection: any) {
      // @ts-expect-error
      this.unbindCurrentCollection();
      this._internal.collection = collection;
      collection &&
        collection.on("change", this._internal.collectionChangedCallback);
    },
    _onNodeDeleted() {
      // @ts-expect-error
      Node.prototype._onNodeDeleted.call(this);
      // @ts-expect-error
      this.unbindCurrentCollection();

      CloudStore.instance.off("save", this._internal.cloudStoreEvents);
    },
    getLimit() {
      const filterSettings = this._internal.filterSettings;

      if (!filterSettings["filterEnableLimit"]) return;
      else return filterSettings["filterLimit"] || 10;
    },
    getSkip() {
      const filterSettings = this._internal.filterSettings;

      if (!filterSettings["filterEnableLimit"]) return;
      else return filterSettings["filterSkip"] || 0;
    },
    scheduleFilter() {
      // @ts-expect-error
      if (this.collectionChangedScheduled) return;
      // @ts-expect-error
      this.collectionChangedScheduled = true;

      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.collectionChangedScheduled = false;
        if (!this._internal.collection) return;

        // Apply filter and write to output collection
        let filtered = [].concat(this._internal.collection.items);

        if (this._internal.enabled) {
          const _filter = this._internal.visualFilter;
          if (_filter !== undefined) {
            const filter = QueryUtils.convertVisualFilter(_filter, {
              queryParameters: this._internal.filterParameters,
              collectionName: this._internal.collectionName,
            });
            if (filter)
              filtered = filtered.filter((m) =>
                QueryUtils.matchesQuery(m, filter)
              );
          }

          const _sort = this._internal.visualSorting;
          if (_sort !== undefined && _sort.length > 0) {
            const sort = QueryUtils.convertVisualSorting(_sort);
            if (sort) filtered.sort(QueryUtils.compareObjects.bind(this, sort));
          }

          // @ts-expect-error
          const skip = this.getSkip();
          if (skip) filtered = filtered.slice(skip, filtered.length);

          // @ts-expect-error
          const limit = this.getLimit();
          if (limit) filtered = filtered.slice(0, limit);
        }

        this._internal.filteredCollection = Collection.create(filtered);

        this.sendSignalOnOutput("modified");
        this.flagOutputDirty("firstItemId");
        this.flagOutputDirty("items");
        this.flagOutputDirty("count");
      });
    },
    setCollectionName(name: string) {
      this._internal.collectionName = name;
    },
    setVisualFilter(value: any) {
      this._internal.visualFilter = value;

      // @ts-expect-error
      if (this.isInputConnected("filter") === false) this.scheduleFilter();
    },
    setVisualSorting(value: any) {
      this._internal.visualSorting = value;

      // @ts-expect-error
      if (this.isInputConnected("filter") === false) this.scheduleFilter();
    },
    setFilterParameter: function (
      // @ts-expect-error
      this: FilterDBModelsNode,
      name: string,
      value: any
    ) {
      this._internal.filterParameters[name] = value;

      if (this.isInputConnected("filter") === false) this.scheduleFilter();
    },
    registerInputIfNeeded(name: string) {
      if (this.hasInput(name)) {
        return;
      }

      if (name === "collectionName")
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setCollectionName.bind(this),
        });

      if (name === "visualFilter")
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setVisualFilter.bind(this),
        });

      if (name === "visualSorting")
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setVisualSorting.bind(this),
        });

      if (name.startsWith("fp-"))
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setFilterParameter.bind(this, name.substring("fp-".length)),
        });

      this.registerInput(name, {
        set(value: any) {
          this._internal.filterSettings[name] = value;
          // @ts-expect-error
          if (this.isInputConnected("filter") === false) this.scheduleFilter();
        },
      });
    },
  },
};

function updatePorts(
  nodeId: string,
  parameters: any,
  editorConnection: EditorConnection,
  dbCollections: any[]
) {
  const ports: Port[] = [];

  ports.push({
    name: "collectionName",
    type: {
      name: "enum",
      enums:
        dbCollections !== undefined
          ? dbCollections.map((c) => {
              return { value: c.name, label: c.name };
            })
          : [],
      allowEditOnly: true,
    },
    displayName: "Class",
    plug: "input",
    group: "General",
  });

  ports.push({
    type: "boolean",
    plug: "input",
    group: "Limit",
    name: "filterEnableLimit",
    displayName: "Use limit",
  });

  if (parameters["filterEnableLimit"]) {
    ports.push({
      type: "number",
      default: 10,
      plug: "input",
      group: "Limit",
      name: "filterLimit",
      displayName: "Limit",
    });

    ports.push({
      type: "number",
      default: 0,
      plug: "input",
      group: "Limit",
      name: "filterSkip",
      displayName: "Skip",
    });
  }

  if (parameters.collectionName !== undefined) {
    const c = dbCollections.find((c) => c.name === parameters.collectionName);
    if (c && c.schema && c.schema.properties) {
      const schema = JSON.parse(JSON.stringify(c.schema));

      const _supportedTypes = {
        Boolean: true,
        String: true,
        Date: true,
        Number: true,
        Pointer: true,
      };
      for (const key in schema.properties) {
        if (!_supportedTypes[schema.properties[key].type]) {
          delete schema.properties[key];
        }
      }

      ports.push({
        name: "visualFilter",
        plug: "input",
        type: { name: "query-filter", schema: schema, allowEditOnly: true },
        displayName: "Filter",
        group: "Filter",
      });

      ports.push({
        name: "visualSorting",
        plug: "input",
        type: { name: "query-sorting", schema: schema, allowEditOnly: true },
        displayName: "Sorting",
        group: "Sorting",
      });
    }

    if (parameters.visualFilter !== undefined) {
      // Find all input ports
      const uniqueInputs: Record<string, boolean> = {};
      _collectInputs(uniqueInputs, parameters.visualFilter);

      Object.keys(uniqueInputs).forEach((input) => {
        ports.push({
          name: "fp-" + input,
          plug: "input",
          type: "*",
          displayName: input,
          group: "Filter Parameters",
        });
      });
    }
  }

  editorConnection.sendDynamicPorts(nodeId, ports);
}

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  graphModel.on("nodeAdded.FilterDBModels", function (node: any) {
    updatePorts(
      node.id,
      node.parameters,
      // @ts-expect-error
      context.editorConnection,
      graphModel.getMetaData("dbCollections")
    );

    node.on("parameterUpdated", function () {
      updatePorts(
        node.id,
        node.parameters,
        // @ts-expect-error
        context.editorConnection,
        graphModel.getMetaData("dbCollections")
      );
    });

    graphModel.on("metadataChanged.dbCollections", function (data: any) {
      CloudStore.invalidateCollections();
      // @ts-expect-error
      updatePorts(node.id, node.parameters, context.editorConnection, data);
    });

    graphModel.on("metadataChanged.systemCollections", function (data: any) {
      CloudStore.invalidateCollections();
      // @ts-expect-error
      updatePorts(node.id, node.parameters, context.editorConnection, data);
    });
  });
}

interface QueryRule {
  rules?: QueryRule[];
  input?: string;
}

function _collectInputs(
  uniqueInputs: Record<string, boolean>,
  query: QueryRule | undefined
) {
  if (query === undefined) {
    return;
  }

  if (query.rules !== undefined) {
    query.rules.forEach((r: QueryRule) => _collectInputs(uniqueInputs, r));
  } else if (query.input !== undefined) {
    uniqueInputs[query.input] = true;
  }
}
