import type { GraphModel } from "../../../models/graphmodel";
import type { NodeContext } from "../../../nodecontext";
import { NodeDefinition } from "../../../nodedefinition";

interface Internal {
  onModelChangedCallback?: (args: { name: string }) => void;
  model?: any;
  error?: string;
  [key: string]: any;
}

export const node: NodeDefinition<Internal> = {
  name: "net.noodl.user.User",
  docs: "https://docs.noodl.net/nodes/data/user/user-node",
  displayNodeName: "User",
  category: "Cloud Services",
  color: "data",
  initialize() {
    const _this = this;
    this._internal.onModelChangedCallback = function (args) {
      if (_this.isInputConnected("fetch")) return;

      if (_this.hasOutput("prop-" + args.name))
        _this.flagOutputDirty("prop-" + args.name);

      if (_this.hasOutput("changed-" + args.name))
        _this.sendSignalOnOutput("changed-" + args.name);

      _this.sendSignalOnOutput("changed");
    };

    // @ts-expect-error
    const userService = NoodlRuntime.Services.UserService.forScope(
      // @ts-expect-error
      this.nodeScope.modelScope
    );

    // @ts-expect-error
    this.setUserModel(userService.current);
    userService.on("loggedIn", () => {
      // @ts-expect-error
      this.setUserModel(userService.current);

      if (this.hasOutput("loggedIn")) this.sendSignalOnOutput("loggedIn");
    });

    userService.on("sessionGained", () => {
      // @ts-expect-error
      this.setUserModel(userService.current);
    });

    userService.on("loggedOut", () => {
      // @ts-expect-error
      this.setUserModel(undefined);
      if (this.hasOutput("loggedOut")) this.sendSignalOnOutput("loggedOut");
    });

    userService.on("sessionLost", () => {
      // @ts-expect-error
      this.setUserModel(undefined);
      if (this.hasOutput("sessionLost")) this.sendSignalOnOutput("sessionLost");
    });
  },
  getInspectInfo() {
    const model = this._internal.model;
    if (!model) return "[No Model]";

    return [
      { type: "text", value: "Id: " + model.getId() },
      { type: "value", value: this._internal.model.data },
    ];
  },
  outputs: {
    id: {
      type: "string",
      displayName: "Id",
      group: "General",
      getter() {
        return this._internal.model !== undefined
          ? this._internal.model.getId()
          : undefined;
      },
    },
    fetched: {
      type: "signal",
      displayName: "Fetched",
      group: "Events",
    },
    changed: {
      type: "signal",
      displayName: "Changed",
      group: "Events",
    },
    failure: {
      type: "signal",
      displayName: "Failure",
      group: "Events",
    },
    error: {
      type: "string",
      displayName: "Error",
      group: "Error",
      getter() {
        return this._internal.error;
      },
    },
    username: {
      type: "string",
      displayName: "Username",
      group: "General",
      getter() {
        return this._internal.model !== undefined
          ? this._internal.model.get("username")
          : undefined;
      },
    },
    email: {
      type: "string",
      displayName: "Email",
      group: "General",
      getter() {
        return this._internal.model !== undefined
          ? this._internal.model.get("email")
          : undefined;
      },
    },
    authenticated: {
      type: "boolean",
      displayName: "Authenticated",
      group: "General",
      getter() {
        return this._internal.model !== undefined;
      },
    },
  },
  inputs: {
    fetch: {
      displayName: "Fetch",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleFetch();
      },
    },
  },
  methods: {
    _onNodeDeleted() {
      // @ts-expect-error
      Node.prototype._onNodeDeleted.call(this);
      if (this._internal.model)
        this._internal.model.off(
          "change",
          this._internal.onModelChangedCallback
        );
    },
    scheduleOnce(type: string, cb: () => void) {
      const _this = this;
      const _type = "hasScheduled" + type;
      if (this._internal[_type]) return;
      this._internal[_type] = true;
      this.scheduleAfterInputsHaveUpdated(function () {
        _this._internal[_type] = false;
        cb();
      });
    },
    setError(err: string) {
      this._internal.error = err;
      this.flagOutputDirty("error");
      this.sendSignalOnOutput("failure");

      if (this.context.editorConnection) {
        this.context.editorConnection.sendWarning(
          this.nodeScope!.componentOwner.name,
          this.id,
          "user-warning",
          {
            message: err,
            showGlobally: true,
          }
        );
      }
    },
    clearWarnings() {
      if (this.context.editorConnection) {
        this.context.editorConnection.clearWarning(
          this.nodeScope!.componentOwner.name,
          this.id,
          "user-warning"
        );
      }
    },
    setUserModel(model: any) {
      const internal = this._internal;

      if (internal.model !== model) {
        // Check if we need to change model
        if (internal.model)
          // Remove old listener if existing
          internal.model.off("change", internal.onModelChangedCallback);

        internal.model = model;
        if (model) model.on("change", internal.onModelChangedCallback);
      }
      this.flagOutputDirty("id");
      this.flagOutputDirty("authenticated");
      this.flagOutputDirty("email");
      this.flagOutputDirty("username");

      // Notify all properties changed
      if (model)
        for (const key in model.data) {
          if (this.hasOutput("prop-" + key))
            this.flagOutputDirty("prop-" + key);
        }
    },
    scheduleFetch() {
      const internal = this._internal;

      // @ts-expect-error
      this.scheduleOnce("Fetch", () => {
        // @ts-expect-error
        const userService = NoodlRuntime.Services.UserService.forScope(
          this.nodeScope!.modelScope
        );
        userService.fetchCurrentUser({
          success: (response: any) => {
            // @ts-expect-error
            this.setUserModel(userService.current);

            this.sendSignalOnOutput("fetched");
          },
          error: (err: string) => {
            // @ts-expect-error
            this.setError(err || "Failed to fetch.");
          },
        });
      });
    },
    registerOutputIfNeeded(name: string) {
      if (this.hasOutput(name)) {
        return;
      }

      if (
        name === "loggedOut" ||
        name === "loggedIn" ||
        name === "sessionLost"
      ) {
        this.registerOutput(name, {
          getter: () => {} /* No getter needed, signal */,
        });
        return;
      }

      if (name.startsWith("prop-"))
        this.registerOutput(name, {
          // @ts-expect-error
          getter: this.getUserProperty.bind(
            this,
            name.substring("prop-".length)
          ),
        });
    },
    getUserProperty(name: string) {
      return this._internal.model !== undefined
        ? this._internal.model.get(name)
        : undefined;
    },
  },
};

interface Port {
  type:
    | {
        name: string;
      }
    | string;
  plug: string;
  group: string;
  name: string;
  displayName: string;
}

interface SchemaProperty {
  type: string;
}

interface Collection {
  name: string;
  schema?: {
    properties: {
      [key: string]: SchemaProperty;
    };
  };
}

function updatePorts(
  nodeId: string,
  parameters: any,
  editorConnection: any,
  systemCollections: Collection[]
) {
  const ports: Port[] = [];

  if (systemCollections) {
    // Fetch ports from collection keys
    const c = systemCollections.find((c) => c.name === "_User");
    if (c && c.schema && c.schema.properties) {
      const props = c.schema.properties;
      const _ignoreKeys = ["authData", "password", "username", "email"];
      for (const key in props) {
        if (_ignoreKeys.indexOf(key) !== -1) continue;

        const p = props[key];
        if (ports.find((_p) => _p.name === key)) continue;

        if (p.type === "Relation") {
        } else {
          // Other schema type ports
          const _typeMap: { [key: string]: string } = {
            String: "string",
            Boolean: "boolean",
            Number: "number",
            Date: "date",
          };

          ports.push({
            type: {
              name: _typeMap[p.type] ? _typeMap[p.type] : "*",
            },
            plug: "output",
            group: "Properties",
            name: "prop-" + key,
            displayName: key,
          });

          ports.push({
            type: "signal",
            plug: "output",
            group: "Changed Events",
            displayName: key + " Changed",
            name: "changed-" + key,
          });
        }
      }
    }
  }

  // @ts-expect-error
  if (typeof _noodl_cloud_runtime_version === "undefined") {
    // On the client we have some extra outputs
    ports.push({
      plug: "output",
      name: "loggedIn",
      type: "signal",
      displayName: "Logged In",
      group: "Events",
    });

    ports.push({
      plug: "output",
      name: "loggedOut",
      type: "signal",
      displayName: "Logged Out",
      group: "Events",
    });

    ports.push({
      plug: "output",
      name: "sessionLost",
      type: "signal",
      displayName: "Session Lost",
      group: "Events",
    });
  }

  editorConnection.sendDynamicPorts(nodeId, ports);
}

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  function _managePortsForNode(node: any) {
    updatePorts(
      node.id,
      node.parameters,
      context.editorConnection,
      graphModel.getMetaData("systemCollections")
    );

    node.on("parameterUpdated", function (event: any) {
      updatePorts(
        node.id,
        node.parameters,
        context.editorConnection,
        graphModel.getMetaData("systemCollections")
      );
    });

    graphModel.on("metadataChanged.systemCollections", function (data: any) {
      updatePorts(node.id, node.parameters, context.editorConnection, data);
    });
  }

  graphModel.on("editorImportComplete", () => {
    graphModel.on("nodeAdded.net.noodl.user.User", function (node: any) {
      _managePortsForNode(node);
    });

    for (const node of graphModel.getNodesWithType("net.noodl.user.User")) {
      _managePortsForNode(node);
    }
  });
}
