import { createSetter } from "@fluxscape/runtime/src/edgetriggeredinput";
import { GraphModel } from "@fluxscape/runtime";
import { NodeContext } from "@fluxscape/runtime/src/nodecontext";
import { NodeDefinition } from "@fluxscape/runtime/src/nodedefinition";

interface ClosePopupNodeInternal {
  resultValues: Record<string, any>;
  results?: string[];
  closeActions?: string[];
  closeCallback?: (action: string, resultValues: Record<string, any>) => void;
  closeAction?: string;
  hasScheduledClose?: boolean;
}

export const node: NodeDefinition<ClosePopupNodeInternal> = {
  name: "NavigationClosePopup",
  displayNodeName: "Close Popup",
  category: "Navigation",
  docs: "https://docs.noodl.net/nodes/popups/close-popup",
  _internal: {
    resultValues: {},
  },
  inputs: {
    results: {
      type: { name: "stringlist", allowEditOnly: true },
      group: "Results",
      set(value: string[]) {
        this._internal.results = value;
      },
    },
    closeActions: {
      type: { name: "stringlist", allowEditOnly: true },
      group: "Close Actions",
      set(value: string[]) {
        this._internal.closeActions = value;
      },
    },
    close: {
      type: "Signal",
      displayName: "Close",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleClose();
      },
    },
  },
  methods: {
    setResultValue(key: string, value: any) {
      this._internal.resultValues[key] = value;
    },
    _setCloseCallback(
      cb: (action: string, resultValues: Record<string, any>) => void
    ) {
      this._internal.closeCallback = cb;
    },
    scheduleClose() {
      const _this = this;
      const internal = this._internal;
      if (!internal.hasScheduledClose) {
        internal.hasScheduledClose = true;
        this.scheduleAfterInputsHaveUpdated(function () {
          internal.hasScheduledClose = false;
          // @ts-expect-error
          _this.close();
        });
      }
    },
    close() {
      if (this._internal.closeCallback) {
        this._internal.closeCallback(
          this._internal.closeAction || "",
          this._internal.resultValues
        );
      }
    },
    closeActionTriggered(name: string) {
      this._internal.closeAction = name;
      // @ts-expect-error
      this.scheduleClose();
    },
    registerInputIfNeeded(name: string) {
      if (this.hasInput(name)) {
        return;
      }

      if (name.startsWith("result-"))
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setResultValue.bind(this, name.substring("result-".length)),
        });

      if (name.startsWith("closeAction-"))
        return this.registerInput(name, {
          set: createSetter({
            // @ts-expect-error
            valueChangedToTrue: this.closeActionTriggered.bind(this, name),
          }),
        });
    },
  },
};

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  function _managePortsForNode(node: any) {
    function _updatePorts() {
      const ports: any[] = [];

      // Add results inputs
      const results = node.parameters["results"];
      if (results) {
        const resultsArray = results ? results.split(",") : undefined;
        for (const p of resultsArray) {
          ports.push({
            type: {
              name: "*",
            },
            plug: "input",
            group: "Results",
            name: "result-" + p,
            displayName: p,
          });
        }
      }

      // Add close actions
      const closeActions = node.parameters["closeActions"];
      if (closeActions) {
        const closeActionsArray = closeActions
          ? closeActions.split(",")
          : undefined;
        for (const p of closeActionsArray) {
          ports.push({
            type: "signal",
            plug: "input",
            group: "Close Actions",
            name: "closeAction-" + p,
            displayName: p,
          });
        }
      }

      context.editorConnection?.sendDynamicPorts(node.id, ports);
    }

    _updatePorts();
    node.on("parameterUpdated", function (event: any) {
      if (event.name === "results" || event.name === "closeActions") {
        _updatePorts();
      }
    });
  }

  graphModel.on("editorImportComplete", () => {
    graphModel.on("nodeAdded.NavigationClosePopup", function (node: any) {
      _managePortsForNode(node);
    });

    for (const node of graphModel.getNodesWithType("NavigationClosePopup")) {
      _managePortsForNode(node);
    }
  });
}
