import { EditorConnection } from "@fluxscape/runtime";
import { GraphModel } from "@fluxscape/runtime";

export default class GraphWarnings {
  private graphModel: any;
  private editorConnection: any;

  constructor(graphModel: GraphModel, editorConnection: EditorConnection) {
    this.graphModel = graphModel;
    this.editorConnection = editorConnection;

    this.graphModel
      .getAllComponents()
      .forEach((c: any) => this._bindComponentModel(c));
    this.graphModel.on(
      "componentAdded",
      (c: any) => this._bindComponentModel(c),
      this
    );
    this.graphModel.on(
      "componentRemoved",
      (c: any) => c.removeListenersWithRef(this),
      this
    );
  }

  private _bindComponentModel(c: any) {
    c.on("rootAdded", () => this._evaluateWarnings(c), this);
    c.on(
      "rootRemoved",
      (root: any) => {
        this.editorConnection.clearWarning(
          c.name,
          root,
          "multiple-visual-roots-warning"
        );
        this._evaluateWarnings(c);
      },
      this
    );
    this._evaluateWarnings(c);
  }

  private _evaluateWarnings(c: any) {
    const roots = c.getRoots();

    if (roots.length === 0) return;

    this.editorConnection.clearWarning(
      c.name,
      roots[0],
      "multiple-visual-roots-warning"
    );
    for (let i = 1; i < roots.length; i++) {
      this.editorConnection.sendWarning(
        c.name,
        roots[i],
        "multiple-visual-roots-warning",
        {
          message:
            "This node is detached from the main node tree<br>and won't be rendered",
          level: "info",
        }
      );
    }
  }

  dispose() {
    this.graphModel.getAllComponents().forEach((c: any) => {
      c.removeListenersWithRef(this);
    });

    this.graphModel.removeListenersWithRef(this);
  }
}
