import { NodeDefinition, Model, Node } from "@fluxscape/runtime";

interface Internal {
  name: string;
  value: any;
  variablesModel: any;
  onModelChangedCallback: (args: { name: string }) => void;
}

export const node: NodeDefinition<Internal> = {
  name: "Variable2",
  displayNodeName: "Variable",
  docs: "https://docs.noodl.net/nodes/data/variable/variable-node",
  category: "Data",
  usePortAsLabel: "name",
  color: "data",
  initialize() {
    const internal = this._internal;

    this._internal.onModelChangedCallback = (args) => {
      if (!this.isInputConnected("fetch") && args.name === internal.name) {
        this.sendSignalOnOutput("changed");
        this.flagOutputDirty("value");
      }
    };

    internal.variablesModel = Model.get("--ndl--global-variables");
    internal.variablesModel.on("change", this._internal.onModelChangedCallback);
  },
  getInspectInfo() {
    if (this._internal.name) {
      return this._internal.variablesModel.get(this._internal.name);
    }
    return "[No value set]";
  },
  outputs: {
    name: {
      type: "string",
      displayName: "Name",
      group: "General",
      getter() {
        return this._internal.name;
      },
    },
    changed: {
      type: "signal",
      displayName: "Changed",
      group: "Events",
    },
    fetched: {
      type: "signal",
      displayName: "Fetched",
      group: "Events",
    },
    value: {
      type: "*",
      displayName: "Value",
      group: "General",
      getter() {
        const internal = this._internal;
        if (!internal.name) return;
        return internal.variablesModel.get(internal.name);
      },
    },
  },
  inputs: {
    name: {
      type: {
        name: "string",
        identifierOf: "VariableName",
        identifierDisplayName: "Variable names",
      },
      displayName: "Name",
      group: "General",
      set(value: string) {
        if (!this.isInputConnected("fetch")) {
          // @ts-expect-error
          this.setVariableName(value);
        } else {
          this._internal.name = value; // Wait to fetch data
          this.flagOutputDirty("name");
        }
      },
    },
    fetch: {
      displayName: "Fetch",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.setVariableName(this._internal.name);
      },
    },
    value: {
      type: "*",
      displayName: "Value",
      group: "General",
      set(value: any) {
        this._internal.value = value;
        // @ts-expect-error
        this.scheduleStore();
      },
    },
  },
  prototypeExtensions: {
    scheduleStore() {
      // @ts-expect-error
      if (this.hasScheduledStore) return;
      // @ts-expect-error
      this.hasScheduledStore = true;

      const internal = this._internal;
      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.hasScheduledStore = false;
        internal.variablesModel.set(internal.name, internal.value);
      });
    },
    setVariableName(name: string) {
      this._internal.name = name;
      this.flagOutputDirty("name");
      this.flagOutputDirty("value");
      this.sendSignalOnOutput("fetched");
    },
    _onNodeDeleted() {
      Node.prototype._onNodeDeleted.call(this);
      this._internal.variablesModel.off(
        "change",
        this._internal.onModelChangedCallback
      );
    },
  },
};
