import NoodlRuntime from "@fluxscape/runtime/src/runtime";
import { RouterHandler } from "../nodes/navigation/router-handler";

interface ShowPopupOptions {
  onClosePopup: (action: string | undefined, results: any) => void;
}

interface NavigateOptions {
  query?: Record<string, string>;
}

interface Navigation {
  _noodlRuntime: NoodlRuntime | undefined;
  showPopup(
    componentPath: string,
    params: any
  ): Promise<{ action: string | undefined; parameters: any }>;
  navigate(routerName: string, targetPageName: string, params: any): void;
  navigateToPath(path: string, options?: NavigateOptions): void;
}

const navigation: Navigation = {
  /**
   * This is set by "packages/noodl-viewer-react/src/noodl-js-api.js"
   */
  _noodlRuntime: undefined,

  async showPopup(
    componentPath: string,
    params: any
  ): Promise<{ action: string | undefined; parameters: any }> {
    return new Promise<{ action: string | undefined; parameters: any }>(
      (resolve) => {
        navigation._noodlRuntime!.context.showPopup(componentPath, params, {
          onClosePopup: (action, results) => {
            resolve({
              action: action?.replace("closeAction-", ""),
              parameters: results,
            });
          },
        });
      }
    );
  },

  navigate(routerName: string, targetPageName: string, params: any): void {
    RouterHandler.instance.navigate(routerName, {
      target: targetPageName,
      params: params,
    });
  },

  navigateToPath(path: string, options?: NavigateOptions): void {
    let hashPath: string | undefined, urlPath: string | undefined;
    const navigationPathType =
      NoodlRuntime.instance.getProjectSettings()["navigationPathType"];
    if (navigationPathType === undefined || navigationPathType === "hash") {
      hashPath = path;
    } else {
      urlPath = path;
    }

    const query: string[] = [];
    if (options && options.query !== undefined) {
      for (const key in options.query) {
        query.push(`${key}=${options.query[key]}`);
      }
    }

    const compiledUrl =
      (urlPath !== undefined ? urlPath : "") +
      (query.length >= 1 ? "?" + query.join("&") : "") +
      (hashPath !== undefined ? "#" + hashPath : "");

    window.history.pushState({}, "", compiledUrl);
    dispatchEvent(new PopStateEvent("popstate", {}));
  },
};

export default navigation;
