import type { ComponentModel } from "../models/componentmodel";
import { Node } from "../node";
import { NodeScope } from "../nodescope";

let componentIdCounter = 0;

interface Internal {
  childRoot: any;
  componentOutputValues: Record<string, any>;
  componentOutputs: any[];
  componentInputs: any[];
  inputValues: Record<string, any>;
  roots: any[];
  instanceId: string;
  creatorCallbacks?: {
    onOutputChanged?: (name: string, value: any, prevValue: any) => void;
  };
}

export class ComponentInstanceNode extends Node<Internal> {
  nodeScope: NodeScope;
  parentNodeScope: NodeScope | null;
  _internal: Internal;
  componentModel?: ComponentModel;
  parent?: any;
  cachedChildren?: any;
  name?: string;

  constructor(context: any, id: string, parentNodeScope: NodeScope | null) {
    super(context, id);

    this.nodeScope = new NodeScope(context, this);
    this.parentNodeScope = parentNodeScope;
    this._internal = {
      childRoot: null,
      componentOutputValues: {},
      componentOutputs: [],
      componentInputs: [],
      inputValues: {},
      roots: [],
      instanceId: "__$ndl_componentInstaceId" + componentIdCounter,
    };

    this.nodeScope.modelScope = parentNodeScope
      ? parentNodeScope.modelScope
      : undefined;

    componentIdCounter++;
  }

  async setComponentModel(componentModel: ComponentModel) {
    this.componentModel = componentModel;
    const self = this;

    await this.nodeScope.setComponentModel(componentModel);

    this._internal.componentInputs =
      this.nodeScope.getNodesWithType("Component Inputs");
    this._internal.componentOutputs =
      this.nodeScope.getNodesWithType("Component Outputs");

    Object.values(componentModel.getInputPorts()).forEach(
      this.registerComponentInputPort.bind(this)
    );
    Object.values(componentModel.getOutputPorts()).forEach(
      this.registerComponentOutputPort.bind(this)
    );

    const roots = componentModel.roots || [];
    this._internal.roots = roots.map((id: string) =>
      this.nodeScope.getNodeWithId(id)
    );

    componentModel.on(
      "rootAdded",
      (id: string) => {
        this._internal.roots.push(this.nodeScope.getNodeWithId(id));
        this.forceUpdate();
      },
      this
    );

    componentModel.on(
      "rootRemoved",
      function (this: ComponentInstanceNode, id: string) {
        const index = this._internal.roots.findIndex(
          (root: any) => root.id === id
        );
        if (index !== -1) {
          this._internal.roots.splice(index, 1);
        }
        this.forceUpdate();
      },
      this
    );

    componentModel.on(
      "inputPortAdded",
      this.registerComponentInputPort.bind(this),
      this
    );
    componentModel.on(
      "outputPortAdded",
      this.registerComponentOutputPort.bind(this),
      this
    );

    componentModel.on(
      "inputPortRemoved",
      function (port: { name: string }) {
        if (self.hasInput(port.name)) {
          self.deregisterInput(port.name);
        }
      },
      this
    );

    componentModel.on(
      "outputPortRemoved",
      function (this: ComponentInstanceNode, port: { name: string }) {
        if (this.hasOutput(port.name)) {
          self.deregisterOutput(port.name);
        }
      },
      this
    );

    componentModel.on(
      "nodeAdded",
      function (node: { type: string; id: string }) {
        if (node.type === "Component Inputs") {
          self._internal.componentInputs.push(
            self.nodeScope.getNodeWithId(node.id)
          );
        } else if (node.type === "Component Outputs") {
          self._internal.componentOutputs.push(
            self.nodeScope.getNodeWithId(node.id)
          );
        }
      },
      this
    );

    componentModel.on(
      "nodeRemoved",
      function (node: { type: string; id: string }) {
        function removeNodesWithId(array: any[], id: string) {
          return array.filter((e) => e.id !== id);
        }
        if (node.type === "Component Inputs") {
          self._internal.componentInputs = removeNodesWithId(
            self._internal.componentInputs,
            node.id
          );
        } else if (node.type === "Component Outputs") {
          self._internal.componentOutputs = removeNodesWithId(
            self._internal.componentOutputs,
            node.id
          );
        }
      },
      this
    );

    componentModel.on(
      "renamed",
      function (event: { newName: string }) {
        self.name = event.newName;
      },
      this
    );
  }

  _onNodeDeleted() {
    if (this.componentModel) {
      this.componentModel.removeListenersWithRef(this);
      this.componentModel = undefined;
    }

    this.nodeScope.reset();
    super._onNodeDeleted();
  }

  registerComponentInputPort(port: { name: string }) {
    this.registerInput(port.name, {
      set: function (this: ComponentInstanceNode, value: any) {
        this._internal.inputValues[port.name] = value;
        this._internal.componentInputs.forEach(function (componentInput) {
          componentInput.registerOutputIfNeeded(port.name);
          componentInput.flagOutputDirty(port.name);
        });
      },
    });
  }

  registerComponentOutputPort(port: { name: string }) {
    this.registerOutput(port.name, {
      getter() {
        return this._internal.componentOutputValues[port.name];
      },
    });
  }

  setOutputFromComponentOutput(name: string, value: any) {
    if (this.hasOutput(name) === false) {
      return;
    }

    this._internal.creatorCallbacks?.onOutputChanged?.(
      name,
      value,
      this._internal.componentOutputValues[name]
    );

    this._internal.componentOutputValues[name] = value;
    this.flagOutputDirty(name);
  }

  setChildRoot(node: any) {
    const prevChildRoot = this._internal.childRoot;
    const newChildRoot = node;

    this._internal.childRoot = newChildRoot;

    if (this.model && this.model.children) {
      const parentNodeScope = this.parentNodeScope;

      const children = this.model.children
        .filter((child) => child.type !== "Component Children")
        .map((child) => parentNodeScope?.getNodeWithId(child.id));

      if (prevChildRoot) {
        for (let i = 0; i < children.length; i++) {
          if (prevChildRoot.isChild(children[i])) {
            prevChildRoot.removeChild(children[i]);
          }
        }
      }

      if (newChildRoot) {
        for (let i = 0; i < children.length; i++) {
          const child = children[i];
          const index = child.model.parent.children.indexOf(child.model);
          this.addChild(child, index);
        }
      }
    }
  }

  getChildRootIndex(): number {
    if (!this._internal.childRoot?.model?.children) {
      return 0;
    }

    const children = this._internal.childRoot.model.children;

    for (let i = 0; i < children.length; i++) {
      if (children[i].type === "Component Children") {
        return i;
      }
    }

    return 0;
  }

  getChildRoot() {
    if (this._internal.childRoot) {
      return this._internal.childRoot;
    }
    return null;
  }

  getRoots() {
    return this._internal.roots;
  }

  /** Added for SSR Support */
  triggerDidMount() {
    this._internal.roots.forEach((root) => {
      root.triggerDidMount?.();
    });
  }

  render() {
    if (this._internal.roots.length === 0) {
      return null;
    }

    return this._internal.roots[0].render();
  }

  setChildIndex(childIndex: number) {
    // NOTE: setChildIndex can be undefined when it is not a React node,
    //       but still a visual node like the foreach (Repeater) node.
    this.getRoots().forEach((root) => root.setChildIndex?.(childIndex));
  }

  addChild(child: any, index: number) {
    this.getChildRoot().addChild(child, index + this.getChildRootIndex());
  }

  removeChild(child: any) {
    this.getChildRoot().removeChild(child);
  }

  getChildren() {
    const childRoot = this.getChildRoot();
    return childRoot ? childRoot.getChildren() : [];
  }

  isChild(child: any) {
    if (!this.getChildRoot()) {
      return false;
    }

    return this.getChildRoot().isChild(child);
  }

  contains(node: any) {
    return this.getRoots().some((root) => root.contains?.(node));
  }

  _performDirtyUpdate() {
    super._performDirtyUpdate();

    const componentInputs = this._internal.componentInputs;
    for (let i = 0, len = componentInputs.length; i < len; i++) {
      componentInputs[i].flagDirty();
    }

    this._internal.componentOutputs.forEach(function (componentOutput) {
      componentOutput.flagDirty();
    });
  }

  getRef() {
    const root = this._internal.roots[0];
    return root ? root.getRef() : undefined;
  }

  update() {
    super.update();

    this._internal.componentOutputs.forEach(function (componentOutput) {
      componentOutput.update();
    });
  }

  forceUpdate() {
    if (!this.parent) return;

    //the parent will need to re-render the roots of this component instance
    //TODO: make this use a cleaner API, and only invalidate the affected child, instead of all children
    this.parent.cachedChildren = undefined;
    this.parent.forceUpdate();
  }

  getInstanceId() {
    return this._internal.instanceId;
  }
}
