import { extend } from "../../../nodedefinition";
import { createDefinition } from "./variablebase";

interface NodeInstance {
  _internal: {
    currentValue: string;
  };
  flagOutputDirty: (name: string) => void;
}

const StringNode = createDefinition({
  name: "String",
  docs: "https://docs.noodl.net/nodes/data/string",
  shortDesc: "Contains a string (text).",
  startValue: "",
  nodeDoubleClickAction: {
    focusPort: "value",
  },
  type: {
    name: "string",
  },
  cast(value: any): string {
    return String(value);
  },
  onChanged: function (this: NodeInstance) {
    this.flagOutputDirty("length");
  },
});

extend(StringNode, {
  usePortAsLabel: "value",
  portLabelTruncationMode: "length",
  outputs: {
    length: {
      type: "number",
      displayName: "Length",
      getter: function (this: NodeInstance): number {
        return this._internal.currentValue.length;
      },
    },
  },
});

export const node = StringNode;
