import { NodeDefinition } from "@fluxscape/runtime";
import { CloudFile } from "@fluxscape/runtime/src/api/cloudfile";
import { CloudStore } from "@fluxscape/runtime/src/api/cloudstore";

interface Internal {
  file: any;
  cloudFile: CloudFile | null;
  error: string | null;
  errorStatus: number;
  progressTotal: number;
  progressLoaded: number;
  response: any;
}

export const node: NodeDefinition<Internal> = {
  name: "Upload File",
  docs: "https://docs.noodl.net/nodes/data/cloud-data/upload-file",
  category: "Cloud Services",
  color: "data",
  getInspectInfo() {
    return this._internal.response;
  },
  inputs: {
    file: {
      group: "General",
      displayName: "File",
      type: "*",
      set(file: any) {
        this._internal.file = file;
      },
    },
    upload: {
      type: "signal",
      displayName: "Upload",
      group: "Actions",
      valueChangedToTrue() {
        this.scheduleAfterInputsHaveUpdated(() => {
          const file = this._internal.file;

          if (!file) {
            // @ts-expect-error
            this.setError("No file specified");
            return;
          }

          // @ts-expect-error
          CloudStore.instance.uploadFile({
            file,
            onUploadProgress: (p: { total: number; loaded: number }) => {
              this._internal.progressTotal = p.total;
              this._internal.progressLoaded = p.loaded;

              this.flagOutputDirty("progressTotalBytes");
              this.flagOutputDirty("progressLoadedBytes");
              this.flagOutputDirty("progressLoadedPercent");
              this.sendSignalOnOutput("progressChanged");
            },
            success: (response: any) => {
              this._internal.cloudFile = new CloudFile(response);
              this.flagOutputDirty("cloudFile");
              this.sendSignalOnOutput("success");
            },
            // @ts-expect-error
            error: (e: any) => this.setError(e),
          });
        });
      },
    },
  },
  outputs: {
    cloudFile: {
      group: "General",
      displayName: "Cloud File",
      type: "cloudfile",
      get() {
        return this._internal.cloudFile;
      },
    },
    success: {
      group: "Events",
      displayName: "Success",
      type: "signal",
    },
    failure: {
      group: "Events",
      displayName: "Failure",
      type: "signal",
    },
    error: {
      type: "string",
      displayName: "Error",
      group: "Error",
      get() {
        return this._internal.error;
      },
    },
    errorStatus: {
      type: "number",
      displayName: "Error Status Code",
      group: "Error",
      get() {
        return this._internal.errorStatus;
      },
    },
    progressChanged: {
      type: "signal",
      displayName: "Progress Changed",
      group: "Events",
    },
    progressTotalBytes: {
      type: "number",
      displayName: "Total Bytes",
      group: "Progress",
      get() {
        return this._internal.progressTotal;
      },
    },
    progressLoadedBytes: {
      type: "number",
      displayName: "Uploaded Bytes",
      group: "Progress",
      get() {
        return this._internal.progressLoaded;
      },
    },
    progressLoadedPercent: {
      type: "number",
      displayName: "Uploaded Percent",
      group: "Progress",
      get() {
        if (!this._internal.progressTotal) return 0;
        return (
          (this._internal.progressLoaded / this._internal.progressTotal) * 100
        );
      },
    },
  },
  methods: {
    setError(err: any) {
      this._internal.error = err.hasOwnProperty("error") ? err.error : err;
      this._internal.errorStatus = err.code || err.status || 0;
      this.flagOutputDirty("error");
      this.flagOutputDirty("errorStatus");
      this.sendSignalOnOutput("failure");
    },
  },
};
