import { NodeDefinition } from "@fluxscape/runtime";
import EaseCurves from "../../easecurves";

const defaultDuration = 300;

interface Internal {
  currentNumber: number;
  numberInitialized: boolean;
  animationStarted: boolean;
  setCurrentNumberEnabled: boolean;
  overrideValue: number;
  _animation: any;
}

export const node: NodeDefinition<Internal> = {
  name: "net.noodl.animatetovalue",
  docs: "https://docs.noodl.net/nodes/logic/animate-to-value",
  displayName: "Animate To Value",
  shortDesc:
    "This node can interpolate smoothly from the current value to a target value.",
  category: "Animation",
  initialize() {
    const _internal = this._internal;

    _internal.currentNumber = 0;
    _internal.numberInitialized = false;
    _internal.animationStarted = false;
    _internal.setCurrentNumberEnabled = false;
    _internal.overrideValue = 0;

    _internal._animation = this.context.timerScheduler.createTimer({
      duration: defaultDuration,
      startValue: 0,
      endValue: 0,
      ease: EaseCurves.easeOut,
      onStart: () => {
        _internal.animationStarted = true;
      },
      onRunning: (t) => {
        // @ts-expect-error
        _internal.currentNumber = this.ease(this.startValue, this.endValue, t);
        this.flagOutputDirty("currentValue");
      },
      onFinish: () => {
        this.sendSignalOnOutput("atTargetValue");
      },
    });
  },
  getInspectInfo() {
    return this._internal.currentNumber;
  },
  inputs: {
    targetValue: {
      type: { name: "number" },
      displayName: "Target Value",
      group: "Target Value",
      default: undefined,
      set(value) {
        if (value === true) {
          value = 1;
        } else if (value === false) {
          value = 0;
        }

        value = Number(value);

        if (isNaN(value)) {
          return;
        }

        const internal = this._internal;

        if (!internal.numberInitialized) {
          internal.currentNumber = value;
          internal.numberInitialized = true;
          internal._animation.endValue = value;
          this.flagOutputDirty("currentValue");
          return;
        } else if (value === internal._animation.endValue) {
          return;
        }

        internal._animation.startValue = internal.currentNumber;
        internal._animation.endValue = value;
        internal._animation.start();
      },
    },
    duration: {
      type: "number",
      group: "Parameters",
      displayName: "Duration",
      default: defaultDuration,
      set(value) {
        this._internal._animation.duration = value;
      },
    },
    delay: {
      type: "number",
      group: "Parameters",
      displayName: "Delay",
      default: 0,
      set(value) {
        this._internal._animation.delay = value;
      },
    },
    easingCurve: {
      type: {
        name: "enum",
        enums: [
          { value: "easeOut", label: "Ease Out" },
          { value: "easeIn", label: "Ease In" },
          { value: "linear", label: "Linear" },
          { value: "easeInOut", label: "Ease In Out" },
        ],
      },
      default: "easeOut",
      displayName: "Easing Curve",
      group: "Parameters",
      set(value) {
        this._internal._animation.ease = EaseCurves[value];
      },
    },
  },
  outputs: {
    currentValue: {
      type: "number",
      displayName: "Current Value",
      group: "Current State",
      getter() {
        return this._internal.currentNumber;
      },
    },
    atTargetValue: {
      type: "signal",
      displayName: "At Target Value",
      group: "Signals",
    },
  },
};
