import { Model, ModelScope } from "../../../model";
import { CloudStore } from "../../../api/cloudstore";
import { NodeDefinition } from "../../../nodedefinition";
import type { NodeModule } from "../../../runtime";

interface BaseOptions {
  includeInputProperties?: boolean;
  includeRelations?: boolean;
  includeInputs?: boolean;
  includeOutputs?: boolean;
}

function _addBaseInfo<TInternal extends Record<string, any> = {}>(
  def: NodeModule<TInternal>,
  opts?: BaseOptions
): void {
  const _includeInputProperties =
    opts === undefined || opts.includeInputProperties;
  const _includeRelations = opts !== undefined && opts.includeRelations;

  Object.assign(def.node, {
    category: "Data",
    color: "data",
    inputs: def.node.inputs || {},
    outputs: def.node.outputs || {},
    methods: def.node.methods || {},
  });

  // Outputs
  // @ts-expect-error
  Object.assign(def.node.outputs, {
    failure: {
      type: "signal",
      displayName: "Failure",
      group: "Events",
    },
    error: {
      type: "string",
      displayName: "Error",
      group: "Error",
      getter: function () {
        // @ts-expect-error
        return this._internal.error;
      },
    },
  });

  // Methods
  // @ts-expect-error
  Object.assign(def.node.methods, {
    scheduleOnce: function (type: string, cb: () => void) {
      const _this = this;
      const _type = "hasScheduled" + type;
      // @ts-expect-error
      if (this._internal[_type]) return;
      // @ts-expect-error
      this._internal[_type] = true;
      // @ts-expect-error
      this.scheduleAfterInputsHaveUpdated(function () {
        // @ts-expect-error
        _this._internal[_type] = false;
        cb();
      });
    },
    checkWarningsBeforeCloudOp() {
      //clear all errors first
      // @ts-expect-error
      this.clearWarnings();

      // @ts-expect-error
      if (!this._internal.collectionId) {
        // @ts-expect-error
        this.setError("No class name specified");
        return false;
      }

      return true;
    },
    setError: function (err: string) {
      // @ts-expect-error
      this._internal.error = err;
      // @ts-expect-error
      this.flagOutputDirty("error");
      // @ts-expect-error
      this.sendSignalOnOutput("failure");

      // @ts-expect-error
      if (this.context.editorConnection) {
        // @ts-expect-error
        this.context.editorConnection.sendWarning(
          // @ts-expect-error
          this.nodeScope.componentOwner.name,
          // @ts-expect-error
          this.id,
          "storage-op-warning",
          {
            message: err,
            showGlobally: true,
          }
        );
      }
    },
    clearWarnings() {
      // @ts-expect-error
      if (this.context.editorConnection) {
        // @ts-expect-error
        this.context.editorConnection.clearWarning(
          // @ts-expect-error
          this.nodeScope.componentOwner.name,
          // @ts-expect-error
          this.id,
          "storage-op-warning"
        );
      }
    },
  });

  // Setup
  Object.assign(def, {
    setup: function (context: any, graphModel: any) {
      if (
        !context.editorConnection ||
        !context.editorConnection.isRunningLocally()
      ) {
        return;
      }

      function _managePortsForNode(node: any) {
        function _updatePorts() {
          const ports: any[] = [];

          const dbCollections = graphModel.getMetaData("dbCollections");
          const systemCollections = graphModel.getMetaData("systemCollections");

          const _systemClasses = [
            { label: "User", value: "_User" },
            { label: "Role", value: "_Role" },
          ];

          const parameters = node.parameters;

          ports.push({
            name: "collectionName",
            displayName: "Class",
            group: "General",
            type: {
              name: "enum",
              enums: _systemClasses.concat(
                dbCollections !== undefined
                  ? dbCollections.map((c: any) => {
                      return { value: c.name, label: c.name };
                    })
                  : []
              ),
              allowEditOnly: true,
            },
            plug: "input",
          });

          if (_includeRelations && parameters.collectionName && dbCollections) {
            // Fetch ports from collection keys
            let c = dbCollections.find(
              (c: any) => c.name === parameters.collectionName
            );
            if (c === undefined && systemCollections)
              c = systemCollections.find(
                (c: any) => c.name === parameters.collectionName
              );
            if (c && c.schema && c.schema.properties) {
              const props = c.schema.properties;
              const enums = Object.keys(props)
                .filter((key) => props[key].type === "Relation")
                .map((key) => ({ label: key, value: key }));

              ports.push({
                name: "relationProperty",
                displayName: "Relation",
                group: "General",
                type: { name: "enum", enums: enums, allowEditOnly: true },
                plug: "input",
              });
            }
          }

          if (
            _includeInputProperties &&
            parameters.collectionName &&
            dbCollections
          ) {
            const _typeMap: { [key: string]: string } = {
              String: "string",
              Boolean: "boolean",
              Number: "number",
              Date: "date",
            };

            // Fetch ports from collection keys
            let c = dbCollections.find(
              (c: any) => c.name === parameters.collectionName
            );
            if (c === undefined && systemCollections)
              c = systemCollections.find(
                (c: any) => c.name === parameters.collectionName
              );
            if (c && c.schema && c.schema.properties) {
              const props = c.schema.properties;
              for (const key in props) {
                const p = props[key];
                if (ports.find((_p) => _p.name === key)) continue;

                ports.push({
                  type: {
                    name: _typeMap[p.type] ? _typeMap[p.type] : "*",
                  },
                  plug: "input",
                  group: "Properties",
                  name: "prop-" + key,
                  displayName: key,
                });
              }
            }
          }

          // @ts-expect-error
          def._additionalDynamicPorts?.(node, ports, graphModel);

          context.editorConnection.sendDynamicPorts(node.id, ports);
        }

        _updatePorts();

        node.on("parameterUpdated", function () {
          _updatePorts();
        });

        graphModel.on("metadataChanged.dbCollections", function () {
          CloudStore.invalidateCollections();
          _updatePorts();
        });

        graphModel.on("metadataChanged.systemCollections", function () {
          CloudStore.invalidateCollections();
          _updatePorts();
        });
      }

      graphModel.on("editorImportComplete", () => {
        graphModel.on("nodeAdded." + def.node.name, function (node: any) {
          _managePortsForNode(node);
        });

        for (const node of graphModel.getNodesWithType(def.node.name)) {
          _managePortsForNode(node);
        }
      });
    },
  });
}

function _addModelId(
  def: {
    node: NodeDefinition<any>;
  },
  opts?: BaseOptions
) {
  const _methods = Object.assign({}, def.node.methods);

  const _includeInputs = opts === undefined || opts.includeInputs;
  const _includeOutputs = opts === undefined || opts.includeOutputs;

  Object.assign(def.node, {
    inputs: def.node.inputs || {},
    outputs: def.node.outputs || {},
    methods: def.node.methods || {},
  });

  if (_includeInputs) {
    Object.assign(def.node, {
      usePortAsLabel: "collectionName",
    });

    def.node.dynamicports = (def.node.dynamicports || []).concat([
      {
        name: "conditionalports/extended",
        condition: "idSource = explicit OR idSource NOT SET",
        inputs: ["modelId"],
      },
    ]);

    // Inputs
    // @ts-expect-error
    Object.assign(def.node.inputs, {
      idSource: {
        type: {
          name: "enum",
          enums: [
            { label: "Specify explicitly", value: "explicit" },
            { label: "From repeater", value: "foreach" },
          ],
          allowEditOnly: true,
        },
        default: "explicit",
        displayName: "Id Source",
        group: "General",
        tooltip:
          "Choose if you want to specify the Id explicitly, \n or if you want it to be that of the current record in a repeater.",
        set: function (value: string) {
          if (value === "foreach") {
            // @ts-expect-error
            this.scheduleAfterInputsHaveUpdated(() => {
              // Find closest nodescope that have a _forEachModel
              // @ts-expect-error
              let component = this.nodeScope.componentOwner;
              while (
                component !== undefined &&
                component._forEachModel === undefined &&
                component.parentNodeScope
              ) {
                component = component.parentNodeScope.componentOwner;
              }
              // @ts-expect-error
              this.setModel(
                component !== undefined ? component._forEachModel : undefined
              );
            });
          }
        },
      },
      modelId: {
        type: {
          name: "string",
          identifierOf: "ModelName",
          identifierDisplayName: "Object Ids",
        },
        displayName: "Id",
        group: "General",
        set: function (value: any) {
          if (value instanceof Model) value = value.getId(); // Can be passed as model as well
          // @ts-expect-error
          this._internal.modelId = value; // Wait to fetch data
          // @ts-expect-error
          this.setModelID(value);
        },
      },
    });
  }

  // Outputs
  if (_includeOutputs) {
    // @ts-expect-error
    Object.assign(def.node.outputs, {
      id: {
        type: "string",
        displayName: "Id",
        group: "General",
        getter: function () {
          // @ts-expect-error
          return this._internal.model
            ? // @ts-expect-error
              this._internal.model.getId()
            : // @ts-expect-error
              this._internal.modelId;
        },
      },
    });
  }

  // Methods
  // @ts-expect-error
  Object.assign(def.node.methods, {
    setCollectionID: function (id: string) {
      // @ts-expect-error
      this._internal.collectionId = id;
      // @ts-expect-error
      this.clearWarnings();
    },
    setModelID: function (id: string) {
      // @ts-expect-error
      const model = (this.nodeScope.modelScope || Model).get(id);
      // @ts-expect-error
      this.setModel(model);
    },
    setModel: function (model: any) {
      // @ts-expect-error
      this._internal.model = model;
      // @ts-expect-error
      this.flagOutputDirty("id");
    },
    registerInputIfNeeded: function (name: string) {
      // @ts-expect-error
      if (this.hasInput(name)) {
        return;
      }

      if (name === "collectionName")
        // @ts-expect-error
        this.registerInput(name, {
          // @ts-expect-error
          set: this.setCollectionID.bind(this),
        });

      _methods &&
        _methods.registerInputIfNeeded &&
        // @ts-expect-error
        _methods.registerInputIfNeeded.call(this, name);
    },
  });
}

function _addInputProperties<TInternal extends Record<string, any> = {}>(
  def: NodeModule<TInternal>
) {
  const _def = { node: Object.assign({}, def.node), setup: def.setup };
  const _methods = Object.assign({}, def.node.methods);

  Object.assign(def.node, {
    inputs: def.node.inputs || {},
    outputs: def.node.outputs || {},
    methods: def.node.methods || {},
  });

  Object.assign(def.node, {
    initialize: function () {
      // @ts-expect-error
      const internal = this._internal;
      internal.inputValues = {};

      // @ts-expect-error
      _def.node.initialize && _def.node.initialize.call(this);
    },
  });

  // Outputs
  // @ts-expect-error
  Object.assign(def.node.outputs, {});

  // Inputs
  // @ts-expect-error
  Object.assign(def.node.inputs, {});

  // Methods
  // @ts-expect-error
  Object.assign(def.node.methods, {
    registerInputIfNeeded: function (name: string) {
      // @ts-expect-error
      if (this.hasInput(name)) {
        return;
      }

      if (name.startsWith("prop-"))
        // @ts-expect-error
        this.registerInput(name, {
          // @ts-expect-error
          set: this._setInputValue.bind(this, name.substring("prop-".length)),
        });

      _methods &&
        _methods.registerInputIfNeeded &&
        // @ts-expect-error
        _methods.registerInputIfNeeded.call(this, name);
    },
    _setInputValue: function (name: string, value: any) {
      // @ts-expect-error
      this._internal.inputValues[name] = value;
    },
  });
}

function _addRelationProperty(def: NodeModule) {
  const _methods = Object.assign({}, def.node.methods);

  Object.assign(def.node, {
    inputs: def.node.inputs || {},
    outputs: def.node.outputs || {},
    methods: def.node.methods || {},
  });

  // Inputs
  // @ts-expect-error
  Object.assign(def.node.inputs, {
    targetId: {
      type: { name: "string", allowConnectionsOnly: true },
      displayName: "Target Record Id",
      group: "General",
      set: function (value: string) {
        // @ts-expect-error
        this._internal.targetModelId = value;
      },
    },
  });

  // Methods
  // @ts-expect-error
  Object.assign(def.node.methods, {
    registerInputIfNeeded: function (name: string) {
      // @ts-expect-error
      if (this.hasInput(name)) {
        return;
      }

      if (name === "relationProperty")
        // @ts-expect-error
        this.registerInput(name, {
          // @ts-expect-error
          set: this.setRelationProperty.bind(this),
        });

      _methods &&
        _methods.registerInputIfNeeded &&
        // @ts-expect-error
        _methods.registerInputIfNeeded.call(this, name);
    },
    setRelationProperty: function (value: string) {
      // @ts-expect-error
      this._internal.relationProperty = value;
    },
  });
}

function _getCurrentUser(modelScope: ModelScope): string | undefined {
  // @ts-expect-error _noodl_cloud_runtime_version
  if (typeof _noodl_cloud_runtime_version === "undefined") {
    // We are running in browser, try to find the current user

    const _cu =
      localStorage["Parse/" + CloudStore.instance.appId + "/currentUser"];
    if (_cu !== undefined) {
      let cu;
      try {
        cu = JSON.parse(_cu);
      } catch (e) {}

      return cu !== undefined ? cu.objectId : undefined;
    }
  } else {
    // Assume we are running in cloud runtime
    const request = modelScope.get("Request");
    // @ts-expect-error
    return request.UserId;
  }
}

interface AccessControlRule {
  id: string;
  label: string;
}

function _addAccessControl<TInternal extends Record<string, any> = {}>(
  def: NodeModule<TInternal>
) {
  const _def = { node: Object.assign({}, def.node), setup: def.setup };
  const _methods = Object.assign({}, def.node.methods);

  Object.assign(def.node, {
    inputs: def.node.inputs || {},
    outputs: def.node.outputs || {},
    methods: def.node.methods || {},
  });

  Object.assign(def.node, {
    initialize() {
      // @ts-expect-error
      const internal = this._internal;
      internal.accessControl = {};

      // @ts-expect-error
      _def.node.initialize && _def.node.initialize.call(this);
    },
  });

  // Inputs
  // @ts-expect-error
  Object.assign(def.node.inputs, {
    accessControl: {
      type: { name: "proplist", autoName: "Rule", allowEditOnly: true },
      index: 1000,
      displayName: "Access Control Rules",
      group: "Access Control Rules",
      set(value: AccessControlRule[]) {
        // @ts-expect-error
        this._internal.accessControlRules = value;
      },
    },
  });

  // Dynamic ports
  // @ts-expect-error
  const _super = def._additionalDynamicPorts;
  // @ts-expect-error
  def._additionalDynamicPorts = function (
    node: any,
    ports: any[],
    graphModel: any
  ) {
    if (
      node.parameters["accessControl"] !== undefined &&
      node.parameters["accessControl"].length > 0
    ) {
      node.parameters["accessControl"].forEach((ac: AccessControlRule) => {
        const prefix = "acl-" + ac.id;
        // User or role?
        ports.push({
          name: prefix + "-target",
          displayName: "Target",
          editorName: ac.label + " | Target",
          plug: "input",
          type: {
            name: "enum",
            enums: [
              { value: "user", label: "User" },
              { value: "everyone", label: "Everyone" },
              { value: "role", label: "Role" },
            ],
            allowEditOnly: true,
          },
          group: ac.label + " Access Rule",
          default: "user",
          parent: "accessControl",
          parentItemId: ac.id,
        });

        if (node.parameters[prefix + "-target"] === "role") {
          ports.push({
            name: prefix + "-role",
            displayName: "Role",
            editorName: ac.label + " | Role",
            group: ac.label + " Access Rule",
            plug: "input",
            type: "string",
            parent: "accessControl",
            parentItemId: ac.id,
          });
        } else if (
          node.parameters[prefix + "-target"] === undefined ||
          node.parameters[prefix + "-target"] === "user"
        ) {
          ports.push({
            name: prefix + "-userid",
            displayName: "User Id",
            group: ac.label + " Access Rule",
            editorName: ac.label + " | User Id",
            plug: "input",
            type: { name: "string", allowConnectionsOnly: true },
            parent: "accessControl",
            parentItemId: ac.id,
          });
        }

        // Read
        ports.push({
          name: prefix + "-read",
          displayName: "Read",
          editorName: ac.label + " | Read",
          group: ac.label + " Access Rule",
          plug: "input",
          type: { name: "boolean" },
          default: true,
          parent: "accessControl",
          parentItemId: ac.id,
        });

        // Write
        ports.push({
          name: prefix + "-write",
          displayName: "Write",
          editorName: ac.label + " | Write",
          group: ac.label + " Access Rule",
          plug: "input",
          type: { name: "boolean" },
          default: true,
          parent: "accessControl",
          parentItemId: ac.id,
        });
      });
    }

    _super && _super(node, ports, graphModel);
  };

  // Methods
  // @ts-expect-error
  Object.assign(def.node.methods, {
    registerInputIfNeeded: function (name: string) {
      // @ts-expect-error
      if (this.hasInput(name)) {
        return;
      }

      if (name.startsWith("acl-"))
        // @ts-expect-error
        this.registerInput(name, {
          // @ts-expect-error
          set: this.setAccessControl.bind(this, name),
        });

      _methods &&
        _methods.registerInputIfNeeded &&
        // @ts-expect-error
        _methods.registerInputIfNeeded.call(this, name);
    },
    _getACL: function () {
      let acl: { [key: string]: { read: boolean; write: boolean } } = {};

      function _rule(rule: any) {
        return {
          read: rule.read === undefined ? true : rule.read,
          write: rule.write === undefined ? true : rule.write,
        };
      }

      // @ts-expect-error
      const currentUserId = _getCurrentUser(this.nodeScope.modelScope);

      // @ts-expect-error
      if (this._internal.accessControlRules !== undefined) {
        // @ts-expect-error
        this._internal.accessControlRules.forEach((r: AccessControlRule) => {
          // @ts-expect-error
          const rule = this._internal.accessControl[r.id];

          if (rule === undefined) {
            const userId = currentUserId;
            if (userId !== undefined) acl[userId] = { write: true, read: true };
          } else if (rule.target === "everyone") {
            acl["*"] = _rule(rule);
          } else if (rule.target === "user") {
            const userId = rule.userid || currentUserId;
            acl[userId] = _rule(rule);
          } else if (rule.target === "role") {
            acl["role:" + rule.role] = _rule(rule);
          }
        });
      }

      return Object.keys(acl).length > 0 ? acl : undefined;
    },
    setAccessControl: function (name: string, value: any) {
      const _parts = name.split("-");

      // @ts-expect-error
      if (this._internal.accessControl[_parts[1]] === undefined)
        // @ts-expect-error
        this._internal.accessControl[_parts[1]] = {};
      // @ts-expect-error
      this._internal.accessControl[_parts[1]][_parts[2]] = value;
    },
  });
}

export {
  _addInputProperties as addInputProperties,
  _addModelId as addModelId,
  _addBaseInfo as addBaseInfo,
  _addRelationProperty as addRelationProperty,
  _addAccessControl as addAccessControl,
};
