export class CloudFile {
  private name: string;
  private url: string;

  constructor({ name, url }: { name: string; url: string }) {
    this.name = name;
    this.url = url;
  }

  getUrl(): string {
    return this.url;
  }

  getName(): string {
    return this.name;
  }

  toString(): string {
    return this.url;
  }
}
