export class Transition {
  private _frame: () => void;
  private cb?: () => void;
  private transitionForward: boolean = false;
  private startTime: number = 0;

  timing: { delay: number; dur: number } = { delay: 0, dur: 0 };

  constructor() {
    this._frame = this.frame.bind(this);
    this.timing = { delay: 0, dur: 0 }; // Initialize timing with default values
  }

  start(args: { end: () => void; back: boolean }) {
    this.cb = args.end;

    if (this.timing.delay + this.timing.dur === 0) {
      this.end();
    } else {
      this.transitionForward = !args.back;
      this.startTime = window.performance.now();
      requestAnimationFrame(this._frame);
    }
  }

  frame() {
    const t =
      (window.performance.now() - (this.startTime + this.timing.delay)) /
      this.timing.dur;
    const _t = Math.max(0, Math.min(t, 1));

    this.transitionForward ? this.forward(_t) : this.back(_t);

    if (
      window.performance.now() <=
      this.startTime + this.timing.dur + this.timing.delay
    ) {
      requestAnimationFrame(this._frame);
    } else {
      this.end();
    }
  }

  end() {
    this.cb && this.cb();
  }

  forward(t: number) {
    // Implementation of forward transition
  }

  back(t: number) {
    // Implementation of back transition
  }
}
