import { Model } from "../../../model";
import { CloudStore } from "../../../api/cloudstore";
import { NodeDefinition } from "../../../nodedefinition";
import { createSetter } from "../../../edgetriggeredinput";
import { NodeContext } from "../../../nodecontext";
import { GraphModel } from "../../../models/graphmodel";
import { Port } from "../../../models/componentmodel";

interface Internal {
  inputValues: Record<string, any>;
  relationModelIds: Record<string, any>;
  onModelChangedCallback?: (args: { name: string }) => void;
  model?: Model;
  modelId?: string;
  collectionId?: string;
  error?: string;
  [key: string]: any;
}

interface Parameters {
  collectionName?: string;
}

export const node: NodeDefinition<Internal> = {
  name: "DbModel2",
  docs: "https://docs.noodl.net/nodes/data/cloud-data/record",
  displayNodeName: "Record",
  shortDesc: "Database model",
  category: "Cloud Services",
  usePortAsLabel: "collectionName",
  color: "data",
  dynamicports: [
    {
      name: "conditionalports/extended",
      condition: "idSource = explicit OR idSource NOT SET",
      inputs: ["modelId"],
    },
  ],
  initialize: function () {
    const internal = this._internal;
    internal.inputValues = {};
    internal.relationModelIds = {};

    const _this = this;
    this._internal.onModelChangedCallback = function (args) {
      if (_this.isInputConnected("fetch")) return;

      if (_this.hasOutput("prop-" + args.name))
        _this.flagOutputDirty("prop-" + args.name);

      if (_this.hasOutput("changed-" + args.name))
        _this.sendSignalOnOutput("changed-" + args.name);

      _this.sendSignalOnOutput("changed");
    };
  },
  getInspectInfo() {
    const model = this._internal.model;
    if (!model) return "[No Record]";

    return [
      { type: "text", value: "Id: " + model.getId() },
      { type: "value", value: model.data },
    ];
  },
  outputs: {
    id: {
      type: "string",
      displayName: "Id",
      group: "General",
      getter: function () {
        return this._internal.model
          ? this._internal.model.getId()
          : this._internal.modelId;
      },
    },
    fetched: {
      type: "signal",
      displayName: "Fetched",
      group: "Events",
    },
    changed: {
      type: "signal",
      displayName: "Changed",
      group: "Events",
    },
    failure: {
      type: "signal",
      displayName: "Failure",
      group: "Events",
    },
    error: {
      type: "string",
      displayName: "Error",
      group: "Error",
      getter: function () {
        return this._internal.error;
      },
    },
  },
  inputs: {
    idSource: {
      type: {
        name: "enum",
        enums: [
          { label: "Specify explicitly", value: "explicit" },
          { label: "From repeater", value: "foreach" },
        ],
        allowEditOnly: true,
      },
      default: "explicit",
      displayName: "Id Source",
      group: "General",
      set(value) {
        if (value === "foreach") {
          this.scheduleAfterInputsHaveUpdated(() => {
            // Find closest nodescope that have a _forEachModel
            let component = this.nodeScope!.componentOwner;
            while (
              component !== undefined &&
              component._forEachModel === undefined &&
              component.parentNodeScope
            ) {
              component = component.parentNodeScope.componentOwner;
            }
            // @ts-expect-error
            this.setModel(
              component !== undefined ? component._forEachModel : undefined
            );
          });
        }
      },
    },
    modelId: {
      type: { name: "string", allowConnectionsOnly: true },
      displayName: "Id",
      group: "General",
      set(value: string | Model | object) {
        if (value instanceof Model) {
          // Can be passed as model as well
          value = value.getId();
        } else if (typeof value === "object") {
          // If this is an js object, dereference it
          value = Model.create(value).getId();
        }

        this._internal.modelId = value; // Wait to fetch data
        if (this.isInputConnected("fetch") === false) {
          // @ts-expect-error
          this.setModelID(value);
        } else {
          this.flagOutputDirty("id");
        }
      },
    },
    fetch: {
      displayName: "Fetch",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleFetch();
      },
    },
  },
  methods: {
    setCollectionID(id: string) {
      this._internal.collectionId = id;
    },
    setModelID(id: string) {
      const model = (this.nodeScope!.modelScope || Model).get(id);
      // @ts-expect-error
      this.setModel(model);
    },
    setModel(model: Model) {
      if (this._internal.model) {
        // Remove old listener if existing
        this._internal.model.off(
          "change",
          // @ts-expect-error
          this._internal.onModelChangedCallback
        );
      }

      this._internal.model = model;
      this.flagOutputDirty("id");
      // @ts-expect-error
      model.on("change", this._internal.onModelChangedCallback);

      // We have a new model, mark all outputs as dirty
      for (const key in model.data) {
        if (this.hasOutput("prop-" + key)) {
          this.flagOutputDirty("prop-" + key);
        }
      }
      this.sendSignalOnOutput("fetched");
    },
    _onNodeDeleted() {
      // @ts-expect-error
      Node.prototype._onNodeDeleted.call(this);
      if (this._internal.model)
        this._internal.model.off(
          "change",
          // @ts-expect-error
          this._internal.onModelChangedCallback
        );
    },
    scheduleOnce(type: string, cb: () => void) {
      const _this = this;
      const _type = "hasScheduled" + type;
      if (this._internal[_type]) return;
      this._internal[_type] = true;
      this.scheduleAfterInputsHaveUpdated(function () {
        _this._internal[_type] = false;
        cb();
      });
    },
    setError(err: string) {
      this._internal.error = err;
      this.flagOutputDirty("error");
      this.sendSignalOnOutput("failure");

      if (this.context.editorConnection) {
        this.context.editorConnection.sendWarning(
          this.nodeScope!.componentOwner.name,
          this.id,
          "storage-op-warning",
          {
            message: err,
            showGlobally: true,
          }
        );
      }
    },
    clearWarnings() {
      if (this.context.editorConnection) {
        this.context.editorConnection.clearWarning(
          this.nodeScope!.componentOwner.name,
          this.id,
          "storage-op-warning"
        );
      }
    },
    scheduleFetch() {
      const _this = this;
      const internal = this._internal;

      // @ts-expect-error
      this.scheduleOnce("Fetch", function () {
        // Don't do fetch if no id
        if (internal.modelId === undefined || internal.modelId === "") {
          // @ts-expect-error
          _this.setError("Missing Id.");
          return;
        }

        // @ts-expect-error
        const cloudstore = CloudStore.forScope(_this.nodeScope.modelScope);
        cloudstore.fetch({
          // @ts-expect-error
          collection: internal.collectionId,
          objectId: internal.modelId, // Get the objectId part of the model id
          success: function (response: any) {
            // @ts-expect-error
            const model = cloudstore._fromJSON(response, internal.collectionId);
            if (internal.model !== model) {
              // Check if we need to change model
              if (internal.model) {
                // Remove old listener if existing
                // @ts-expect-error
                internal.model.off("change", internal.onModelChangedCallback);
              }

              internal.model = model;
              model.on("change", internal.onModelChangedCallback);
            }
            _this.flagOutputDirty("id");

            delete response.objectId;

            for (const key in response) {
              if (_this.hasOutput("prop-" + key)) {
                _this.flagOutputDirty("prop-" + key);
              }
            }

            _this.sendSignalOnOutput("fetched");
          },
          error: function (err: string) {
            // @ts-expect-error
            _this.setError(err || "Failed to fetch.");
          },
        });
      });
    },
    scheduleStore: function () {
      const internal = this._internal;
      if (!internal.model) return;

      // @ts-expect-error
      this.scheduleOnce("Store", function () {
        for (const i in internal.inputValues) {
          // @ts-expect-error
          internal.model.set(i, internal.inputValues[i], { resolve: true });
        }
      });
    },
    registerOutputIfNeeded: function (name: string) {
      if (this.hasOutput(name)) {
        return;
      }

      if (name.startsWith("prop-"))
        this.registerOutput(name, {
          getter: userOutputGetter.bind(this, name.substring("prop-".length)),
        });
    },
    registerInputIfNeeded: function (name: string) {
      if (this.hasInput(name)) {
        return;
      }

      const dynamicSignals: Record<string, () => void> = {};

      if (dynamicSignals[name])
        return this.registerInput(name, {
          set: createSetter({
            valueChangedToTrue: dynamicSignals[name],
          }),
        });

      const dynamicSetters: Record<string, (value: any) => void> = {
        // @ts-expect-error
        collectionName: this.setCollectionID.bind(this),
      };

      if (dynamicSetters[name])
        return this.registerInput(name, {
          set: dynamicSetters[name],
        });

      if (name.startsWith("prop-"))
        this.registerInput(name, {
          set: userInputSetter.bind(this, name.substring("prop-".length)),
        });
    },
  },
};

function userOutputGetter(name: string) {
  // @ts-expect-error
  return this._internal.model
    ? // @ts-expect-error
      this._internal.model.get(name, { resolve: true })
    : undefined;
}

function userInputSetter(name: string, value: any) {
  // @ts-expect-error
  this._internal.inputValues[name] = value;
}

function updatePorts(
  nodeId: string,
  parameters: Parameters,
  editorConnection: any,
  graphModel: any
) {
  const ports: Port[] = [];

  const dbCollections = graphModel.getMetaData("dbCollections");
  const systemCollections = graphModel.getMetaData("systemCollections");

  const _systemClasses = [
    { label: "User", value: "_User" },
    { label: "Role", value: "_Role" },
  ];
  ports.push({
    name: "collectionName",
    displayName: "Class",
    group: "General",
    type: {
      name: "enum",
      enums: _systemClasses.concat(
        dbCollections !== undefined
          ? dbCollections.map((c) => {
              return { value: c.name, label: c.name };
            })
          : []
      ),
      allowEditOnly: true,
    },
    plug: "input",
  });

  if (parameters.collectionName && dbCollections) {
    // Fetch ports from collection keys
    let c = dbCollections.find((c) => c.name === parameters.collectionName);
    if (c === undefined && systemCollections)
      c = systemCollections.find((c) => c.name === parameters.collectionName);
    if (c && c.schema && c.schema.properties) {
      const props = c.schema.properties;
      for (const key in props) {
        const p = props[key];
        if (ports.find((_p) => _p.name === key)) continue;

        if (p.type !== "Relation") {
          // Other schema type ports
          const _typeMap: Record<string, string> = {
            String: "string",
            Boolean: "boolean",
            Number: "number",
            Date: "date",
          };

          ports.push({
            type: {
              name: _typeMap[p.type] ? _typeMap[p.type] : "*",
            },
            plug: "output",
            group: "Properties",
            name: "prop-" + key,
            displayName: key,
          });

          ports.push({
            type: "signal",
            plug: "output",
            group: "Changed Events",
            displayName: key + " Changed",
            name: "changed-" + key,
          });
        }
      }
    }
  }

  editorConnection.sendDynamicPorts(nodeId, ports);
}

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  function _managePortsForNode(node: any) {
    updatePorts(node.id, node.parameters, context.editorConnection, graphModel);

    node.on("parameterUpdated", function () {
      updatePorts(
        node.id,
        node.parameters,
        context.editorConnection,
        graphModel
      );
    });

    graphModel.on("metadataChanged.dbCollections", function () {
      CloudStore.invalidateCollections();
      updatePorts(
        node.id,
        node.parameters,
        context.editorConnection,
        graphModel
      );
    });

    graphModel.on("metadataChanged.systemCollections", function () {
      CloudStore.invalidateCollections();
      updatePorts(
        node.id,
        node.parameters,
        context.editorConnection,
        graphModel
      );
    });
  }

  graphModel.on("editorImportComplete", () => {
    graphModel.on("nodeAdded.DbModel2", function (node: any) {
      _managePortsForNode(node);
    });

    for (const node of graphModel.getNodesWithType("DbModel2")) {
      _managePortsForNode(node);
    }
  });
}
