import {
  Node,
  Model,
  createSetter,
  Collection,
  NodeDefinition,
} from "@fluxscape/runtime";

interface Internal {
  collectionChangedCallback: () => void;
  enabled: boolean;
  filterSettings: Record<string, any>;
  filteredCollection?: Collection<Model>;
  collection?: Collection<Model>;
  collectionChangedScheduled?: boolean;
}

const applyFilter = (
  item: Record<string, any>,
  filter: Record<string, any>
): boolean => {
  for (const key in filter) {
    const op = filter[key];

    if (op["$neq"] !== undefined) {
      if (!(item[key] != op["$neq"])) return false;
    } else if (item[key] === undefined) return false;
    else if (op["$eq"] !== undefined && !(item[key] == op["$eq"])) return false;
    else if (op["$gt"] !== undefined && !(item[key] > op["$gt"])) return false;
    else if (op["$lt"] !== undefined && !(item[key] < op["$lt"])) return false;
    else if (op["$gte"] !== undefined && !(item[key] >= op["$gte"]))
      return false;
    else if (op["$lte"] !== undefined && !(item[key] <= op["$lte"]))
      return false;
    else if (op["$regex"] !== undefined) {
      const a = item[key] + ""; // Convert to string
      const regex = new RegExp(
        op["$regex"],
        op["$case"] !== true ? "i" : undefined
      );
      if (!regex.test(a)) return false;
    }
  }
  return true;
};

const sorter = function (this: Record<string, number>, a: any, b: any): number {
  if (a instanceof Model) a = a.data;
  if (b instanceof Model) b = b.data;

  for (const key in this) {
    const _a = a[key];
    const _b = b[key];
    if (_a !== _b) {
      if (typeof _a === "string" && typeof _b === "string") {
        return this[key] === 1 ? (_a > _b ? 1 : -1) : _a > _b ? -1 : 1;
      } else if (typeof _a === "number" && typeof _b === "number") {
        return this[key] === 1 ? _a - _b : _b - _a;
      } else {
        return this[key] === 1 ? (_a > _b ? 1 : -1) : _a > _b ? -1 : 1;
      }
    }
  }
  return 0;
};

export const node: NodeDefinition<Internal> = {
  name: "Filter Collection",
  docs: "https://docs.noodl.net/nodes/data/array/array-filter",
  displayNodeName: "Array Filter",
  shortDesc: "Filter, sort and limit array",
  category: "Data",
  color: "data",
  initialize: function () {
    this._internal.collectionChangedCallback = () => {
      if (this.isInputConnected("filter") === true) return;
      // @ts-expect-error
      this.scheduleFilter();
    };

    this._internal.enabled = true;
    this._internal.filterSettings = {};
  },
  getInspectInfo() {
    const collection = this._internal.filteredCollection;

    if (!collection) {
      return { type: "text", value: "[Not executed yet]" };
    }

    return [
      {
        type: "text",
        value: "Id: " + collection.getId(),
      },
      {
        type: "value",
        value: collection.items,
      },
    ];
  },
  inputs: {
    items: {
      type: "array",
      displayName: "Items",
      group: "General",
      set(value) {
        // @ts-expect-error
        this.bindCollection(value);
        // @ts-expect-error
        if (!this.isInputConnected("filter")) this.scheduleFilter();
      },
    },
    enabled: {
      type: "boolean",
      group: "General",
      displayName: "Enabled",
      default: true,
      set: function (value) {
        this._internal.enabled = value;
        // @ts-expect-error
        if (!this.isInputConnected("filter")) this.scheduleFilter();
      },
    },
    filter: {
      type: "signal",
      group: "Actions",
      displayName: "Filter",
      valueChangedToTrue: function () {
        // @ts-expect-error
        this.scheduleFilter();
      },
    },
  },
  outputs: {
    items: {
      type: "array",
      displayName: "Items",
      group: "General",
      getter: function () {
        return this._internal.filteredCollection;
      },
    },
    firstItemId: {
      type: "string",
      displayName: "First Item Id",
      group: "General",
      getter: function () {
        if (this._internal.filteredCollection !== undefined) {
          const firstItem = this._internal.filteredCollection.get(0);
          if (firstItem !== undefined) return firstItem.getId();
        }
      },
    },
    count: {
      type: "number",
      displayName: "Count",
      group: "General",
      getter: function () {
        return this._internal.filteredCollection
          ? this._internal.filteredCollection.size()
          : 0;
      },
    },
    modified: {
      group: "Events",
      type: "signal",
      displayName: "Filtered",
    },
  },
  prototypeExtensions: {
    unbindCurrentCollection: function () {
      const collection = this._internal.collection;
      if (!collection) return;
      // @ts-expect-error
      collection.off("change", this._internal.collectionChangedCallback);
      this._internal.collection = undefined;
    },
    bindCollection: function (collection: Collection<Model>) {
      // @ts-expect-error
      this.unbindCurrentCollection();
      this._internal.collection = collection;
      collection &&
        // @ts-expect-error
        collection.on("change", this._internal.collectionChangedCallback);
    },
    _onNodeDeleted: function () {
      Node.prototype._onNodeDeleted.call(this);
      // @ts-expect-error
      this.unbindCurrentCollection();
    },
    // @ts-expect-error
    getFilter: function () {
      const filterSettings = this._internal.filterSettings;
      const options = ["case"];

      if (filterSettings["filterFilter"]) {
        const filters = filterSettings["filterFilter"].split(",");
        const _filter: Record<string, any> = {};
        filters.forEach((f) => {
          const op = "$" + (filterSettings["filterFilterOp-" + f] || "eq");
          _filter[f] = {};
          _filter[f][op] = filterSettings["filterFilterValue-" + f];

          options.forEach((o) => {
            const option = filterSettings["filterFilterOption-" + o + "-" + f];
            if (option) _filter[f]["$" + o] = option;
          });
        });
        return _filter;
      }
    },
    // @ts-expect-error
    getSort: function () {
      const filterSettings = this._internal.filterSettings;

      if (filterSettings["filterSort"]) {
        const sort = filterSettings["filterSort"].split(",");
        const _sort: Record<string, number> = {};
        sort.forEach((s) => {
          _sort[s] =
            filterSettings["filterSort-" + s] === "descending" ? -1 : 1;
        });
        return _sort;
      }
    },
    getLimit: function () {
      const filterSettings = this._internal.filterSettings;

      if (!filterSettings["filterEnableLimit"]) return;
      return filterSettings["filterLimit"] || 10;
    },
    getSkip: function () {
      const filterSettings = this._internal.filterSettings;

      if (!filterSettings["filterEnableLimit"]) return;
      return filterSettings["filterSkip"] || 0;
    },
    scheduleFilter: function () {
      // @ts-expect-error
      if (this.collectionChangedScheduled) return;
      // @ts-expect-error
      this.collectionChangedScheduled = true;

      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.collectionChangedScheduled = false;
        if (!this._internal.collection) return;

        // @ts-expect-error
        let filtered = [].concat(this._internal.collection.items);

        if (this._internal.enabled) {
          // @ts-expect-error
          const filter = this.getFilter();
          if (filter)
            // @ts-expect-error
            filtered = filtered.filter((m) => applyFilter(m.data, filter));

          // @ts-expect-error
          const sort = this.getSort();
          if (sort) filtered.sort(sorter.bind(sort));

          // @ts-expect-error
          const skip = this.getSkip();
          if (skip) filtered = filtered.slice(skip, filtered.length);

          // @ts-expect-error
          const limit = this.getLimit();
          if (limit) filtered = filtered.slice(0, limit);
        }

        this._internal.filteredCollection = Collection.create(filtered);

        this.sendSignalOnOutput("modified");
        this.flagOutputDirty("firstItemId");
        this.flagOutputDirty("items");
        this.flagOutputDirty("count");
      });
    },
    registerInputIfNeeded: function (name: string) {
      if (this.hasInput(name)) {
        return;
      }

      this.registerInput(name, {
        set: userInputSetter.bind(this, name),
      });
    },
  },
};

function userInputSetter(name: string, value: any) {
  // @ts-expect-error
  this._internal.filterSettings[name] = value;
  // @ts-expect-error
  if (!this.isInputConnected("filter")) this.scheduleFilter();
}

function updatePorts(
  nodeId: string,
  parameters: Record<string, any>,
  editorConnection: any,
  dbCollections: any
) {
  const ports: any[] = [];

  ports.push({
    type: "boolean",
    plug: "input",
    group: "Limit",
    name: "filterEnableLimit",
    displayName: "Use limit",
  });

  if (parameters["filterEnableLimit"]) {
    ports.push({
      type: "number",
      default: 10,
      plug: "input",
      group: "Limit",
      name: "filterLimit",
      displayName: "Limit",
    });

    ports.push({
      type: "number",
      default: 0,
      plug: "input",
      group: "Limit",
      name: "filterSkip",
      displayName: "Skip",
    });
  }

  ports.push({
    type: { name: "stringlist", allowEditOnly: true },
    plug: "input",
    group: "Filter",
    name: "filterFilter",
    displayName: "Filter",
  });

  ports.push({
    type: { name: "stringlist", allowEditOnly: true },
    plug: "input",
    group: "Sort",
    name: "filterSort",
    displayName: "Sort",
  });

  const filterOps = {
    string: [
      { value: "eq", label: "Equals" },
      { value: "neq", label: "Not Equals" },
      { value: "regex", label: "Matches RegEx" },
    ],
    boolean: [
      { value: "eq", label: "Equals" },
      { value: "neq", label: "Not Equals" },
    ],
    number: [
      { value: "eq", label: "Equals" },
      { value: "neq", label: "Not Equals" },
      { value: "lt", label: "Less than" },
      { value: "gt", label: "Greater than" },
      { value: "gte", label: "Greater than or equal" },
      { value: "lte", label: "Less than or equal" },
    ],
  };

  if (parameters["filterFilter"]) {
    const filters = parameters["filterFilter"].split(",");
    filters.forEach((f) => {
      ports.push({
        type: {
          name: "enum",
          enums: [
            { value: "string", label: "String" },
            { value: "number", label: "Number" },
            { value: "boolean", label: "Boolean" },
          ],
        },
        default: "string",
        plug: "input",
        group: f + " filter",
        displayName: "Type",
        editorName: f + " filter | Type",
        name: "filterFilterType-" + f,
      });

      const type = parameters["filterFilterType-" + f];

      ports.push({
        type: { name: "enum", enums: filterOps[type || "string"] },
        default: "eq",
        plug: "input",
        group: f + " filter",
        displayName: "Op",
        editorName: f + " filter| Op",
        name: "filterFilterOp-" + f,
      });

      if (parameters["filterFilterOp-" + f] === "regex") {
        ports.push({
          type: "boolean",
          default: false,
          plug: "input",
          group: f + " filter",
          displayName: "Case sensitive",
          editorName: f + " filter| Case",
          name: "filterFilterOption-case-" + f,
        });
      }

      ports.push({
        type: type || "string",
        plug: "input",
        group: f + " filter",
        displayName: "Value",
        editorName: f + " Filter Value",
        name: "filterFilterValue-" + f,
      });
    });
  }

  if (parameters["filterSort"]) {
    const filters = parameters["filterSort"].split(",");
    filters.forEach((f) => {
      ports.push({
        type: {
          name: "enum",
          enums: [
            { value: "ascending", label: "Ascending" },
            { value: "descending", label: "Descending" },
          ],
        },
        default: "ascending",
        plug: "input",
        group: f + " sort",
        displayName: "Sort",
        editorName: f + " sorting",
        name: "filterSort-" + f,
      });
    });
  }

  editorConnection.sendDynamicPorts(nodeId, ports);
}

export function setup(context: any, graphModel: any) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  graphModel.on("nodeAdded.Filter Collection", function (node: any) {
    updatePorts(
      node.id,
      node.parameters,
      context.editorConnection,
      graphModel.getMetaData("dbCollections")
    );

    node.on("parameterUpdated", function (event: any) {
      if (event.name.startsWith("filter")) {
        updatePorts(
          node.id,
          node.parameters,
          context.editorConnection,
          graphModel.getMetaData("dbCollections")
        );
      }
    });

    graphModel.on("metadataChanged.dbCollections", function (data: any) {
      updatePorts(node.id, node.parameters, context.editorConnection, data);
    });
  });
}
