import { Model } from "../model";
import { Collection } from "../collection";
import { CloudFile } from "./cloudfile";
import { EventEmitter } from "eventemitter3";
import NoodlRuntime from "../runtime";

interface ProtectedFields {
  _common: string[];
  _User: string[];
  [key: string]: string[];
}

const _protectedFields: ProtectedFields = {
  _common: ["_createdAt", "_updatedAt", "objectId"],
  _User: ["_email_verify_token"],
};

interface CloudCollection {
  name: string;
  schema: {
    properties?: {
      [key: string]: {
        type: string;
        targetClass?: string;
      };
    };
  };
}

interface CloudStoreOptions {
  collection: string;
  objectId?: string;
  where?: any;
  limit?: number;
  skip?: number;
  include?: string[] | string;
  select?: string[] | string;
  sort?: string[] | string;
  count?: boolean;
  keys?: string[] | string;
  excludeKeys?: string[] | string;
  data?: any;
  acl?: any;
  properties?: { [key: string]: number };
  key?: string;
  targetObjectId?: string;
  targetClass?: string;
  file?: File;
  success: (data?: any, count?: number) => void;
  error: (error?: any) => void;
  onUploadProgress?: (progress: ProgressEvent) => void;
}

interface GroupOptions {
  avg?: string;
  sum?: string;
  max?: string;
  min?: string;
  distinct?: string;
}

interface AggregateOptions extends CloudStoreOptions {
  group: { [key: string]: GroupOptions };
}

function _removeProtectedFields(data: any, className?: string): any {
  const _data = Object.assign({}, data);
  _protectedFields._common.forEach((f) => delete _data[f]);
  if (className && _protectedFields[className])
    _protectedFields[className].forEach((f) => delete _data[f]);

  return _data;
}

export class CloudStore {
  static _instance: CloudStore;
  static get instance() {
    if (CloudStore._instance === undefined)
      CloudStore._instance = new CloudStore();
    return CloudStore._instance;
  }

  static _collections: { [key: string]: CloudCollection } | undefined;
  static get collections() {
    if (CloudStore._collections === undefined) {
      CloudStore._collections = {};
      const dbCollections =
        NoodlRuntime.instance.getMetaData("dbCollections") || [];
      dbCollections.forEach((c) => {
        if (CloudStore._collections) {
          CloudStore._collections[c.name] = c;
        }
      });

      const systemCollections =
        NoodlRuntime.instance.getMetaData("systemCollections") || [];
      systemCollections.forEach((c) => {
        if (CloudStore._collections) {
          CloudStore._collections[c.name] = c;
        }
      });
    }
    return CloudStore._collections;
  }

  appId?: string;
  private endpoint?: string;
  private dbVersionMajor?: number;
  private events: EventEmitter;
  private modelScope: typeof Model;

  constructor(modelScope?: typeof Model) {
    this._initCloudServices();

    this.events = new EventEmitter();
    // this.events.setMaxListeners(10000);
    this.modelScope = modelScope || Model;
  }

  _fromJSON(
    item: Parameters<typeof CloudStore._fromJSON>[0],
    collectionName: Parameters<typeof CloudStore._fromJSON>[1]
  ) {
    return CloudStore._fromJSON(item, collectionName, this.modelScope);
  }

  _deserializeJSON(
    data: Parameters<typeof CloudStore._deserializeJSON>[0],
    type: Parameters<typeof CloudStore._deserializeJSON>[1]
  ) {
    return CloudStore._deserializeJSON(data, type, this.modelScope);
  }

  _serializeObject(
    data: Parameters<typeof CloudStore._serializeObject>[0],
    collectionName: Parameters<typeof CloudStore._serializeObject>[1]
  ) {
    return CloudStore._serializeObject(data, collectionName, this.modelScope);
  }

  _initCloudServices(): void {
    CloudStore._collections = undefined; // clear collection cache, so it's refetched

    const cloudServices = NoodlRuntime.instance.getMetaData("cloudservices");
    if (cloudServices) {
      this.appId = cloudServices.appId;
      this.endpoint = cloudServices.endpoint;
    }

    const dbVersionMajor = NoodlRuntime.instance.getMetaData("dbVersionMajor");
    this.dbVersionMajor = dbVersionMajor;
  }

  on(
    event: string | symbol,
    fn: (...args: any[]) => void,
    context?: any
  ): void {
    this.events.on.apply(this.events, [event, fn, context]);
  }

  off(
    event: string | symbol,
    fn?: (...args: any[]) => void,
    context?: any,
    once?: boolean
  ): void {
    this.events.off.apply(this.events, [event, fn, context, once]);
  }

  private _makeRequest(
    path: string,
    options: {
      method?: string;
      content?: any;
      contentType?: string;
      success: (response: any) => void;
      error: (error: any) => void;
      onUploadProgress?: (progress: ProgressEvent) => void;
    }
  ): void {
    if (!this.appId) throw new Error("App ID is not set");

    // @ts-expect-error _noodl_cloud_runtime_version
    if (typeof _noodl_cloud_runtime_version === "undefined") {
      // Running in browser
      const xhr = new XMLHttpRequest();

      xhr.onreadystatechange = function () {
        if (xhr.readyState === 4) {
          let json;
          try {
            // In SSR, we dont have xhr.response
            json = JSON.parse(xhr.response || xhr.responseText);
          } catch (e) {}

          if (xhr.status === 200 || xhr.status === 201) {
            options.success(json);
          } else
            options.error(
              json || { error: xhr.responseText, status: xhr.status }
            );
        }
      };

      xhr.open(options.method || "GET", this.endpoint + path, true);

      xhr.setRequestHeader("X-Parse-Application-Id", this.appId);
      // @ts-expect-error
      if (typeof _noodl_cloudservices !== "undefined") {
        xhr.setRequestHeader(
          "X-Parse-Master-Key",
          // @ts-expect-error
          _noodl_cloudservices.masterKey
        );
      }

      // Check for current users
      const _cu = localStorage["Parse/" + this.appId + "/currentUser"];
      if (_cu !== undefined) {
        try {
          const currentUser = JSON.parse(_cu);
          xhr.setRequestHeader(
            "X-Parse-Session-Token",
            currentUser.sessionToken
          );
        } catch (e) {
          // Failed to extract session token
        }
      }

      if (options.onUploadProgress) {
        xhr.upload.onprogress = (pe) => {
          if (options.onUploadProgress) {
            options.onUploadProgress(pe);
          }
        };
      }

      if (options.content instanceof File) {
        xhr.send(options.content);
      } else {
        xhr.setRequestHeader("Content-Type", "application/json");
        xhr.send(JSON.stringify(options.content));
      }
    } else {
      // Running in cloud runtime
      const endpoint =
        // @ts-expect-error
        typeof _noodl_cloudservices !== "undefined"
          ? // @ts-expect-error
            _noodl_cloudservices.endpoint
          : this.endpoint;
      const appId =
        // @ts-expect-error
        typeof _noodl_cloudservices !== "undefined"
          ? // @ts-expect-error
            _noodl_cloudservices.appId
          : this.appId;
      const masterKey =
        // @ts-expect-error
        typeof _noodl_cloudservices !== "undefined"
          ? // @ts-expect-error
            _noodl_cloudservices.masterKey
          : undefined;

      fetch(endpoint + path, {
        method: options.method || "GET",
        headers: {
          "X-Parse-Application-Id": appId,
          "X-Parse-Master-Key": masterKey,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(options.content),
      })
        .then((r) => {
          if (r.status === 200 || r.status === 201) {
            if (options.method === "DELETE") {
              options.success(undefined);
            } else {
              r.json()
                .then((json) => options.success(json))
                .catch((e) =>
                  options.error({
                    error: "CloudStore: Failed to get json result.",
                  })
                );
            }
          } else {
            if (options.method === "DELETE") {
              options.error({ error: "Failed to delete." });
            } else {
              r.json()
                .then((json) => options.error(json))
                .catch((e) => options.error({ error: "Failed to fetch." }));
            }
          }
        })
        .catch((e) => {
          options.error({ error: e.message });
        });
    }
  }

  query(options: CloudStoreOptions): void {
    this._makeRequest("/classes/" + options.collection, {
      method: "POST",
      content: {
        _method: "GET",
        where: options.where,
        limit: options.limit,
        skip: options.skip,
        include: Array.isArray(options.include)
          ? options.include.join(",")
          : options.include,
        keys: Array.isArray(options.select)
          ? options.select.join(",")
          : options.select,
        order: Array.isArray(options.sort)
          ? options.sort.join(",")
          : options.sort,
        count: options.count,
      },
      success: function (response) {
        options.success(response.results, response.count);
      },
      error: function () {
        options.error();
      },
    });
  }

  aggregate(options: AggregateOptions): void {
    const args: string[] = [];

    if (!options.group) {
      options.error("You need to provide group option.");
      return;
    }

    if (options.limit) args.push("limit=" + options.limit);
    if (options.skip) args.push("skip=" + options.skip);

    const grouping: { [key: string]: any } = {};

    Object.keys(options.group).forEach((k) => {
      const _g: { [key: string]: string } = {};
      const group = options.group[k];
      if (group["avg"] !== undefined) _g["$avg"] = "$" + group["avg"];
      else if (group["sum"] !== undefined) _g["$sum"] = "$" + group["sum"];
      else if (group["max"] !== undefined) _g["$max"] = "$" + group["max"];
      else if (group["min"] !== undefined) _g["$min"] = "$" + group["min"];
      else if (group["distinct"] !== undefined)
        _g["$addToSet"] = "$" + group["distinct"];

      grouping[k] = _g;
    });

    // I don't know which version the API was changed, lets just say above 4 for now.
    if (this.dbVersionMajor && this.dbVersionMajor > 4) {
      grouping._id = null;

      if (options.where)
        args.push(
          "$match=" + encodeURIComponent(JSON.stringify(options.where))
        );

      args.push("$group=" + JSON.stringify(grouping));
    } else {
      grouping.objectId = null;

      if (options.where)
        args.push("match=" + encodeURIComponent(JSON.stringify(options.where)));

      args.push("group=" + JSON.stringify(grouping));
    }

    this._makeRequest(
      "/aggregate/" +
        options.collection +
        (args.length > 0 ? "?" + args.join("&") : ""),
      {
        success: function (response) {
          const res: { [key: string]: any } = {};

          if (!response.results || response.results.length !== 1) {
            options.success({}); // No result
            return;
          }

          Object.keys(options.group).forEach((k) => {
            res[k] = response.results[0][k];
          });

          options.success(res);
        },
        error: function () {
          options.error();
        },
      }
    );
  }

  count(options: CloudStoreOptions): void {
    const args: string[] = [];

    if (options.where)
      args.push("where=" + encodeURIComponent(JSON.stringify(options.where)));
    args.push("limit=0");
    args.push("count=1");

    this._makeRequest(
      "/classes/" +
        options.collection +
        (args.length > 0 ? "?" + args.join("&") : ""),
      {
        success: function (response) {
          options.success(response.count);
        },
        error: function () {
          options.error();
        },
      }
    );
  }

  distinct(options: CloudStoreOptions & { property: string }): void {
    const args: string[] = [];

    if (options.where)
      args.push("where=" + encodeURIComponent(JSON.stringify(options.where)));
    args.push("distinct=" + options.property);

    this._makeRequest(
      "/aggregate/" +
        options.collection +
        (args.length > 0 ? "?" + args.join("&") : ""),
      {
        success: function (response) {
          options.success(response.results);
        },
        error: function () {
          options.error();
        },
      }
    );
  }

  fetch(options: CloudStoreOptions): void {
    const args: string[] = [];

    if (options.include) {
      args.push(
        "include=" +
          (Array.isArray(options.include)
            ? options.include.join(",")
            : options.include)
      );
    }

    if (options.keys) {
      args.push(
        "keys=" +
          (Array.isArray(options.keys) ? options.keys.join(",") : options.keys)
      );
    }

    if (options.excludeKeys) {
      args.push(
        "excludeKeys=" +
          (Array.isArray(options.excludeKeys)
            ? options.excludeKeys.join(",")
            : options.excludeKeys)
      );
    }

    this._makeRequest(
      "/classes/" +
        options.collection +
        "/" +
        options.objectId +
        (args.length > 0 ? "?" + args.join("&") : ""),
      {
        method: "GET",
        success: (response) => {
          options.success(response);
          this.events.emit("fetch", {
            type: "fetch",
            objectId: options.objectId,
            object: response,
            collection: options.collection,
          });
        },
        error: function (res) {
          options.error(res.error);
        },
      }
    );
  }

  create(options: CloudStoreOptions): void {
    this._makeRequest("/classes/" + options.collection, {
      method: "POST",
      content: Object.assign(
        _removeProtectedFields(
          CloudStore._serializeObject(options.data, options.collection),
          options.collection
        ),
        { ACL: options.acl }
      ),
      success: (response) => {
        const _obj = Object.assign({}, options.data, response);
        options.success(_obj);
        this.events.emit("create", {
          type: "create",
          objectId: options.objectId,
          object: _obj,
          collection: options.collection,
        });
      },
      error: function (res) {
        options.error(res.error);
      },
    });
  }

  increment(options: CloudStoreOptions): void {
    const data: { [key: string]: { __op: string; amount: number } } = {};

    for (let key in options.properties) {
      data[key] = { __op: "Increment", amount: options.properties[key] };
    }

    this._makeRequest(
      "/classes/" + options.collection + "/" + options.objectId,
      {
        method: "PUT",
        content: data,
        success: (response) => {
          options.success(response);
        },
        error: function (res) {
          options.error(res.error);
        },
      }
    );
  }

  save(options: CloudStoreOptions): void {
    const _data = Object.assign({}, options.data);
    delete _data.createdAt;
    delete _data.updatedAt;

    this._makeRequest(
      "/classes/" + options.collection + "/" + options.objectId,
      {
        method: "PUT",
        content: Object.assign(
          _removeProtectedFields(
            CloudStore._serializeObject(_data, options.collection),
            options.collection
          ),
          {
            ACL: options.acl,
          }
        ),
        success: (response) => {
          options.success(response);
          this.events.emit("save", {
            type: "save",
            objectId: options.objectId,
            object: Object.assign({}, options.data, response),
            collection: options.collection,
          });
        },
        error: function (res) {
          options.error(res.error);
        },
      }
    );
  }

  delete(options: CloudStoreOptions): void {
    this._makeRequest(
      "/classes/" + options.collection + "/" + options.objectId,
      {
        method: "DELETE",
        success: () => {
          options.success();
          this.events.emit("delete", {
            type: "delete",
            objectId: options.objectId,
            collection: options.collection,
          });
        },
        error: function (res) {
          options.error(res.error);
        },
      }
    );
  }

  addRelation(options: CloudStoreOptions): void {
    if (!options.key) throw new Error("Key is required");

    const _content: { [key: string]: any } = {};
    _content[options.key] = {
      __op: "AddRelation",
      objects: [
        {
          __type: "Pointer",
          objectId: options.targetObjectId,
          className: options.targetClass,
        },
      ],
    };
    this._makeRequest(
      "/classes/" + options.collection + "/" + options.objectId,
      {
        method: "PUT",
        content: _content,
        success: function (response) {
          options.success(response);
        },
        error: function (res) {
          options.error(res.error);
        },
      }
    );
  }

  removeRelation(options: CloudStoreOptions): void {
    if (!options.key) throw new Error("Key is required");

    const _content: { [key: string]: any } = {};
    _content[options.key] = {
      __op: "RemoveRelation",
      objects: [
        {
          __type: "Pointer",
          objectId: options.targetObjectId,
          className: options.targetClass,
        },
      ],
    };
    this._makeRequest(
      "/classes/" + options.collection + "/" + options.objectId,
      {
        method: "PUT",
        content: _content,
        success: function (response) {
          options.success(response);
        },
        error: function (res) {
          options.error(res.error);
        },
      }
    );
  }

  uploadFile(options: CloudStoreOptions): void {
    if (!options.file) throw new Error("File is required");

    this._makeRequest("/files/" + options.file.name, {
      method: "POST",
      content: options.file,
      contentType: options.file.type,
      success: (response) =>
        options.success(Object.assign({}, options.data, response)),
      error: (err) => options.error(err),
      onUploadProgress: options.onUploadProgress,
    });
  }

  deleteFile(options: CloudStoreOptions): void {
    if (!options.file) throw new Error("File is required");
    this._makeRequest("/files/" + options.file.name, {
      method: "DELETE",
      success: (response) =>
        options.success(Object.assign({}, options.data, response)),
      error: (err) => options.error(err),
    });
  }

  // TODO: What is modelScope?
  static forScope(modelScope?: typeof Model): CloudStore {
    if (modelScope === undefined) return CloudStore.instance;
    // @ts-expect-error
    if (modelScope._cloudStore) return modelScope._cloudStore;

    // @ts-expect-error
    modelScope._cloudStore = new CloudStore(modelScope);
    // @ts-expect-error
    return modelScope._cloudStore;
  }

  static invalidateCollections(): void {
    // @ts-expect-error
    _collections = undefined;
  }

  static _fromJSON(
    item: any,
    collectionName: string,
    modelScope?: typeof Model
  ): any {
    const modelStore = modelScope || Model;

    const model = modelStore.get(item.objectId);
    model._class = collectionName;

    let schema;
    if (
      collectionName !== undefined &&
      // @ts-expect-error
      CloudStore._collections[collectionName] !== undefined
    ) {
      // @ts-expect-error
      schema = CloudStore._collections[collectionName].schema;
    }

    for (const key in item) {
      if (key === "objectId" || key === "ACL") {
        continue;
      }

      const _type =
        schema && schema.properties && schema.properties[key]
          ? schema.properties[key].type
          : undefined;
      model.set(key, CloudStore._deserializeJSON(item[key], _type, modelScope));
    }

    return model;
  }

  static _serializeObject(
    data: any,
    collectionName: string,
    modelScope?: typeof Model
  ): any {
    let schema;
    // @ts-expect-error
    if (CloudStore._collections[collectionName])
      // @ts-expect-error
      schema = CloudStore._collections[collectionName].schema;

    for (const key in data) {
      const _type =
        schema && schema.properties && schema.properties[key]
          ? schema.properties[key].type
          : undefined;

      if (data[key] === undefined || data[key] === null) {
        // Keep null and undefined as is
      } else if (_type === "Pointer" && typeof data[key] === "string") {
        // This is a string pointer to an object
        data[key] = {
          __type: "Pointer",
          className: schema.properties[key].targetClass,
          objectId: data[key],
        };
      } else if (
        _type === "Pointer" &&
        typeof data[key] === "object" &&
        (modelScope || Model).instanceOf(data[key])
      ) {
        // This is an embedded object that should be stored as pointer
        data[key] = {
          __type: "Pointer",
          className: schema.properties[key].targetClass,
          objectId: data[key].getId(),
        };
      } else if (
        _type === "Date" &&
        (typeof data[key] === "string" || data[key] instanceof Date)
      ) {
        data[key] = {
          __type: "Date",
          iso: data[key] instanceof Date ? data[key].toISOString() : data[key],
        };
      } else if (_type === "File" && data[key] instanceof CloudFile) {
        const cloudFile = data[key];
        data[key] = {
          __type: "File",
          url: cloudFile.getUrl(),
          name: cloudFile.getName(),
        };
      } else if (
        _type === "Array" &&
        typeof data[key] === "string" &&
        Collection.exists(data[key])
      ) {
        data[key] = _toJSON(Collection.get(data[key]));
      } else if (
        _type === "Object" &&
        typeof data[key] === "string" &&
        (modelScope || Model).exists(data[key])
      ) {
        data[key] = _toJSON((modelScope || Model).get(data[key]));
      } else if (_type === "GeoPoint" && typeof data[key] === "object") {
        data[key] = {
          __type: "GeoPoint",
          latitude: Number(data[key].latitude),
          longitude: Number(data[key].longitude),
        };
      } else data[key] = _toJSON(data[key]);
    }

    return data;
  }

  static _deserializeJSON(
    data: any,
    type?: string,
    modelScope?: typeof Model
  ): any {
    if (data === undefined) return;
    if (data === null) return null;

    if (type === "Relation" && data.__type === "Relation") {
      return undefined; // Ignore relation fields
    } else if (type === "Pointer" && data.__type === "Pointer") {
      // This is a pointer type, resolve into id
      return data.objectId;
    } else if (type === "Date" && data.__type === "Date") {
      return new Date(data.iso);
    } else if (type === "Date" && typeof data === "string") {
      return new Date(data);
    } else if (type === "File" && data.__type === "File") {
      return new CloudFile(data);
    } else if (type === "GeoPoint" && data.__type === "GeoPoint") {
      return {
        latitude: data.latitude,
        longitude: data.longitude,
      };
    } else if (_isArrayOfObjects(data)) {
      const a: any[] = [];
      for (let i = 0; i < data.length; i++) {
        a.push(CloudStore._deserializeJSON(data[i], undefined, modelScope));
      }
      const c = Collection.get();
      c.set(a);
      return c;
    } else if (Array.isArray(data)) return data;
    // This is an array with mixed data, just return it
    else if (
      data &&
      data.__type === "Object" &&
      data.className !== undefined &&
      data.objectId !== undefined
    ) {
      const _data = Object.assign({}, data);
      delete _data.className;
      delete _data.__type;
      return CloudStore._fromJSON(_data, data.className, modelScope);
    } else if (typeof data === "object" && data !== null) {
      const m = (modelScope || Model).get();
      for (const key in data) {
        m.set(
          key,
          CloudStore._deserializeJSON(data[key], undefined, modelScope)
        );
      }
      return m;
    } else return data;
  }
}

function _isArrayOfObjects(a: any[]): boolean {
  if (!Array.isArray(a)) return false;
  for (let i = 0; i < a.length; i++)
    if (typeof a[i] !== "object" || a[i] === null) return false;

  return true;
}

function _toJSON(obj: any): any {
  if (obj instanceof Model) {
    const res: { [key: string]: any } = {};
    for (const key in obj.data) {
      res[key] = _toJSON(obj.data[key]);
    }
    return res;
  } else if (obj instanceof Collection) {
    const res: any[] = [];
    obj.items.forEach((m) => {
      res.push(_toJSON(m));
    });
    return res;
  }
  return obj;
}
