import {
  Node,
  NodeDefinition,
  logJavaScriptNodeError,
  guid,
  NodeContext,
  GraphModel,
  JavascriptNodeParser,
} from "@fluxscape/runtime";

const defaultCode = "";

interface Internal {
  inputValues: Record<string, any>;
  outputValues: Record<string, any>;
  outputProperties: Record<string, any>;
  runScheduled: boolean;
  setupScheduled: boolean;
  runNextFrameScheduled: boolean;
  isWaitingForExternalFileToLoad: boolean;
  useExternalFile: boolean;
  runFunction?: Function;
  destroyFunction?: Function;
  setupFunction?: Function;
  hasParsedCode: boolean;
  changedInputs: Record<string, any>;
  signalScheduled: Record<string, boolean>;
  killed: boolean;
  inputQueue: Array<{ name: string; value: any }>;
  userFunctionScope: {
    createComponent: (componentName: string) => any;
    deleteComponent: (component: any) => void;
    flagOutputDirty: (name: string) => void;
    runNextFrame: () => void;
    sendSignalOnOutput: (name: string) => void;
  };
  onFrameStart: () => void;
}

export const node: NodeDefinition<Internal> = {
  name: "Javascript2",
  docs: "https://docs.noodl.net/nodes/javascript/script",
  displayNodeName: "Script",
  category: "CustomCode",
  color: "javascript",
  nodeDoubleClickAction: {
    focusPort: "Code",
  },
  searchTags: ["javascript"],
  exportDynamicPorts: true,
  initialize: function () {
    const internal: Internal = this._internal;
    internal.inputValues = {};
    internal.outputValues = {};
    internal.outputProperties = {};
    internal.runScheduled = false;
    internal.setupScheduled = false;
    internal.runNextFrameScheduled = false;
    internal.isWaitingForExternalFileToLoad = false;
    internal.useExternalFile = false;
    internal.runFunction = undefined;
    internal.destroyFunction = undefined;
    internal.setupFunction = undefined;
    internal.hasParsedCode = false;
    internal.changedInputs = {};
    internal.signalScheduled = {};
    internal.killed = false;
    internal.inputQueue = [];

    const self = this;
    internal.userFunctionScope = {
      createComponent(componentName: string) {
        if (
          componentName &&
          componentName.length > 0 &&
          componentName[0] !== "/"
        ) {
          componentName = "/" + componentName;
        }
        // @ts-expect-error
        return self.nodeScope.createNode(componentName, guid());
      },
      deleteComponent(component) {
        // @ts-expect-error
        self.nodeScope.deleteNode(component);
      },
      flagOutputDirty(name: string) {
        if (!name) {
          throw new Error("Output port name must be specified");
        }
        self.flagOutputDirty(name);
      },
      runNextFrame() {
        if (internal.runNextFrameScheduled) {
          return;
        }
        internal.runNextFrameScheduled = true;
        self.context.scheduleNextFrame(function () {
          internal.runNextFrameScheduled = false;
          if (!internal.killed) {
            scheduleRun.call(self);
          }
        });
      },
      sendSignalOnOutput(name: string) {
        self.sendSignalOnOutput(name);
      },
    };

    internal.onFrameStart = onFrameStart.bind(this);
  },
  dynamicports: [
    {
      condition: "useExternalFile = no OR useExternalFile NOT SET",
      inputs: ["code"],
    },
    {
      condition: "useExternalFile = yes",
      inputs: ["externalFile"],
    },
  ],
  inputs: {
    scriptInputs: {
      type: {
        name: "proplist",
        allowEditOnly: true,
      },
      group: "Script Inputs",
      set(value: any) {
        // ignore
      },
    },
    scriptOutputs: {
      type: {
        name: "proplist",
        allowEditOnly: true,
      },
      group: "Script Outputs",
      set(value: any) {
        // ignore
      },
    },
    useExternalFile: {
      type: {
        name: "enum",
        enums: [
          {
            value: "yes",
            label: "Yes",
          },
          {
            value: "no",
            label: "No",
          },
        ],
        allowEditOnly: true,
      },
      default: "no",
      displayName: "Use External File",
      group: "Code",
      set(value: string) {
        this._internal.isWaitingForExternalFileToLoad = value === "yes";
        this._internal.useExternalFile = value === "yes";
      },
    },
    code: {
      displayName: "Code",
      group: "Code",
      type: {
        name: "string",
        allowEditOnly: true,
        codeeditor: "javascript",
      },
      default: defaultCode,
      set(value: string) {
        if (!value) {
          return;
        }
        const self = this;
        this.scheduleAfterInputsHaveUpdated(function () {
          if (this._internal.useExternalFile === false) {
            // @ts-expect-error
            this._callDestroyFunction();
            const parser = JavascriptNodeParser.createFromCode(value, {
              node: this,
            });
            // @ts-expect-error
            self._onCodeParsed(parser);
          }
        });
      },
    },
    externalFile: {
      displayName: "File Path",
      group: "Code",
      type: {
        name: "source",
        allowEditOnly: true,
      },
      set(url: string) {
        if (this._internal.useExternalFile === false) {
          return;
        }

        const self = this;
        JavascriptNodeParser.createFromURL(
          url,
          function (parser) {
            self._internal.isWaitingForExternalFileToLoad = false;
            // @ts-expect-error
            self._onCodeParsed(parser);
          },
          {
            node: this,
          }
        );
      },
    },
  },
  prototypeExtensions: {
    _onNodeDeleted() {
      Node.prototype._onNodeDeleted.call(this);
      this._internal.killed = true;
      // @ts-expect-error
      this._callDestroyFunction();
    },
    update() {
      if (this._internal.isWaitingForExternalFileToLoad === true) {
        // @ts-expect-error
        this._dirty = false;
      } else {
        Node.prototype.update.call(this);
      }
    },
    _onCodeParsed(parser: any) {
      const editorConnection = this.context.editorConnection;

      if (editorConnection) {
        for (const w of [
          "js-destroy-waring",
          "js-run-waring",
          "js-setup-waring",
        ]) {
          editorConnection.clearWarning(
            // @ts-expect-error
            this.nodeScope.componentOwner.name,
            this.id,
            w
          );
        }
      }

      if (parser.error) {
        return;
      }

      // register all color inputs with type 'color' to enable color resolving
      // @ts-expect-error
      Object.keys(this.model.inputPorts).forEach((name) => {
        // @ts-expect-error
        const type = this.model.inputPorts[name].type;
        // @ts-expect-error
        if (type === "color" || type.name === "color") {
          this._internal.inputValues[name] = undefined;

          if (!this.hasInput(name)) {
            this.registerInput(name, {
              type: "color",
              set: userInputSetter.bind(this, name),
            });
          } else {
            // input was registered before js was done parsing
            // patch it instead of creating a new one
            // @ts-expect-error
            this.getInput(name).type = "color";
          }
        }
      });

      // @ts-expect-error
      Object.keys(this.model.outputPorts).forEach((name) => {
        // @ts-expect-error
        this.registerOutputIfNeeded(name);
      });

      this._internal.setupFunction = parser.setup;
      this._internal.runFunction = parser.change; // Run function is actually called change
      this._internal.destroyFunction = parser.destroy;

      // @ts-expect-error
      this._internal.definedObject = parser.definedObject;

      if (this._internal.setupFunction) {
        scheduleSetup.call(this);
      }

      if (this._internal.runFunction) {
        scheduleRun.call(this);
      }

      this._internal.hasParsedCode = true;

      // set all the inputs that arrived before the code was parsed
      if (this._internal.inputQueue) {
        for (const { name, value } of this._internal.inputQueue) {
          this.setInputValue(name, value);
        }

        // delete the queue, not needed anymore
        // @ts-expect-error
        this._internal.inputQueue = undefined;
      }

      // Node API
      parser.apis.Node.Inputs = this._internal.inputValues;
      parser.apis.Node.Outputs = this._internal.outputProperties;
    },
    registerInputIfNeeded(name: string) {
      if (this.hasInput(name)) {
        return;
      }

      this._internal.inputValues[name] = undefined;

      this.registerInput(name, {
        set: userInputSetter.bind(this, name),
      });
    },
    registerOutputIfNeeded(name: string) {
      if (this.hasOutput(name)) {
        return;
      }

      const self = this;

      const isSignal =
        // @ts-expect-error
        _typename(this.model.outputPorts[name].type) === "signal";

      Object.defineProperty(this._internal.outputProperties, name, {
        set(value) {
          if (isSignal) return; // Cannot set signal functions

          self._internal.outputValues[name] = value;
          self.flagOutputDirty(name);
        },
        get() {
          if (isSignal)
            return () => {
              if (self.hasOutput(name)) self.sendSignalOnOutput(name);
            };
          return self._internal.outputValues[name];
        },
      });

      this.registerOutput(name, {
        getter: userOutputGetter.bind(this, name),
      });
    },
    _callRunFunction() {
      const internal = this._internal;
      if (!internal.runFunction || internal.killed) {
        return;
      }

      try {
        internal.runFunction.call(
          internal.userFunctionScope,
          internal.inputValues,
          internal.outputProperties,
          internal.changedInputs
        );
      } catch (e) {
        logJavaScriptNodeError(e);

        if (
          this.context.editorConnection &&
          this.context.isWarningTypeEnabled("javascriptExecution")
        ) {
          this.context.editorConnection.sendWarning(
            // @ts-expect-error
            this.nodeScope.componentOwner.name,
            this.id,
            "js-run-waring",
            {
              showGlobally: true,
              // @ts-expect-error
              message: "<strong>run</strong>: " + e.message,
            }
          );
        }
      }
    },
    _callSignalFunction(name: string) {
      const internal = this._internal;
      // @ts-expect-error
      if (!internal.definedObject || internal.killed) {
        return;
      }

      if (
        // @ts-expect-error
        !internal.definedObject[name] ||
        // @ts-expect-error
        typeof internal.definedObject[name] !== "function"
      ) {
        return;
      }

      try {
        // @ts-expect-error
        internal.definedObject[name].call(
          internal.userFunctionScope,
          internal.inputValues,
          internal.outputProperties
        );
      } catch (e) {
        console.log(
          "Error in JS node signal function code.",
          // @ts-expect-error
          Object.getPrototypeOf(e).constructor.name + ": " + e.message
        );
        if (
          this.context.editorConnection &&
          this.context.isWarningTypeEnabled("javascriptExecution")
        ) {
          this.context.editorConnection.sendWarning(
            // @ts-expect-error
            this.nodeScope.componentOwner.name,
            this.id,
            "js-run-waring",
            {
              showGlobally: true,
              // @ts-expect-error
              message: "<strong>run</strong>: " + e.message,
            }
          );
        }
      }
    },
    _callDestroyFunction() {
      const internal = this._internal;

      if (!internal.destroyFunction) {
        return;
      }

      try {
        internal.destroyFunction.call(
          internal.userFunctionScope,
          internal.inputValues,
          internal.outputProperties
        );
      } catch (e) {
        console.log(
          "Error in JS node destroy code.",
          // @ts-expect-error
          Object.getPrototypeOf(e).constructor.name + ": " + e.message
        );
        if (
          this.context.editorConnection &&
          this.context.isWarningTypeEnabled("javascriptExecution")
        ) {
          this.context.editorConnection.sendWarning(
            // @ts-expect-error
            this.nodeScope.componentOwner.name,
            this.id,
            "js-destroy-waring",
            {
              showGlobally: true,
              // @ts-expect-error
              message: "<strong>setup</strong>: " + e.message,
            }
          );
        }
      }
    },
    _callSetupFunction() {
      const internal = this._internal;
      if (!internal.setupFunction || internal.killed) {
        return;
      }

      try {
        internal.setupFunction.call(
          internal.userFunctionScope,
          internal.inputValues,
          internal.outputProperties
        );
      } catch (e) {
        console.log(
          "Error in JS node setup code.",
          // @ts-expect-error
          Object.getPrototypeOf(e).constructor.name + ": " + e.message
        );
        if (
          this.context.editorConnection &&
          this.context.isWarningTypeEnabled("javascriptExecution")
        ) {
          this.context.editorConnection.sendWarning(
            // @ts-expect-error
            this.nodeScope.componentOwner.name,
            this.id,
            "js-setup-waring",
            {
              showGlobally: true,
              // @ts-expect-error
              message: "<strong>setup</strong>: " + e.message,
            }
          );
        }
      }
    },
  },
};

function scheduleSetup() {
  // @ts-expect-error
  if (this._internal.setupScheduled) {
    return;
  }

  // @ts-expect-error
  this._internal.setupScheduled = true;
  // @ts-expect-error
  this.scheduleAfterInputsHaveUpdated(function () {
    // @ts-expect-error
    if (!this._internal.killed) {
      // @ts-expect-error
      this._callSetupFunction();
      // @ts-expect-error
      this._internal.setupScheduled = false;
    }
  });
}

function scheduleRun() {
  // @ts-expect-error
  if (this._internal.runScheduled || this._internal.killed) {
    return;
  }

  // @ts-expect-error
  this._internal.runScheduled = true;
  // @ts-expect-error
  this.scheduleAfterInputsHaveUpdated(function () {
    // @ts-expect-error
    if (!this._internal.killed) {
      // @ts-expect-error
      this._callRunFunction();
      // @ts-expect-error
      this._internal.changedInputs = {};
      // @ts-expect-error
      this._internal.runScheduled = false;
    }
  });
}

function scheduleSignal(name: string) {
  // @ts-expect-error
  if (this._internal.signalScheduled[name] || this._internal.killed) {
    return;
  }

  // @ts-expect-error
  this._internal.signalScheduled[name] = true;
  // @ts-expect-error
  this.scheduleAfterInputsHaveUpdated(function () {
    // @ts-expect-error
    if (!this._internal.killed) {
      // @ts-expect-error
      this._callSignalFunction(name);
      // @ts-expect-error
      this._internal.signalScheduled[name] = false;
    }
  });
}

function onFrameStart() {
  // @ts-expect-error
  this._internal.runNextFrame = false;
  // @ts-expect-error
  scheduleRun.call(this);
}

function _typename(type: any) {
  if (typeof type === "string") return type;
  else return type.name;
}

function userInputSetter(name: string, value: any) {
  // @ts-expect-error
  if (this._internal.hasParsedCode === true) {
    if (
      // @ts-expect-error
      this.model.inputPorts[name] !== undefined &&
      // @ts-expect-error
      _typename(this.model.inputPorts[name].type) === "signal"
    ) {
      // If this is a signal, call the signal function
      if (
        // @ts-expect-error
        this._internal.definedObject &&
        // @ts-expect-error
        typeof this._internal.definedObject[name] === "function"
      ) {
        // This is a signal input, schedule a call to the signal function
        // @ts-expect-error
        if (value) scheduleSignal.call(this, name);
      }
    } else {
      // @ts-expect-error
      this._internal.inputValues[name] = value;
      // @ts-expect-error
      this._internal.changedInputs[name] = true;
      // @ts-expect-error
      scheduleRun.call(this);
    }
  } else {
    // inputs are arriving before the code is parsed
    // queue them up and set them later to make sure signals are
    // properly recognized
    // @ts-expect-error
    this._internal.inputQueue.push({
      name,
      value,
    });
  }
}

function userOutputGetter(name: string) {
  // @ts-expect-error
  return this._internal.outputValues[name];
}

function _parseAndSourceJavascript(
  nodeModel: any,
  context: any,
  fn: (ports: any) => void
) {
  const editorConnection = context.editorConnection;

  if (!nodeModel.parameters) {
    return;
  }

  function clearWarnings() {
    for (const w of [
      "js-parse-waring",
      "js-destroy-waring",
      "js-run-waring",
      "js-setup-waring",
    ]) {
      editorConnection.clearWarning(nodeModel.component.name, nodeModel.id, w);
    }
  }

  function onCodeParsed(parser: any) {
    if (parser.error) {
      editorConnection.sendWarning(
        nodeModel.component.name,
        nodeModel.id,
        "js-parse-waring",
        {
          showGlobally: true,
          message: parser.error,
        }
      );
    } else {
      clearWarnings();
    }

    fn(parser.getPorts());
  }

  if (
    nodeModel.parameters.externalFile &&
    nodeModel.parameters.useExternalFile === "yes"
  ) {
    const url = nodeModel.parameters.externalFile;
    JavascriptNodeParser.createFromURL(url, onCodeParsed);
  } else if (nodeModel.parameters.code) {
    const parser = JavascriptNodeParser.createFromCode(
      nodeModel.parameters.code
    );
    onCodeParsed(parser);
  } else {
    // no code, just send empty port list
    clearWarnings();
    fn([]);
  }
}

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  function _managePortsForNode(node: any) {
    function _updatePorts() {
      const ports: any[] = [];

      const _inputTypeEnums = [
        {
          value: "string",
          label: "String",
        },
        {
          value: "boolean",
          label: "Boolean",
        },
        {
          value: "number",
          label: "Number",
        },
        {
          value: "object",
          label: "Object",
        },
        {
          value: "array",
          label: "Array",
        },
      ];

      const _outputTypeEnums = [
        {
          value: "string",
          label: "String",
        },
        {
          value: "boolean",
          label: "Boolean",
        },
        {
          value: "number",
          label: "Number",
        },
        {
          value: "object",
          label: "Object",
        },
        {
          value: "array",
          label: "Array",
        },
        {
          value: "signal",
          label: "Signal",
        },
      ];

      // Outputs
      if (
        node.parameters["scriptOutputs"] !== undefined &&
        node.parameters["scriptOutputs"].length > 0
      ) {
        node.parameters["scriptOutputs"].forEach((p: any) => {
          // Type for output
          ports.push({
            name: "outtype-" + p.label,
            displayName: "Type",
            plug: "input",
            type: {
              name: "enum",
              enums: _outputTypeEnums,
              allowEditOnly: true,
            },
            default: "string",
            parent: "scriptOutputs",
            parentItemId: p.id,
          });

          // Value for output
          ports.push({
            name: p.label,
            plug: "output",
            type: node.parameters["outtype-" + p.label] || "*",
            group: "Outputs",
          });
        });
      }

      // Inputs
      if (
        node.parameters["scriptInputs"] !== undefined &&
        node.parameters["scriptInputs"].length > 0
      ) {
        node.parameters["scriptInputs"].forEach((p: any) => {
          // Type for input
          ports.push({
            name: "intype-" + p.label,
            displayName: "Type",
            plug: "input",
            type: {
              name: "enum",
              enums: _inputTypeEnums,
              allowEditOnly: true,
            },
            default: "string",
            parent: "scriptInputs",
            parentItemId: p.id,
          });

          // Default Value for input
          ports.push({
            name: p.label,
            plug: "input",
            type: node.parameters["intype-" + p.label] || "string",
            group: "Inputs",
          });
        });
      }

      _parseAndSourceJavascript(node, context, function (_ports: any) {
        // Merge in ports from script
        _ports.forEach((p: any) => {
          if (ports.find((_p) => _p.name === p.name && _p.plug === p.plug))
            return; // Port already exists

          ports.push(p);
        });

        context.editorConnection?.sendDynamicPorts(node.id, ports);
      });
    }

    _updatePorts();
    node.on("parameterUpdated", function () {
      _updatePorts();
    });
  }

  graphModel.on("editorImportComplete", () => {
    graphModel.on("nodeAdded.Javascript2", function (node: any) {
      _managePortsForNode(node);
    });

    for (const node of graphModel.getNodesWithType("Javascript2")) {
      _managePortsForNode(node);
    }
  });
}
