import NoodlRuntime from "@fluxscape/runtime";
import { GraphModel } from "@fluxscape/runtime";
import { NodeContext } from "@fluxscape/runtime/src/nodecontext";
import { NodeDefinition } from "@fluxscape/runtime/src/nodedefinition";

interface Internal {
  params: Record<string, any>;
  query: Record<string, any>;
  openInNewTab: boolean;
  path?: string;
  queryNames?: string;
  hasScheduledNavigate?: boolean;
}

export const node: NodeDefinition<Internal> = {
  name: "PageStackNavigateToPath",
  displayNodeName: "Navigate To Path",
  category: "Navigation",
  docs: "https://docs.noodl.net/nodes/navigation/navigate-to-path",
  initialize() {
    const internal = this._internal;
    internal.params = {};
    internal.query = {};
    internal.openInNewTab = false;
  },
  inputs: {
    path: {
      type: { name: "string" },
      displayName: "Path",
      group: "General",
      set(value: string) {
        this._internal.path = value;
      },
    },
    queryNames: {
      type: { name: "stringlist", allowEditOnly: true },
      displayName: "Query",
      group: "Query",
      set(value: string) {
        this._internal.queryNames = value;
      },
    },
    openInNewTab: {
      index: 10,
      displayName: "Open in new tab",
      group: "General",
      default: false,
      type: "boolean",
      set(value: boolean) {
        this._internal.openInNewTab = !!value;
      },
    },
    navigate: {
      displayName: "Navigate",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleNavigate();
      },
    },
  },
  outputs: {},
  methods: {
    scheduleNavigate() {
      const internal = this._internal;

      if (!internal.hasScheduledNavigate) {
        internal.hasScheduledNavigate = true;
        this.scheduleAfterInputsHaveUpdated(() => {
          internal.hasScheduledNavigate = false;
          // @ts-expect-error
          this.navigate();
        });
      }
    },
    navigate() {
      const internal = this._internal;

      let formattedPath = internal.path;
      if (formattedPath === undefined) return;

      const matches = internal.path?.match(/\{[A-Za-z0-9_]*\}/g);
      let inputs: string[] = [];
      if (matches) {
        inputs = matches.map((name) => name.replace("{", "").replace("}", ""));
      }

      inputs.forEach((name) => {
        const v = internal.params[name];
        formattedPath = formattedPath!.replace(
          "{" + name + "}",
          v !== undefined ? v : ""
        );
      });

      let urlPath: string | undefined, hashPath: string | undefined;
      const navigationPathType =
        NoodlRuntime.instance.getProjectSettings()["navigationPathType"];
      if (navigationPathType === undefined || navigationPathType === "hash")
        hashPath = formattedPath;
      else urlPath = formattedPath;

      const query: string[] = [];
      if (internal.queryNames !== undefined) {
        internal.queryNames.split(",").forEach((q) => {
          if (internal.query[q] !== undefined) {
            query.push(q + "=" + internal.query[q]);
          }
        });
      }

      const compiledUrl =
        (urlPath !== undefined ? urlPath : "") +
        (query.length >= 1 ? "?" + query.join("&") : "") +
        (hashPath !== undefined ? "#" + hashPath : "");

      if (this._internal.openInNewTab) {
        window.open(compiledUrl, "_blank");
      } else {
        window.history.pushState({}, "", compiledUrl);
        dispatchEvent(new PopStateEvent("popstate", {}));
      }
    },
    setParam(name: string, value: any) {
      this._internal.params[name] = value;
    },
    setQuery(name: string, value: any) {
      this._internal.query[name] = value;
    },
    registerInputIfNeeded(name: string) {
      if (this.hasInput(name)) {
        return;
      }

      if (name.startsWith("p-"))
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setParam.bind(this, name.substring("p-".length)),
        });

      if (name.startsWith("q-"))
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setQuery.bind(this, name.substring("q-".length)),
        });
    },
  },
};

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  function _managePortsForNode(node: any) {
    function _updatePorts() {
      const ports: any[] = [];

      if (node.parameters["path"] !== undefined) {
        const inputs =
          node.parameters["path"].match(/\{[A-Za-z0-9_]*\}/g) || [];
        const portsNames = inputs.map((def) =>
          def.replace("{", "").replace("}", "")
        );

        const uniquePorts = portsNames
          //get unique names
          .filter((value, index, self) => self.indexOf(value) === index)
          //and map names to ports
          .map((name) => ({
            name: "p-" + name,
            displayName: name,
            group: "Parameter",
            type: "*",
            plug: "input",
          }));

        ports.push(...uniquePorts);
      }

      if (node.parameters["queryNames"] !== undefined) {
        node.parameters["queryNames"].split(",").forEach((q) => {
          ports.push({
            name: "q-" + q,
            displayName: q,
            group: "Query",
            plug: "input",
            type: "*",
          });
        });
      }

      context.editorConnection?.sendDynamicPorts(node.id, ports);
    }

    _updatePorts();
    node.on("parameterUpdated", function (event: any) {
      _updatePorts();
    });
  }

  graphModel.on("editorImportComplete", () => {
    graphModel.on("nodeAdded.PageStackNavigateToPath", function (node: any) {
      _managePortsForNode(node);
    });

    for (const node of graphModel.getNodesWithType("PageStackNavigateToPath")) {
      _managePortsForNode(node);
    }
  });
}
