import { NodeDefinition } from "@fluxscape/runtime/src/nodedefinition";
import { NodeContext } from "@fluxscape/runtime/src/nodecontext";
import { GraphModel } from "@fluxscape/runtime";

interface Internal {
  params: Record<string, any>;
}

export const node: NodeDefinition<Internal> = {
  name: "PageInputs",
  displayNodeName: "Page Inputs",
  category: "Navigation",
  docs: "https://docs.noodl.net/nodes/navigation/page-inputs",
  color: "component",
  initialize() {
    this._internal.params = {};
  },
  inputs: {
    pathParams: {
      type: { name: "stringlist", allowEditOnly: true },
      group: "Path Parameters",
    },
    queryParams: {
      type: { name: "stringlist", allowEditOnly: true },
      group: "Query Parameters",
    },
  },
  outputs: {},
  methods: {
    _setPageParams(params: Record<string, any>) {
      for (const key in params) {
        this._internal.params[key] = params[key];
        if (this.hasOutput("pm-" + key)) this.flagOutputDirty("pm-" + key);
      }
    },
    getPageParam(name: string) {
      return this._internal.params[name];
    },
    registerOutputIfNeeded(name: string) {
      if (this.hasOutput(name)) {
        return;
      }

      if (name.startsWith("pm-"))
        return this.registerOutput(name, {
          // @ts-expect-error
          getter: this.getPageParam.bind(this, name.substring("pm-".length)),
        });
    },
  },
};

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  function _managePortsForNode(node: any) {
    function _updatePorts() {
      const ports: any[] = [];
      const uniqueNames: Record<string, boolean> = {};

      if (node.parameters["pathParams"] !== undefined) {
        node.parameters["pathParams"].split(",").forEach((p: string) => {
          uniqueNames[p] = true;
        });
      }

      if (node.parameters["queryParams"] !== undefined) {
        node.parameters["queryParams"].split(",").forEach((p: string) => {
          uniqueNames[p] = true;
        });
      }

      Object.keys(uniqueNames).forEach((outputName) => {
        ports.push({
          name: "pm-" + outputName,
          displayName: outputName,
          type: "*",
          plug: "output",
          group: "Parameters",
        });
      });

      context.editorConnection?.sendDynamicPorts(node.id, ports);
    }

    // Track page node in this component, update if there are any changes
    _updatePorts();
    node.on("parameterUpdated", function () {
      _updatePorts();
    });
  }

  graphModel.on("editorImportComplete", () => {
    graphModel.on("nodeAdded.PageInputs", function (node: any) {
      _managePortsForNode(node);
    });

    for (const node of graphModel.getNodesWithType("PageInputs")) {
      _managePortsForNode(node);
    }
  });
}
