import { GraphModel } from "@fluxscape/runtime";
import { NodeContext } from "@fluxscape/runtime/src/nodecontext";
import { NodeDefinition } from "@fluxscape/runtime/src/nodedefinition";

interface Internal {
  resultValues: Record<string, any>;
  results?: string[];
  backActions?: string[];
  backCallback?: (args: any) => void;
  backAction?: string;
  hasScheduledNavigate?: boolean;
}

export const node: NodeDefinition<Internal> = {
  name: "PageStackNavigateBack",
  displayNodeName: "Pop Component Stack",
  category: "Navigation",
  docs: "https://docs.noodl.net/nodes/component-stack/pop-component",
  inputs: {
    navigate: {
      displayName: "Navigate",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleNavigate();
      },
    },
    results: {
      type: { name: "stringlist", allowEditOnly: true },
      group: "Results",
      set(value) {
        this._internal.results = value;
      },
    },
    backActions: {
      type: { name: "stringlist", allowEditOnly: true },
      group: "Back Actions",
      set(value) {
        this._internal.backActions = value;
      },
    },
  },
  initialize() {
    this._internal.resultValues = {};
  },
  methods: {
    scheduleNavigate() {
      var _this = this;
      var internal = this._internal;
      if (!internal.hasScheduledNavigate) {
        internal.hasScheduledNavigate = true;
        this.scheduleAfterInputsHaveUpdated(function () {
          internal.hasScheduledNavigate = false;
          // @ts-expect-error
          _this.navigate();
        });
      }
    },
    _setBackCallback(cb) {
      this._internal.backCallback = cb;
    },
    navigate() {
      if (this._internal.backCallback === undefined) return;

      this._internal.backCallback({
        backAction: this._internal.backAction,
        results: this._internal.resultValues,
      });
    },
    setResultValue(key: string, value: any) {
      this._internal.resultValues[key] = value;
    },
    backActionTriggered(name: string) {
      this._internal.backAction = name;
      // @ts-expect-error
      this.scheduleNavigate();
    },
    registerInputIfNeeded(name: string) {
      if (this.hasInput(name)) {
        return;
      }

      if (name.startsWith("result-")) {
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setResultValue.bind(this, name.substring("result-".length)),
        });
      }

      if (name.startsWith("backAction-")) {
        return this.registerInput(name, {
          set: (value) => {
            // @ts-expect-error
            value && this.backActionTriggered(name);
          },
        });
      }
    },
  },
};

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  function _managePortsForNode(node: any) {
    function _updatePorts() {
      var ports: any[] = [];

      // Add results inputs
      var results = node.parameters.results;
      if (results) {
        results = results ? results.split(",") : undefined;
        for (var i in results) {
          var p = results[i];

          ports.push({
            type: {
              name: "*",
            },
            plug: "input",
            group: "Results",
            name: "result-" + p,
            displayName: p,
          });
        }
      }

      // Add back actions
      var backActions = node.parameters.backActions;
      if (backActions) {
        backActions = backActions ? backActions.split(",") : undefined;
        for (var i in backActions) {
          var p = backActions[i];

          ports.push({
            type: "signal",
            plug: "input",
            group: "Back Actions",
            name: "backAction-" + p,
            displayName: p,
          });
        }
      }

      context.editorConnection?.sendDynamicPorts(node.id, ports);
    }

    _updatePorts();
    node.on("parameterUpdated", function (event: any) {
      if (event.name === "results" || event.name === "backActions") {
        _updatePorts();
      }
    });
  }

  graphModel.on("editorImportComplete", () => {
    graphModel.on("nodeAdded.PageStackNavigateBack", function (node: any) {
      _managePortsForNode(node);
    });

    for (const node of graphModel.getNodesWithType("PageStackNavigateBack")) {
      _managePortsForNode(node);
    }
  });
}
