import * as DbModelCRUDBase from "./dbmodelcrudbase";
import { CloudStore } from "../../../api/cloudstore";
import { NodeDefinition } from "../../../nodedefinition";
import { Model } from "../../../model";

interface Internal {
  model?: Model;
  collectionId?: string;
  nodeScope: {
    ModelScope?: typeof Model;
  };
}

const DeleteDbModelPropertiedNodeDefinition: {
  node: NodeDefinition<Internal>;
} = {
  node: {
    name: "DeleteDbModelProperties",
    category: "",
    docs: "https://docs.noodl.net/nodes/data/cloud-data/delete-record",
    displayNodeName: "Delete Record",
    shortDesc:
      "Stores any amount of properties and can be used standalone or together with Collections and For Each nodes.",
    inputs: {
      store: {
        displayName: "Do",
        group: "Actions",
        valueChangedToTrue() {
          // @ts-expect-error
          this.storageDelete();
        },
      },
    },
    outputs: {
      deleted: {
        type: "signal",
        displayName: "Success",
        group: "Events",
      },
    },
    methods: {
      storageDelete() {
        const internal = this._internal;

        // @ts-expect-error
        if (!this.checkWarningsBeforeCloudOp()) return;

        // @ts-expect-error
        this.scheduleOnce("StorageDelete", () => {
          if (!internal.model) {
            // @ts-expect-error
            this.setError("Missing Record Id");
            return;
          }

          // @ts-expect-error
          CloudStore.forScope(this.nodeScope.ModelScope).delete({
            // @ts-expect-error
            collection: internal.collectionId,
            objectId: internal.model.getId(), // Get the objectId part of the model id,
            success: () => {
              // @ts-expect-error
              internal.model!.notify("delete"); // Notify that this model has been deleted
              this.sendSignalOnOutput("deleted");
            },
            error: (err: string) => {
              // @ts-expect-error
              this.setError(err || "Failed to delete.");
            },
          });
        });
      },
    },
  },
};

DbModelCRUDBase.addBaseInfo(DeleteDbModelPropertiedNodeDefinition, {
  includeInputProperties: false,
});
DbModelCRUDBase.addModelId(DeleteDbModelPropertiedNodeDefinition);

export const node = DeleteDbModelPropertiedNodeDefinition.node;
