import type { Node } from "../../node";
import { guid } from "../../utils";
import { Model } from "../../model";
import { NodeDefinition } from "../../nodedefinition";

interface Internal {
  queuedOperations: Function[];
  state: "idle" | "running" | "aborted";
  maxRunningTasks: number;
  activeTasks: Map<string, any>;
  hasScheduledRun?: boolean;
  hasScheduledAbort?: boolean;
  template?: any;
  items?: any[];
  stopOnFailure?: boolean;
  numTasks?: number;
  failedTasks?: number;
  completedTasks?: number;
  queuedTasks?: any[];
  runningTasks?: number;
}

function sendSignalOnInput(itemNode: any, name: string): void {
  itemNode.queueInput(name, true); // send signal
  itemNode.queueInput(name, false);
}

export const node: NodeDefinition<Internal> = {
  name: "RunTasks",
  displayNodeName: "Run Tasks",
  docs: "https://docs.noodl.net/nodes/data/run-tasks",
  color: "data",
  category: "Data",
  initialize(): void {
    this._internal = {
      queuedOperations: [],
      state: "idle",
      maxRunningTasks: 10,
      activeTasks: new Map(), //id => ComponentInstanceNode
    } as Internal;
  },
  inputs: {
    items: {
      group: "Data",
      displayName: "Items",
      type: "array",
      set(value: any[]): void {
        if (!value) return;
        if (value === this._internal.items) return;

        this._internal.items = value;
      },
    },
    stopOnFailure: {
      group: "General",
      displayName: "Stop On Failure",
      type: "boolean",
      default: false,
      set(value: boolean): void {
        this._internal.stopOnFailure = value;
      },
    },
    maxRunningTasks: {
      group: "General",
      displayName: "Max Running Tasks",
      type: "number",
      default: 10,
      set(value: number): void {
        this._internal.maxRunningTasks = value;
      },
    },
    taskTemplate: {
      type: "component",
      displayName: "Template",
      group: "General",
      set(value: any): void {
        this._internal.template = value;
      },
    },
    run: {
      group: "General",
      displayName: "Do",
      type: "signal",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleRun();
      },
    },
    abort: {
      group: "General",
      displayName: "Abort",
      type: "signal",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleAbort();
      },
    },
  },
  outputs: {
    success: {
      type: "signal",
      group: "Events",
      displayName: "Success",
    },
    failure: {
      type: "signal",
      group: "Events",
      displayName: "Failure",
    },
    done: {
      type: "signal",
      group: "Events",
      displayName: "Done",
    },
    aborted: {
      type: "signal",
      group: "Events",
      displayName: "Aborted",
    },
  },
  methods: {
    scheduleRun(): void {
      const internal = this._internal;
      if (!internal.hasScheduledRun) {
        internal.hasScheduledRun = true;
        this.scheduleAfterInputsHaveUpdated(() => {
          // @ts-expect-error
          this._queueOperation(() => {
            internal.hasScheduledRun = false;
            // @ts-expect-error
            this.run();
          });
        });
      }
    },
    scheduleAbort(): void {
      const internal = this._internal;
      if (!internal.hasScheduledAbort) {
        internal.hasScheduledAbort = true;
        this.scheduleAfterInputsHaveUpdated(() => {
          // @ts-expect-error
          this._queueOperation(() => {
            internal.hasScheduledAbort = false;
            // @ts-expect-error
            this.abort();
          });
        });
      }
    },
    async createTaskComponent(item: any): Promise<any> {
      const internal = this._internal;

      const modelScope = this.nodeScope!.modelScope || Model;
      const model = modelScope.create(item);

      const itemNode = await this.nodeScope!.createNode(
        internal.template,
        guid(),
        {
          _forEachModel: model,
          _forEachNode: this,
        }
      );

      // This is needed to make sure any action connected to "Do"
      // is not run directly
      const _isInputConnected = itemNode.isInputConnected.bind(itemNode);
      itemNode.isInputConnected = (name: string): boolean => {
        if (name === "Do") return true;
        return _isInputConnected(name);
      };

      // Set the Id as an input
      if (itemNode.hasInput("Id")) {
        itemNode.setInputValue("Id", model.getId());
      }
      if (itemNode.hasInput("id")) {
        itemNode.setInputValue("id", model.getId());
      }

      // Push all other values also as inputs
      // if they exist as component inputs
      for (const inputKey in itemNode._inputs) {
        if (model.data[inputKey] !== undefined)
          itemNode.setInputValue(inputKey, model.data[inputKey]);
      }

      // capture signals
      itemNode._internal.creatorCallbacks = {
        onOutputChanged: (
          name: string,
          value: boolean,
          oldValue: boolean | undefined
        ) => {
          if (
            (oldValue === false || oldValue === undefined) &&
            value === true
          ) {
            // @ts-expect-error
            this.itemOutputSignalTriggered(name, model, itemNode);
          }
        },
      };

      return itemNode;
    },
    async startTask(task: any): Promise<void> {
      const internal = this._internal;

      try {
        // @ts-expect-error
        const taskComponent = await this.createTaskComponent(task);
        // @ts-expect-error
        internal.runningTasks++;
        sendSignalOnInput(taskComponent, "Do");
        internal.activeTasks.set(taskComponent.id, taskComponent);
      } catch (e) {
        // Something went wrong starting the task
        console.log(e);
      }
    },
    async run(): Promise<void> {
      const internal = this._internal;

      if (this.context.editorConnection) {
        if (internal.state !== "idle") {
          this.context.editorConnection.sendWarning(
            this.nodeScope!.componentOwner.name,
            this.id,
            "run-tasks",
            {
              message: "Cannot start when not in idle mode",
            }
          );
        } else if (!internal.template) {
          this.context.editorConnection.sendWarning(
            this.nodeScope!.componentOwner.name,
            this.id,
            "run-tasks",
            {
              message: "No task template specified.",
            }
          );
        } else if (!internal.items) {
          this.context.editorConnection.sendWarning(
            this.nodeScope!.componentOwner.name,
            this.id,
            "run-tasks",
            {
              message: "No items array provided.",
            }
          );
        } else {
          this.context.editorConnection.clearWarning(
            this.nodeScope!.componentOwner.name,
            this.id,
            "run-tasks"
          );
        }
      }

      if (internal.state !== "idle") {
        return;
      }

      if (!internal.template) {
        return;
      }

      if (!internal.items) {
        return;
      }

      internal.state = "running";
      internal.numTasks = internal.items.length;
      internal.failedTasks = 0;
      internal.completedTasks = 0;
      // @ts-expect-error
      internal.queuedTasks = [].concat(internal.items);
      internal.runningTasks = 0;

      // No tasks
      if (internal.items.length === 0) {
        this.sendSignalOnOutput("success");
        internal.state = "idle";
      }

      // Start tasks
      for (
        let i = 0;
        i < Math.min(internal.maxRunningTasks, internal.queuedTasks.length);
        i++
      ) {
        const task = internal.queuedTasks.shift();
        if (!task) break;

        // @ts-expect-error
        this.startTask(task);
      }
    },
    abort(): void {
      const internal = this._internal;
      internal.state = "aborted";
    },
    itemOutputSignalTriggered(name: string, model: any, itemNode: any): void {
      const internal = this._internal;

      if (internal.state === "idle") {
        // Signal while we are not running is ignored
        return;
      }

      const checkDone = () => {
        if (internal.state === "aborted") {
          this.sendSignalOnOutput("aborted");
          internal.state = "idle";
          return;
        }

        if (internal.completedTasks === internal.numTasks) {
          if (internal.failedTasks === 0) this.sendSignalOnOutput("success");
          else this.sendSignalOnOutput("failure");
          this.sendSignalOnOutput("done");
          internal.state = "idle";
        } else {
          if (internal.stopOnFailure) {
            // Only continue if there are no failed tasks, otherwise aborted
            if (internal.failedTasks === 0) {
              // @ts-expect-error
              internal.runningTasks++;
              // @ts-expect-error
              const task = internal.queuedTasks.shift();
              // @ts-expect-error
              if (task) this.startTask(task);
            } else {
              this.sendSignalOnOutput("failure");
              this.sendSignalOnOutput("aborted");
            }
          } else {
            // @ts-expect-error
            internal.runningTasks++;
            // @ts-expect-error
            const task = internal.queuedTasks.shift();
            // @ts-expect-error
            if (task) this.startTask(task);
          }
        }
      };

      if (name === "Success") {
        // @ts-expect-error
        internal.completedTasks++;
        // @ts-expect-error
        internal.runningTasks--;
        checkDone();
      } else if (name === "Failure") {
        // @ts-expect-error
        internal.completedTasks++;
        // @ts-expect-error
        internal.failedTasks++;
        // @ts-expect-error
        internal.runningTasks--;
        checkDone();
      }

      internal.activeTasks.delete(itemNode.id);
      this.nodeScope!.deleteNode(itemNode);
    },
    _queueOperation(op: Function): void {
      this._internal.queuedOperations.push(op);
      // @ts-expect-error
      this._runQueueOperations();
    },
    async _runQueueOperations(): Promise<void> {
      // @ts-expect-error
      if (this.runningOperations) {
        return;
      }
      // @ts-expect-error
      this.runningOperations = true;

      while (this._internal.queuedOperations.length) {
        const op = this._internal.queuedOperations.shift();
        // @ts-expect-error
        await op();
      }

      // @ts-expect-error
      this.runningOperations = false;
    },
  },
  // @ts-expect-error
  _deleteAllTasks(): void {
    for (const taskComponent of this._internal.activeTasks) {
      // @ts-expect-error
      this.nodeScope.deleteNode(taskComponent);
    }
    this._internal.activeTasks.clear();
  },
  _onNodeDeleted(): void {
    // @ts-expect-error
    Node.prototype._onNodeDeleted.call(this);
    // @ts-expect-error
    this._deleteAllTasks();
  },
};
