import { NodeDefinition } from "../../../nodedefinition";

interface NodeArgs {
  name: string;
  docs?: string;
  shortDesc?: string;
  nodeDoubleClickAction?:
    | string
    | {
        focusPort: string;
      };
  type: { name: string };
  startValue: any;
  cast: (value: any) => any;
  onChanged?: () => void;
}

export interface Internal {
  currentValue: any;
  latestValue: any;
}

export function createDefinition(args: NodeArgs): NodeDefinition<Internal> {
  return {
    name: args.name,
    docs: args.docs,
    shortDesc: args.shortDesc,
    nodeDoubleClickAction: args.nodeDoubleClickAction,
    category: "Variables",
    initialize() {
      this._internal.currentValue = args.startValue;
      this._internal.latestValue = 0;
    },
    getInspectInfo() {
      const type = args.type.name === "color" ? "color" : "text";
      return [{ type, value: this._internal.currentValue }];
    },
    inputs: {
      value: {
        type: args.type,
        displayName: "Value",
        default: args.startValue,
        set(value: any) {
          if (this.isInputConnected("saveValue") === false) {
            // @ts-expect-error
            this.setValueTo(value);
          } else {
            this._internal.latestValue = value;
          }
        },
      },
      saveValue: {
        displayName: "Set",
        valueChangedToTrue() {
          this.scheduleAfterInputsHaveUpdated(function () {
            // @ts-expect-error
            this.setValueTo(this._internal.latestValue);
            this.sendSignalOnOutput("stored");
          });
        },
      },
    },
    outputs: {
      savedValue: {
        type: args.type.name,
        displayName: "Value",
        getter() {
          return this._internal.currentValue;
        },
      },
      changed: {
        type: "signal",
        displayName: "Changed",
      },
      stored: {
        type: "signal",
        displayName: "Stored",
      },
    },
    prototypeExtensions: {
      setValueTo(value: any) {
        value = args.cast(value);
        const changed = this._internal.currentValue !== value;
        this._internal.currentValue = value;

        if (changed) {
          this.flagOutputDirty("savedValue");
          this.sendSignalOnOutput("changed");
          args.onChanged && args.onChanged.call(this);
        }
      },
    },
  };
}
