import { RadioButtonGroup } from "../../components/controls/RadioButtonGroup";
import { flexDirectionValues } from "../../constants/flex";
import { guid } from "@fluxscape/runtime";
import NodeSharedPortDefinitions from "../../node-shared-port-definitions";
import { createNodeFromReactComponent } from "../../react-component-node";

const RadioButtonGroupNode = {
  name: "Radio Button Group",
  displayName: "Radio Button Group",
  docs: "https://docs.noodl.net/nodes/ui-controls/radio-button-group",
  allowChildren: true,
  noodlNodeAsProp: true,
  useVariants: false,
  connectionPanel: {
    groupPriority: [
      "General",
      "Style",
      "Actions",
      "Events",
      "Mounted",
      "States",
    ],
  },
  initialize() {
    // @ts-expect-error
    this.props.name = "radio-" + guid();

    // @ts-expect-error
    this.props.valueChanged = (value) => {
      // @ts-expect-error
      const changed = this._internal.value !== value;
      // @ts-expect-error
      this._internal.value = value;
      // @ts-expect-error
      this.props.value = value;
      if (changed) {
        // @ts-expect-error
        this.forceUpdate();
        // @ts-expect-error
        this.flagOutputDirty("value");
        // @ts-expect-error
        this.sendSignalOnOutput("onChange");
      }
    };
  },
  getReactComponent() {
    return RadioButtonGroup;
  },
  defaultCss: {
    display: "flex",
    position: "relative",
    flexDirection: "column",
  },
  inputs: {
    flexDirection: {
      //don't rename for backwards compat
      index: 11,
      displayName: "Layout",
      group: "Layout",
      type: {
        name: "enum",
        enums: [
          { label: "Vertical", value: "column" },
          { label: "Horizontal", value: "row" },
        ],
      },
      default: "column",
      set(value) {
        // @ts-expect-error
        this.props.layout = value;

        if (value !== "none") {
          // @ts-expect-error
          this.setStyle({ flexDirection: value });
        } else {
          // @ts-expect-error
          this.removeStyle(["flexDirection"]);
        }

        // @ts-expect-error
        if (this.context.editorConnection) {
          // Send warning if the value is wrong
          if (value !== "none" && !flexDirectionValues.includes(value)) {
            // @ts-expect-error
            this.context.editorConnection.sendWarning(
              // @ts-expect-error
              this.nodeScope.componentOwner.name,
              // @ts-expect-error
              this.id,
              "layout-warning",
              {
                message:
                  "Invalid Layout value has to be a valid flex-direction value.",
              }
            );
          } else {
            // @ts-expect-error
            this.context.editorConnection.clearWarning(
              // @ts-expect-error
              this.nodeScope.componentOwner.name,
              // @ts-expect-error
              this.id,
              "layout-warning"
            );
          }
        }

        // @ts-expect-error
        this.forceUpdate();
      },
    },
    value: {
      index: 20,
      type: "string",
      displayName: "Value",
      group: "General",
      set: function (value) {
        if (typeof value !== "string" && value.toString !== undefined)
          value = value.toString();
        if (typeof value !== "string") return;

        // @ts-expect-error
        const changed = value !== this._internal.value;
        // @ts-expect-error
        this.props.value = this._internal.value = value;

        if (changed) {
          // @ts-expect-error
          this.forceUpdate();
          // @ts-expect-error
          this.flagOutputDirty("value");
        }
      },
    },
  },
  outputs: {
    value: {
      type: "string",
      displayName: "Value",
      group: "States",
      getter: function () {
        // @ts-expect-error
        return this._internal.value;
      },
    },
    onChange: {
      type: "signal",
      displayName: "Changed",
      group: "Events",
    },
  },
  inputProps: {},
  outputProps: {},
};

NodeSharedPortDefinitions.addDimensions(RadioButtonGroupNode, {
  defaultSizeMode: "contentSize",
  contentLabel: "Content",
});
NodeSharedPortDefinitions.addAlignInputs(RadioButtonGroupNode);
NodeSharedPortDefinitions.addTransformInputs(RadioButtonGroupNode);
NodeSharedPortDefinitions.addMarginInputs(RadioButtonGroupNode);
NodeSharedPortDefinitions.addPaddingInputs(RadioButtonGroupNode);
NodeSharedPortDefinitions.addSharedVisualInputs(RadioButtonGroupNode);

export default createNodeFromReactComponent(RadioButtonGroupNode);
