import { NodeContext } from "./nodecontext";
import { EditorConnection, Platform } from "./editorconnection";
import { generateNodeLibrary } from "./nodelibraryexport";
import { Module, generateProjectSettings } from "./projectsettings";
import { GraphModel } from "./models/graphmodel";
import { Node } from "./node";
import { handleEvent } from "./editormodeleventshandler";
import { Services } from "./services";
import { createSetter } from "./edgetriggeredinput";
import { EventEmitter } from "eventemitter3";
import { asyncPool } from "./async-pool";
import { defineNode, NodeDefinition } from "./nodedefinition";
import { registerNodes } from "./runtime.nodes";

interface RuntimeArgs {
  type?: string;
  platform: Platform;
  runDeployed?: boolean;
  dontCreateRootComponent?: boolean;
  componentFilter?: (component: any) => boolean;
  editorConnection?: any;
}

/**
 * Defines a node which contains a node definition and a setup function.
 */
export type NodeModule<TInternal extends Record<string, any> = {}> = {
  node: NodeDefinition<TInternal>;
  setup?: (context: NodeContext, graphModel: GraphModel) => void;
};

export default class NoodlRuntime {
  static instance: NoodlRuntime;
  static Services = Services;
  static Node = Node;
  static NodeDefinition = { defineNode };
  static EdgeTriggeredInput = createSetter;

  private type: string;
  private noodlModules: Module[];
  eventEmitter: EventEmitter;
  private updateScheduled: boolean;
  rootComponent: any;
  private _currentLoadedData: any;
  private isWaitingForExport: boolean;
  graphModel: GraphModel;
  private errorHandlers: ((message: string) => void)[];
  private frameNumber: number;
  private dontCreateRootComponent: boolean;
  private componentFilter?: (component: any) => boolean;
  private runningInEditor: boolean;
  private platform: Platform;
  editorConnection: EditorConnection;
  context: NodeContext;
  private projectSettings?: any;
  private lastSentNodeLibrary?: string;
  private _disableLoad?: boolean;

  constructor(args: RuntimeArgs) {
    args.platform = args.platform || {};
    NoodlRuntime.instance = this;

    this.type = args.type || "browser";
    this.noodlModules = [];
    this.eventEmitter = new EventEmitter();
    this.updateScheduled = false;
    this.rootComponent = null;
    this._currentLoadedData = null;
    this.isWaitingForExport = true;
    this.graphModel = new GraphModel();
    this.errorHandlers = [];
    this.frameNumber = 0;
    this.dontCreateRootComponent = !!args.dontCreateRootComponent;
    this.componentFilter = args.componentFilter;

    this.runningInEditor = args.runDeployed ? false : true;

    this.platform = {
      requestUpdate: args.platform.requestUpdate,
      getCurrentTime: args.platform.getCurrentTime,
      webSocketOptions: args.platform.webSocketOptions,
      objectToString: args.platform.objectToString,
    };

    if (!args.platform.requestUpdate) {
      throw new Error("platform.requestUpdate must be set");
    }

    if (!args.platform.getCurrentTime) {
      throw new Error("platform.getCurrentTime must be set");
    }

    this.editorConnection = new EditorConnection({
      platform: args.platform,
      runtimeType: this.type,
    });

    this.context = new NodeContext({
      runningInEditor: args.runDeployed ? false : true,
      editorConnection: this.editorConnection,
      platform: this.platform,
      graphModel: this.graphModel,
    });

    this.context.eventEmitter.on(
      "scheduleUpdate",
      this.scheduleUpdate.bind(this)
    );

    if (!args.runDeployed) {
      this._setupEditorCommunication(args);
    }

    this.registerGraphModelListeners();

    registerNodes(this);
  }

  async prefetchBundles(
    bundleNames: string[],
    numParallelFetches: number
  ): Promise<void> {
    await asyncPool(numParallelFetches, bundleNames, async (name) => {
      await this.context.fetchComponentBundle(name);
    });
  }

  private _setupEditorCommunication(args: RuntimeArgs): void {
    function objectEquals(x: any, y: any): boolean {
      if (x === null || x === undefined || y === null || y === undefined) {
        return x === y;
      }
      if (x === y) {
        return true;
      }
      if (Array.isArray(x) && x.length !== y.length) {
        return false;
      }

      if (!(x instanceof Object)) {
        return false;
      }
      if (!(y instanceof Object)) {
        return false;
      }

      const p = Object.keys(x);
      return (
        Object.keys(y).every((i) => p.indexOf(i) !== -1) &&
        p.every((i) => objectEquals(x[i], y[i]))
      );
    }

    this.editorConnection.on("exportDataFull", async (exportData: any) => {
      if (this.graphModel.isEmpty() === false) {
        this.reload();
        return;
      }

      this.isWaitingForExport = false;
      if (objectEquals(this._currentLoadedData, exportData) === false) {
        if (this.componentFilter) {
          exportData.components = exportData.components.filter((c: any) =>
            this.componentFilter!(c)
          );
        }

        await this.setData(exportData);

        if (exportData.componentIndex) {
          const allBundles = Object.keys(exportData.componentIndex);
          await this.prefetchBundles(allBundles, 2);
        }

        this.graphModel.emit("editorImportComplete");
      }
    });

    this.editorConnection.on("reload", this.reload.bind(this));
    this.editorConnection.on(
      "modelUpdate",
      this.onModelUpdateReceived.bind(this)
    );
    this.editorConnection.on(
      "metadataUpdate",
      this.onMetaDataUpdateReceived.bind(this)
    );

    this.editorConnection.on("connected", () => {
      this.sendNodeLibrary();
    });
  }

  setDebugInspectorsEnabled(enabled: boolean): void {
    this.context.setDebugInspectorsEnabled(enabled);
  }

  registerModule(module: Module): void {
    if (module.nodes) {
      for (let nodeDefinition of module.nodes) {
        if (!nodeDefinition.node) nodeDefinition = { node: nodeDefinition };
        nodeDefinition.node.module = module.name || "Unknown Module";
        this.registerNode(nodeDefinition);
      }
    }

    this.noodlModules.push(module);
  }

  private registerGraphModelListeners(): void {
    this.graphModel.on(
      "componentAdded",
      (component: any) => {
        this.context.registerComponentModel(component);
      },
      this
    );

    this.graphModel.on(
      "componentRemoved",
      (component: any) => {
        this.context.deregisterComponentModel(component);
      },
      this
    );
  }

  reload(): void {
    location.reload();
  }

  registerNode(nodeDefinition: NodeModule): void {
    if (nodeDefinition.node) {
      const definedNode = defineNode(nodeDefinition.node);
      this.context.nodeRegister.register(definedNode);

      definedNode.setupNumberedInputDynamicPorts &&
        definedNode.setupNumberedInputDynamicPorts(
          this.context,
          this.graphModel
        );
    } else {
      // TODO: Should this just be nodeDefinition?
      this.context.nodeRegister.register(nodeDefinition.node);
    }

    nodeDefinition.setup && nodeDefinition.setup(this.context, this.graphModel);
  }

  private async _setRootComponent(
    rootComponentName: string | null
  ): Promise<void> {
    if (this.rootComponent && this.rootComponent.name === rootComponentName)
      return;

    if (this.rootComponent) {
      this.rootComponent.model &&
        this.rootComponent.model.removeListenersWithRef(this);
      this.rootComponent = undefined;
    }

    if (rootComponentName) {
      this.rootComponent = await this.context.createComponentInstanceNode(
        rootComponentName,
        "rootComponent",
        null
      );

      this.rootComponent.componentModel.on(
        "rootAdded",
        () => this.eventEmitter.emit("rootComponentUpdated"),
        this
      );
      this.rootComponent.componentModel.on(
        "rootRemoved",
        () => this.eventEmitter.emit("rootComponentUpdated"),
        this
      );

      this.context.setRootComponent(this.rootComponent);
    }

    this.eventEmitter.emit("rootComponentUpdated");
  }

  async setData(graphData: any): Promise<void> {
    if (this._disableLoad) return;

    this._currentLoadedData = graphData;
    await this.graphModel.importEditorData(graphData);

    for (const module of this.noodlModules) {
      typeof module.setup === "function" && module.setup();
    }

    if (this.dontCreateRootComponent !== true) {
      await this._setRootComponent(this.graphModel.rootComponent || null);

      this.graphModel.on("rootComponentNameUpdated", (name: string) => {
        this._setRootComponent(name);
      });

      this.graphModel.on("componentRemoved", (componentModel: any) => {
        if (
          this.rootComponent &&
          this.rootComponent.name === componentModel.name
        ) {
          this._setRootComponent(null);
        }
      });

      this.graphModel.on("componentAdded", (componentModel: any) => {
        setTimeout(() => {
          if (
            !this.rootComponent &&
            this.graphModel.rootComponent === componentModel.name
          ) {
            this._setRootComponent(componentModel.name);
          }
        }, 1);
      });
    }

    this.scheduleUpdate();
  }

  scheduleUpdate(): void {
    if (this.updateScheduled) {
      return;
    }

    this.updateScheduled = true;
    this.platform.requestUpdate?.(this._doUpdate.bind(this));
  }

  private _doUpdate(): void {
    this.updateScheduled = false;

    this.context.currentFrameTime = this.platform.getCurrentTime();

    this.context.eventEmitter.emit("frameStart");

    this.context.update();

    this.context.eventEmitter.emit("frameEnd");

    this.frameNumber++;
  }

  setProjectSettings(settings: any): void {
    this.projectSettings = settings;
  }

  getNodeLibrary(): string {
    const projectSettings = generateProjectSettings(
      this.graphModel.getSettings(),
      this.noodlModules
    );

    if (this.projectSettings) {
      this.projectSettings.ports &&
        (projectSettings.ports = projectSettings.ports.concat(
          this.projectSettings.ports
        ));
      this.projectSettings.dynamicports &&
        (projectSettings.dynamicports = projectSettings.ports.concat(
          this.projectSettings.dynamicports
        ));
    }

    const nodeLibrary = generateNodeLibrary(this.context.nodeRegister);
    nodeLibrary.projectsettings = projectSettings;
    return JSON.stringify(nodeLibrary, null, 3);
  }

  sendNodeLibrary(): void {
    const nodeLibrary = this.getNodeLibrary();
    if (this.lastSentNodeLibrary !== nodeLibrary) {
      this.lastSentNodeLibrary = nodeLibrary;
      this.editorConnection.sendNodeLibrary(nodeLibrary);
    }
  }

  connectToEditor(address: string): void {
    this.editorConnection.connect(address);
  }

  onMetaDataUpdateReceived(event: any): void {
    if (!this.graphModel.isEmpty()) {
      handleEvent(this.context, this.graphModel, event);
    }
  }

  async onModelUpdateReceived(event: any): Promise<void> {
    if (this.isWaitingForExport) {
      return;
    }

    if (event.type === "projectInstanceChanged") {
      this.reload();
    } else if (this.graphModel.isEmpty() === false) {
      await handleEvent(this.context, this.graphModel, event);
    }
  }

  addErrorHandler(callback: (message: string) => void): void {
    this.errorHandlers.push(callback);
  }

  reportError(message: string): void {
    this.errorHandlers.forEach((eh) => {
      eh(message);
    });
  }

  getProjectSettings(): any {
    return this.graphModel.getSettings();
  }

  getMetaData(key: string): any {
    return this.graphModel.getMetaData(key);
  }
}
