import { CloudFile } from "../../../api/cloudfile";
import { NodeDefinition } from "../../../nodedefinition";

interface Internal {
  cloudFile?: CloudFile;
}

export const node: NodeDefinition<Internal> = {
  name: "Cloud File",
  docs: "https://docs.noodl.net/nodes/data/cloud-data/cloud-file",
  category: "Cloud Services",
  color: "data",
  getInspectInfo(): string | undefined {
    return this._internal.cloudFile?.getUrl();
  },
  outputs: {
    url: {
      type: "string",
      displayName: "URL",
      group: "General",
      getter(): string | undefined {
        return this._internal.cloudFile?.getUrl();
      },
    },
    name: {
      type: "string",
      displayName: "Name",
      group: "General",
      getter(): string | undefined {
        if (!this._internal.cloudFile) return undefined;

        //parse prefixes the file with a guid_
        //remove it so the name is the same as the original file name
        const n = this._internal.cloudFile.getName().split("_");
        return n.length === 1 ? n[0] : n.slice(1).join("_");
      },
    },
  },
  inputs: {
    file: {
      type: "cloudfile",
      displayName: "Cloud File",
      group: "General",
      set(value: unknown): void {
        if (!(value instanceof CloudFile)) {
          return;
        }
        this._internal.cloudFile = value;
        this.flagOutputDirty("name");
        this.flagOutputDirty("url");
      },
    },
  },
};
