import { Collection, Model, NodeDefinition } from "@fluxscape/runtime";

interface Internal {
  modifyId: string;
  collection: Collection<Model>;
}

export const node: NodeDefinition<Internal> = {
  name: "CollectionInsert",
  docs: "https://docs.noodl.net/nodes/data/array/insert-into-array",
  displayNodeName: "Insert Object Into Array",
  shortDesc:
    "A collection of models, mainly used together with a For Each Node.",
  category: "Data",
  usePortAsLabel: "collectionId",
  color: "data",
  inputs: {
    collectionId: {
      type: {
        name: "string",
        identifierOf: "CollectionName",
        identifierDisplayName: "Array Ids",
      },
      displayName: "Array Id",
      group: "General",
      set(value: string | Collection<Model>) {
        if (value instanceof Collection) value = value.getId();
        // Can be passed as collection as well
        // @ts-expect-error
        this.setCollectionID(value);
      },
    },
    modifyId: {
      type: { name: "string", allowConnectionsOnly: true },
      displayName: "Object Id",
      group: "Modify",
      set(value: string) {
        this._internal.modifyId = value;
      },
    },
    add: {
      displayName: "Do",
      group: "Actions",
      valueChangedToTrue: function () {
        const internal = this._internal;

        this.scheduleAfterInputsHaveUpdated(() => {
          if (this.context.editorConnection) {
            this.context.editorConnection.clearWarning(
              // @ts-expect-error
              this.nodeScope.componentOwner.name,
              this.id,
              "insert-warning"
            );
          }

          if (internal.modifyId === undefined) {
            if (this.context.editorConnection) {
              this.context.editorConnection.sendWarning(
                // @ts-expect-error
                this.nodeScope.componentOwner.name,
                this.id,
                "insert-warning",
                {
                  showGlobally: true,
                  message: "No Object Id specified",
                }
              );
            }
            return;
          }

          if (internal.collection === undefined) {
            if (this.context.editorConnection) {
              this.context.editorConnection.sendWarning(
                // @ts-expect-error
                this.nodeScope.componentOwner.name,
                this.id,
                "insert-warning",
                {
                  showGlobally: true,
                  message: "No Array Id specified",
                }
              );
            }
            return;
          }

          const model = Model.get(internal.modifyId);
          internal.collection.add(model);
          this.sendSignalOnOutput("modified");
        });
      },
    },
  },
  outputs: {
    modified: {
      group: "Events",
      type: "signal",
      displayName: "Done",
    },
  },
  methods: {
    setCollectionID(id: string) {
      // @ts-expect-error
      this.setCollection(Collection.get(id));
    },
    setCollection(collection: Collection<Model>) {
      this._internal.collection = collection;
    },
  },
};
