import { NodeDefinition } from "../../../nodedefinition";
import { NodeContext } from "../../../nodecontext";
import { GraphModel } from "../../../models/graphmodel";
import { Port } from "../../../models/componentmodel";
import { Model } from "../../../model";

interface Internal {
  inputValues: Record<string, any>;
  dirtyValues: Record<string, any>;
  model?: Model;
  modelId?: string;
  onModelChangedCallback: (args: { name: string }) => void;
}

interface Parameters {
  properties?: string;
}

export const node: NodeDefinition<Internal> = {
  name: "Model2",
  docs: "https://docs.noodl.net/nodes/data/object/object-node",
  displayNodeName: "Object",
  shortDesc:
    "Stores any amount of properties and can be used standalone or together with Collections and For Each nodes.",
  category: "Data",
  usePortAsLabel: "modelId",
  color: "data",
  dynamicports: [
    {
      name: "conditionalports/extended",
      condition: "idSource = explicit OR idSource NOT SET",
      inputs: ["modelId"],
    },
  ],
  initialize() {
    const internal = this._internal;
    internal.inputValues = {};
    internal.dirtyValues = {};

    internal.onModelChangedCallback = (args) => {
      if (this.isInputConnected("fetch") === true) return;

      if (this.hasOutput("prop-" + args.name))
        this.flagOutputDirty("prop-" + args.name);

      if (this.hasOutput("changed-" + args.name))
        this.sendSignalOnOutput("changed-" + args.name);

      this.sendSignalOnOutput("changed");
    };
  },
  getInspectInfo() {
    const model = this._internal.model;
    if (!model) return "[No Object]";

    return [
      { type: "text", value: "Id: " + model.getId() },
      { type: "value", value: model.data },
    ];
  },
  outputs: {
    id: {
      type: "string",
      displayName: "Id",
      group: "General",
      getter() {
        return this._internal.model
          ? this._internal.model.getId()
          : this._internal.modelId;
      },
    },
    changed: {
      type: "signal",
      displayName: "Changed",
      group: "Events",
    },
    fetched: {
      type: "signal",
      displayName: "Fetched",
      group: "Events",
    },
  },
  inputs: {
    idSource: {
      type: {
        name: "enum",
        enums: [
          { label: "Specify explicitly", value: "explicit" },
          { label: "From repeater", value: "foreach" },
        ],
        allowEditOnly: true,
      },
      default: "explicit",
      displayName: "Get Id from",
      group: "General",
      set(value: string) {
        if (value === "foreach") {
          this.scheduleAfterInputsHaveUpdated(() => {
            // Find closest nodescope that have a _forEachModel
            let component = this.nodeScope!.componentOwner;
            while (
              component !== undefined &&
              (component as any)._forEachModel === undefined &&
              component.parentNodeScope
            ) {
              component = component.parentNodeScope.componentOwner;
            }
            // @ts-expect-error
            this.setModel(
              component !== undefined
                ? (component as any)._forEachModel
                : undefined
            );
          });
        }
      },
    },
    modelId: {
      type: {
        name: "string",
        identifierOf: "ModelName",
        identifierDisplayName: "Object Ids",
      },
      displayName: "Id",
      group: "General",
      set(value: string | Model | Record<string, any>) {
        if (value instanceof Model) value = value.getId();
        // Can be passed as model as well
        else if (typeof value === "object") value = Model.create(value).getId(); // If this is an js object, dereference it

        this._internal.modelId = value; // Wait to fetch data
        // @ts-expect-error
        if (this.isInputConnected("fetch") === false) this.setModelID(value);
        else {
          this.flagOutputDirty("id");
        }
      },
    },
    properties: {
      type: { name: "stringlist", allowEditOnly: true },
      displayName: "Properties",
      group: "Properties",
      set(value: string) {},
    },
    fetch: {
      displayName: "Fetch",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleSetModel();
      },
    },
  },
  prototypeExtensions: {
    scheduleStore() {
      if ((this as any).hasScheduledStore) return;
      (this as any).hasScheduledStore = true;

      const internal = this._internal;
      this.scheduleAfterInputsHaveUpdated(() => {
        (this as any).hasScheduledStore = false;
        if (!internal.model) return;

        for (const i in internal.dirtyValues) {
          internal.model.set(i, internal.inputValues[i], { resolve: true });
        }
        internal.dirtyValues = {}; // Reset dirty values
      });
    },
    scheduleSetModel() {
      if ((this as any).hasScheduledSetModel) return;
      (this as any).hasScheduledSetModel = true;

      this.scheduleAfterInputsHaveUpdated(() => {
        (this as any).hasScheduledSetModel = false;
        // @ts-expect-error
        this.setModelID(this._internal.modelId);
      });
    },
    setModelID(id: string) {
      const model = (this.nodeScope!.modelScope || Model).get(id);
      // @ts-expect-error
      this.setModel(model);
      this.sendSignalOnOutput("fetched");
    },
    setModel(model?: Model) {
      if (this._internal.model)
        // Remove old listener if existing
        this._internal.model.off(
          "change",
          this._internal.onModelChangedCallback
        );

      this._internal.model = model;
      this.flagOutputDirty("id");

      // In set idSource, we are calling setModel with undefined
      if (model) {
        model.on("change", this._internal.onModelChangedCallback);

        // We have a new model, mark all outputs as dirty
        for (const key in model.data) {
          if (this.hasOutput("prop-" + key))
            this.flagOutputDirty("prop-" + key);
        }
      }
    },
    _onNodeDeleted() {
      if (this._internal.model)
        this._internal.model.off(
          "change",
          this._internal.onModelChangedCallback
        );
    },
    registerOutputIfNeeded(name: string) {
      if (this.hasOutput(name)) {
        return;
      }

      if (name.startsWith("prop-"))
        this.registerOutput(name, {
          getter: userOutputGetter.bind(this, name.substring("prop-".length)),
        });
    },
    registerInputIfNeeded(name: string) {
      if (this.hasInput(name)) {
        return;
      }

      if (name.startsWith("prop-"))
        this.registerInput(name, {
          set: userInputSetter.bind(this, name.substring("prop-".length)),
        });
    },
  },
};

function userOutputGetter(this: any, name: string) {
  return this._internal.model
    ? this._internal.model.get(name, { resolve: true })
    : undefined;
}

function userInputSetter(this: any, name: string, value: any) {
  this._internal.inputValues[name] = value;

  // Store on change if no connection to store or new
  const model = this._internal.model;
  const valueChanged = model ? model.get(name) !== value : true;
  if (valueChanged) {
    this._internal.dirtyValues[name] = true;
    this.scheduleStore();
  }
}

function updatePorts(
  nodeId: string,
  parameters: Parameters,
  editorConnection: any
) {
  const ports: Port[] = [];

  // Add value outputs
  const properties = parameters.properties;
  if (properties) {
    const propArray = properties ? properties.split(",") : undefined;
    if (propArray) {
      for (const p of propArray) {
        ports.push({
          type: {
            name: "*",
            allowConnectionsOnly: true,
          },
          plug: "input/output",
          group: "Properties",
          name: "prop-" + p,
          displayName: p,
        });

        ports.push({
          type: "signal",
          plug: "output",
          group: "Changed Events",
          displayName: p + " Changed",
          name: "changed-" + p,
        });
      }
    }
  }

  editorConnection.sendDynamicPorts(nodeId, ports, {
    detectRenamed: {
      plug: "input/output",
    },
  });
}

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  graphModel.on("nodeAdded.Model2", function (node) {
    updatePorts(node.id, node.parameters, context.editorConnection);

    node.on("parameterUpdated", function () {
      updatePorts(node.id, node.parameters, context.editorConnection);
    });
  });
}
