import React, { useEffect } from "react";
import ASyncQueue from "../../async-queue";
import { createNodeFromReactComponent } from "../../react-component-node";
import NavigationHandler from "./navigation-handler";
import NoodlRuntime, { guid } from "@fluxscape/runtime";
import { Transitions } from "./transitions";
import { ReactNodeDefinition } from "../../ReactNodeDefinition";

interface PageStackProps {
  didMount: () => void;
  willUnmount: () => void;
  style: React.CSSProperties;
  children: React.ReactNode;
}

const PageStackReactComponent: React.FC<PageStackProps> = (props) => {
  const { didMount, willUnmount, style, children } = props;

  useEffect(() => {
    didMount();
    return () => {
      willUnmount();
    };
  }, [didMount, willUnmount]);

  return <div style={style}>{children}</div>;
};

interface Internal {
  stack: any[];
  topPageName: string;
  stackDepth: number;
  pageInfo: Record<string, any>;
  asyncQueue: ASyncQueue;
  isMounted: boolean;
  name: string;
  useRoutes: boolean;
  pages: any[];
  hasScheduledReset: boolean;
  startPageId: string;
  startPage: string;
  remainingNavigationPath: string[] | undefined;
  isTransitioning: boolean;
}

const PageStack: ReactNodeDefinition<Internal> = {
  name: "Page Stack",
  displayNodeName: "Component Stack",
  category: "Visuals",
  docs: "https://docs.noodl.net/nodes/component-stack/component-stack-node",
  useVariants: false,
  initialize() {
    this._internal.stack = [];
    this._internal.topPageName = "";
    this._internal.stackDepth = 0;
    this._internal.pageInfo = {};
    this._internal.asyncQueue = new ASyncQueue();

    // @ts-expect-error
    this.onScheduleReset = () => {
      // @ts-expect-error
      this.scheduleReset();
    };

    // @ts-expect-error
    this.props.didMount = () => {
      this._internal.isMounted = true;

      // @ts-expect-error
      if (window.history && window.history.pushState) {
        // @ts-expect-error
        window.addEventListener("popstate", this.onScheduleReset);
      } else {
        // @ts-expect-error
        window.addEventListener("hashchange", this.onScheduleReset);
      }

      // @ts-expect-error
      this._registerPageStack();
    };

    // @ts-expect-error
    this.props.willUnmount = () => {
      this._internal.isMounted = false;
      // @ts-expect-error
      window.removeEventListener("popstate", this.onScheduleReset);
      // @ts-expect-error
      window.removeEventListener("hashchange", this.onScheduleReset);
      // @ts-expect-error
      this._deregisterPageStack();
    };
  },
  getInspectInfo() {
    if (this._internal.stack.length === 0) {
      return "No active page";
    }

    const info = [{ type: "text", value: "Active Components:" }];

    return info.concat(
      this._internal.stack.map((p) => {
        // @ts-expect-error
        const pageInfo = this._findPage(p.pageId);
        return {
          type: "text",
          value: "- " + pageInfo.label,
        };
      })
    );
  },
  defaultCss: {
    width: "100%",
    flex: "1 1 100%",
    position: "relative",
    display: "flex",
    flexDirection: "column",
  },
  // @ts-expect-error
  getReactComponent() {
    return PageStackReactComponent;
  },
  inputs: {
    name: {
      type: { name: "string", identifierOf: "PackStack" },
      displayName: "Name",
      group: "General",
      default: "Main",
      set(value) {
        // @ts-expect-error
        this._deregisterPageStack();
        this._internal.name = value;

        if (this._internal.isMounted) {
          // @ts-expect-error
          this._registerPageStack();
        }
      },
    },
    useRoutes: {
      type: "boolean",
      displayName: "Use Routes",
      group: "General",
      default: false,
      set(value) {
        this._internal.useRoutes = !!value;
      },
    },
    clip: {
      displayName: "Clip Content",
      type: "boolean",
      group: "Layout",
      default: true,
      set(value) {
        if (value) {
          // @ts-expect-error
          this.setStyle({ overflow: "hidden" });
        } else {
          // @ts-expect-error
          this.removeStyle(["overflow"]);
        }
      },
    },
    pages: {
      type: "proplist",
      displayName: "Components",
      group: "Components",
      set(value) {
        this._internal.pages = value;
        if (this._internal.isMounted) {
          // @ts-expect-error
          this.scheduleReset();
        }
      },
    },
    reset: {
      type: "signal",
      displayName: "Reset",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleReset();
      },
    },
  },
  inputCss: {
    backgroundColor: {
      type: "color",
      displayName: "Background Color",
      group: "Style",
      default: "transparent",
      applyDefault: false,
    },
  },
  outputs: {
    topPageName: {
      type: "string",
      displayName: "Top Component Name",
      group: "General",
      get() {
        return this._internal.topPageName;
      },
    },
    stackDepth: {
      type: "number",
      displayName: "Stack Depth",
      group: "General",
      get() {
        return this._internal.stackDepth;
      },
    },
  },
  methods: {
    _registerPageStack() {
      NavigationHandler.instance.registerPageStack(this._internal.name, this);
    },
    _deregisterPageStack() {
      NavigationHandler.instance.deregisterPageStack(this._internal.name, this);
    },
    _findPage(pageIdOrLabel: string) {
      if (this._internal.pageInfo[pageIdOrLabel]) {
        const pageInfo = this._internal.pageInfo[pageIdOrLabel];
        const pageRef = this._internal.pages.find(
          (x) => x.id === pageIdOrLabel
        );
        return {
          component: String(pageInfo.component),
          label: String(pageRef.label),
          id: String(pageIdOrLabel),
        };
      }

      const pageRef = this._internal.pages.find(
        (x) => x.label === pageIdOrLabel
      );
      if (pageRef) {
        const pageInfo = this._internal.pageInfo[pageRef.id];
        return {
          component: String(pageInfo.component),
          label: String(pageRef.label),
          id: String(pageRef.id),
        };
      }

      return undefined;
    },
    setPageOutputs(outputs: Record<string, any>) {
      for (const prop in outputs) {
        this._internal[prop] = outputs[prop];
        this.flagOutputDirty(prop);
      }
    },
    scheduleReset() {
      const internal = this._internal;
      if (!internal.hasScheduledReset) {
        internal.hasScheduledReset = true;
        this.scheduleAfterInputsHaveUpdated(() => {
          internal.hasScheduledReset = false;
          // @ts-expect-error
          this.reset();
        });
      }
    },
    createPageContainer() {
      const group = this.nodeScope!.createPrimitiveNode("Group");
      group.setStyle({ flex: "1 0 100%" });
      return group;
    },
    reset() {
      // @ts-expect-error
      this._internal.asyncQueue.enqueue(this.resetAsync.bind(this));
    },
    async resetAsync() {
      // @ts-expect-error
      const children = this.getChildren();
      for (const c of children) {
        // @ts-expect-error
        this.removeChild(c);
        // @ts-expect-error
        this.nodeScope.deleteNode(c);
      }

      if (
        this._internal.pages === undefined ||
        this._internal.pages.length === 0
      )
        return;

      let startPageId;
      let params = {};

      // @ts-expect-error
      const pageFromUrl = this.matchPageFromUrl();
      if (pageFromUrl !== undefined) {
        startPageId = pageFromUrl.pageId;
        params = { ...pageFromUrl.query, ...pageFromUrl.params };
      } else {
        startPageId = this._internal.startPageId;
        if (startPageId === undefined) startPageId = this._internal.pages[0].id;
      }

      // @ts-expect-error
      const pageInfo = this._findPage(startPageId);
      if (pageInfo === undefined || pageInfo.component === undefined) {
        return;
      }

      // @ts-expect-error
      const content = await this.nodeScope.createNode(
        pageInfo.component,
        guid()
      );

      for (const key in params) {
        content.setInputValue(key, params[key]);
      }

      // @ts-expect-error
      const group = this.createPageContainer();
      group.addChild(content);

      // @ts-expect-error
      this.addChild(group);
      this._internal.stack = [
        {
          from: null,
          page: group,
          pageId: startPageId,
          pageInfo: pageInfo,
          params: params,
          componentName: this._internal.startPage,
        },
      ];

      // @ts-expect-error
      this.setPageOutputs({
        topPageName: pageInfo.label,
        stackDepth: this._internal.stack.length,
      });
    },
    getRelativeURL() {
      const top = this._internal.stack[this._internal.stack.length - 1];
      if (top === undefined) return;

      let urlPath = top.pageInfo.path;
      if (urlPath === undefined) {
        const pageItem = this._internal.pages.find((p) => p.id == top.pageId);
        if (pageItem === undefined) return;

        urlPath = pageItem.label.replace(/\s+/g, "-").toLowerCase();
      }

      const paramsInPath = urlPath.match(/{([^}]+)}/g);
      const params = { ...top.params };
      if (paramsInPath) {
        for (const param of paramsInPath) {
          const key = param.replace(/[{}]/g, "");
          if (top.params[key] !== undefined) {
            urlPath = urlPath.replace(param, encodeURIComponent(params[key]));
            delete params[key];
          }
        }
      }

      const query = Object.keys(params).map((key) => ({
        name: key,
        value: params[key],
      }));

      if (urlPath.startsWith("/")) urlPath = urlPath.substring(1);

      return {
        path: urlPath,
        query: query,
      };
    },
    getNavigationAbsoluteURL() {
      // @ts-expect-error
      let parent = this.parent;

      while (
        parent !== undefined &&
        typeof parent.getNavigationAbsoluteURL !== "function"
      ) {
        parent = parent.getVisualParentNode();
      }

      const parentUrl =
        parent === undefined
          ? { path: "", query: [] }
          : parent.getNavigationAbsoluteURL();
      // @ts-expect-error
      const thisUrl = this.getRelativeURL();
      if (thisUrl === undefined) return parentUrl;

      return {
        path:
          parentUrl.path +
          (parentUrl.path.endsWith("/") ? "" : "/") +
          thisUrl.path,
        query: parentUrl.query.concat(thisUrl.query),
      };
    },
    _getLocationPath() {
      const navigationPathType =
        NoodlRuntime.instance.getProjectSettings()["navigationPathType"];
      if (navigationPathType === undefined || navigationPathType === "hash") {
        let hash = location.hash;
        if (hash) {
          if (hash[0] === "#") hash = hash.substring(1);
          if (hash[0] === "/") hash = hash.substring(1);
        }
        return hash;
      } else {
        let path = location.pathname;
        if (path) {
          if (path[0] === "/") path = path.substring(1);
        }
        return path;
      }
    },
    _getSearchParams() {
      const pl = /\+/g;
      const search = /([^&=]+)=?([^&]*)/g;
      const decode = (s: string) => decodeURIComponent(s.replace(pl, " "));
      const query = window.location.search.substring(1);

      let match: RegExpExecArray | null;
      const urlParams: Record<string, string> = {};
      while ((match = search.exec(query))) {
        urlParams[decode(match[1])] = decode(match[2]);
      }

      return urlParams;
    },
    getNavigationRemainingPath() {
      return this._internal.remainingNavigationPath;
    },
    matchPageFromUrl(url?: string) {
      if (!this._internal.useRoutes) return;
      if (
        this._internal.pages === undefined ||
        this._internal.pages.length === 0
      )
        return;

      // @ts-expect-error
      let parent = this.parent;
      while (
        parent !== undefined &&
        typeof parent.getNavigationRemainingPath !== "function"
      ) {
        parent = parent.getVisualParentNode();
      }

      const pathParts =
        parent === undefined
          ? // @ts-expect-error
            this._getLocationPath().split("/").filter(Boolean)
          : parent.getNavigationRemainingPath();
      if (pathParts === undefined) return;

      // @ts-expect-error
      const urlQuery = this._getSearchParams();

      const _matchPathParts = (path: string[], pattern: string[]) => {
        const params: Record<string, string> = {};
        for (let i = 0; i < pattern.length; i++) {
          if (path[i] === undefined) return;

          const _p = pattern[i];
          if (_p[0] === "{" && _p[_p.length - 1] === "}") {
            params[_p.substring(1, _p.length - 1)] = decodeURIComponent(
              path[i]
            );
          } else if (_p !== path[i]) return;
        }
        return {
          params: params,
          remainingPathParts: path.splice(pattern.length),
        };
      };

      for (const page of this._internal.pages) {
        const pageInfo = this._internal.pageInfo[page.id];
        if (pageInfo === undefined) continue;

        let pagePattern = pageInfo.path;
        if (pagePattern === undefined) {
          pagePattern = page.label.replace(/\s+/g, "-").toLowerCase();
        }
        if (pagePattern[0] === "/") pagePattern = pagePattern.substring(1);

        const match = _matchPathParts(pathParts, pagePattern.split("/"));
        if (match) {
          this._internal.remainingNavigationPath = match.remainingPathParts;
          return {
            pageId: page.id,
            params: match.params,
            query: urlQuery,
          };
        }
      }
    },
    _updateUrlWithTopPage() {
      if (this._internal.useRoutes && window.history !== undefined) {
        // @ts-expect-error
        const url = this.getNavigationAbsoluteURL();

        let urlPath, hashPath;
        const navigationPathType =
          NoodlRuntime.instance.getProjectSettings()["navigationPathType"];
        if (navigationPathType === undefined || navigationPathType === "hash") {
          hashPath = url.path;
        } else {
          urlPath = url.path;
        }

        const query = url.query.map((q) => `${q.name}=${q.value}`);
        const compiledUrl =
          (urlPath !== undefined ? urlPath : "") +
          (query.length >= 1 ? "?" + query.join("&") : "") +
          (hashPath !== undefined ? "#" + hashPath : "");

        this._internal.remainingNavigationPath = undefined;
        window.history.pushState({}, "", compiledUrl);
      }
    },
    replace(args: any) {
      // @ts-expect-error
      this._internal.asyncQueue.enqueue(this.replaceAsync.bind(this, args));
    },
    async replaceAsync(args: any) {
      if (
        this._internal.pages === undefined ||
        this._internal.pages.length === 0
      ) {
        return;
      }

      if (this._internal.isTransitioning) {
        return;
      }

      const pageId = args.target || this._internal.pages[0].id;

      // @ts-expect-error
      const pageInfo = this._findPage(pageId);
      if (pageInfo === undefined || pageInfo.component === undefined) {
        return;
      }

      // @ts-expect-error
      const children = this.getChildren();
      for (const c of children) {
        // @ts-expect-error
        this.removeChild(c);
        // @ts-expect-error
        this.nodeScope.deleteNode(c);
      }

      // @ts-expect-error
      const group = this.createPageContainer();
      // @ts-expect-error
      const content = await this.nodeScope.createNode(
        pageInfo.component,
        guid()
      );
      for (const key in args.params) {
        content.setInputValue(key, args.params[key]);
      }
      group.addChild(content);
      // @ts-expect-error
      this.addChild(group);

      this._internal.stack = [
        {
          from: null,
          page: group,
          pageId: pageId,
          pageInfo: pageInfo,
          params: args.params,
          componentName: args.target,
        },
      ];

      // @ts-expect-error
      this.setPageOutputs({
        topPageName: pageInfo.label,
        stackDepth: this._internal.stack.length,
      });

      // @ts-expect-error
      this._updateUrlWithTopPage();
      args.hasNavigated && args.hasNavigated();
    },
    navigate(args: any) {
      // @ts-expect-error
      this._internal.asyncQueue.enqueue(this.navigateAsync.bind(this, args));
    },
    async navigateAsync(args: any) {
      if (
        this._internal.pages === undefined ||
        this._internal.pages.length === 0
      ) {
        return;
      }

      if (this._internal.isTransitioning) {
        return;
      }

      const pageId = args.target || this._internal.pages[0].id;

      // @ts-expect-error
      const pageInfo = this._findPage(pageId);
      if (pageInfo === undefined || pageInfo.component === undefined) {
        return;
      }

      // @ts-expect-error
      const group = this.createPageContainer();
      group.setInputValue("position", "absolute");

      // @ts-expect-error
      const content = await this.nodeScope.createNode(
        pageInfo.component,
        guid()
      );
      for (const key in args.params) {
        content.setInputValue(key, args.params[key]);
      }
      group.addChild(content);

      const navigateBackNodes = content.nodeScope.getNodesWithType(
        "PageStackNavigateBack"
      );
      if (navigateBackNodes && navigateBackNodes.length > 0) {
        for (const node of navigateBackNodes) {
          // @ts-expect-error
          node._setBackCallback(this.back.bind(this));
        }
      }

      const top = this._internal.stack[this._internal.stack.length - 1];
      const newTop = {
        from: top.page,
        page: group,
        pageInfo: pageInfo,
        pageId: pageId,
        params: args.params,
        transition: new Transitions[args.transition.type || "Push"](
          top.page,
          group,
          args.transition
        ),
        backCallback: args.backCallback,
        componentName: args.target,
      };
      this._internal.stack.push(newTop);
      // @ts-expect-error
      this.setPageOutputs({
        topPageName: pageInfo.label,
        stackDepth: this._internal.stack.length,
      });
      // @ts-expect-error
      this._updateUrlWithTopPage();

      newTop.transition.forward(0);
      this._internal.isTransitioning = true;
      newTop.transition.start({
        end: () => {
          this._internal.isTransitioning = false;
          // @ts-expect-error
          this.removeChild(top.page);
          group.setInputValue("position", "relative");
        },
      });

      // @ts-expect-error
      this.addChild(group);
      args.hasNavigated && args.hasNavigated();
    },
    back(args: any) {
      if (this._internal.stack.length <= 1) return;
      if (this._internal.isTransitioning) return;

      const top = this._internal.stack[this._internal.stack.length - 1];
      top.page.setInputValue("position", "absolute");
      // @ts-expect-error
      this.addChild(top.from, 0);
      top.backCallback && top.backCallback(args.backAction, args.results);

      // @ts-expect-error
      const pageInfo = this._findPage(
        this._internal.stack[this._internal.stack.length - 2].pageId
      );
      // @ts-expect-error
      this.setPageOutputs({
        topPageName: pageInfo.label,
        stackDepth: this._internal.stack.length - 1,
      });

      this._internal.isTransitioning = true;
      top.transition.start({
        end: () => {
          this._internal.isTransitioning = false;
          top.page.setInputValue("position", "relative");
          // @ts-expect-error
          this.removeChild(top.page);
          // @ts-expect-error
          this.nodeScope.deleteNode(top.page);
          this._internal.stack.pop();
          // @ts-expect-error
          this._updateUrlWithTopPage();
        },
        back: true,
      });
    },
    setPageComponent(pageId: string, component: any) {
      const internal = this._internal;
      if (!internal.pageInfo[pageId]) internal.pageInfo[pageId] = {};
      internal.pageInfo[pageId].component = component;
    },
    setPagePath(pageId: string, path: string) {
      const internal = this._internal;
      if (!internal.pageInfo[pageId]) internal.pageInfo[pageId] = {};
      internal.pageInfo[pageId].path = path;
    },
    setStartPage(pageId: string) {
      this._internal.startPageId = pageId;
    },
    registerInputIfNeeded(name: string) {
      if (this.hasInput(name)) {
        return;
      }

      if (name.startsWith("pageComp-"))
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setPageComponent.bind(
            this,
            name.substring("pageComp-".length)
          ),
        });

      if (name.startsWith("pagePath-"))
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setPagePath.bind(this, name.substring("pagePath-".length)),
        });

      if (name === "startPage")
        return this.registerInput(name, {
          // @ts-expect-error
          set: this.setStartPage.bind(this),
        });
    },
  },
  setup(context: any, graphModel: any) {
    if (
      !context.editorConnection ||
      !context.editorConnection.isRunningLocally()
    ) {
      return;
    }

    function _managePortsForNode(node: any) {
      function _updatePorts() {
        const ports: any[] = [];

        if (
          node.parameters["pages"] !== undefined &&
          node.parameters["pages"].length > 0
        ) {
          node.parameters["pages"].forEach((p: any) => {
            ports.push({
              name: "pageComp-" + p.id,
              displayName: "Component",
              editorName: p.label + " | Component",
              plug: "input",
              type: "component",
              parent: "pages",
              parentItemId: p.id,
            });

            if (node.parameters["useRoutes"] === true) {
              ports.push({
                name: "pagePath-" + p.id,
                displayName: "Path",
                editorName: p.label + " | Path",
                plug: "input",
                type: "string",
                default: p.label.replace(/\s+/g, "-").toLowerCase(),
                parent: "pages",
                parentItemId: p.id,
              });
            }
          });

          ports.push({
            plug: "input",
            type: {
              name: "enum",
              enums: node.parameters["pages"].map((p: any) => ({
                label: p.label,
                value: p.id,
              })),
              allowEditOnly: true,
            },
            group: "General",
            displayName: "Start Page",
            name: "startPage",
            default: node.parameters["pages"][0].id,
          });
        }

        context.editorConnection.sendDynamicPorts(node.id, ports);
      }

      _updatePorts();
      node.on("parameterUpdated", function (ev: any) {
        if (ev.name === "pages" || ev.name === "useRoutes") _updatePorts();
      });
    }

    graphModel.on("editorImportComplete", () => {
      graphModel.on("nodeAdded.Page Stack", function (node: any) {
        _managePortsForNode(node);
      });

      for (const node of graphModel.getNodesWithType("Page Stack")) {
        _managePortsForNode(node);
      }
    });
  },
};

export default createNodeFromReactComponent(PageStack);
