/**
 * Executes async functions with a concurrency limit
 * @param poolLimit Maximum number of concurrent executions
 * @param values Array of input values to process
 * @param iteratorFn Function to execute for each value
 * @returns Promise that resolves with array of results
 */
export async function asyncPool<T, R>(
  poolLimit: number,
  values: T[],
  iteratorFn: (item: T, array: T[]) => Promise<R>
): Promise<R[]> {
  const ret: Promise<R>[] = [];
  const executing: Promise<void>[] = [];

  for (const item of values) {
    const p = Promise.resolve().then(() => iteratorFn(item, values));
    ret.push(p);

    if (poolLimit <= values.length) {
      const e = p.then((): void => {
        executing.splice(executing.indexOf(e), 1);
      });
      executing.push(e);
      if (executing.length >= poolLimit) {
        await Promise.race(executing);
      }
    }
  }

  return Promise.all(ret);
}
