import { GraphModel } from "./models/graphmodel";
import { NodeScope } from "./nodescope";

export interface Variant {
  name: string;
  typename: string;
  variantName?: string;
  parameters: { [key: string]: any };
  stateParameters: { [key: string]: { [key: string]: any } };
  defaultStateTransitions: { [key: string]: any };
  stateTransitions: { [key: string]: { [key: string]: any } };
}

interface ConstructorParams {
  graphModel?: GraphModel;
  getNodeScope: () => NodeScope | null;
}

export class Variants {
  private getNodeScope: () => NodeScope | null;
  private graphModel?: GraphModel;

  constructor({ graphModel, getNodeScope }: ConstructorParams) {
    this.getNodeScope = getNodeScope;

    if (graphModel) {
      this.graphModel = graphModel;
      graphModel.on("variantUpdated", (variant: Variant) =>
        this.onVariantUpdated(variant)
      );
    }
  }

  getVariant(typename: string, name: string | undefined): Variant | undefined {
    if (!this.graphModel) return undefined;
    if (!name) return undefined;

    return this.graphModel.getVariant(typename, name);
  }

  private onVariantUpdated(variant: Variant): void {
    const nodeScope = this.getNodeScope();
    if (!nodeScope) return;

    //get all nodes with the type the variant applies to
    const nodes = nodeScope.getNodesWithTypeRecursive(variant.typename);

    //and filter for the ones using the updated variant
    const nodesWithVariant = nodes.filter((node) => {
      //if a variant has been set during runtime, it'll override the value from the model
      if (typeof node.variant === "object")
        return node.variant.name === variant.name;

      if (typeof node.variant === "string")
        return node.variant === variant.name;

      //otherwise check the model (which always matches the editor)
      return node.model && node.model.variant === variant.name;
    });

    //and re-apply the variant
    for (const node of nodesWithVariant) {
      node.setVariant(variant);
    }
  }
}
