import { Model } from "./model";

interface ListenerMap {
  [key: string]: ((args: any) => Promise<void>)[];
}

declare global {
  interface Array<T> {
    items: T[];
    each: typeof Array.prototype.forEach;
    size(): number;
    get(index: number): T;
    getId(): string;
    id: string;
    set(src: T[]): void;
    notify(event: string, args: any): Promise<void>;
    contains(item: T): boolean;
    add(item: T): Promise<void>;
    remove(item: T): void;
    addAtIndex(item: T, index: number): Promise<void>;
    removeAtIndex(idx: number): Promise<void>;
    on(event: string, listener: (args: any) => Promise<void>): void;
    off(event: string, listener: (args: any) => Promise<void>): void;
    _id?: string;
    _listeners?: ListenerMap;
  }
}

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "items")) {
  Object.defineProperty(Array.prototype, "items", {
    enumerable: false,
    get() {
      return this;
    },
    set(data: any[]) {
      this.set(data);
    },
  });
}

Object.defineProperty(Array.prototype, "each", {
  enumerable: false,
  writable: false,
  value: Array.prototype.forEach,
});

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "size")) {
  Object.defineProperty(Array.prototype, "size", {
    enumerable: false,
    writable: false,
    value: function (): number {
      return this.length;
    },
  });
}

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "get")) {
  Object.defineProperty(Array.prototype, "get", {
    enumerable: false,
    writable: false,
    value: function <T>(index: number): T {
      return this[index];
    },
  });
}

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "getId")) {
  Object.defineProperty(Array.prototype, "getId", {
    enumerable: false,
    writable: false,
    value: function (): string {
      return this._id as string;
    },
  });
}

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "id")) {
  Object.defineProperty(Array.prototype, "id", {
    enumerable: false,
    get() {
      return this.getId();
    },
  });
}

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "set")) {
  Object.defineProperty(Array.prototype, "set", {
    enumerable: false,
    writable: false,
    value: function <T extends Model>(src: T[]): void {
      let length: number, i: number;

      if (src === this) return;

      src = src || []; //handle if src is undefined

      function keyIndex(a: T[]): Record<string, T> {
        const keys: Record<string, T> = {};
        const length = a.length;
        for (let i = 0; i < length; i++) {
          const item = a[i];
          keys[item.getId()] = item;
        }
        return keys;
      }

      // Src can be a collection, or an array
      const bItems: T[] = [];
      length = src.length;
      for (i = 0; i < length; i++) {
        const item = src[i];
        if (Model.instanceOf(item)) bItems.push(item);
        // @ts-expect-error TODO: What is this?
        else bItems.push(Model.create(item));
      }

      const aItems = this.items;
      const aKeys = keyIndex(aItems);
      const bKeys = keyIndex(bItems);

      // First remove all items not in the new collection
      length = aItems.length;
      for (i = 0; i < length; i++) {
        if (!bKeys.hasOwnProperty(aItems[i].getId())) {
          // This item is not present in new collection, remove it
          this.removeAtIndex(i);
          i--;
          length--;
        }
      }

      // Reorder items
      for (i = 0; i < Math.min(aItems.length, bItems.length); i++) {
        if (aItems[i] !== bItems[i]) {
          if (aKeys.hasOwnProperty(bItems[i].getId())) {
            // The bItem exist in the collection but is in the wrong place
            this.remove(bItems[i]);
          }

          // This is a new item, add it at correct index
          this.addAtIndex(bItems[i], i);
        }
      }

      // Add remaining items
      for (i = aItems.length; i < bItems.length; i++) {
        this.add(bItems[i]);
      }
    },
  });
}

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "notify")) {
  Object.defineProperty(Array.prototype, "notify", {
    enumerable: false,
    writable: false,
    value: async function (event: string, args: any): Promise<void> {
      if (!this._listeners) return;
      if (!this._listeners[event]) return;

      const l = this._listeners[event].slice(); //clone in case listeners array is modified in the callbacks
      for (let i = 0; i < l.length; i++) {
        await l[i](args);
      }
    },
  });
}

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "contains")) {
  Object.defineProperty(Array.prototype, "contains", {
    enumerable: false,
    writable: false,
    value: function <T>(item: T): boolean {
      return this.indexOf(item) !== -1;
    },
  });
}

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "add")) {
  Object.defineProperty(Array.prototype, "add", {
    enumerable: false,
    writable: false,
    value: async function <T extends Model>(item: T): Promise<void> {
      if (this.contains(item)) return; // Already contains item

      this.items.push(item);
      await this.notify("add", { item: item, index: this.items.length - 1 });
      await this.notify("change");
      item.notify("add", { collection: this });
    },
  });
}

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "remove")) {
  Object.defineProperty(Array.prototype, "remove", {
    enumerable: false,
    writable: false,
    value: function <T>(item: T): void {
      const idx = this.items.indexOf(item);
      if (idx !== -1) this.removeAtIndex(idx);
    },
  });
}

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "addAtIndex")) {
  Object.defineProperty(Array.prototype, "addAtIndex", {
    enumerable: false,
    writable: false,
    value: async function <T extends Model>(
      item: T,
      index: number
    ): Promise<void> {
      if (this.contains(item)) return; // Already contains item

      this.items.splice(index, 0, item);
      await this.notify("add", { item: item, index: index });
      await this.notify("change");
      item.notify("add", { collection: this, index: index });
    },
  });
}

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "removeAtIndex")) {
  Object.defineProperty(Array.prototype, "removeAtIndex", {
    enumerable: false,
    writable: false,
    value: async function (idx: number): Promise<void> {
      const item = this.items[idx];
      this.items.splice(idx, 1);
      await this.notify("remove", { item: item, index: idx });
      await this.notify("change");
      await item.notify("remove", { collection: this });
    },
  });
}

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "on")) {
  Object.defineProperty(Array.prototype, "on", {
    enumerable: false,
    writable: false,
    value: function (
      event: string,
      listener: (args: any) => Promise<void>
    ): void {
      if (!this._listeners)
        Object.defineProperty(this, "_listeners", {
          enumerable: false,
          writable: false,
          value: {},
        });
      if (!this._listeners[event]) this._listeners[event] = [];
      this._listeners[event].push(listener);
    },
  });
}

if (!Object.prototype.hasOwnProperty.call(Array.prototype, "off")) {
  Object.defineProperty(Array.prototype, "off", {
    enumerable: false,
    writable: false,
    value: function (
      event: string,
      listener: (args: any) => Promise<void>
    ): void {
      if (!this._listeners) return;
      if (!this._listeners[event]) return;
      const idx = this._listeners[event].indexOf(listener);
      if (idx !== -1) this._listeners[event].splice(idx, 1);
    },
  });
}

export class Collection<T extends Model> extends Array<T> {
  static _collections: Record<string, Collection<any>> = {};

  static create<T extends Model>(items?: T[]): Collection<T> {
    const name = Model.guid();
    Collection._collections[name] = new Collection<T>();
    Object.defineProperty(Collection._collections[name], "_id", {
      enumerable: false,
      writable: false,
      value: name,
    });
    if (items) {
      Collection._collections[name].set(items);
    }
    return Collection._collections[name];
  }

  static get<T extends Model>(name?: string): Collection<T> {
    if (name === undefined) name = Model.guid();
    if (!Collection._collections[name]) {
      Collection._collections[name] = new Collection<T>();
      Object.defineProperty(Collection._collections[name], "_id", {
        enumerable: false,
        writable: false,
        value: name,
      });
    }

    return Collection._collections[name];
  }

  static instanceOf(collection: any): boolean {
    return collection instanceof Collection;
  }

  static exists(name: string): boolean {
    return Collection._collections[name] !== undefined;
  }
}
