interface Port {
  [key: string]: any;
}

export interface Module {
  settings?: Port[];
  name?: string;
  nodes?: any[];
  setup?: () => void;
}

interface ProjectSettings {
  [key: string]: any;
}

interface ProjectSettingsResult {
  dynamicports: any[];
  ports: Port[];
}

function addModuleSettings(
  result: ProjectSettingsResult,
  modules: Module[]
): void {
  // Add module settings
  for (let i = 0; i < modules.length; i++) {
    const m = modules[i];
    if (m.settings) {
      m.settings.forEach((p: Port) => {
        result.ports.push(p);
      });
    }
  }
}

export function generateProjectSettings(
  // TODO: What is this?
  projectSettings: ProjectSettings,
  modules: Module[]
): ProjectSettingsResult {
  const result: ProjectSettingsResult = {
    dynamicports: [],
    ports: [],
  };

  addModuleSettings(result, modules);

  return result;
}
