import { createDefinition } from "./variablebase";

export const node = createDefinition({
  name: "Number",
  docs: "https://docs.noodl.net/nodes/data/number",
  startValue: 0,
  nodeDoubleClickAction: {
    focusPort: "value",
  },
  type: {
    name: "number",
  },
  cast(value: any): number {
    return Number(value);
  },
});
