import type { NodeDefinition } from "../../nodedefinition";

interface Internal {
  inputs: string[];
  mappings: string[];
  currentInputString?: string;
  defaultMapping?: string;
  mappedString?: string;
  hasScheduledFetch?: boolean;
}

export const node: NodeDefinition<Internal> = {
  name: "String Mapper",
  docs: "https://docs.noodl.net/nodes/string-manipulation/string-mapper",
  category: "Utilities",
  initialize() {
    this._internal = {
      inputs: [],
      mappings: [],
    };
  },
  getInspectInfo() {
    return this._internal.mappedString;
  },
  numberedInputs: {
    input: {
      type: "string",
      displayPrefix: "Input",
      group: "Inputs",
      index: 10,
      createSetter(index: number) {
        return function (value: string) {
          value = value === undefined ? "" : String(value);
          this._internal.inputs[index] = value;
          // @ts-expect-error
          this.scheduleMapping();
        };
      },
    },
    output: {
      type: "string",
      displayPrefix: "Mapping",
      index: 1001,
      group: "Mappings",
      createSetter(index: number) {
        return function (value: string) {
          value = value === undefined ? "" : String(value);
          this._internal.mappings[index] = value;
          // @ts-expect-error
          this.scheduleMapping();
        };
      },
    },
  },
  inputs: {
    inputString: {
      type: {
        name: "string",
      },
      index: 1,
      displayName: "Input String",
      set(value: unknown) {
        this._internal.currentInputString =
          value !== undefined ? String(value) : undefined;
        // @ts-expect-error
        this.scheduleMapping();
      },
    },
    defaultMapping: {
      type: "string",
      displayName: "Default",
      index: 1000,
      group: "Mappings",
      set(value: string) {
        this._internal.defaultMapping = value;
        // @ts-expect-error
        this.scheduleMapping();
      },
    },
  },
  outputs: {
    mappedString: {
      type: "string",
      displayName: "Mapped String",
      group: "Value",
      getter() {
        return this._internal.mappedString;
      },
    },
  },
  prototypeExtensions: {
    doMapping() {
      this._internal.hasScheduledFetch = false;
      const idx = this._internal.inputs.indexOf(
        this._internal.currentInputString!
      );
      if (idx !== -1)
        this._internal.mappedString = this._internal.mappings[idx];
      else this._internal.mappedString = this._internal.defaultMapping;

      this.flagOutputDirty("mappedString");
    },
    scheduleMapping() {
      const internal = this._internal;
      if (!internal.hasScheduledFetch) {
        internal.hasScheduledFetch = true;
        // @ts-expect-error
        this.scheduleAfterInputsHaveUpdated(this.doMapping.bind(this));
      }
    },
  },
};
