import type { NodeDefinition } from "../../nodedefinition";

interface Internal {}

export const node: NodeDefinition<Internal> = {
  name: "Condition",
  docs: "https://docs.noodl.net/nodes/utilities/logic/condition",
  category: "Logic",
  initialize() {},
  getInspectInfo() {
    const condition = this.getInputValue("condition");
    let value: unknown;
    if (condition === undefined) {
      value = "[No input]";
    } else {
      value = condition;
    }
    return [
      {
        type: "value",
        value,
      },
    ];
  },
  inputs: {
    condition: {
      type: "boolean",
      displayName: "Condition",
      group: "General",
      set(_value: boolean) {
        if (!this.isInputConnected("eval")) {
          // Evaluate right away
          // @ts-expect-error
          this.scheduleEvaluate();
        }
      },
    },
    eval: {
      type: "signal",
      displayName: "Evaluate",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleEvaluate();
      },
    },
  },
  outputs: {
    ontrue: {
      type: "signal",
      displayName: "On True",
      group: "Events",
    },
    onfalse: {
      type: "signal",
      displayName: "On False",
      group: "Events",
    },
    result: {
      type: "boolean",
      displayName: "Is True",
      group: "Booleans",
      getter() {
        return !!this.getInputValue("condition");
      },
    },
    isfalse: {
      type: "boolean",
      displayName: "Is False",
      group: "Booleans",
      getter() {
        return !this.getInputValue("condition");
      },
    },
  },
  methods: {
    scheduleEvaluate() {
      this.scheduleAfterInputsHaveUpdated(() => {
        this.flagOutputDirty("result");
        this.flagOutputDirty("isfalse");

        const condition = this.getInputValue("condition");
        this.sendSignalOnOutput(condition ? "ontrue" : "onfalse");
      });
    },
  },
};
