import { Collection, NodeDefinition } from "@fluxscape/runtime";

interface Internal {
  sourceCollection: any[];
  collection?: Collection<any>;
  collectionId?: string;
  hasScheduledNew: boolean;
}

export const node: NodeDefinition<Internal> = {
  name: "CollectionNew",
  docs: "https://docs.noodl.net/nodes/data/array/create-new-array",
  displayNodeName: "Create New Array",
  shortDesc:
    "A collection of models, mainly used together with a For Each Node.",
  category: "Data",
  color: "data",
  initialize() {},
  inputs: {
    new: {
      displayName: "Do",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleNew();
      },
    },
    items: {
      type: "array",
      group: "General",
      displayName: "Items",
      set(value: any[]) {
        this._internal.sourceCollection = value;
      },
    },
  },
  outputs: {
    id: {
      type: "string",
      displayName: "Id",
      group: "General",
      getter() {
        return this._internal.collection
          ? this._internal.collection.getId()
          : this._internal.collectionId;
      },
    },
    created: {
      group: "Events",
      type: "signal",
      displayName: "Done",
    },
  },
  prototypeExtensions: {
    setCollectionID(id: string) {
      // @ts-expect-error
      this.setCollection(Collection.get(id));
    },
    setCollection(collection: Collection<any>) {
      this._internal.collection = collection;
      this.flagOutputDirty("id");
    },
    scheduleNew() {
      // @ts-expect-error
      if (this.hasScheduledNew) return;
      // @ts-expect-error
      this.hasScheduledNew = true;

      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.hasScheduledNew = false;

        const collection = Collection.get();
        if (this._internal.sourceCollection !== undefined)
          collection.set(this._internal.sourceCollection);

        // @ts-expect-error
        this.setCollection(collection);

        this.sendSignalOnOutput("created");
      });
    },
  },
};
